<script>
import axios from 'axios'
import qs from 'qs'
import Exif from 'exif-js'
export default {
  install(Vue, args) {
    var commonConfig = Vue.prototype.CommonConfig,
      commonMethods = Vue.prototype.CommonMethods;

    var uploader = {};
    uploader.partSize = commonConfig.uploader.partSize || 1024 * 1024 * 4; //片段大小
    uploader.concurrencyCount = commonConfig.uploader.concurrencyCount || 2; //并发数
    uploader.uploadHost = commonMethods.getUploadHost();
    uploader.checkUrl = commonConfig.uploader.checkUrl;
    uploader.uploadUrl = commonConfig.uploader.uploadUrl;
    uploader.processUrl = commonConfig.uploader.processUrl;
    uploader.workId = args.workId;
    uploader.userId = args.userId;
    uploader.whereFrom = args.whereFrom;
    uploader.queue = []; //待上传队列
    uploader.uploadingXHRs = []; //上传中的队列
    uploader.processXHRs = [];//处理中的队列
    uploader.errorXHRs = [];//出错的队列
    uploader.speed = 0;//上传速度
    uploader.speedBuffer = 0;//上传速度的贤内助
    uploader.speedTimer = null;//上传速度定时器
    uploader.checkTimer = null;//上传速度监测定时器

    Vue.prototype.CreateFile = function(args) {
      var files = args.files, //要创建的文件数组
        salt = args.salt;//加盐，这里一般是该作品的userid

      uploader.onSuccess = args.onSuccess;//单个照片成功事件
      uploader.onError = args.onError;//单个照片错误事件
      uploader.onAllSuccess = args.onAllSuccess;//全部完成事件
      uploader.onProgress = args.onProgress;//进度事件
      uploader.onGlobalSpeed = args.onGlobalSpeed;//全局速度事件

      var that = this;

      var i = 0, fileWrappers = [];

      var judge = function() {
        if (i < files.length) {
          onloadFile();
        } else {
          //检查上传列表
          checkQueueList();
        }
      };

      //加载文件
      var onloadFile = function() {
        var file = files[i];

        try {
          var fr = new FileReader();
          fr.onload = function(e) {
            //照片路径
            var result = this.result;
            //计算md5
            var md5 = matchingMD5(result, file, salt);
            //获取本地照片路径
            var src = window.URL ? window.URL.createObjectURL(file) : result;
            //加载照片
            onloadImage(file, src, md5);
          };
          fr.readAsDataURL(file);
        } catch (error) {
          if (typeof onError === 'function') {
            onError(uploader, error);
          }
        }
      };

      //加载照片
      var onloadImage = function(file, src, md5) {
        var img = new Image();
        img.onload = function(e) {
          var width = img.width,
            height = img.height;

          Exif.getData(file, function() {
            var orientation = Exif.getTag(this, 'Orientation'),
              shootingTime = Exif.getTag(this, 'DateTime');

            if (shootingTime) {
              shootingTime = checkDate(shootingTime);
            }

            var photo = {
              that: that,
              file: file,
              src: src,
              md5: md5,
              orientation: orientation,
              width: width,
              height: height,
              shootingTime: shootingTime,
              reCount: 0,
              uploadProgress: 'wait',
              xhr: null
            };

            //插入待上传列表
            uploader.queue.push(photo);

            //插入photos列表
            pushPhotosList(photo);

            //检查上传列表
            checkQueueList();

            i++;
            judge();
          });
        }
        img.src = src;
      }

      judge();

      //计算速度
      if (!uploader.speedTimer) {
        uploader.speedTimer = setInterval(function() {
          uploader.speed = uploader.speedBuffer;
          uploader.speedBuffer = 0;

          // //上传卡住
          // if (uploader.speed == 0) {
          //   if (!uploader.checkTimer) {
          //     uploader.checkTimer = setTimeout(function() {
          //       //暂停下再重新上传
          //       that.AbortUploadAll();
          //       that.ContinueUpload();
          //     }, 10000);
          //   }
          // } else {
          //   clearTimeout(uploader.checkTimer);
          //   uploader.checkTimer = null;
          // }

          if (typeof uploader.onGlobalSpeed === 'function') {
            uploader.onGlobalSpeed({
              speed: uploader.speed
            });
          }
        }, 1000);
      }
    };

    //检查上传队列
    function checkQueueList() {
      if (uploader.queue.length != 0 && uploader.uploadingXHRs.length < uploader.concurrencyCount) {
        //移到上传中队列
        var photo = uploader.queue.shift();
        uploader.uploadingXHRs.push(photo);
        checkPhoto(photo);
      } else if (uploader.queue.length == 0 && uploader.uploadingXHRs.length == 0 && uploader.errorXHRs.length == 0) {
        //全部完成回调
        onUploadAllSuccess();
      }
    }

    //检查照片
    function checkPhoto(photo) {
      var xhr = new XMLHttpRequest();
      xhr.open("POST", uploader.uploadHost + uploader.checkUrl, true);

      xhr.onload = function(e) {
        var data = JSON.parse(e.target.response);
        if (data.exist) {//已存在
          //移出上传文件列表
          removeXHRsList(uploader.uploadingXHRs, photo);

          checkQueueList();

          onUploadSuccess(photo, '照片已存在');
        } else {//不存在
          uploadPhoto(photo);
        }
      }

      xhr.onerror = function(e) {
        //网络问题才会进来这里
        onUploadError(photo, e);
      }

      var formData = new FormData;
      formData.append("md5", photo.md5);
      formData.append("fileId", photo.md5);
      formData.append("shootTime", photo.shootTime);
      formData.append("thisHost", window.location.origin);

      xhr.send(formData);
      photo.xhr = xhr;
    }

    //照片上传
    function uploadPhoto(photo) {
      var total = 0;

      var xhr = new XMLHttpRequest();
      xhr.open("POST", uploader.uploadHost + uploader.uploadUrl, true);

      //上传完执行
      xhr.onload = function(e) {
        var data = JSON.parse(e.target.response);
        if (data.isSuccess == true) {
          //移出上传文件列表
          removeXHRsList(uploader.uploadingXHRs, photo);
          //添加到处理中列表
          addXHRsList(uploader.processXHRs, photo);
          //检测上传列表
          checkQueueList();
          //处理图片
          createImageFile(photo);
        } else {
          alert(data.msg);
        }
      }

      //上传过程中执行
      xhr.upload.onprogress = function(e) {
        var progress = e.loaded / e.total;

        uploader.speedBuffer += e.loaded - total;
        total = e.loaded;

        //变更进度
        onUploadProgress(photo, progress);
      };

      xhr.onerror = function(e) {
        //网络问题才会进来这里
        onUploadError(photo, e);
      }

      var formData = new FormData;
      formData.append("md5", photo.md5);
      formData.append("file", photo.file);
      formData.append("fileId", photo.md5);
      formData.append("start", 0);
      formData.append("fileSize", photo.file.size);

      xhr.send(formData);
      photo.xhr = xhr;
    }

    //处理图片
    function createImageFile(photo) {
      var xhr = new XMLHttpRequest();
      xhr.open("POST", uploader.uploadHost + uploader.processUrl, true);

      //上传完执行
      xhr.onload = function(e) {
        var data = JSON.parse(e.target.response);
        if (data.isSuccess) {
          //移出上传文件列表
          removeXHRsList(uploader.processXHRs, photo);
          //检测上传列表
          checkQueueList();

          onUploadSuccess(photo, '处理成功');
        } else {
          //重试处理
          if (photo.reCount < 3) {
            photo.reCount++;
            console.log('失败了' + photo.reCount + '次');
            createImageFile(photo);
          } else {
            onUploadError(photo, '处理失败');
          }
        }
      }

      xhr.onerror = function(e) {
        //网络问题才会进来这里
        onUploadError(photo, e);
      }

      var formData = new FormData;
      formData.append("userId", uploader.userId);
      formData.append("workId", uploader.workId);
      formData.append("whereFrom", uploader.whereFrom);
      formData.append("fileId", photo.md5);
      formData.append("md5", photo.md5);
      formData.append("fileName", photo.file.name);
      formData.append("fileSize", photo.file.size);
      formData.append("lastModifiedDate", convertData(photo.file.lastModifiedDate));
      formData.append("orientation", photo.orientation);
      formData.append("dateTime", photo.shootingTime);
      formData.append("maxSize", 3600);

      xhr.send(formData);
      photo.xhr = xhr;
    }

    //转变日期格式
    function convertData(d) {
      return d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds();
    }

    //检查时间格式
    function checkDate(d) {
      let date = new Date(d);

      if (date != "Invalid Date") {
        let time = convertData(date);
        //如果拍摄时间是'0000:00:00 00:00:00'存数据库报错
        if (time == "0000/00/00 00:00:00") {
          return undefined;
        }

        return time;
      }
      return undefined;
    }

    //插入photos列表
    function pushPhotosList(photoInfo) {
      var photos = photoInfo.that.$store.state.photos;
      var count = 0;
      for (var i = 0; i < photos.length; i++) {
        var photo = photos[i];
        if (photo.id == photoInfo.md5) {
          count++;
        }
      }

      var info = {
        bitmapFormat: 1,
        fileName: photoInfo.file.name,
        id: photoInfo.md5,
        lastModifiedDate: photoInfo.file.lastModifiedDate,
        orientation: photoInfo.orientation,
        originalWidth: photoInfo.width,
        originalHeight: photoInfo.height,
        shootTime: photoInfo.shootTime,
        src: photoInfo.src,
        uploadTime: new Date(),
        uploadProgress: photoInfo.uploadProgress
      };

      count == 0 && photoInfo.that.$store.commit('addPhoto', info);
    }

    //移出列表
    function removeXHRsList(list, photo) {
      for (var i = 0; i < list.length; i++) {
        var md5 = list[i].md5;
        if (md5 == photo.md5) {
          list.splice(i, 1);
        }
      }
    }

    //加入列表
    function addXHRsList(list, photo) {
      list.push(photo);
    }

    //计算md5
    function matchingMD5(result, file, salt) {
      var md5 = CryptoJS.MD5(result + file.name + file.lastModified + salt).toString();
      return [md5.substring(0, 8), md5.substring(8, 12), md5.substring(12, 16), md5.substring(16, 20), md5.substring(20, 32)].join('-');
    }

    //失败回调
    function onUploadError(photo, msg) {
      photo.xhr = null;

      onUploadProgress(photo, 'fail');

      uploader.errorXHRs.push(photo);

      if (typeof uploader.onError == 'function') {
        uploader.onError(photo, msg);
      }
    }

    //成功回调
    function onUploadSuccess(photo, msg) {
      photo.xhr = null;

      onUploadProgress(photo, 'success');

      if (typeof uploader.onSuccess == 'function') {
        var existCount = uploader.queue.length + uploader.uploadingXHRs.length + uploader.errorXHRs.length + uploader.processXHRs.length;
        uploader.onSuccess({
          photo: photo,
          msg: msg,
          existCount: existCount
        });
      }
    }

    //进度回调
    function onUploadProgress(photo, progress) {
      console.log(progress);
      photo.that.$store.commit('changeUploadProgress', {
        id: photo.md5,
        uploadProgress: progress
      });
    }

    //全部成功回调
    function onUploadAllSuccess(){
      if (typeof uploader.onAllSuccess == 'function') {
        uploader.onAllSuccess({
          speed: 0
        });
      }
    }

    function findPhoto(list, id) {
      for (var i = 0; i < list.length; i++) {
        if (list[i].md5 == id) {
          return list.splice(i, 1)[0];
        }
      }
      return null;
    }

    //【上传失败重新上传】重新上传失败项
    Vue.prototype.ReUpload = function(id) {
      var photo = findPhoto(uploader.errorXHRs, id);
      if (photo) {
        uploader.queue.push(photo);
        checkQueueList();
      }
    };

    //【删除照片】终止上传
    Vue.prototype.AbortUpload = function(id) {
      var photo = findPhoto(uploader.uploadingXHRs, id) || findPhoto(uploader.queue, id);

      if (photo && photo.xhr != null && typeof photo.xhr.abort == 'function') {
        photo.xhr.abort();
        photo.xhr = null;
      }
    };

    //【暂停】终止上传-全部
    Vue.prototype.AbortUploadAll = function() {
      for (var i = 0; i < uploader.uploadingXHRs.length; i++) {
        var photo = uploader.uploadingXHRs[i];
        if (photo && photo.xhr != null && typeof photo.xhr.abort == 'function') {
          photo.xhr.abort();
          photo.xhr = null;
        }
        uploader.queue.unshift(photo);
      }
      uploader.uploadingXHRs = [];
    };

    //【继续上传】继续上传
    Vue.prototype.ContinueUpload = function() {
      checkQueueList();
    }
  }
}

