<script>
import axios from 'axios'
import qs from 'qs'

export default {
  install(Vue) {
    function matches(el, selector) {
      return (el.matches || el.matchesSelector || el.msMatchesSelector || el.mozMatchesSelector || el.webkitMatchesSelector || el.oMatchesSelector).call(el, selector);
    }

    function closest(el, selector) {
      var matchesSelector = el.matches || el.webkitMatchesSelector || el.mozMatchesSelector || el.msMatchesSelector;
      if (matchesSelector.call(el, selector)) {
        if(!matchesSelector.call(el.parentNode, selector)){
          return el
        }
        return closest(el.parentNode, selector)
      }
      return el
    }

    //删除照片
    function deletePhoto(workId, photoId, callback) {
      axios.post('http://www.mimoprint.com/works/RemovePhoto ', qs.stringify({
        workId: workId,
        photoId: photoId,
        hostName: window.location.origin
      })).then(function(res) {
        if (res.isSuccess) {
          removePhoto(photoId);
          if (typeof callback == 'function') {
            callback();
          }
        } else {
          alert('删除失败');
        }
      }).catch(function(ex) {
        alert(ex);
      })
    }

    //移除photos里的照片
    function removePhoto(id) {
      var photos = this.$store.state.photos;

      for (var i in photos) {
        if (photos[i].id == id) {
          photos.splice(i, 1);
          continue;
        }
      }

      this.$store.commit('setState', { photos: photos });
    };

    //删除照片
    Vue.prototype.DeletePhotos = function(workId, photos, callback) {
      if (typeof photos == 'object') {
        for (var i in photos) {
          var photoId = photos[i];
          deletePhoto(workId, photoId, callback);
        }
      } else {
        var photoId = photos;
        deletePhoto(workId, photoId, callback);
      }
    };

    Vue.prototype.GetImage = function(id) {
      var photos = this.$store.state.photos;
      for (var i in photos) {
        if (photos[i].id == id) {
          return photos[i]
        }
      }
    };

    Vue.prototype.GetImageSize = function(src, callback) {
      var img = new Image();
      img.onload = function(e) {
        callback({
          width: img.width,
          height: img.height
        })
      };
      img.src = src;
    };

    //找到在哪个节点上
    Vue.prototype.GetOverEl = function(x, y, className, e) {
      var overImgbox, overPage, overEl = {};
      var el = document.elementFromPoint(x, y);
      if (el) {
        overImgbox = matches(el, className.imgbox);
        overPage = matches(el, className.page);

        var targetImg, targetPage ;
        if(overImgbox){
          targetImg = closest(el, className.imgbox);
        }
        if(overPage){
          targetPage = closest(el, className.page);
        }
        overEl.boxId = overImgbox ? targetImg.getAttribute("data-id") : '';
        overEl.pageNum = overPage ? targetPage.getAttribute("data-num") : '';
      }

      //在图片框上
      overImgbox && (overEl.type = 'imgbox');

      //在page上 && 不在图片框上
      overPage && !overImgbox && (overEl.type = 'page');

      return overEl;
    }
  }
}
</script>



