<template>
  <div class="right-btn-menu" v-show="showMenu" :style="{left: x+'px', top: y+'px'}">
    <template v-if="!tagType">
      <span class="menubtn" v-if="undoData && undoData.length > 0" @click.prevent="undo()">撤销</span>
      <span class="menubtn" v-if="redoData && redoData.length > 0" @click.prevent="redo()">重做</span>
      <span class="menubtn" @click.prevent.stop="checkAll()">全选</span>
      <span class="menubtn" v-if="copyData.length > 0" @click.prevent="paste($event)">粘贴</span>
      <span class="menubtn" @click.prevent="deleteAllBox()">清空页</span>
      <span class="menubtn" @click.prevent="reApplyTemplate()">重排版</span>
    </template>
    <template v-else>
      <span class="menubtn" v-if="boxData.image && multipleSelectedList.length < 2 && showEditImageBtn()" @click.prevent.stop="editImage()">编辑照片</span>
      <span class="menubtn" v-if="tagType == 'textbox' && multipleSelectedList.length < 2" @click.prevent.stop="editText()">编辑文字</span>
      <span class="menubtn" v-if="tagType != 'patternbox'" @click.prevent="cut()">剪切</span>
      <span class="menubtn" v-if="tagType != 'patternbox'" @click.prevent="copy()">复制</span>
      <span class="menubtn" @click.prevent="DeleteSelectedBox()">移除框</span>
      <span class="menubtn" v-if="boxData.image  && multipleSelectedList.length < 2" @click.prevent="deleteImage()">移除图片</span>
    </template>
  </div>
</template>
<script>
export default {
  name: 'rightBtnMenu',
  props: [
  ],
  data() {
    return {
      showMenu: false,
      checkBox: false,
      mineChange: false,
      x: 0,
      y: 0,
      tagType: '',
    }
  },
  watch: {
    selected() {
      if(!this.mineChange){
        this.showMenu = false;
      }
      else{
        this.mineChange = false;
      }
    }
  },
  computed: {
    selected() {
      return this.$store.state.selected;
    },
    bookInfo() {
      return this.$store.state.bookInfo;
    },
    boxData() {
      return this.selected.data;
    },
    multipleSelectedList() {
      return this.$store.state.multipleSelected.list;
    },
    undoData() {
      return this.$store.state.undoData[this.selected.pageNum];
    },
    redoData() {
      return this.$store.state.redoData[this.selected.pageNum];
    },
    copyData() {
      return this.$store.state.copyData;
    },
    templates() {
      return this.$store.state.templates;
    },
    myTemplates() {
      return this.$store.state.myTemplates;
    },
  },
  methods: {
    closest(el, className) {
      if(el != document.body){
        if(el.classList.contains(className)){
          return el;
        }
        return this.closest(el.parentNode, className);
      }
      return null;
    },
    getBoxType(name) {
      var type = '';
      switch(name){
        case 'shape':
          type = 'colorbox';
          break
        case 'img':
          type = 'imagebox';
          break
        case 'txt':
          type = 'textbox';
          break
        case 'pattern':
          type = 'patternbox';
          break
        case 'decora':
          type = 'decoration';
          break
      };
      return type;
    },
    //获取元素
    getElFromAttr(parentEl, className, tagName, value) {
      var els = parentEl.querySelectorAll(className);
      var el = '';
      Array.prototype.forEach.call(els, (item) => {
        if(item.getAttribute(tagName) == value){
          el = item
        }
      })
      return el;
    },
    undo() {
      this.$store.commit("undo", this.selected.pageNum);
      this.$store.dispatch("animation")
    },
    redo() {
      this.$store.commit("redo", this.selected.pageNum);
      this.$store.dispatch("animation")
    },
    checkAll() {
      this.$store.commit("checkAll");
    },
    deleteAllBox() {
      this.$store.commit("deleteAllBox")
    },
    cut() {
      this.copy();
      this.DeleteSelectedBox();
    },
    copy() {
      this.$store.commit("copyBox");
    },
    paste(e) {
      var pageNum = this.selected.pageNum,
          pageData = isNaN(pageNum * 1) ? this.bookInfo[pageNum] : this.bookInfo.page_list[pageNum],
          lastIndex = this.GetLastIndex(pageData) || 1,
          pageEl = document.querySelector(".page.active"),
          deltaX = e.clientX - pageEl.getBoundingClientRect().left,
          deltaY = e.clientY - pageEl.getBoundingClientRect().top;
      var scale = this.$store.state.editPannelScale;
      var x = this.pxToMm(deltaX) / scale,
          y = this.pxToMm(deltaY) / scale;
      var data = JSON.parse(JSON.stringify(this.copyData));
      var prevW = 0,
          prevH = 0;
      if(!data.length) return ;
      for(var i = 0; i< data.length; i++){
        data[i].x = x - data[i].width / 2;
        data[i].y = y - data[i].height / 2;

        if(prevW == data[i].width && prevH == data[i].height){
          data[i].x += 10;
          data[i].y += 10;
        }

        if(data[i].x + data[i].width / 2 >= pageData.width){
          data[i].x = pageData.width - data[i].width / 2
        }
        if(data[i].y + data[i].height / 2 >= pageData.height){
          data[i].y = pageData.height - data[i].height / 2
        }

        var type = this.getBoxType(data[i].name) + "_list";

        var lastBox = pageData[type][pageData[type].length - 1]
        data[i].box_id = lastBox ? lastBox.box_id + 1 : 1;
        data[i].index = lastIndex ? lastIndex + 1 + data[i].index : 1;

        this.$store.commit("addBox", {pageNum: pageNum, boxType: type, box: data[i]});
        prevW = data[i].width;
        prevH = data[i].height;
      }

      if(data.length < 2){
        this.$nextTick(() => {
          var el = this.getElFromAttr(pageEl, '.box', 'data-name', data[0].name + data[0].box_id);
          setTimeout(() => {
            this.$store.commit("setState",{selected: {pageNum: pageNum, el: el, state: true, data: data[0]}})
          }, 300)
        })
      }
      else{
        this.$store.commit("setState",{multipleSelected: {pageNum: pageNum, list: data}})
      }
      this.showMenu = false;
      e.stopPropagation();
    },
    deleteImage() {
      this.$store.commit("changeBoxData", {
        pageNum: this.selected.pageNum,
        boxType: this.tagType,
        boxId: this.selected.data.box_id,
        data: {
          image: null
        },
      })
    },
    editText() {
      this.showMenu = false;
      this.$store.commit("setState", {showEditTextPannle: true})
    },
    editImage() {
      var el = this.selected.el;
      this.$store.commit("setState", {editImage: true});
      el.click();
      setTimeout(() => {
        this.$store.commit("setState", {editImage: false});
      }, 0)
    },
    showEditImageBtn() {
      var el = document.querySelectorAll(".img_cover");
      return !el.length;
    },
    reApplyTemplate() {
      var pageNum = this.selected.pageNum;
      var pageData = isNaN(pageNum * 1) ? this.bookInfo[pageNum] : this.bookInfo.page_list[pageNum];

      var templateData = this.getTemplateData(pageData.template_name);

      var pageType = (parseFloat(pageData.type) === this.Config.pageType['普通单页'] || (parseFloat(pageData.type)) === this.Config.pageType['跨页']) ? (templateData.cross_type === 'T' ? this.Config.pageType['跨页'] : this.Config.pageType['普通单页']) : pageData.type;

      if(!templateData.name) return;

      var newPage = this.genNewPage(pageNum, this.namespace(pageData.template_name), pageType, templateData);

      var photos = this.$store.state.photos;
      for (var i = 0; i < pageData.imagebox_list.length; i++) {
        var curImgbox = pageData.imagebox_list[i], curPhotos
        if (!curImgbox.image || curImgbox.themeImage) {
          continue
        }
        curPhotos = photos.filter(c => c.id == curImgbox.image.id)
        if (curPhotos.length == 0) {
          continue
        }
        var curPhoto = curPhotos[0]
        var closestDefinition = 0, closestNewImgbox = null  //#region 从原来的页上找出精度最接近的图片框
        for (var j = 0; j < newPage.imagebox_list.length; j++) {
          var curNewImgbox = newPage.imagebox_list[j]
          if (curNewImgbox.image) {
            continue
          }
          var curDefinition
          if (curPhoto.originalWidth / curPhoto.originalHeight >= curNewImgbox.width / curNewImgbox.height) {
              curDefinition = this.computeDefinition(curPhoto.originalWidth, curNewImgbox.width);
          } else {
              curDefinition = this.computeDefinition(curPhoto.originalWidth, curNewImgbox.height * curPhoto.originalWidth / curPhoto.originalHeight);
          }

          if (curDefinition > closestDefinition) {
              closestDefinition = curDefinition;
              closestNewImgbox = curNewImgbox;
          }
        }

        if (closestNewImgbox) {
          var photo = curPhoto
          var imageWidth, imageHeight
          if (photo.originalWidth / photo.originalHeight >= closestNewImgbox.width / closestNewImgbox.height) {
            imageWidth = closestNewImgbox.height * photo.originalWidth / photo.originalHeight;
            imageHeight = closestNewImgbox.height;
          } else {
            imageWidth = closestNewImgbox.width;
            imageHeight = closestNewImgbox.width * photo.originalHeight / photo.originalWidth;
          }

          var imageX = (closestNewImgbox.width - imageWidth) / 2,
              imageY = (closestNewImgbox.height - imageHeight) / 2;

          closestNewImgbox.image = {
            x: imageX,
            y: imageY,
            id: curImgbox.image.id,
            width: imageWidth,
            rotation: 0,
            fileName: photo.fileName
          };
        }
      }

      for (var i = 0; i < newPage.textbox_list.length; i++) {
        var textbox = pageData.textbox_list[i]
        var newTextbox = newPage.textbox_list[i]
        if (textbox) {
          // newTextbox.content = editorStatus == 0 ? textbox.content : newTextbox.content;
          //换模版保持原来字体
          // newTextbox.font_id = editorStatus == 0 ? textbox.font_id : newTextbox.font_id;
        } else {
          // newTextbox.content = editorStatus == 0 ? '' : newTextbox.content;
        }
      }
      this.$store.commit('changePageData',{pageNum: this.$store.state.selected.pageNum, data: newPage})
      this.$store.commit('setState', {selected: {el: null, pageNum: this.$store.state.selected.pageNum, data: {}, state: false } })
      this.$store.dispatch("animation")
    },
    getTemplateData(name) {
      var templates = JSON.parse(JSON.stringify(this.templates)),
          myTemplates = JSON.parse(JSON.stringify(this.myTemplates));
      var nameData = name.split('.'),
          list = [],
          lastName = '',
          data = {};
      if(nameData[0].indexOf('page') != -1){
        //内页
        list = templates[nameData[0]][nameData[1]].concat(myTemplates["M"+nameData[0]]);
        lastName = nameData[2];
      }else{
        //其他页
        list = templates[nameData[0]].concat(myTemplates["M"+nameData[0]]);
        lastName = nameData[1];
      }
      var l = '';
      for(var i = 0; i < list.length; i++){
        if(list[i] && list[i].name == lastName){
          data = list[i]
        }
      }
      return data;
    },
    genNewPage(pageNum, namespace, pageType, template, isFixImageTheme) {
      var that = this;
      var gen = function (list, genNewBox) {
        var boxList = []
        for (var i = 0; i < list.length; i++) {
          var box = genNewBox(list[i])
          boxList.push(box)
        }
        return boxList
      }
      return {
        num: pageNum.toString(),
        template_name: (namespace + '.' + template.name),
        width: template.width,
        height: template.height,
        imagebox_list: (function () {
          var imgboxList = []
          var boxList = template.imageBoxList || template.imagebox_list || [] //兼容版式数据跟自定义版式数据的格式问题
          for (var i = 0; i < boxList.length; i++) {
            var newImgbox, imgbox = boxList[i]
            if ((isFixImageTheme && pageType == 3) || (imgbox.name == 'logo' && pageType == 4)) {
              // if(window.IsWorkShop == 2) continue;
              newImgbox = that.genNewImgboxFixImage(boxList[i])
            } else {
              newImgbox = that.genNewImgbox(boxList[i])
            }
            imgboxList.push(newImgbox)
          }
          return imgboxList
        })(),
        textbox_list: gen(template.textBoxList || template.textbox_list || [], this.genNewTextbox),
        colorbox_list: gen(template.shapeBoxList || template.colorbox_list || [], this.genNewShapebox),
        decoration_list: gen(template.decorationboxList || template.decoration_list || [], this.genNewDecoration),
        shadingbox_list: gen(template.shadingBoxList || template.shadingbox_list || [], this.genNewShading),
        patternbox_list: gen(template.patternBoxList || template.patternbox_list || [], this.genNewPattern),
        background_color: template.backgroundColor || template.background_color || "#fff",
        type: pageType
      }
    },
    genNewImgbox (imgboxTemplate) {
      return {
        name: imgboxTemplate.name,
        index: imgboxTemplate.index == undefined ? 1 : imgboxTemplate.index,
        width: imgboxTemplate.width * 1,
        height: imgboxTemplate.height * 1,
        x: imgboxTemplate.x * 1,
        y: imgboxTemplate.y * 1,
        rotation: imgboxTemplate.rotation  == undefined ? 0 : imgboxTemplate.rotation,
        border_width: imgboxTemplate.border_width || 0,
        border_color: imgboxTemplate.border_color || '#ffffff',
        box_id: imgboxTemplate.box_id || null,
        locked: imgboxTemplate.locked || false,
        image: null,
      }
    },
    genNewImgboxFixImage(imgboxTemplate) {//保留照片信息的
      return {
        name: imgboxTemplate.name,
        index: imgboxTemplate.index == undefined ? 1 : imgboxTemplate.index,
        width: imgboxTemplate.width * 1,
        height: imgboxTemplate.height * 1,
        x: imgboxTemplate.x * 1,
        y: imgboxTemplate.y * 1,
        rotation: imgboxTemplate.rotation == undefined ? 0 : imgboxTemplate.rotation,
        border_width: imgboxTemplate.border_width || 0,
        border_color: imgboxTemplate.border_color || '#ffffff',
        box_id: imgboxTemplate.box_id || null,
        locked: imgboxTemplate.locked || false,
        image: imgboxTemplate.image || null,
        themeImage: imgboxTemplate.themeImage || null
      };
    },
    genNewTextbox(textboxTemplate) {
      return {
        name: textboxTemplate.name,
        index: textboxTemplate.index == undefined ? 1 : textboxTemplate.index,
        width: textboxTemplate.width * 1,
        height: textboxTemplate.height * 1,
        x: textboxTemplate.x * 1,
        y: textboxTemplate.y * 1,
        rotation: textboxTemplate.rotation == undefined ? 0 : textboxTemplate.rotation,
        content: window.EditorStatus == 0 ? '' : textboxTemplate.content == undefined ? "" : textboxTemplate.content,
        pt: textboxTemplate.pt,
        color: textboxTemplate.color,
        box_id: textboxTemplate.box_id || null,
        font_id: textboxTemplate.font_id || "101",
        align: textboxTemplate.align,
        space: textboxTemplate.space,
        leading: textboxTemplate.leading || -1, //((parseFloat(textboxTemplate.leading) === 0 || parseFloat(textboxTemplate.leading) < 0) ? -1 : textboxTemplate.pt * textboxTemplate.leading)
        opacity: textboxTemplate.opacity || 1,
        readonly: textboxTemplate.readonly || 1,
        locked: textboxTemplate.locked || false,
      };
    },
    genNewShapebox(shapgeTemplate) {
      return {
        border_color: shapgeTemplate.border_color,
        border_width: shapgeTemplate.border_width,
        color: shapgeTemplate.color,
        color_id: shapgeTemplate.color_id,
        height: shapgeTemplate.height * 1,
        index: shapgeTemplate.index == undefined ? 1 : shapgeTemplate.index,
        locked: shapgeTemplate.locked || false,
        name: shapgeTemplate.name,
        box_id: shapgeTemplate.box_id || null,
        opacity: shapgeTemplate.opacity,
        rotation: shapgeTemplate.rotation == undefined ? 0 : shapgeTemplate.rotation,
        width: shapgeTemplate.width * 1,
        x: shapgeTemplate.x * 1,
        y: shapgeTemplate.y * 1
      };
    },
    genNewDecoration(decorationTemplate) {
      return {
        decoration_id: decorationTemplate.decoration_id,
        height: decorationTemplate.height * 1,
        index: decorationTemplate.index == undefined ? 1 : decorationTemplate.index,
        locked: decorationTemplate.locked || false,
        name: decorationTemplate.name,
        rotation: decorationTemplate.rotation == undefined ? 0 : decorationTemplate.rotation,
        src: decorationTemplate.src,
        box_id: decorationTemplate.box_id || null,
        width: decorationTemplate.width * 1,
        x: decorationTemplate.x * 1,
        y: decorationTemplate.y * 1,
        type: decorationTemplate.type || null,
        color: decorationTemplate.color,
        text: decorationTemplate.text || null
      }
    },
    genNewShading(shadingTemplate) {
      return {
        id: shadingTemplate.id,
        height: shadingTemplate.height * 1,
        index: shadingTemplate.index == undefined ? 1 : shadingTemplate.index,
        locked: shadingTemplate.locked || false,
        box_id: shadingTemplate.box_id || null,
        name: shadingTemplate.name,
        imgWidth: shadingTemplate.imgWidth,
        rotation: shadingTemplate.rotation == undefined ? 0 : shadingTemplate.rotation,
        width: shadingTemplate.width * 1,
        x: shadingTemplate.x * 1,
        y: shadingTemplate.y * 1,
        editUrl: shadingTemplate.editUrl,
        thumbUrl: shadingTemplate.thumbUrl
      }
    },
    genNewPattern(patternTemplate) {
      return {
        id: patternTemplate.id,
        x: patternTemplate.x * 1,
        y: patternTemplate.y * 1,
        box_id: patternTemplate.box_id || null,
        width: patternTemplate.width * 1,
        height: patternTemplate.height * 1,
        index: patternTemplate.index == undefined ? -1 : patternTemplate.index,
        locked: patternTemplate.locked || false,
        name: patternTemplate.name,
        imgWidth: patternTemplate.imgWidth,
        rotation: patternTemplate.rotation == undefined ? 0 : patternTemplate.rotation,
        src: patternTemplate.src,
        type: patternTemplate.type
      }
    },
    namespace (tempName) {
      var tempType = ''
      if (!tempName) return themeTemp
      var tempType = tempName.split(".")[0]
      tempType = tempType.replace(/M/, '') //兼容自定义模板的名称
      var ns = null,
          pageNum = this.selected.pageNum;
      switch (tempType) {
        case "cover":
        case "spine":
        case "front_flap":
        case "copyright":
        case "back_cover":
          ns = tempType;
          break
        case "page":
          if (Number.parseInt(pageNum) % 2 == 0) {
            ns = tempType +'.left'
          } else {
            ns = tempType + '.right'
          }
          break
      }
      return ns
    },
    computeDefinition (imgOriginalWidthPx, imgWidthMM) {
      var imgOriginalWidthMM = this.pxToMm(imgOriginalWidthPx); //算出图原样印刷出来的毫米宽度
      return imgOriginalWidthMM / imgWidthMM; //清晰度 = 原始宽度 / 印刷宽度
    },
  },
  mounted () {
    document.body.oncontextmenu = (e) => {
      if(this.$store.state.showEditTextPannle){
        if(document.activeElement.tagName != 'TEXTAREA' && document.activeElement.tagName != 'INPUT') {
          this.$store.commit("setState", {showEditTextPannle: false});
        }
      }
      if(e.ctrlKey) return;
      if(this.$store.state.viewType == 'preview') return;
      var boxEl = this.closest(e.target, 'box'),
          page = this.closest(e.target, 'page');
      if(!page) {
        this.showMenu = false;
        return
      };
      var pageNum = page.getAttribute("data-num");
      var boxType = boxEl ? boxEl.getAttribute("data-type") : null,
          boxId = boxEl ? boxEl.getAttribute("data-name").replace(/[^0-9]/ig, '') : null;

      var boxData = "";

      if(boxEl){ //右键点击box
        var pageData = isNaN(pageNum * 1) ? this.bookInfo[pageNum] : this.bookInfo.page_list[pageNum];
        Array.prototype.forEach.call(pageData[boxType + '_list'], (item) => {
          if(item.box_id == boxId){
            boxData = item
          }
        })

        var checkEd = false; //点击的是否选中中的

        var multipleList = this.multipleSelectedList;
        if(multipleList.length > 1){
          Array.prototype.forEach((item) => {
            if(item.box_id == boxId && item.name == boxData.name){
              checkEd = true;
            }
          })
        }

        if(this.multipleSelectedList.length > 1){
          if(pageNum != this.$store.state.multipleSelected.pageNum){
            this.$store.commit("setState", { multipleSelected: {pageNum: pageNum, list: []} })
          }
          else{
            checkEd && this.$store.commit("setState", {selected: {pageNum: pageNum, data: boxData, state: false, el: boxEl}})
          }
        }
        else{
          this.$store.commit("setState", {selected: {pageNum: pageNum, data: boxData, state: true, el: boxEl}})
        }
      }
      else{ //点击的是空白的
        this.$store.commit("setState", {selected: {pageNum: pageNum, data: {}, state: false, el: null}, multipleSelected: {pageNum: pageNum, list: []}})
      }

      this.mineChange = true;
      this.tagType = boxType;
      this.showMenu = true;
      this.x = e.clientX + 1;
      this.y = e.clientY + 1;
      e.preventDefault();
      e.stopPropagation();
    }

    document.body.onclick = (e) => {
      this.showMenu = false;
    }

    // document.oncontextmenu = (e) => {
    //   this.showMenu = false;
    // }

  },
}
</script>
<style lang="scss">
  .right-btn-menu{
    position: absolute;
    z-index: 2002;
    border: 1px solid rgba(0,0,0,0.2);
    -webkit-box-shadow: 0 2px 2px rgba(0,0,0,0.2);
    -moz-box-shadow: 0 2px 2px rgba(0,0,0,0.2);
    box-shadow: 0 2px 2px rgba(0,0,0,0.2);
    padding: 3px 0;
    background-color: white;
    .menubtn{
      padding: 6px 10px;
      display: block;
      color: #333;
      cursor: pointer;
      &:hover{
        background-color: #eee;
      }
    }
  }
</style>



