<template>
  <transition name="fadeIn">
    <div id="edit" :class="{'--preview': viewType == 'preview'}" ref="edit">
      <BoxToolBar :selected="selected" :themeColor="$store.state.theme.color" :edit="edit" :isSpecialCover="checkCover"></BoxToolBar>
      <div id="eidt_pannel" ref="editPannel"
        :data-num="$store.state.selected.pageNum"
        :data-nocover="Config.productInfo[Config.productId].noCover && $store.state.selected.pageNum == 'front_cover'"
        :data-specialcover="Config.productInfo[Config.productId].specialCover && $store.state.selected.pageNum == 'front_cover'"
        @mousedown="dragPannel($event)" @mousewheel="zoomWheel($event)"
        :style="{
          width: mmToPx(w) + 'px',
          height: mmToPx(h) + 'px',
          transform: 'translate(' + mmToPx(x) + 'px,' + mmToPx(y) + 'px) scale(' + scale + ')',
          transition: zooming ? 'all .3s' : undefined
        }"
      >
        <div class="shadow_line" v-if="shadow"></div>
        <Cover :currentPage="$store.state.currentPage" :scale="scale"></Cover>
        <Page v-for="(pageNum, index) in $store.state.currentPage" :key="pageNum" :pageNum="pageNum" :scale="scale"></Page>
      </div>

      <Resize :selected="selected" :edit="edit" :editPannel="editPannel" :scale="scale" :fixZoomScaleBox="Config.fixZoomScaleBox"></Resize>
      <EditTextPannel :scale="scale"></EditTextPannel>
      <Subline :selected="selected" :scale="scale" ></Subline>
      <EditZoombar :el="editPannel" :scale="scale" @autoPannel="autoPannel" @btnZoomPannel="btnZoomPannel" @wheelZoomPannel="wheelZoomPannel"></EditZoombar>
      <WarningPannel></WarningPannel>
      <button v-show="viewType == 'preview'" v-on:click="showEdit" id="preview-button_return_edit" class="dialog-button">{{$t('editPannel.back')}}</button>
    </div>
  </transition>
</template>

<script>
import BoxToolBar from '@/components/common/boxToolbar'
import Cover from '@/components/book/cover'
import Page from '@/components/book/page'
import Resize from '@/components/common/resize'
import Subline from '@/components/common/subline'
import EditZoombar from '@/components/book/editZoombar'
import WarningPannel from '@/components/common/warningPannel'
import EditTextPannel from '@/components/common/editTextPannel'

export default {
  name: 'editPannel',
  data() {
    return {
      x: 0,
      y: 0,
      scale: 1,//编辑区域缩放比例
      editAreaScale: this.Config.editAreaScale,//编辑区域占比
      edit: null,
      editPannel: null,
      currentPage: this.$store.state.currentPage,//当前选中的页
      dragPannelLock: true,//画布拖动锁
      zooming: false,//缩放中
      timer: null//计时器
    }
  },
  mounted() {
    this.edit = this.$refs.edit;
    this.editPannel = this.$refs.editPannel;

    this.autoPannel(true);
    window.onresize = () => {
      this.autoPannel(true);
    }
  },
  updated() {
    //当页码不一时才重新渲染
    if(this.currentPage != this.$store.state.currentPage){
      this.currentPage = this.$store.state.currentPage;
      this.autoPannel(true);
    }
  },
  components: {
    BoxToolBar,
    Page,
    Resize,
    Subline,
    EditZoombar,
    WarningPannel,
    EditTextPannel,
    Cover
  },
  computed: {
    selected() {
      return this.$store.state.selected;
    },
    viewType() {
      return this.$store.state.viewType;
    },
    w(){
      return this.setPageSize().w;
    },
    h(){
      return this.setPageSize().h;
    },
    //画布中间的书脊阴影
    shadow(){
      return this.setPageSize().shadow;
    },
    checkCover(){
      var info = this.Config.productInfo[this.Config.productId];
      return info.specialCover && this.$store.state.selected.pageNum == 'front_cover';
    }
  },
  watch: {
    viewType: {
      handler () {
        this.$nextTick(()=>{
          this.autoPannel();
        })
      },
      deep: true
    },
  },
  methods: {
    dragPannel(e){
      if(e.button == 2){
        e.preventDefault();
        return
      }
      if(!this.dragPannelLock || this.$store.state.viewType == 'preview'){
        this.DragDown(e, {
          dragStart: this.dragStart,
          drag: this.drag,
          dragEnd: this.dragEnd
        });
      }
    },
    dragStart(e, args){
      this.startX = this.x;
      this.startY = this.y;

      this.$store.commit('setState', {selected: { state: false, data: {}, el: null, pageNum: this.selected.pageNum }})
    },
    drag(e, args){
      var el = this.$refs.editPannel,
        edit = this.$refs.edit;

      var x = this.startX + this.pxToMm(args.x),
        y = this.startY + this.pxToMm(args.y),
        elW = this.w * this.scale,
        elH = this.h * this.scale,
        editW = this.pxToMm(edit.clientWidth),
        editH = this.pxToMm(edit.clientHeight),
        value = this.pxToMm(100);

      x = x < -(elW - value) ? -(elW - value) : x;
      x = x > editW - value ? editW - value : x;
      y = y < -(elH - value) ? -(elH - value) : y;
      y = y > editH - value ? editH - value : y;

      this.x = x;
      this.y = y;
    },
    dragEnd(e, args){

    },
    setScale(args) {
      //设置数据
      this.scale = args.scale;
      this.x = this.pxToMm(args.offset.x);
      this.y = this.pxToMm(args.offset.y);
      this.$store.commit("setState", { editPannelScale: args.scale});
    },
    setPageSize() {
      var bookInfo = this.$store.state.bookInfo,
        currentPage = this.$store.state.currentPage;

      var w = 0, h = 0, isCross = false, shadow = false;

      for (var i = 0; i < currentPage.length; i++) {
        var pageNum = currentPage[i];
        var page = isNaN(pageNum) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];

        if (isCross) {
          continue;
        }

        //折页特殊对待
        if(pageNum == 'front_flap'){
          w = bookInfo['title_page'].width * 2 - this.Config.bleed * 2;
        }else{
          w += page.width * 1;
        }

        h = page.height;

        //预览时候减去出血位
        if(this.viewType == 'preview'){
          var bleed = 0;
          if (this.Config.noBleed.indexOf(pageNum) != -1) {
            // bleed = MimoBook.backCoverInfo[this.Config.productId] ? MimoBook.backCoverInfo[this.Config.productId].bleed : this.Config.bleed;
          } else {
            bleed = this.Config.bleed;
          }
          w -= bleed * 2;
          h -= bleed * 2;
        }
        page.type == 2 && (isCross = true);
      }

      currentPage.length == 2 && (shadow = true);

      return {
        w: w,
        h: h,
        shadow: shadow
      }
    },
    zoomWheel(e) {
      var delta = e.wheelDelta || e.delta;
      if (delta) {
        var type = delta > 0 ? 'zoomin' : 'zoomout';
        for (var i = 0; i < this.$children.length; i++) {
          if (typeof this.$children[i].setScaleByType == 'function') {
            this.$children[i].setScaleByType(type, {x: e.pageX, y: e.pageY});
          }
        }
      }
    },
    //自适用编辑区域
    autoPannel(limit){
      this.dragPannelLock = true;

      !limit && this.setAnimation();

      //获取正确的宽高
      var size = this.getWH();

      //适应编辑区域
      this.AutoCanvas({
        el: this.$refs.edit,
        w: this.mmToPx(size.w),
        h: this.mmToPx(size.h),
        callback: (args) => {
          //特殊封面进行偏移
          if(this.Config.productInfo[this.Config.productId].specialCover && this.selected.pageNum == 'front_cover'){
            var coverInfo = this.Config.specialCoverInfo[this.Config.productId];
            args.offset.x = Math.abs(coverInfo.coverLeft) * args.scale + args.offset.x;
            args.offset.y = Math.abs(coverInfo.coverTop) * args.scale + args.offset.y;
          }
          this.setScale(args);
        }
      });

      this.$store.commit('setState', {selected: { state: false, data: {}, el: null, pageNum: this.selected.pageNum }})
    },
    //按钮缩放编辑区域
    btnZoomPannel(scale, mouseXy) {
      this.dragPannelLock = false;

      this.setAnimation();

      //适应编辑区域
      this.CanvasFocusZoom({
        el: this.$refs.edit,
        w: this.mmToPx(this.w),
        h: this.mmToPx(this.h),
        x: this.mmToPx(this.x),
        y: this.mmToPx(this.y),
        scale: scale,
        lastScale: this.scale,
        callback: this.setScale
      });

      this.$store.commit('setState', {selected: { state: false, data: {}, el: null, pageNum: this.selected.pageNum }})
    },
    //滚轮缩放编辑区域
    wheelZoomPannel(scale, mouseXy) {
      this.dragPannelLock = false;

      this.setAnimation();

      //获取正确的宽高
      var size = this.getWH();

      //适应编辑区域
      this.WheelFocusZoom({
        el: this.$refs.edit,
        w: this.mmToPx(size.w),
        h: this.mmToPx(size.h),
        x: this.mmToPx(this.x),
        y: this.mmToPx(this.y),
        scale: scale,
        lastScale: this.scale,
        mouseXy: mouseXy,
        callback: this.setScale
      });

      this.$store.commit('setState', {selected: { state: false, data: {}, el: null, pageNum: this.selected.pageNum }})
    },
    //设置过渡效果
    setAnimation(){
      var that = this;
      this.zooming = true;

      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      this.timer = setTimeout(function() {
        that.zooming = false;
        that.timer = null;
      }, 300);
    },
    showEdit () {
      this.$store.commit('setState', {viewType: 'common'})
    },
    getWH(){
      //特殊封面区别对待哎
      var data = {};
      if(this.Config.productInfo[this.Config.productId].specialCover && this.selected.pageNum == 'front_cover'){
        data.w = this.pxToMm(this.Config.specialCoverInfo[this.Config.productId].coverWidth);
        data.h = this.pxToMm(this.Config.specialCoverInfo[this.Config.productId].coverHeight);
      }else{
        data.w = this.w;
        data.h = this.h;
      }
      return data;
    }
  }
}
</script>

<style lang="scss">
#edit {
  position: absolute;
  top: 105px;
  left: 215px;
  right: 166px;
  bottom: 127px;
  z-index: 1000;
  overflow: hidden;
  -webkit-box-shadow: inset 0 1px 8px #aaa;
  -moz-box-shadow: inset 0 1px 8px #aaa;
  box-shadow: inset 0 1px 8px #aaa;
}

#edit.--preview {
  top: 0;
  left: 0;
  right: 0;
}

#eidt_pannel {
  position: absolute;
  background: #fff;
  -ms-transform-origin: 0 0;
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  -o-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-box-shadow: 0 1px 10px #999;
  -moz-box-shadow: 0 1px 10px #999;
  box-shadow: 0 1px 10px #999;

  &[data-num=front_cover]:before{
    content: '';
    position: absolute;
    top: -4%;
    left: -3%;
    right: 3%;
    bottom: 100%;
    background-image: url(./../../assets/book/image/spine/top_left.png), url(./../../assets/book/image/spine/top_right.png), url(./../../assets/book/image/spine/top_repeat.png);
    background-position: left top, right top, left top;
    background-repeat: no-repeat, no-repeat, repeat-x;
    background-size: 4% 100%, contain, contain;
  }

  &[data-num=front_cover]:after{
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: -3%;
    right: 100%;
    background-image: url(./../../assets/book/image/spine/left_bottom.png), url(./../../assets/book/image/spine/left_repeat.png);
    background-position: left bottom, left top;
    background-repeat: no-repeat, repeat-y;
    background-size: contain;
  }

  &[data-num=front_cover][data-nocover=true]{
    background: none;
    box-shadow: none;
  }

  &[data-num=front_cover][data-nocover=true]:before,
  &[data-num=front_cover][data-specialcover=true]:before{
    content: none;
  }

  &[data-num=front_cover][data-nocover=true]:after,
  &[data-num=front_cover][data-specialcover=true]:after{
    content: none;
  }

  &[data-num=spine]:after{
    content: '';
    position: absolute;
    top: -6%;
    bottom: -7%;
    left: -60%;
    right: -600%;
    background-image: url(./../../assets/book/image/spine/spine_bg.png);
    background-position: left top;
    background-repeat: no-repeat;
    background-size: contain;
  }

  .shadow_line {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0px;
    right: 0;
    margin: auto;
    -webkit-box-shadow: 0 0 18px 1px black;
    -moz-box-shadow: 0 0 18px 1px black;
    box-shadow: 0 0 18px 1px black;
    z-index: 1000;
    width: 1px;
    background: #999;
  }
}

#preview-button_return_edit {
  position: absolute;
  bottom: 22px;
  left: 0;
  height: 38px;
  width: 105px;
  font-size: 14px;
  border: none;
  border-bottom: 4px solid #ac4030;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  line-height: 1em;

  &:active {
    border-bottom: 0;
    opacity: 0.8;
  }

  &:hover {
    border-bottom: 3px solid #ac4030;
  }

  &:before {
    content: '\e6d4';
    font-family: iconfont;
    font-size: 22px;
    vertical-align: middle;
    position: relative;
    left: -3px;
  }
}

.fadeIn-enter-active,.fadeIn-leave-active{
  transition: all .6s;
}
.fadeIn-enter,.fadeIn-leave-to{
  opacity: 0;
}
.fadeIn-enter-to,.fadeIn-leave{
  opacity: 1;
}
</style>



