//生成页数据
function generateNewPage(num, namespace, pageType, pageTemplate) {
  var generate = function (list, generateNewBox) {
      var boxList = [];

      for (var i = 0; i < list.length; i++) {
          var box = generateNewBox(list[i]);
          boxList.push(box);
      }
      return boxList;
  }

  return {
      num: num.toString(),
      template_name: (namespace + '.' + pageTemplate.name),
      width: pageTemplate.width,
      height: pageTemplate.height,
      imagebox_list: generate(pageTemplate.imageBoxList || pageTemplate.imagebox_list || [], generateNewImgbox),
      textbox_list: generate(pageTemplate.textBoxList || pageTemplate.textbox_list || [], generateNewTextbox),
      colorbox_list: generate(pageTemplate.shapeBoxList || pageTemplate.colorbox_list || [], generateNewShapebox),
      decoration_list: generate(pageTemplate.decorationboxList || pageTemplate.decoration_list || [], generateNewDecoration),
      shadingbox_list: generate(pageTemplate.shadingBoxList || pageTemplate.shadingbox_list || [], generateNewShading),
      patternbox_list: generate(pageTemplate.patternBoxList || pageTemplate.patternbox_list || [], generateNewPattern),
      background_color: pageTemplate.backgroundColor || pageTemplate.background_color || "#fff",
      type: pageType
  };
}

function generateNewTextbox(textboxTemplate) {
  return {
      name: textboxTemplate.name,
      index: textboxTemplate.index == undefined ? 1 : textboxTemplate.index,
      width: textboxTemplate.width,
      height: textboxTemplate.height,
      x: textboxTemplate.x,
      y: textboxTemplate.y,
      rotation: textboxTemplate.rotation == undefined ? 0 : textboxTemplate.rotation,
      content: window.EditorStatus == 0 ? '' : textboxTemplate.content == undefined ? "" : textboxTemplate.content,
      pt: textboxTemplate.pt,
      color: textboxTemplate.color,
      font_id: textboxTemplate.font_id || "101",
      align: textboxTemplate.align,
      space: textboxTemplate.space,
      leading: textboxTemplate.leading || -1,
      opacity: textboxTemplate.opacity || 1,
      readonly: textboxTemplate.readonly || 1,
      locked: textboxTemplate.locked,
  };
}

function generateNewImgbox(imgboxTemplate) {
  return {
      name: imgboxTemplate.name,
      index: imgboxTemplate.index == undefined ? 1 : imgboxTemplate.index,
      width: imgboxTemplate.width,
      height: imgboxTemplate.height,
      x: imgboxTemplate.x,
      y: imgboxTemplate.y,
      rotation: imgboxTemplate.rotation == undefined ? 0 : imgboxTemplate.rotation,
      border_width: imgboxTemplate.border_width || 0,
      border_color: imgboxTemplate.border_color ||'#ffffff',
      box_id: imgboxTemplate.box_id || null,
      locked: imgboxTemplate.locked,
      image: null,
  };
}

function generateNewShapebox(shapgeTemplate) {
  return {
      border_color: shapgeTemplate.border_color,
      border_width: shapgeTemplate.border_width,
      color: shapgeTemplate.color,
      color_id: shapgeTemplate.color_id,
      height: shapgeTemplate.height,
      index: shapgeTemplate.index == undefined ? 1 : shapgeTemplate.index,
      locked: shapgeTemplate.locked,
      name: shapgeTemplate.name,
      opacity: shapgeTemplate.opacity,
      rotation: shapgeTemplate.rotation == undefined ? 0 : shapgeTemplate.rotation,
      width: shapgeTemplate.width,
      x: shapgeTemplate.x,
      y: shapgeTemplate.y
  };
}

function generateNewDecoration(decorationTemplate) {
  return {
      decoration_id: decorationTemplate.decoration_id,
      height: decorationTemplate.height,
      index: decorationTemplate.index == undefined ? 1 : decorationTemplate.index,
      locked: decorationTemplate.locked,
      name: decorationTemplate.name,
      rotation: decorationTemplate.rotation == undefined ? 0 : decorationTemplate.rotation,
      src: decorationTemplate.src,
      width: decorationTemplate.width,
      x: decorationTemplate.x,
      y: decorationTemplate.y,
      type: decorationTemplate.type || null,
      color: decorationTemplate.color,
      text: decorationTemplate.text || null
  }
}

function generateNewShading(shadingTemplate) {
  return {
      id: shadingTemplate.id,
      height: shadingTemplate.height,
      index: shadingTemplate.index == undefined ? 1 : shadingTemplate.index,
      locked: shadingTemplate.locked,
      name: shadingTemplate.name,
      imgWidth: shadingTemplate.imgWidth,
      rotation: shadingTemplate.rotation == undefined ? 0 : shadingTemplate.rotation,
      width: shadingTemplate.width,
      x: shadingTemplate.x,
      y: shadingTemplate.y,
      editUrl: shadingTemplate.editUrl,
      thumbUrl: shadingTemplate.thumbUrl
  }
}

function generateNewPattern(patternTemplate) {
  return {
      id: patternTemplate.id,
      x: patternTemplate.x,
      y: patternTemplate.y,
      width: patternTemplate.width,
      height: patternTemplate.height,
      index: patternTemplate.index == undefined ? -1 : patternTemplate.index,
      locked: patternTemplate.locked,
      name: patternTemplate.name,
      imgWidth: patternTemplate.imgWidth,
      rotation: patternTemplate.rotation == undefined ? 0 : patternTemplate.rotation,
      src: patternTemplate.src,
      type: patternTemplate.type
  }
}


//获取box数据

var getBoxInfo = function(pageData, boxType, boxId) {
  for (var i in pageData[boxType]) {
    if (pageData[boxType][i].box_id == boxId) {
      return {
        data: pageData[boxType][i],
        index: i
      }
    }
  }
}


export default{
  generateNewPage,
  getBoxInfo,
}



