<template>
  <div id="photo_store">
    <div class="photo_store_title section_title">{{$t('photoStore.title')}}</div>
    <div id="photo_store_header">
      <div class="add_photo" :class="{'en': $i18n.locale == 'en-us'}" @click="switchUploadBtn = !switchUploadBtn">
        <p class="add_photo_text">{{$t("photoStore.addPhotos")}}</p>
        <div class="add_photo_icon" :style="{
          animation: switchUploadBtn ? 'spin 1s linear 0.25' : 'spin2 1s linear 0.25'
        }"></div>
        <div ref="addBtnVessel" class="add_photo_btn" :style="{
          height: switchUploadBtn ? '122px' : 0
        }">
          <ul>
            <li id="upload_pc" class="upload_btn_list">
              <p>{{$t("photoStore.pc")}}</p>
              <input id="upload_image" type="file" multiple="multiple" @change="selectFiles($event)">
            </li>
            <li id="upload_phone" class="upload_btn_list">
              <p>{{$t("photoStore.phone")}}</p>
            </li>
            <li id="upload_mimo" class="upload_btn_list">
              <p>{{$t("photoStore.mimo")}}</p>
            </li>
          </ul>
        </div>
      </div>
      <div class="photo_record">
        <label class="used_hide">
          <input type="checkbox" v-model="isChecked">
          <span>{{$t("photoStore.usedHide")}}</span>
        </label>
        <div class="used_count">{{CommonMethods.replaceText($t("photoStore.usedCount"), {totalCount: totalCount, usedCount: usedCount})}}</div>
      </div>
      <div class="photo_progress">
        <div class="photo_progress_count">{{CommonMethods.replaceText($t("photoStore.remain"),{count: uploadInfo.existCount})}}</div>
        <div class="photo_progress_speed">{{getSpeed}}</div>
      </div>
    </div>
    <div id="photo_store_list" ref="photo_store_list" class="mini-scroll photo_store_list"  :class="$store.state.photos.length == 0 ? 'not_img' : undefined" :style="{
      top: uploadInfo.state ? '130px' : ($store.state.photos.length > 0 ? '110px' : '90px'),
      bottom: uploadInfo.state ? '65px' : '35px'
    }">
      <ul>
        <transition-group name="fade">
          <li ref="thumbImage" class="thumbImage" :class="{'active': deleteBtn.btn && deleteBtn.id == photo.id, 'focus': checkFocus(photo.id , index+1)}" :ref="index+1" v-for="(photo, index) in $store.state.photos" :key="photo.id" v-if="!(usePhotosHide && photo.use)" @mousemove="setDeleteInfo(photo, true)" @mouseleave="setDeleteInfo(photo)" @click.stop="showPreviewImage($event, photo)" @mousedown="mouseDown($event, photo)">
            <photoStoreThumb :photo="photo" :canvasImg="$store.state.canvasImageList[photo.id]" @addCanvasImg="addCanvasImg"></photoStoreThumb>
            <span class="progress_bar iconfont" :class="setProgressClass(photo)" @click="photo.uploadProgress == 'fail' ? reUpload($event, photo.id) : undefined">{{setProgressText(photo)}}</span>
            <span class="button_close" @click="showDeleteDialog($event)" @mousedown.stop></span>
            <span class="find_photo" v-if="$store.state.photoUseInfo[photo.id] && $store.state.photoUseInfo[photo.id].length != 0" @click.stop="changePageNum($event, photo)"  @mousedown.stop>{{showPhotoUsePage(photo)}}</span>
          </li>
        </transition-group>
      </ul>
    </div>
    <div id="photo_store_footer">
      <div class="footer_btn_vessel">
        <div class="retryall" @click="ReUpload()">{{$t("photoStore.reUpload")}}</div>
        <div class="pause" @click="uploadInfo.paused ? abortUpload() : continueUpload()">{{uploadInfo.paused ? $t("photoStore.abort") : $t("photoStore.goon")}}</div>
      </div>
      <div class="footer_btn_vessel">
        <div class="delete_all_photo_btn" @click="showDeleteAllDialog()"></div>
        <div class="photo_orderby">
          <div class="photo_orderby_title">{{$t("photoStore.sortPhotos")}}：</div>
          <select class="photo_orderby_btn" @change="photoSort($event)">
            <option value="uploadtime">{{$t("photoStore.uploadtime")}}</option>
            <option value="shoottime">{{$t("photoStore.shoottime")}}</option>
            <option value="filename">{{$t("photoStore.filename")}}</option>
          </select>
        </div>
      </div>
    </div>
    <div id="photo_popup">
      <div class="delete_photo dialog dialog-body" :class="deleteBtn.dialog ? 'dialog-show' : ''" :style="{
        left: deleteBtn.x + 'px',
        top: deleteBtn.y + 'px'
      }">
        <div class="delete_photo_title">{{$t("photoStore.delete.title")}}</div>
        <div class="delete_photo_content">{{$t("photoStore.delete.text")}}</div>
        <div class="delete_photo_btn">
          <div class="delete_photo_btn_list dialog-button" @click="dialogDeletePhoto(true)">{{$t("photoStore.delete.yes")}}</div>
          <div class="delete_photo_btn_list dialog-button" @click="dialogDeletePhoto(false)">{{$t("photoStore.delete.no")}}</div>
        </div>
        <div class="dialog-button-close" @click="dialogDeletePhoto(false)">
          <span class="text">×</span>
        </div>
      </div>
      <div class="delete_all_photo dialog dialog-body" :class="deleteAllBtn.dialog ? 'dialog-show' : ''">
        <div class="delete_photo_title">{{$t("photoStore.deleteAll.title")}}</div>
        <div class="delete_photo_content" v-html="$t('photoStore.deleteAll.text1')"></div>
        <div class="delete_photo_content">{{$t('photoStore.deleteAll.text2')}}</div>
        <p class="delete_photo_content" v-html="$t('photoStore.deleteAll.text3')"></p>
        <div class="delete_photo_btn">
          <div class="delete_photo_btn_list dialog-button" @click="dialogAllDeletePhoto(true)">{{$t('photoStore.deleteAll.yes')}}</div>
          <div class="delete_photo_btn_list dialog-button" @click="dialogAllDeletePhoto(false)">{{$t('photoStore.deleteAll.no')}}</div>
        </div>
        <div class="dialog-button-close" @click="dialogAllDeletePhoto(false)">
          <span class="text">×</span>
        </div>
      </div>
      <div id="photo_preview" v-if="preview.dialog" :style="{
        left: preview.x + 'px',
        top: preview.y + 'px',
        animation: preview.dialog ? 'fadein-right 0.6s': 'fadeout-right 0.6s'
      }">
        <img :src="preview.src">
        <div class="photo_preview_item">{{preview.name}}</div>
        <div class="photo_preview_item">{{preview.size}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import PhotoStoreThumb from '@/components/common/photoStoreThumb'
import '@/components/common/photoAction'
export default {
  name: 'photostore',
  data() {
    return {
      // totalCount: 20,//总照片数量
      // usedCount: 10,//已使用数据
      usePhotosHide: false,//隐藏已使用照片
      isChecked: false,//隐藏已使用checkbox的标记
      switchUploadBtn: false,//上传按钮开关
      deleteBtn: {//删除按钮信息
        id: '',//照片id
        use: false,//是否使用
        btn: false,//删除按钮是否显示
        dialog: false,//弹窗是否显示
        x: 0,//弹窗x
        y: 0//弹窗y
      },
      deleteAllBtn: {
        dialog: false,//弹窗是否显示
        x: 0,//弹窗x
        y: 0//弹窗y
      },
      preview: {//预览图信息
        dialog: false,//弹窗是否显示
        src: '',
        name: '',
        size: '',
        x: 0,
        y: 0
      },
      uploadInfo: {
        state: false,//是否正在上传
        existCount: 0,//剩下未上传完的数量
        speed: 0,//上传速度
        paused: true// 暂停/继续按钮标记
      },
      dragPhoto: null//拖动的照片信息
    }
  },
  components: {
    PhotoStoreThumb
  },
  created() {
    var that = this;

    this.switchUsePhotos(true);

    document.addEventListener('click', function(e) {
      if (that.switchUploadBtn && e.target.className != 'add_photo_text') {
        that.$refs.addBtnVessel.style.height = 0;
        that.switchUploadBtn = false;
      }
    });
  },
  watch: {
    isChecked() {
      this.switchUsePhotos();
    }
  },
  computed: {
    totalCount() {//总照片数量
      return this.$store.state.photos.length;
    },
    usedCount() {//已使用数据
      var count = 0;
      for(name in this.$store.state.photoUseInfo){
        if(this.$store.state.photoUseInfo.hasOwnProperty(name)) count++;
      }
      return count;
    },
    getSpeed(){
      var speed = this.uploadInfo.speed;
      if (speed > 1024 * 1024) {
          speed = (speed / (1024 * 1024)).toFixed(1) + 'MB/s';
      } else {
          speed = Math.round(speed / 1024) + 'KB/s';
      }
      return speed;
    },
    dragStorePhoto() {
      return this.$store.state.dragStorePhoto;
    },
    editPannelScale() {
      return this.$store.state.editPannelScale;
    }
  },
  methods: {
    abortUpload(){
      this.uploadInfo.paused = false;
      this.AbortUploadAll();
    },
    continueUpload(){
      this.uploadInfo.paused = true;
      this.ContinueUpload();
    },
    reUpload(e, id){
      e.stopPropagation();
      this.ReUpload(id);
    },
    selectFiles(e) {
      var that = this;
      var files = e.target.files;

      this.uploadInfo.state = true;
      this.uploadInfo.existCount += files.length;

      this.CreateFile({
        files: files,
        salt: this.Config.workUserId,
        onSuccess: function(e) {
          //单个成功回调
          var photo = e.photo,
            msg = e.msg,
            existCount = e.existCount;

          that.uploadInfo.existCount = existCount;
        },
        onError: function() {
          //单个错误回调
        },
        onAllSuccess: function(e){
          //全部完成
          var speed = e.speed;

          that.uploadInfo.state = false;
          that.uploadInfo.existCount = speed;
        },
        onProgress: function(){
          //单个进度事件
        },
        onGlobalSpeed: function(e){
          //每秒全局速度
          var speed = e.speed;

          that.uploadInfo.speed = speed;
        }
      });
    },
    setProgressClass(photo) {
      if (isNaN(photo.uploadProgress)) {
        return photo.uploadProgress;
      }
      return 'uploading';
    },
    setProgressText(photo) {
      if (isNaN(photo.uploadProgress)) {
        return '';
      }else if(photo.uploadProgress == 1){
        return '处理中';
      }
      return parseInt(photo.uploadProgress) * 100 + '%';
    },
    switchUsePhotos(fixed) {
      this.$store.commit('setPhotosUseState');
      !fixed && (this.usePhotosHide = !this.usePhotosHide);
    },
    setDeleteInfo(photo, set) {
      if (set) {
        this.deleteBtn.btn = true;
        this.deleteBtn.id = photo.id;
        this.deleteBtn.use = photo.use;
      } else {
        this.deleteBtn.btn = false;
        this.preview.dialog = false;
      }
    },
    showPhotoUsePage(photo) {
      var pageNum = this.$store.state.photoUseInfo[photo.id][0];
      if (pageNum == undefined) return;

      var text = '';
      switch (pageNum) {
        case 'front_cover':
          text = '封';
          break;
        case 'front_flap':
          text = '折';
          break;
        case 'title_page':
          text = '扉';
          break;
        case 'copyright':
          text = '版';
          break;
        default:
          text = parseInt(pageNum) + 2;
      }

      return text;
    },
    showDeleteDialog(e) {
      e.stopPropagation();
      this.deleteBtn.dialog = true;
      this.deleteBtn.x = e.clientX;
      this.deleteBtn.y = e.clientY - 105;
    },
    showDeleteAllDialog(e) {
      this.deleteAllBtn.dialog = true;
    },
    dialogDeletePhoto(type) {
      if (!this.deleteBtn.use || !type) {
        if (type) {
          this.AbortUpload(this.deleteBtn.id);
          this.DeletePhotos(this.$store.state.workId, this.deleteBtn.id);
        }
        this.deleteBtn.dialog = false;
        this.deleteBtn.x = 0;
        this.deleteBtn.y = 0;
      } else {
        alert('该照片已经使用不能删除！');
      }
    },
    dialogAllDeletePhoto(type) {
      if (type) {
        var idList = [];
        for (var i = 0; i < this.$store.state.photos.length; i++) {
          idList.push(this.$store.state.photos[i].id);
        }
        this.DeletePhotos(this.$store.state.workId, idList);
      }
      this.deleteAllBtn.dialog = false;
    },
    changePageNum(e, photo) {
      e.stopPropagation();

      this.$store.commit('changePhotoUseInfo', { id: photo.id });
    },
    photoSort(e, value) {
      var type = e.target.value;
      this.$store.commit('photoSrot', type);
    },
    showPreviewImage(e, photo) {
      var that = this;
      var src = this.CommonMethods.joinPhotoHost(photo.src);
      this.GetImageSize(src, function(args) {
        var previewH = 300 / (args.width / args.height) + 45;
        var top = e.clientY - previewH / 2 - 105;
        var totalH = top + previewH + 110;
        top = totalH > window.innerHeight ? top - (totalH - window.innerHeight) : top;
        that.preview.src = src;
        that.preview.name = photo.fileName;
        that.preview.size = photo.originalWidth + 'x' + photo.originalHeight;
        that.preview.x = 210;
        that.preview.y = top;
        that.preview.dialog = true;
      });
    },
    mouseDown(e, photo){
      this.dragPhoto = photo;
      this.DragDown(e, {
        dragStart: this.dragStart,
        drag: this.drag,
        dragEnd: this.dragEnd
      })
    },
    dragStart(e) {
      var targetEl = this.closest(e.target, 'thumbImage').querySelectorAll("canvas")[0]

      this.$store.commit('setDragStorePhoto', {
        imgId: this.dragPhoto.id,
        src: this.dragPhoto.src.replace(/_eidt/, '_thumb'),
        state: true,
        w: targetEl.width,
        h: targetEl.height,
        x: e.pageX,
        y: e.pageY
      });

      document.body.style.cursor = '-webkit-grab';
    },
    drag(e, args) {
      var overEl = this.GetOverEl(e.pageX, e.pageY, this.Config.dragOver, e);
      this.$store.commit('setDragStorePhoto', {
        x: e.pageX,
        y: e.pageY,
        pageNum: overEl.pageNum,
        boxId: overEl.boxId
      });
    },
    dragEnd(e, args) {
      var overEl = this.GetOverEl(e.pageX, e.pageY, this.Config.dragOver, e);

      var page = document.querySelector(".page[data-num='" + overEl.pageNum + "']");
      if(overEl.type == 'page'){
        var x =  (this.dragStorePhoto.x - page.getBoundingClientRect().left) / this.editPannelScale,
            y = (this.dragStorePhoto.y - page.getBoundingClientRect().top) / this.editPannelScale;
        var imageInfo = {
          imgId: this.dragStorePhoto.imgId,
          x: this.pxToMm(x) - 30,
          y: this.pxToMm(y) - 20
        }
        this.AddBox(overEl.pageNum, 'imgbox', imageInfo)
      }
      else if(overEl.type == 'imgbox'){
        this.ChangeImage(overEl.pageNum, overEl.boxId, this.dragStorePhoto.imgId)
      }
      this.$store.dispatch("animation");
      document.body.style.cursor = '';

      this.$store.commit('setDragStorePhoto', {
        pageNum: '',
        boxId: '',
        imgId: '',
        state: false,
        src: '',
        w: 0,
        h: 0,
        x: 0,
        y: 0
      });
    },
    closest(el, selector) {
      if(el.classList){
        if(el.classList.contains(selector)){
          return el
        }
        else{
          return this.closest(el.parentNode, selector)
        }
      }
      return null
    },
    checkFocus(id, index) {
      if(id == this.$store.state.photoListFocusId){
        this.$refs.photo_store_list.scrollTop = this.$refs[index][0].offsetTop - 91 * 3
      }
      return id == this.$store.state.photoListFocusId
    },
    addCanvasImg(data){
      this.$store.commit('addCanvasImage', data);
    }
  }
}
</script>
<style lang="scss">
@keyframes spin {
  from {
    transform: rotate(0);
    -ms-transform: rotate(0);
    -webkit-transform: rotate(0);
    -o-transform: rotate(0);
    -moz-transform: rotate(0)
  }

  to {
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -moz-transform: rotate(360deg)
  }
}

@keyframes spin2 {
  from {
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -moz-transform: rotate(360deg)
  }

  to {
    transform: rotate(0);
    -ms-transform: rotate(0);
    -webkit-transform: rotate(0);
    -o-transform: rotate(0);
    -moz-transform: rotate(0)
  }
}

@keyframes fadein-right {
  from {
    opacity: 0;
    -moz-transform: translateX(8px);
    -ms-transform: translateX(8px);
    -o-transform: translateX(8px);
    -webkit-transform: translateX(8px);
    transform: translateX(8px);
  }

  to {
    opacity: 1;
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}

@keyframes fadeout-right {
  from {
    opacity: 1;
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }

  to {
    opacity: 0;
    -moz-transform: translateX(8px);
    -ms-transform: translateX(8px);
    -o-transform: translateX(8px);
    -webkit-transform: translateX(8px);
    transform: translateX(8px);
  }
}

#photo_store {
  position: absolute;
  left: 0;
  top: 105px;
  bottom: 0;
  width: 215px;
  background: #fff;
  // box-shadow: 1px 1px 5px #ccc;
  z-index: 1002;
  .photo_store_title {
    line-height: 28px;
    padding-left: 20px;
  }

  #photo_store_header {


    .add_photo {
      position: relative;
      height: 40px;
      line-height: 40px;
      border: solid 1px #ddd;
      border-radius: 3px;
      text-align: center;
      background: #fff;
      box-shadow: 1px 1px 5px #ddd;
      cursor: pointer;
      margin: 10px;

      &.en{
        p{
          text-indent: 1em;
        }
      }

      p {
        font-size: 18px;
        color: #6a3236;
      }

      .add_photo_icon {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 35px;
        width: 20px;
        height: 20px;
        margin: auto;

        &:after,
        &:before {
          content: "";
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          background: #6a3236;
        }

        &:after {
          width: 16px;
          height: 2px;
        }

        &:before {
          width: 2px;
          height: 16px;
        }
      }

      .add_photo_btn {
        position: absolute;
        top: 40px;
        z-index: 1000;
        height: 0;
        background: #fff;
        border: solid 1px #ddd;
        border-radius: 3px;
        box-shadow: 1px 1px 5px #ddd;
        overflow: hidden;
        transition: all 0.3s;

        .upload_btn_list {
          position: relative;
          width: 192px;
          height: 40px;
          line-height: 40px;
          text-align: center;
          overflow: hidden;

          >p {
            font-size: 14px;
            color: #555;
          }

          #upload_image {
            position: absolute;
            top: -40px;
            left: 0;
            width: 192px;
            height: 80px;
            cursor: pointer;
          }

          &:hover {
            background: #e6effc;
          }

          &:before {
            content: "";
            position: absolute;
            left: 25px;
            top: 0;
            bottom: 0;
            margin: auto;
            width: 24px;
            height: 24px;
          }
        }

        #upload_pc:before {
          background: url(../../assets/book/image/photostore/icon_upload_pc.png) no-repeat;
        }

        #upload_phone:before {
          background: url(../../assets/book/image/photostore/icon_upload_h5.png) no-repeat;
        }

        #upload_mimo:before {
          background: url(../../assets/book/image/photostore/icon_upload_mimo.png) no-repeat;
        }
      }
    }

    .photo_record {
      text-align-last: justify;
      height: 20px;

      .used_hide {
        display: inline-block;
        vertical-align: middle;
        margin-left: 10px;

        input,
        span {
          display: inline-block;
          vertical-align: top;
          line-height: 1;
        }
      }

      .used_count {
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
      }
    }

    .photo_progress{
      text-align-last: justify;

      .photo_progress_count,
      .photo_progress_speed{
        display: inline-block;
        vertical-align: middle;
        margin: 0 10px;
      }
    }
  }

  #photo_store_list {
    position: absolute;
    bottom: 36px;
    top: 90px;
    left: 0;
    right: 0;
    z-index: 1;
    background: white;
    overflow-y: scroll;
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
    border-top: 1px solid #dedede;
    border-bottom: 1px solid #dedede;

    &.not_img:after {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      background: url(../../assets/book/image/photostore/icon_upload_drag.png) no-repeat;
      width: 151px;
      height: 128px;
    }

    >ul {
      padding: 5px;
      overflow: hidden;

      li {
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        height: 86px;
        width: 86px;
        line-height: 86px;
        text-align: center;
        float: left;
        font-size: 0;
        margin: 5px;
        position: relative;
        cursor: move;
        cursor: -webkit-grab;
        background: #f4f4f4;

        &.fadeout {
          -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
        }

        >canvas {
          max-width: 100%;
          max-height: 100%;
          vertical-align: middle;
          -moz-box-sizing: border-box;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          -webkit-animation: fade-in 0.4s;
          -moz-animation: fade-in 0.4s;
          -o-animation: fade-in 0.4s;
          animation: fade-in 0.4s;
        }

        .progress_bar {
          position: absolute;
          left: 0;
          top: 0;
          background: #93cfe5;
          font-size: 12px;
          font-family: iconfont, 'Microsoft YaHei', SimHei;
          color: white;
          padding: 2px;
          line-height: 1em;
          z-index: 1;
          -moz-backface-visibility: hidden;
          -webkit-backface-visibility: hidden;
          backface-visibility: hidden;

          &.success {
            background: #71d375;

            &:before {
              content: '\e6a7';
            }
          }

          &.wait {
            background: #93cfe5;

            &:before {
              content: '待上传';
            }
          }

          &.fail {
            background: red;
            cursor: pointer;

            &:before {
              content: '点击重试';
            }
          }

          &.uploading {
            background: #31afbc;
          }
        }

        .button_close {
          position: absolute;
          text-align: center;
          border-radius: 11px;
          background: #f65314;
          color: white;
          height: 22px;
          width: 22px;
          top: -8px;
          right: -8px;
          opacity: 0;
          visibility: hidden;
          cursor: pointer;
          -ms-transform: perspective(1000px) translateZ(-100px) rotateY(10deg) rotateX(-15deg);
          -webkit-transform: perspective(1000px) translateZ(-100px) rotateY(10deg) rotateX(-15deg);
          -moz-transform: perspective(1000px) translateZ(-100px) rotateY(10deg) rotateX(-15deg);
          -o-transform: perspective(1000px) translateZ(-100px) rotateY(10deg) rotateX(-15deg);
          transform: perspective(1000px) translateZ(-100px) rotateY(10deg) rotateX(-15deg);
          -webkit-transition-property: all;
          -moz-transition-property: all;
          -o-transition-property: all;
          transition-property: all;
          -webkit-transition-duration: 0.2s;
          -moz-transition-duration: 0.2s;
          -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
          -webkit-backface-visibility: hidden;
          -ms-backface-visibility: hidden;
          -moz-backface-visibility: hidden;
          backface-visibility: hidden;

          &:before,
          &:after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            width: 12px;
            height: 2px;
            background: #fff;
          }

          &:after {
            transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            -webkit-transform: rotate(45deg);
            -o-transform: rotate(45deg);
            -moz-transform: rotate(45deg)
          }

          &:before {
            transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
            -moz-transform: rotate(-45deg)
          }
        }

        &.active {

          .button_close {
            visibility: visible;
            -ms-transform: perspective(1000px) translateZ(0px) rotateY(0deg) rotateX(0deg);
            -webkit-transform: perspective(1000px) translateZ(0px) rotateY(0deg) rotateX(0deg);
            -moz-transform: perspective(1000px) translateZ(0px) rotateY(0deg) rotateX(0deg);
            -o-transform: perspective(1000px) translateZ(0px) rotateY(0deg) rotateX(0deg);
            transform: perspective(1000px) translateZ(0px) rotateY(0deg) rotateX(0deg);
            opacity: 1;
          }
        }

        .find_photo {
          position: absolute;
          top: 35px;
          right: 0;
          width: 20px;
          height: 20px;
          line-height: 20px;
          font-size: 12px;
          color: #fff;
          background: #5d86cd;
          border-radius: 10px;
          cursor: pointer;
          overflow: hidden;
          -moz-backface-visibility: hidden;
          -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
        }
      }

      .fade-enter-active,
      .fade-leave-active {
        transition: all .3s;
      }
      .fade-enter,
      .fade-leave-to {
        transform: perspective(1000px) translateZ(-100px) rotateY(10deg) rotateX(-15deg);
        opacity: 0;
      }
      .fade-enter-to,
      .fade-leave {
        opacity: 1;
        transform: perspective(1000px) translateZ(0px) rotateY(0deg) rotateX(0deg);
      }
    }
  }

  #photo_store_footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 63px;
    -moz-transition: all 0.4s;
    -o-transition: all 0.4s;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
    overflow: hidden;

    .footer_btn_vessel{
      position: relative;
      height: 30px;
      line-height: 30px;
      text-align: center;

      .pause,
      .retryall{
        width: 90px;
        height: 20px;
        line-height: 20px;
        text-align: center;
        color: #666;
        background: #fff;
        background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#ffffff), color-stop(100%,#e5e5e5)); /* Chrome,Safari4+ */
        background: -webkit-linear-gradient(top, #ffffff 0%,#e5e5e5 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
        background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(#e5e5e5));
        background: -webkit-linear-gradient(top, #ffffff 0%, #e5e5e5 100%);
        background: linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%); /* W3C */
        display: inline-block;
        vertical-align: middle;
        font-size: 12px;
        border-radius: 4px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        border: 1px solid #d6d6d6;
        -webkit-box-shadow: 0 1px 1px #dbdbdb;
        box-shadow: 0 1px 1px #dbdbdb;
        margin: 0 2px;
        cursor: pointer;
      }

      .delete_all_photo_btn {
        position: absolute;
        left: 10px;
        top: 0;
        bottom: 0;
        margin: auto;
        width: 20px;
        height: 20px;
        background: url(../../assets/book/image/photostore/icon_trash.png) no-repeat;
        background-size: 80%;
        background-position: center;
        cursor: pointer;
      }

      .photo_orderby {
        position: absolute;
        right: 15px;
        top: 0;
        bottom: 0;
        margin: auto;
        height: 20px;
        line-height: 20px;

        .photo_orderby_title {
          display: inline-block;
          vertical-align: top;
        }

        .photo_orderby_btn {
          display: inline-block;
          vertical-align: top;
        }
      }
    }
  }

  #photo_popup {

    .delete_photo {
      width: 110px;
    }

    .delete_all_photo {
      width: 210px;
      bottom: 5px;
      left: 210px;
      padding: 20px;
    }

    .delete_all_photo,
    .delete_photo {

      .delete_photo_title {
        font-size: 20px;
        font-weight: normal;
      }

      .delete_photo_content {
        margin: 5px 0;

        span{
          color: red;
        }
      }

      .delete_photo_btn {
        text-align: center;

        .delete_photo_btn_list {
          display: inline-block;
          width: 48px;
          height: 24px;
          line-height: 24px;
          margin-right: 4px;
        }
      }
    }

    #photo_preview {
      position: absolute;
      z-index: 2000;
      background: white;
      padding: 5px;
      text-align: center;
      -webkit-box-shadow: 0 1px 6px #999;
      box-shadow: 0 1px 6px #999;
      -ms-transform-origin: -53px 43px;
      -webkit-transform-origin: -53px 43px;
      -moz-transform-origin: -53px 43px;
      -o-transform-origin: -53px 43px;
      transform-origin: -53px 43px;
      -webkit-backface-visibility: hidden;
      -ms-backface-visibility: hidden;
      -moz-backface-visibility: hidden;
      backface-visibility: hidden;
      width: 300px;

      >img {
        max-width: 100%;
        max-height: 500px;
        vertical-align: middle;
        margin-bottom: 5px;
      }

      >.photo_preview_item {
        color: #000;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}
</style>



