<template>
  <div>
    <Minheader v-show="viewType == 'common'"></Minheader>
    <TopTollbar v-show="viewType == 'common'"></TopTollbar>
    <LayoutPannel v-show="viewType == 'common'"></LayoutPannel>
    <PhotoStore v-show="viewType == 'common'"></PhotoStore>
    <EditPannel v-if="!$store.state.showPageSort"></EditPannel>
    <PageSort v-else></PageSort>
    <Navigator></Navigator>
    <RightBtnMenu></RightBtnMenu>
    <DragSizeTip :scale="$store.state.editPannelScale"></DragSizeTip>
    <EditTextPannel :scale="scale"></EditTextPannel>
    <BookParamet></BookParamet>
    <PropsPannel
      ref="propspannel"
      :showState="propsShowState"
      :boxToolXY="boxToolXY"
      :box="$store.state.selected.data"
      :page="getPage"
      :fixZoomScaleBox="Config.fixZoomScaleBox"
      @changeData="changePropsPannelData"
    ></PropsPannel>
    <SaveTip :saveTip="$store.state.saveTip" @changeState="changeSaveTip"></SaveTip>
    <DragStoreImage :thumbInfo="$store.state.dragStorePhoto" :canvasImageList="$store.state.canvasImageList"></DragStoreImage>
  </div>
</template>

<script>
import Minheader from '@/components/book/header'
import TopTollbar from '@/components/book/topToolbar'
import PhotoStore from '@/components/book/photoStore'
import EditPannel from '@/components/book/editPannel'
import LayoutPannel from '@/components/book/layoutPannel'
import Navigator from '@/components/book/navigator'
import PageSort from '@/components/book/pageSort'
import RightBtnMenu from '@/components/common/rightBtnMenu'
import DragSizeTip from '@/components/common/dragSizeTip'
import BookParamet from '@/components/book/bookParamet'
import PropsPannel from '@/components/common/propsPannel'
import EditTextPannel from '@/components/common/editTextPannel'
import SaveTip from '@/components/common/saveTip'
import DragStoreImage from '@/components/common/dragStoreImage'
export default {
  name: 'index',
  data() {
    return {
      editBottom: 127,
    }
  },
  components: {
    Minheader,
    TopTollbar,
    PhotoStore,
    EditPannel,
    LayoutPannel,
    Navigator,
    PageSort,
    RightBtnMenu,
    DragSizeTip,
    BookParamet,
    PropsPannel,
    EditTextPannel,
    SaveTip,
    DragStoreImage
  },
  created() {
    var that = this;
    document.addEventListener('click', function(e) {
      //拖动松开就不触发
      if(that.$store.state.isMove){
        that.$store.commit('setState', {isMove: false});
        return
      }

      var pageNum = that.$store.state.selected.pageNum;

      //点到编辑工具栏不变更框的选中
      if (!that.matches(e.target, '.edit-pagetool, .edit-pagetool *, .resize-wrap, .resize-wrap *, .edit-props, .edit-props *')) {
        that.$store.commit('setState', {
          selected: {
            el: null,
            data: {},
            state: false,
            pageNum: pageNum
          },
          multipleSelected: {
            pageNum: pageNum,
            list: []
          }
        });
      }

      that.$store.commit('setState', {
        multipleSelected: {
          pageNum: pageNum,
          list: []
        }
      });

    });
  },
  mounted() {
    document.body.onkeydown = (e) => {
      this.KeyDown(e)
    };
  },
  computed: {
    viewType () { //common preview
      return this.$store.state.viewType
    },
    getPage(){
      var bookInfo = this.$store.state.bookInfo,
        pageNum = this.$store.state.selected.pageNum;

      return isNaN(pageNum) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
    },
    scale() {
      return this.$store.state.editPannelScale
    },
    propsShowState(){
      return (this.$store.state.selected.state || this.$store.state.isMove || this.$store.state.isKeyDownDire) && this.$store.state.multipleSelected.list.length < 2 && (this.$store.state.boxToolbar.pannelName == 'props' || (this.$refs.propspannel && this.$refs.propspannel.pannel.state));
    },
    boxToolXY(){
      return {
        x: this.$store.state.boxToolbar.x,
        y: this.$store.state.boxToolbar.y
      }
    },
  },
  methods: {
    matches(el, selector) {
      return (el.matches || el.matchesSelector || el.msMatchesSelector || el.mozMatchesSelector || el.webkitMatchesSelector || el.oMatchesSelector).call(el, selector);
    },
    changePropsPannelData(data){
      this.$store.commit('changeBoxData', {
        pageNum: this.$store.state.selected.pageNum,
        boxId: this.$store.state.selected.data.box_id,
        boxType: this.$store.state.selected.el.getAttribute("data-type"),
        data: data
      });

      this.$store.dispatch("animation");
    },
    changeSaveTip(type){
      this.$store.commit("setState", { saveTip: type });
    }
  }
}
</script>

<style>
html {
  background: url('../../assets/book/image/bg.png');
}

</style>



