<template>
  <transition name="fadeIn">
    <div id="pagesort">
      <p class="notice">您可以按住鼠标左键，拖动页面进行排序</p>
      <div id="sortType" class="notice">
        <label id="singleText"><input type="radio" name="sort" value="single" id="single" v-model="sortType"><span>单页排序</span>
        </label>　｜　<label id="doubleText"><input type="radio" id="normal" checked="checked" name="sort" value="normal" v-model="sortType"><span>双页排序</span>
        </label>
      </div>
      <div id="pagesort-buttonarea">
        <button id="pagesort-button_save" class="dialog-button" :class="{'btn_color_01': !altered, 'btn_color_02': altered}" @click="saveChange()">保存修改</button>
        <button id="pagesort-button_cancel" class="dialog-button" @click.stop="showEditPannel()">返回编辑</button>
      </div>
      <div id="pagesort-page_list_wrapper" class="mini-scroll">
        <ul ref="ul">
          <li v-for="(item, i) in pageList" :key="item.pages[0].num" :class="{'disable': item.type != 1 && item.type != 2}" :data-left="pageStyles[i - 1] && pageStyles[i - 1].left " :data-top="pageStyles[i - 1] && pageStyles[i - 1].top" :style="liStyle(i - 1)" :txt="item.cname"  @mousedown.stop="mouseDown($event, item, i)">
            <div class="list_page-item-preview" :class="{'towpage': item.pages.length == 2 && sortType == 'normal'}" :style="itemWidth(item)">
              <pageThumb :template="item.pages[0]" :size="thumbSize(item.pages[0])"></pageThumb>
              <pageThumb :template="item.pages[1]" v-if="item.pages.length == 2 && sortType == 'normal' && item.pages[0].type != 2" :size="thumbSize(item.pages[0])"></pageThumb>
            </div>
          </li>
        </ul>
        <div class="focus-item" v-if="focusTemplate" :style="liStyle(focusTemplate - 1)" :txt="pageList[focusTemplate].cname" >
          <div class="focus-container" :style="itemWidth(pageList[focusTemplate])">
            <pageThumb :template="pageList[focusTemplate].pages[0]" :size="thumbSize(pageList[focusTemplate].pages[0])"></pageThumb>
            <pageThumb :template="pageList[focusTemplate].pages[1]" v-if="pageList[focusTemplate].pages.length == 2 && sortType == 'normal' && pageList[focusTemplate].pages[0].type != 2" :size="thumbSize(pageList[focusTemplate].pages[0])"></pageThumb>
          </div>
        </div>

        <div class="insertLine" v-if="tagIndex != 0" :style="insertLineStyle"></div>
      </div>
    </div>
  </transition>
</template>

<script>
import pageThumb from '@/components/book/pageThumb'
export default {
  name: 'pagesort',
  components: {
    pageThumb,
  },
  data() {
    return {
      sortType: 'normal',
      pageStyles: [],
      pageList: [],
      focusTemplate: '',
      tagIndex: 0,
      altered: false,
    }
  },
  watch: {
    sortType() {
      this.setPageList();
    }
  },
  computed: {
    bookInfo() {
      return this.$store.state.bookInfo // JSON.parse(JSON.stringify(this.$store.state.bookInfo));
    },
    insertLineStyle() {
      var index = this.tagIndex;
      if(index != 0) {
        var l = this.pageStyles[index - 1].left,
            t = this.pageStyles[index - 1].top + 10;
        return {
          left: l + 'px',
          top: t + 'px'
        }
      }
      return ''
    }
  },
  methods: {
    setPageStyles(list) {
      this.$nextTick(() => {
        var styles = [];
        if (this.$el) {
          var pannelWidth = this.$el.getBoundingClientRect().width,
            lastTop = 0,
            lastLeft = 0,
            left = 0,
            top = 0;

          var proInfo = this.Config.productInfo[this.Config.productId],
            coverInfo = this.Config.specialCoverInfo[this.Config.productId];

          for (var i = 1; i < list.length; i++) {
            var page1 = list[i - 1].pages[0];
            var w1, h1;
            if(page1.num == 'front_cover' && (proInfo.noCover || proInfo.specialCover)){
              w1 = coverInfo.coverWidth;
              h1 = coverInfo.coverHeight;
            }else{
              w1 = page1.width;
              h1 = page1.height;
            }

            var width = 70 / h1 * w1;
            width = list[i - 1].type == 1 && this.sortType == 'normal' ? width * 2 + 40 : width + 40;

            var page2 = list[i].pages[0];
            var w2, h2;
            if(page2.num == 'front_cover' && (proInfo.noCover || proInfo.specialCover)){
              w2 = coverInfo.coverWidth;
              h2 = coverInfo.coverHeight;
            }else{
              w2 = page2.width;
              h2 = page2.height;
            }

            var thisWidth = 70 / h2 * w2;
            thisWidth = list[i].type == 1 && this.sortType == 'normal' ? thisWidth * 2 + 40 : thisWidth + 40;

            if (lastLeft + width + thisWidth > pannelWidth) {
              left = 0;
              lastTop += 110
            }
            else {
              left = lastLeft + width;
            }
            lastLeft = left;

            styles.push({
              left: left,
              top: lastTop,
            })
          }
        }
        this.pageStyles = styles;
      })
    },
    itemWidth(item) {
      var proInfo = this.Config.productInfo[this.Config.productId],
        coverInfo = this.Config.specialCoverInfo[this.Config.productId],
        page = item.pages[0];

      var width = 0, w, h;
      if(page.num == 'front_cover' && (proInfo.noCover || proInfo.specialCover)){
        w = coverInfo.coverWidth;
        h = coverInfo.coverHeight;
      }else{
        w = page.width;
        h = page.height;
      }

      width = 70 / h * w;
      if (item.type == 1 && this.sortType == 'normal') {
        width = width * 2
      }

      return {
        width: width + "px",
        height: '70px'
      };
    },
    thumbSize(page){
      var proInfo = this.Config.productInfo[this.Config.productId],
        coverInfo = this.Config.specialCoverInfo[this.Config.productId];

      var width, w, h;
      if(page.num == 'front_cover' && (proInfo.noCover || proInfo.specialCover)){
        w = coverInfo.coverWidth;
        h = coverInfo.coverHeight;
      }else{
        w = page.width;
        h = page.height;
      }

      width = 70 / h * w;
      return {
        w: width + 'px',
        h: '70px'
      }
    },
    liStyle(i) {
      if (this.pageStyles[i]) {
        return {
          "transform": "translate(" + this.pageStyles[i].left + "px, " + this.pageStyles[i].top + "px)"
        }
      }
    },
    mouseDown(e, item, ind) {
      var startX = e.clientX,
          startY = e.clientY;
      var pageList = this.pageList;
      var pageStyles = this.pageStyles;
      var li = e.target.parentNode;
      var normLeft = li.getAttribute("data-left") || 0,
          normTop = li.getAttribute("data-top") || 0;
      var moveX = startX,
          moveY = startY;
      var endLeft = normLeft,
          endTop = normTop,
          tagInd = 0,
          _page = pageList[ind],
          _prePage = pageList[ind - 1],
          _nextPage = pageList[ind + 1],
          curInd = 0;

      var _towPageLength = 0;

      for(var i = 4; i <= ind; i++){
        if(pageList[i].type == 2){
          _towPageLength ++;
        }
      }
      console.log(ind)
      document.body.onmousemove = e => {

        this.focusTemplate = ind;
        moveX = e.clientX,
        moveY = e.clientY;

        var itemLeft = (moveX - startX + normLeft * 1),
            itemTop = (moveY - startY + normTop * 1);
        if (li.nodeName != 'LI') return false;
        li.style.transition = 'none'
        li.style.webkitTransform = 'translate(' + itemLeft + 'px, ' + itemTop + 'px) scale(1.2)';

        var middleWidth = 70 / item.pages[0].height * item.pages[0].width;
        middleWidth = this.sortType == 'normal' ? middleWidth / 2 : middleWidth / 4;
        itemLeft += middleWidth;

        var minuX = middleWidth * 2 + 40, minuY = 70 ;

        for(var i = 0; i < pageStyles.length; i++){
          if(Math.abs(pageStyles[i].left - itemLeft) < minuX){
            if(Math.abs(pageStyles[i].top - itemTop) < minuY){
              minuX = Math.abs(pageStyles[i].left - itemLeft);
              minuY = Math.abs(pageStyles[i].top - itemTop);
              curInd = i + 1;
            }
          }
        }

        if(pageStyles[pageStyles.length - 2].left < itemLeft && pageStyles[pageStyles.length - 2].top < itemTop ){
          curInd = pageStyles.length - 2
        }

        curInd = curInd <= 4 ? 4 : curInd;

        curInd = curInd >= pageStyles.length - 2 ? pageStyles.length - 2 : curInd;

        if(this.sortType != 'normal'){
          var towPageLength = 0;  //按下元素前面有多少个跨页
          if(pageList[ind].type == 2 && pageList[curInd].type == 1){
            for(var i = 3; i < curInd; i++){
              if(pageList[i].type == 2){
                towPageLength ++;
              }
            }

            var type = 1, add = 0;

            if(ind > curInd){ //向前放
              type = -1;
              add --;
            }

            if(towPageLength % 2 == 0){ //前面有偶数个跨页
              if((curInd + add) % 2 == 0){
                curInd += type;
              }
            }
            else{
              if((curInd + add) % 2 != 0){
                curInd += type;
              }
            }
          }
        }
        curInd = curInd <= 4 ? 4 : curInd;
        curInd = curInd >= pageStyles.length - 2 ? pageStyles.length - 2 : curInd;

        this.tagIndex = curInd
      }

      document.body.onmouseup = e => {
        if(startX != moveX || startY != moveY){
          this.altered = true;
        }
        this.focusTemplate = '';
        var endStyle = this.pageStyles[curInd - 1];
        endStyle && (li.style.webkitTransform = 'translate(' + (endStyle.left * 1) + 'px, ' + (endStyle.top * 1) + 'px) scale(1)')

        var hasTowPage = [];
        if(curInd != ind && tagInd != curInd){
          if(this.sortType != 'normal' && pageList[ind].type == 1){
            var i = curInd > ind ? ind : curInd;
            var len = curInd > ind ? curInd : ind;

            for(; i <= len; i++){
              if(pageList[i].type == 2){
                hasTowPage.push(i);
              }
            }
          }
          this.pageList.splice(ind, 1);
          this.pageList.splice(curInd, 0, _page);
        }


        this.$nextTick(() => {
          var jo = ind % 2 == 0; //按下页的奇偶
          var _type = 1, _ind = ind, _curInd = curInd, _curPage;

          if(hasTowPage.length > 0){
            if(ind < curInd){ //向后面拖拽
              if(_towPageLength % 2 == 0){ //之前有偶数个跨页
                _ind = jo ? ind : ind - 1;
                _curInd = jo ? curInd : curInd - 1;
                _curPage = jo ? _nextPage : _prePage;
              }
              else{
                _ind = !jo ? ind : ind - 1;
                _curInd = !jo ? curInd : curInd -1;
                _curPage = !jo ? _nextPage : _prePage;
              }
            }
            else{ //往前面拖拽
              if(_towPageLength % 2 == 0){ //之前有偶数个跨页
                _ind = jo ? ind + 1 : ind;
                _curInd = jo ? curInd + 1 : curInd;
                _curPage = jo ? _nextPage : _prePage;
              }
              else{
                _ind = !jo ? ind + 1 : ind;
                _curInd = !jo ? curInd + 1 : curInd;
                _curPage = !jo ? _nextPage : _prePage;
              }
            }

            this.pageList.splice(_ind, 1);
            this.pageList.splice(_curInd, 0, _curPage);
          }


          this.$nextTick(() => {
            this.tagIndex = 0;
            this.setPageStyles(this.pageList)
            li.style.transition = 'all .5s'
          })
        })

        document.body.onmousemove = null
        document.body.onmouseup = null
      }
    },
    showEditPannel() {
      this.$store.commit("setState", { showPageSort: false })
    },
    setPageList() {
      var book = this.bookInfo,
        list = [];
      if (book.front_cover) {
        list.push({
          cname: '封面',
          num: [book.front_cover.num],
          pages: [book.front_cover],
          type: book.front_cover.type
        })
      }
      if (book.slipcase) {
        list.push({
          cname: '书壳',
          num: [book.slipcase.num],
          pages: [book.slipcase],
          type: book.slipcase.type
        })
      }
      if (book.spine && !this.Config.productInfo[this.Config.productId].limitDraw) { // || window.JpgBook == 1) {
        list.push({
          cname: '书脊',
          num: [book.spine.num],
          pages: [book.spine],
          type: book.spine.type
        })
      }
      if (book.front_flap && !this.Config.productInfo[this.Config.productId].limitDraw) {// || window.JpgBook == 1) {
        list.push({
          cname: '折页',
          num: [book.front_flap.num],
          pages: [book.front_flap],
          type: book.front_flap.type
        })
      }
      if (book.title_page) {
        list.push({
          cname: '1(扉页)',
          num: [book.title_page.num],
          pages: [book.title_page],
          type: book.title_page.type
        })
      }
      if (this.sortType == 'normal') {
        for (var i = 0; i < book.page_list.length; i += 2) {
          var bp1 = book.page_list[i]
          var bp2 = book.page_list[i + 1]
          list.push({
            cname: [i + 2, i + 3].join(' - '),
            num: [i, i + 1],
            pages: [bp1, bp2],
            type: bp1.type
          })
        }
      }
      else {
        for (var i = 0; i < book.page_list.length; i++) {
          var bp1 = book.page_list[i]
          var bp2 = book.page_list[i+1]

          var cname = i + 2;
          var num = [i];
          var pages = [bp1];
          var type = bp1.type;

          if(bp1.type == 2){
            cname = [i + 2, i + 3].join(' - ');
            num = [i, i+1],
            pages = [bp1, bp2]
            i++;
          }

          list.push({
            cname: cname,
            num: num,
            pages: pages,
            type: type
          })
        }
      }

      /*最后copyright....*/
      if (book.copyright) {
        list.push({
          cname: '版权页',
          num: [book.copyright.num],
          pages: [book.copyright],
          type: book.copyright.type
        })
      }
      if (book.back_cover) { //||window.jpgBook ==1
        list.push({
          cname: '封底',
          num: [book.back_cover.num],
          pages: [book.back_cover],
          type: book.back_cover.type
        })
      }

      this.pageList = list;
      this.setPageStyles(list);
    },

    saveChange() {
      if(!this.altered) return false;
      var indexList = [];
      this.pageList.forEach(function(item, i) {
        if(!isNaN(item.num[0])){
          indexList.push(item.num)
        }
      });

      console.log(indexList)
      var oldPageList = JSON.parse(JSON.stringify(this.$store.state.bookInfo.page_list));
      var newPageList = [];

      for(var i = 0; i < indexList.length; i++){
        for(var j = 0; j < indexList[i].length; j++){
          newPageList.push(oldPageList[indexList[i][j]])
        }
      }
      newPageList.forEach((item, i) => {
        item.num = i
      })

      this.$store.commit("changePageList", newPageList)
      this.$store.commit("setState", {showPageSort: false, isChangePage: true})
    }
  },

  mounted() {
    this.setPageList();
    window.onresize = () => {
      this.setPageStyles(this.pageList)
    }
  }
}
</script>

<style lang="scss">
#pagesort{
  position: absolute;
  top: 105px;
  left: 215px;
  right: 166px;
  bottom: 127px;
  .notice{
    color: black;
    font-size: 14px;
    line-height: 14px;
    -webkit-text-shadow: 0 0 1px #aaa;
    text-shadow: 0 0 1px #aaa;
    padding: 20px;
    float: left;
    input{
      vertical-align: top;
      line-height: 1;
      margin-right: 2px;
    }
  }
  #pagesort-buttonarea{
    position: absolute;
    top: 10px;
    right: 20px;
    button{
      height: 28px;
      /* width: 70px; */
      float: left;
      margin-left: 8px;
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      background: #d2523e;
      border: none;
      border-bottom: 2px solid #ac4030;
      outline: none;
      border-radius: 5px;
      color: white;
      padding: 0 10px 0 10px;
      &.btn_color_01{
        background: #B3B0B0;
        border-bottom: 2px solid #939090;
      }
      &:active{
        opacity: .7;
        border-bottom: none;
      }
    }
  }
  #pagesort-page_list_wrapper{
    position: absolute;
    overflow-y: scroll;
    overflow-x: hidden;
    bottom: 0;
    left: 0;
    right: 0;
    top: 50px;
    ul{
      position: relative;
      padding-bottom: 20px;
      li{
        position: absolute;
        cursor: move;
        cursor: -webkit-grab;
        margin: 20px 20px 0 20px;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
        -webkit-backface-visibility: hidden;
        -ms-backface-visibility: hidden;
        -moz-backface-visibility: hidden;
        backface-visibility: hidden;
        .list_page-item-preview{
          width: 70px;
          height: 35px;
          -moz-box-sizing: border-box;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          background: white;
          -webkit-box-shadow: 0 1px 4px #999;
          box-shadow: 0 1px 4px #999;
          margin: 0 auto 3px;
          position: relative;
          .page-thumb-container{
            float: left;
            height: 100%;
            width: 50%;
            position: relative;
            z-index: 1;
            border: 0 solid transparent;
            pointer-events: none;
          }
          &.towpage:after{
            content: '';
            width: 1px;
            background: #ccc;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            -webkit-box-shadow: 0 0 1px #ccc;
            box-shadow: 0 0 1px #ccc;
            z-index: 1;
          }
        }
        &.disable{
          opacity: .6;
          pointer-events: none;
        }
        &:active{
          cursor: -webkit-grabbing;
          opacity: 0.8;
          z-index: 1000;
        }
        &:after{
          content: attr(txt);
          display: block;
          text-align: center;
          line-height: 17px;
          width: 100%;
          color: #8c8c8c;
        }
      }
    }
  }
}
.focus-item{
  position: absolute;
  margin-left: 20px;
  .focus-container{
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    background: #fff;
    -webkit-box-shadow: 0 1px 4px #999;
    box-shadow: 0 1px 4px #999;
    margin: 0 auto 3px;
    position: relative;
    opacity: .6;
    transform: scale(.9);
    .page-thumb-container{
      float: left;
    }
  }
  &:after{
    content: attr(txt);
    text-align: center;
    width: 100%;
    opacity: .2;
    display: block;
  }
  &:before{
    content: ' ';
    position: absolute;
    width: 100%;
    height: 70px;
    top: 0;
    left: 0;
    z-index: 2;
  }
}
.insertLine{
  position: absolute;
  width: 4px;
  height: 110px;
  left: 500px;
  background: #489fd9;
}
</style>



