<template>
  <div class="mini_header">
    <div class="wrapper">
      <div class="logo">
        <img src="../../assets/common/image/mini_logo.png" />
      </div>
      <div class="nav">
        <span>{{$t('header.nav')}}：</span>
        <a href="/" target="_blank">{{$t('header.home')}}</a>
        <a href="/product/book" target="_blank">{{$t('header.printIntro')}}</a>
        <a href="/show" target="_blank">{{$t('header.exhibitionHall')}}</a>
        <a href="/usercenter" target="_blank">{{$t('header.myProduct')}}</a>
        <a href="/about/help" target="_blank">{{$t('header.help')}}</a>
        <a @click="showLang = !showLang">{{$t('header.lang')}}</a>
      </div>
      <span id="bookname" title="标题" style="display: block;">
        <span v-text="$store.state.bookInfo.name"></span>
      </span>
      <div id="lang" v-if="showLang">
        <ul>
          <li @click="changeLang('zh-cn')">{{$t('header.langType.cn')}}</li>
          <li @click="changeLang('zh-hk')">{{$t('header.langType.hk')}}</li>
          <li @click="changeLang('en-us')">{{$t('header.langType.us')}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'header',
  data() {
    return {
      showLang: false
    }
  },
  components: {

  },

  methods: {
    changeLang(type) {
      this.$i18n.locale = type;
    }
  },
}
</script>

<style lang="scss">
.mini_header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1005;
  width: 100%;
  background-color: #4d1e24;
  height: 40px;
  box-shadow: 0 0 2px black;
  position: relative;

  .wrapper {
    // width: 95%;
    margin: 0 auto;
    position: relative;
    .logo {
      position: absolute;
      top: 5px;
      left: 20px;
    }
    .nav {
      // float: right;
      position: absolute;
      right: 20px;
      font-family: 'Microsoft YaHei';
      span, a {
        display: block;
        float: left;
        height: 40px;
        line-height: 40px;
      }
      span {
        color: #8e6659;
      }
      a {
        color: #dec7bf;
        padding: 0 10px;
        text-decoration: none;
        cursor: pointer;
        &:hover {
          background: #683239;
        }
      }
    }
    #bookname {
      position: absolute;
      color: #dec7bf;
      top: 0;
      left: 180px;
      z-index: 1;
      font-size: 15px;
      height: 40px;
      line-height: 40px;
      cursor: pointer;
      &:before {
        content: '《';
        vertical-align: top;
      }
      &:after {
        content: '》';
        vertical-align: top;
      }
      &:hover {
        background: #683239;
      }
    }
  }
}

#lang {
  position: absolute;
  top: 40px;
  right: 20px;
  background: #8e5e64;
  border-radius: 0 0 3px 3px;

  >ul {

    >li {
      width: 80px;
      height: 24px;
      line-height: 24px;
      text-align: center;
      color: #dec7bf;
      cursor: pointer;

      &:hover {
        color: #dec7bf;
        background: #683239;
      }

      &:last-child:hover {
        border-radius: 0 0 3px 3px;
      }
    }
  }
}
</style>



