<template>
	<transition name="jumpIn">
    <div id="toolbar-select-align" class="toolbar-select" @click.stop>
      <div id="toolbar-select-align-body" class="toolbar-select-body">
        <p>{{$t('align.text')}}</p>
        <ul>
          <li v-on:click.stop="align('left')" class="align-item" id="align-left" ypoint="4px">{{$t('align.left')}}</li>
          <li v-on:click.stop="align('center')" class="align-item" id="align-center" ypoint="-24px">{{$t('align.center')}}</li>
          <li v-on:click.stop="align('right')" class="align-item" id="align-right" ypoint="-52px">{{$t('align.right')}}</li>
          <li v-on:click.stop="align('top')" class="align-item" id="align-top" ypoint="-81px">{{$t('align.top')}}</li>
          <li v-on:click.stop="align('middle')" class="align-item" id="align-ju" ypoint="-110px">{{$t('align.middle')}}</li>
          <li v-on:click.stop="align('bottom')" class="align-item" id="align-bottom" ypoint="-137px">{{$t('align.bottom')}}</li>
        </ul>
        <div></div>
        <ul>
          <li v-on:click.stop="align('sameHeight')" class="align-item" id="align-sameHeight" ypoint="-222px">{{$t('align.sameHeight')}}</li>
          <li v-on:click.stop="align('sameWidth')" class="align-item" id="align-sameWidth" ypoint="-249px">{{$t('align.sameWidth')}}</li>
          <li v-on:click.stop="align('sameSize')" class="align-item" id="align-sameSize" ypoint="-278px">{{$t('align.sameSize')}}</li>
        </ul>
      </div>
    </div>
	</transition>
</template>

<script>
export default {
  name: 'align',
  data() {
    return {

    }
  },
  methods: {
    getBoxType (name) {
      var result
      if (/decora/i.test(name)) {
        result = 'decoration'
      } else if (/shape/i.test(name)) {
        result = 'colorbox'
      } else if (/img/i.test(name)) {
        result = 'imagebox'
      } else if (/txt/i.test(name)) {
        result = 'textbox'
      } else if (/pattern/i.test(name)) {
        result = 'patternbox'
      } else if (/shading/i.test(name)) {
        result = 'shadingbox'
      }
      return result;
    },
    align (type) {
      var vm = this;
      var multSelected  = this.$store.state.multipleSelected;
      if (multSelected.list.length >= 2) {
        var firstBox = multSelected.list[0];

        for (var i = 1; i < multSelected.list.length; i++) {
          var box = multSelected.list[i];

          var data= {};
          switch (type) {
            case 'left':
              data.x = parseFloat(firstBox.x);
            break;
            case 'top':
              data.y = parseFloat(firstBox.y);
            break;
            case 'right':
              data.x = parseFloat(firstBox.x) + parseFloat(firstBox.width) - parseFloat(box.width);
            break;
            case 'bottom':
              data.y = parseFloat(firstBox.y) + parseFloat(firstBox.height) - parseFloat(box.height);
            break;;
            case 'center':
              data.x = parseFloat(firstBox.x) + parseFloat(firstBox.width) / 2 - parseFloat(box.width) / 2;
            break;
            case 'middle':
              data.y = parseFloat(firstBox.y) + parseFloat(firstBox.height) / 2 - parseFloat(box.height) / 2;
            break;
            case 'sameHeight':
              data.height = parseFloat(firstBox.height);
            break;
            case 'sameWidth':
              data.width = parseFloat(firstBox.width);
            break;
            case 'sameSize':
              data.height = parseFloat(firstBox.height);
              data.width = parseFloat(firstBox.width);
            break;
          }

          this.$store.commit("changeBoxData", {
            pageNum: multSelected.pageNum,
            boxType: vm.getBoxType(box.name),
            boxId: box.box_id,
            data: data
          });
        }

         this.$store.dispatch("animation")
      }
    }
  }
}
</script>

<style lang="scss">
  #toolbar-select-align-body{
    #align-left {
      background-position: 0 4px;
    }
    #align-center {
      background-position: 0 -24px;
    }
    #align-right {
      background-position: 0 -52px;
    }
    #align-top {
      background-position: 0 -81px;
    }
    #align-ju {
      background-position: 0 -110px;
    }
    #align-bottom {
      background-position: 0 -137px;
    }
    #align-sameHeight {
      background-position: 0 -222px;
    }
    #align-sameWidth {
      background-position: 0 -249px;
    }
    #align-sameSize {
      background-position: 0 -278px;
    }
  }
  .toolbar-select {
    position: absolute;
    background: #ededed;
    z-index: 1000;
    box-shadow: 0 1px 6px #666;
    top: 0;
    left: 0;
    transform-origin: center top;
    backface-visibility: hidden;
  }

  .toolbar-select:before {
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    background: inherit;
    box-shadow: 0 1px 6px #666;
    transform: scaleX(0.8) rotate(45deg);
    top: -4px;
    left: 0;
    right: 0;
    margin: auto;
  }

  .toolbar-select-shading {
    margin-left: -270px;
    background: #FFF;
  }

  .toolbar-select-shading:before {
    margin-left: 35px;
  }

  .toolbar-select-body {
    background: inherit;
    position: relative;
    padding: 10px;
  }

  #toolbar-select-align-body {
    width: 124px;
    height: 275px;
  }

  #toolbar-select-align-body>p {
    background-color: #fffde4;
    padding: 5px;
    border: 1px solid #eedf9e;
    border-radius: 3px;
    margin: -5px -5px 0 -5px;
    font-size: 12px;
    text-align: center;
  }

  #toolbar-select-align-body ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  #toolbar-select-align-body li {
    box-sizing: border-box;
    height: 27px;
    line-height: 27px;
    width: 100%;
    background: url(../../assets/book/image/topToolbar/align.png) 0 0 no-repeat scroll transparent;
    color: #474747;
    padding-left: 26px;
    font-family: '宋体';
    cursor: pointer;
  }

  #toolbar-select-align-body div {
    margin-top: 5px;
    margin-bottom: 5px;
    width: 100%;
    height: 2px;
    box-sizing: border-box;
    border-bottom: 1px solid #f6f6f6;
    border-top: 1px solid #dddddd;
  }

  #toolbar-select-align-body li:hover {
    background-color: #D9E2F0;
  }

  #toolbar-select-align {
    transform-origin: 20px 0;
    z-index: 1001;
  }

  #toolbar-select-align:before {
    margin-left: 20px;
  }
</style>



