<template>
  <div class="page-thumb-container" :style="{width: size.w, height: size.h}">
    <canvas class="page-thumb-canvas"> </canvas>
  </div>
</template>
<script>
if (!CanvasRenderingContext2D.prototype.roundRect) {
  CanvasRenderingContext2D.prototype.roundRect = function(
    x,
    y,
    width,
    height,
    tl,
    tr,
    br,
    bl
  ) {
    var x1,
      x2,
      x3,
      x4,
      y1,
      y2,
      y3,
      y4,
      radii,
      ratio = 0,
      CURVE2KAPPA = 0.5522847498307934;
    ratio = Math.min(
      Math.min(width / (tl + tr), width / (br + bl)),
      Math.min(height / (tl + bl), height / (tr + br))
    );
    if (ratio > 0 && ratio < 1) {
      tl *= ratio;
      tr *= ratio;
      bl *= ratio;
      br *= ratio;
    }
    var xw = x + width;
    var yh = y + height;
    x1 = x + tl;
    x2 = xw - tr;
    x3 = xw - br;
    x4 = x + bl;
    y1 = y + tr;
    y2 = yh - br;
    y3 = yh - bl;
    y4 = y + tl;
    this.beginPath();
    this.moveTo(x1, y);
    this.lineTo(x2, y);
    radii = CURVE2KAPPA * tr;
    this.bezierCurveTo(x2 + radii, y, xw, y1 - radii, xw, y1);
    this.lineTo(xw, y2);
    radii = CURVE2KAPPA * br;
    this.bezierCurveTo(xw, y2 + radii, x3 + radii, yh, x3, yh);
    this.lineTo(x4, yh);
    radii = CURVE2KAPPA * bl;
    this.bezierCurveTo(x4 - radii, yh, x, y3 + radii, x, y3);
    this.lineTo(x, y4);
    radii = CURVE2KAPPA * tl;
    this.bezierCurveTo(x, y4 - radii, x1 - radii, y, x1, y);
  };
}
export default {
  name: "pageThumb",
  props: {
    template: {
      type: Object,
      default() {
        return {};
      }
    },
    size: {
      type: Object,
      default() {
        return {
          w: "100%",
          h: "100%"
        };
      }
    }
  },
  data() {
    return {
      cacheCanvas: null,
      dragTimer: null
    };
  },
  watch: {
    template: {
      handler() {
        //拖动、长按时不绘制
        var that = this;
        if (this.$store.state.isMove || this.$store.state.isKeyDownDire || this.$store.state.isZoom) {
          clearTimeout(this.dragTimer);
          this.dragTimer = null;
          this.dragTimer = setTimeout(function() {
            that.drawCanvas();
          }, 300);
        }else{
          that.drawCanvas();
        }
      },
      deep: true
    }
  },
  computed: {},
  methods: {
    getFontnameById(id) {
      var fonts = {
        "001": "方正兰亭黑_GBK",
        "002": "Microsoft YaHei",
        "003": "FangSong"
      };
    },
    generateEditImgDataURL(photo, onSuccess, size) {
      size = size || 800;
      var img = document.createElement("img");
      img.onload = function() {
        var canvas;
        if (this.width >= this.height) {
          canvas = this.scaleImage(this, size * this.width / this.height, size);
        } else {
          canvas = this.scaleImage(this, size, size * this.height / this.width);
        }
        var src = canvas.toDataURL();
        onSuccess(src);
      };
      // img.src =
    },
    scaleImage(img, width, height) {
      var destCanvas = document.createElement("canvas");
      destCanvas.width = width;
      destCanvas.height = height;
      var destContext = destCanvas.getContext("2d");
      var tempCanvas = document.createElement("canvas");
      var tempCanvas2 = document.createElement("canvas");
      var max = 2500;
      if (img.width >= img.height) {
        tempCanvas.width = tempCanvas2.width = destCanvas.width * 3;
        if (tempCanvas.width > max) {
          tempCanvas.width = tempCanvas2.width = max;
        }
        tempCanvas.height = tempCanvas2.height =
          tempCanvas.width * img.height / img.width;
      } else {
        tempCanvas.height = tempCanvas2.height = destCanvas.height * 3;
        if (tempCanvas.height > max) {
          tempCanvas.height = tempCanvas2.height = max;
        }
        tempCanvas.width = tempCanvas2.width =
          tempCanvas.height * img.width / img.height;
      }
      var tempContext = tempCanvas.getContext("2d");
      var tempContext2 = tempCanvas2.getContext("2d");
      tempContext.drawImage(img, 0, 0, tempCanvas.width, tempCanvas.height); //此处缩放三次为双线性插值缩放算法，画质更好
      tempContext2.drawImage(
        tempCanvas,
        0,
        0,
        tempCanvas.width,
        tempCanvas.height,
        0,
        0,
        tempCanvas.width / 2,
        tempCanvas.height / 2
      );
      destContext.drawImage(
        tempCanvas2,
        0,
        0,
        tempCanvas2.width / 2,
        tempCanvas2.height / 2,
        0,
        0,
        destCanvas.width,
        destCanvas.height
      );

      return destCanvas;
    },
    changeMaterialColor(img, color, callback) {
      var canvas = document.createElement("canvas");
      var context = canvas.getContext("2d");
      canvas.width = img.width;
      canvas.height = img.height;
      context.drawImage(img, 0, 0);
      var imagedata = context.getImageData(0, 0, img.width, img.height);
      var _color = this.toRgb(color);
      for (var i = 0, n = imagedata.data.length; i < n; i += 4) {
        imagedata.data[i + 0] = _color[0];
        imagedata.data[i + 1] = _color[1];
        imagedata.data[i + 2] = _color[2];
      }
      context.putImageData(imagedata, 0, 0);
      callback(canvas);
      context = null;
      canvas = null;
    },
    toRgb(color) {
      var reg = /^#([0-9a-fA-F]{3}|[0-9a-fA-F]{6})$/;
      var sColor = color.toLowerCase();
      if (sColor && reg.test(sColor)) {
        if (sColor.length === 4) {
          var sColorNew = "#";
          for (var i = 1; i < 4; i += 1) {
            sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
          }
          sColor = sColorNew;
        }
        var sColorChange = [];
        for (var i = 1; i < 7; i += 2) {
          sColorChange.push(parseInt("0x" + sColor.slice(i, i + 2)));
        }
        return sColorChange;
      } else {
        return sColor;
      }
    },
    drawImgbox(context, box, photo, onDone) {
      var vm = this;
      var border_width = this.ptToPx(box.border_width || 0);
      var border_color = box.border_color || "#ffffff";
      var width = this.mmToPx(box.width);
      var height = this.mmToPx(box.height);
      var x = this.mmToPx(box.x);
      var y = this.mmToPx(box.y);
      var rotation = box.rotation;
      var image = null;
      if (box.image) {
        image = {
          x: this.mmToPx(box.image.x),
          y: this.mmToPx(box.image.y),
          width: this.mmToPx(box.image.width),
          rotation: box.image.rotation,
          id: box.image.id,
          src: box.image.src
        };
      }
      var themeImage = null;
      if (box.themeImage) {
        themeImage = {
          x: this.mmToPx(box.themeImage.x),
          y: this.mmToPx(box.themeImage.y),
          width: this.mmToPx(box.themeImage.width),
          rotation: box.themeImage.rotation,
          url: box.themeImage.url
        };
      }
      var draw = function(func) {
        context.save();
        context.translate(
          x + border_width + width / 2,
          y + border_width + height / 2
        );
        context.rotate(rotation * Math.PI / 180);
        context.translate(-width / 2, -height / 2);
        context.strokeStyle = border_color;
        context.lineWidth = border_width;
        context.strokeRect(
          -border_width / 2,
          -border_width / 2,
          width + border_width,
          height + border_width
        );
        context.beginPath();
        context.rect(0, 0, width, height);
        func();
        context.restore();
      };

      if ((!image || (!photo && image.id != "logo")) && !themeImage) {
        draw(function() {
          context.fillStyle = "#a1a1a1";
          context.fill();
        });
        if (typeof onDone === "function") {
          onDone();
        }
      } else {
        var img = document.createElement("img");
        img.crossOrigin = "*";
        img.onload = function() {
          draw(function() {
            var _image = themeImage ? themeImage : image;
            context.clip();
            context.translate(
              _image.x + _image.width / 2,
              _image.y + _image.width / 2 * img.height / img.width
            );
            var scale = _image.width / img.width;
            context.scale(scale, scale);
            context.rotate(_image.rotation * Math.PI / 180);
            context.translate(
              -img.width / 2,
              -img.width / 2 * img.height / img.width
            );
            context.drawImage(img, 0, 0);
          });
          img = null;
          if (typeof onDone == "function") {
            onDone();
          }
        };
        img.onerror = function() {
          img = null;
          if (typeof onDone == "function") {
            onDone();
          }
        };
        if (photo) {
          if (photo.src) {
            img.src = this.CommonMethods.joinPhotoHost(photo.src, "_thumb");
          } else {
            //这里暂缺
            // vm.generateEditImgDataURL(photo, function (e) {
            //   img.src = e.dataURL;
            // })
            if (typeof onDone == "function") {
              onDone();
            }
          }
        } else if (themeImage) {
          if (themeImage.url) {
            img.src = this.CommonMethods.joinPhotoHost(photo.src, "_thumb");
          }
        } else if (image.id == "logo") {
          img.src = image.src;
        }
      }
    },
    drawTextbox(context, box, onDone) {
      var width = this.mmToPx(box.width);
      var height = this.mmToPx(box.height);
      var x = this.mmToPx(box.x);
      var y = this.mmToPx(box.y);
      var content = box.content;
      var rotation = box.rotation;
      var font_id = box.font_id;
      var px = this.ptToPx(box.pt);
      var color = box.color || "#000";
      var align = box.align;
      var lineheight =
        box.leading < 0 ? this.ptToPx(box.pt) * 1.2 : this.ptToPx(box.leading);
      var letterSpacing = box.space / 1000 * this.ptToPx(box.pt);

      context.save();
      context.font =
        px + "px " + this.getFontnameById(font_id) + ",font" + font_id;
      context.fillStyle = color;
      context.textAlign = align;
      context.textBaseline = "middle";
      context.translate(x + width / 2, y + height / 2);
      context.rotate(rotation * Math.PI / 180);
      context.translate(-width / 2, -height / 2);
      context.beginPath();
      context.rect(0, 0, width, height);
      if (!content || content.length === 0) {
        context.fillStyle = "#ccc";
        context.fill();
      } else {
        context.clip();
        context.translate(0, lineheight / 2);
        switch (context.textAlign) {
          case "center":
            context.translate(width / 2, 0);
            break;
          case "right":
            context.translate(width, 0);
            break;
        }
        var regex = /([^\x00-\xff])|([\x00-\xff]+|\n)/g; //匹配每一个word（规则与页面相同，一个全角字符为一个word，连续的半角字符或单个换行符为一个word）
        var words =
          content !== undefined && regex.test(content)
            ? content.match(regex)
            : [];
        var lineText = "",
          line = 0,
          lineWordIndex = 0;
        for (var i = 0, len = words.length; i < len; i++) {
          var word = words[i];
          if (
            word !== "\n" &&
            context.measureText(lineText + word).width +
              (lineText + word).length ===
              1
          ) {
            lineText += word; //不换行
            lineWordIndex++;
            if (i < len - 1) {
              continue;
            }
          } else if (
            context.measureText(lineText + word).width +
              (lineText + word).length * letterSpacing >
            width
          ) {
            if (lineWordIndex === 0 && /^[\x00-\xff]+$/.test(word)) {
              var letters = ""; //超过文本框宽度的一行的完全的连续半角字符，折断
              for (var j = 0; j < word.length; j++) {
                var letter = word.charAt(j);
                if (
                  context.measureText(letters + letter).width +
                    (letters + letter).length * letterSpacing <=
                    width ||
                  (letters + letter).length === 1
                ) {
                  letters += letter;
                } else {
                  lineText = letters;
                  words[i] = word.substring(j);
                  break;
                }
              }
            }
            if (word !== "\n" && len != 1) {
              i--;
            }
          }

          var startX = 0; //绘制文字
          var lineWidth =
            context.measureText(lineText).width +
            letterSpacing * lineText.length;
          switch (context.textAlign) {
            case "center":
              startX = -lineWidth / 2;
              break;
            case "right":
              startX = -lineWidth;
              break;
          }

          for (j = 0; j < lineText.length; j++) {
            var subStr = lineText.substring(0, j);
            context.fillText(
              lineText[j],
              startX +
                context.measureText(subStr).width +
                letterSpacing * subStr.length,
              0
            );
          }

          lineText = "";
          lineWordIndex = 0;
          context.translate(0, lineheight);
          line++;
        }
      }
      context.restore();

      if (typeof onDone == "function") {
        onDone();
      }
    },
    drawDecoraBox(context, box, onDone) {
      var vm = this;
      var border_width = 0;
      var border_color = "#ffffff";
      var width = this.mmToPx(box.width);
      var height = this.mmToPx(box.height);
      var x = this.mmToPx(box.x);
      var y = this.mmToPx(box.y);
      var rotation = box.rotation;
      var src = box.src;
      var color = box.color;
      var type = box.type;
      var timestamp = box.timestamp;

      var draw = function() {
        context.save();
        context.beginPath();
        context.rect(x, y, width, height * 2);

        context.clip();
        context.translate(
          x + width / 2,
          y + width / 2 * img.height / img.width
        );
        var scale = width / img.width;
        context.scale(scale, scale);
        context.rotate(rotation * Math.PI / 180);
        context.translate(
          -img.width / 2,
          -img.width / 2 * img.height / img.width
        );
        if (type == 2 || type == 3) {
          //可变色的
          vm.changeMaterialColor(img, color, function(canvas) {
            context.drawImage(canvas, 0, 0);
          });
        } else {
          context.drawImage(img, 0, 0);
        }

        context.restore();

        if (typeof onDone == "function") {
          onDone();
        }
      };

      var img = document.createElement("img");
      img.crossOrigin = "*";
      img.src = "http://cdn-mimocampaign.mimoprint.com" + src.replace(/Theme/, "theme") + "@!w500";

      if (img.complete) {
        // console.log('照片已经存在')
        draw();
      }else{
        img.onload = function() {
          // console.log('加载照片')
          draw();
        };

        img.onerror = function() {
          img = null;
          if (typeof onDone == "function") {
            onDone();
          }
        };
      }
    },
    drawShapebox(context, box, onDone) {
      var border_width = box.border_width;
      var opacity = box.opacity;
      var color = box.color;
      var border_color = box.border_color;
      var width = this.mmToPx(box.width);
      var height = this.mmToPx(box.height);
      var x = this.mmToPx(box.x);
      var y = this.mmToPx(box.y);
      var rotation = box.rotation;

      var draw = function(func) {
        context.save();

        context.translate(
          x + border_width + width / 2,
          y + border_width + height / 2
        );
        context.rotate(rotation * Math.PI / 180);
        context.translate(-width / 2, -height / 2);
        context.roundRect(0, 0, width, height, 5, 5, 5, 5);
        context.strokeStyle = border_color;
        context.lineWidth = border_width;
        context.strokeRect(
          -border_width / 2,
          -border_width / 2,
          width + border_width,
          height + border_width
        );
        context.beginPath();
        context.globalAlpha = opacity;
        context.rect(0, 0, width, height);
        func();
        context.restore();
      };
      draw(function() {
        context.fillStyle = color;
        context.fill();
      });
      if (typeof onDone == "function") {
        onDone();
      }
    },
    drawShadingbox(context, box, onDone) {
      var id = box.id;
      var editUrl = box.editUrl;
      var width = this.mmToPx(box.width);
      var height = this.mmToPx(box.height);
      var x = this.mmToPx(box.x);
      var y = this.mmToPx(box.y);
      var rotation = box.rotation;
      var imgWidth = this.mmToPx(box.imgWidth);
      editUrl =
        "http://cdn-mimocampaign.mimoprint.com" +
        editUrl.replace(/Theme/, "theme").replace(/preview/, "image") +
        "@!w500";

      var img = document.createElement("img");
      img.crossOrigin = "*";
      img.onload = function() {
        context.save();

        context.translate(x + width / 2, y + height / 2);
        context.rotate(rotation * Math.PI / 180);
        context.translate(-width / 2, -height / 2);
        context.beginPath();
        context.rect(0, 0, width, height);
        context.clip();
        context.drawImage(
          img,
          0,
          0,
          imgWidth,
          imgWidth * this.height / this.width
        );

        context.restore();
        img = null;
        if (typeof onDone == "function") {
          onDone();
        }
      };
      img.onerror = function() {
        img = null;
        if (typeof onDone == "function") {
          onDone();
        }
      };
      img.src = editUrl;
    },
    drawPatternbox(context, box, onDone) {
      var id = box.id;
      var src = box.src;
      var width = this.mmToPx(box.width);
      var height = this.mmToPx(box.height);
      var x = this.mmToPx(box.x);
      var y = this.mmToPx(box.y);
      var rotation = box.rotation;
      var imgWidth = this.mmToPx(box.imgWidth);
      var timestamp = box.timestamp;

      var draw = function(){
        context.save();
        context.translate(x + width / 2, y + height / 2);
        context.rotate(rotation * Math.PI / 180);
        context.beginPath();
        context.rect(0, 0, width, height);
        context.clip();
        context.drawImage(
          img,
          0,
          0,
          imgWidth,
          imgWidth * img.height / img.width
        );
        context.restore();
        img = null;
        if (typeof onDone == "function") {
          onDone();
        }
      }

      var img = document.createElement("img");
      img.crossOrigin = "*";
      img.src = "http://cdn-mimocampaign.mimoprint.com" + src + "@!w500";
      if (img.complete) {
        // console.log('照片已经存在')
        draw();
      }else{
        img.onload = function() {
          // console.log('加载照片')
          draw();
        };

        img.onerror = function() {
          img = null;
          if (typeof onDone == "function") {
            onDone();
          }
        };
      }
    },
    drawCanvas() {
      var vm = this;
      var template = this.template;
      if ("width" in template) {
        var canvas = this.$el.querySelector("canvas");
        if (!vm.cacheCanvas) {
          vm.cacheCanvas = document.createElement("canvas");
        }

        vm.cacheCanvas.width = this.mmToPx(template.width);
        vm.cacheCanvas.height = this.mmToPx(template.height);

        var context = vm.cacheCanvas.getContext("2d");
        context.fillStyle = template.background_color;
        context.fillRect(0, 0, vm.cacheCanvas.width, vm.cacheCanvas.height);
        //|| [] 容错，避免list是null导致报错
        var boxes = []
          .concat(template.imagebox_list || [])
          .concat(template.textbox_list || [])
          .concat(template.decoration_list || [])
          .concat(template.colorbox_list || [])
          .concat(template.shadingbox_list || [])
          .concat(template.patternbox_list || []);

        boxes.sort(function(a, b) {
          return a.index - b.index;
        });

        var photoKeyValues = {},
          photos = vm.$store.state.photos;
        for (var i = 0; i < photos.length; i++) {
          photoKeyValues[photos[i].id] = photos[i];
        }

        var curIndex = 0;

        var judge = function() {
          if (curIndex < boxes.length) {
            step();
          } else {
            var w, h;
            if (/[%]/.test(vm.size.w) || /[%]/.test(vm.size.h)) {
              w = parseFloat(canvas.parentElement.style.width);
              h = parseFloat(canvas.parentElement.style.height);
            } else {
              w = parseFloat(vm.size.w);
              h = parseFloat(vm.size.h);
            }

            var drawPannel = function(_canvas){
              var resultCanvas = vm.scaleImage(_canvas, w, h);
              canvas.width = w;
              canvas.height = h;
              canvas.getContext("2d").drawImage(resultCanvas, 0, 0);
            }

            var info = vm.Config.productInfo[vm.Config.productId];

            //封面额外处理
            if(template.type == 3 && info.noCover){
              //封面 && 无封面
              cover(false, function(_canvas){
                drawPannel(_canvas);
              });
            }else if(template.type == 3 && info.specialCover){
              //封面 && 特殊封面
              cover(true, function(_canvas){
                drawPannel(_canvas);
              });
            }else {
              drawPannel(vm.cacheCanvas);
            }
          }
        };

        var step = function() {
          let box = boxes[curIndex];
          if (box.name.indexOf("img") >= 0) {
            //图片框
            if (template.num === "copyright") {
              if (
                ["black", "#000000", "#000"].some(function(color) {
                  return color === template.background_color;
                })
              ) {
                if (box.name === "imglogo") {
                  curIndex++;
                  judge();
                  return;
                }
              } else {
                if (box.name === "imglogoblackbg") {
                  curIndex++;
                  judge();
                  return;
                }
              }
            }
            vm.drawImgbox(context, box, box.image ? photoKeyValues[box.image.id] : null, function() {
              curIndex++;
              judge();
            });
          } else if (box.name.indexOf("txt") >= 0) {
            vm.drawTextbox(context, box, function() {
              curIndex++;
              judge();
            });
          } else if (box.name.indexOf("decora") >= 0) {
            vm.drawDecoraBox(context, box, function() {
              curIndex++;
              judge();
            });
          } else if (box.name.indexOf("shape") >= 0) {
            vm.drawShapebox(context, box, function() {
              curIndex++;
              judge();
            });
          } else if (box.name.indexOf("shading") >= 0) {
            vm.drawShadingbox(context, box, function() {
              curIndex++;
              judge();
            });
          } else if (box.name.indexOf("pattern") >= 0) {
            vm.drawPatternbox(context, box, function() {
              curIndex++;
              judge();
            });
          } else {
            curIndex++;
            judge();
          }
        };

        var cover = function(type, callback){
          var img = new Image();
          img.onload = function(){
            var canvas = document.createElement('canvas');
            var cxt = canvas.getContext('2d');

            var info = vm.Config.specialCoverInfo[vm.Config.productId];

            canvas.width = info.coverWidth;
            canvas.height = info.coverHeight;

            type && cxt.drawImage(vm.cacheCanvas, -info.coverLeft, -info.coverTop);
            cxt.drawImage(img, 0, 0, info.coverWidth, info.coverHeight);

            callback(canvas, info.coverWidth / info.coverHeight * 35, 35);
          }
          img.src = 'http://www.mimoprint.com/static/img/editorhtml5/series/b' + vm.Config.productId + '/book_series_b' + vm.Config.productId + '_' + vm.$store.state.coverType + '_' + vm.$store.state.coverColor + '.png'
        }

        judge();
      }
    }
  },
  mounted() {
    this.drawCanvas();
  }
};
</script>
<style>
.page-thumb-container {
  position: relative;
}
.page-thumb-canvas {
  width: 100%;
  height: 100%;
}
</style>





