import Vue from 'vue'
import App from './book.vue'
import router from './router/book.js'
import store from './store/book/index.js'

import VueI18n from 'vue-i18n'
import Pretreatment from '@/components/book/pretreatment2'
import Common from '@/config/common.vue'
import Config from '@/config/bookconfig.vue'
import Drag from '@/components/common/drag'
import Select from '@/components/common/select'
import EditCanvas from '@/components/common/editCanvas'
import PhotoAction from '@/components/common/photoAction'
import SizeConverter from '@/components/common/sizeConverter'
import ChangeBookData from '@/components/common/changeBookData'
import ZIndex from '@/components/common/zindex'
import UploadFiles from '@/components/common/uploadFiles'
import KeyEvents from '@/components/common/keyEvents'

import './assets/common/css/common.css'
import './assets/common/css/iconfont.css'
import './assets/book/css/common.css'
import './assets/book/css/fontFile.css'


Vue.config.productionTip = false;

Vue.use(Pretreatment, function (args) {
  store.commit('setState', args.state);
  Vue.use(Common, args.commonConfig);
  Vue.use(Config, args.bookConfig);
  Vue.use(Drag);
  Vue.use(Select);
  Vue.use(PhotoAction);
  Vue.use(SizeConverter, 108);
  Vue.use(ChangeBookData);
  Vue.use(ZIndex);
  Vue.use(EditCanvas, Vue.prototype.Config.editAreaScale);
  Vue.use(UploadFiles, {
    workId: Vue.prototype.Config.workId,
    userId: Vue.prototype.Config.workUserId,
    whereFrom: Vue.prototype.Config.whereFrom
  })
  Vue.use(VueI18n);
  Vue.use(KeyEvents);

  var i18n = new VueI18n({
    locale: Vue.prototype.Config.lang,    // 语言标识
    messages: {
      'zh-cn': require('@/assets/book/script/zh-cn'),   // 简体中文语言包
      'zh-hk': require('@/assets/book/script/zh-hk'),   // 繁体中文语言包
      'en-us': require('@/assets/book/script/en-us')    // 英文语言包
    }
  })

  new Vue({
    el: '#app',
    router,
    store,
    i18n,
    template: '<App/>',
    components: { App }
  })
});



