<template>
    <div id="edit-pagetool" ref="pagetool" @click.stop>
      <div id="edit-boxtool-body" v-if="boxData && multipleSelected.list.length < 2" :key="boxData.name + boxData.box_id" :style="{left: editBodyLeft}">
        <span
          :data-title="$t('boxToolbar.locked')"
          v-if="boxData.name != 'pattern' && !isSpecialCover"
          class="edit-boxtool-button edit-button-lock"
          :class="{'lock': boxData.locked, 'unlock': !boxData.locked, 'blink': $store.state.isLockedDrag}"
          @click="changeLock">
          <span>123123</span>
        </span>

        <template v-if="boxData.image && boxData.image != null">
          <span class="edit-boxtool-button edit-button-zoomin" :data-title="$t('boxToolbar.zoomin')" @mousedown="imgZoom(10)" @mouseout="clearTimer()" @mouseup="clearTimer()"></span>
          <span class="edit-boxtool-button edit-button-zoomout" :data-title="$t('boxToolbar.zoomout')" @mousedown="imgZoom(-10)" @mouseout="clearTimer()" @mouseup="clearTimer()"></span>
          <span class="edit-boxtool-button edit-button-adjust" :data-title="$t('boxToolbar.adjust')" @click="imgAdJust()"></span>
          <span class="edit-boxtool-button edit-button-imgfill" :data-title="$t('boxToolbar.imgfill')" @click="imgFill()"></span>
          <span class="edit-boxtool-button edit-button-antirotatephoto" :data-title="$t('boxToolbar.antirotatephoto')" @click="imgAntiRotate()"></span>
          <span class="edit-boxtool-button edit-button-rotatephoto" :data-title="$t('boxToolbar.rotatephoto')" @click="imgRotate()"></span>
        </template>

        <template v-if="boxType != 'textbox' && boxType != 'decoration' && boxType != 'patternbox'  && !isSpecialCover">
          <span
            class="edit-boxtool-button edit-button-borderwidth"
            id="edit-button-borderwidth"
            :data-title="$t('boxToolbar.borderwidth')"
            @click.stop="showPannel($event, 'borderWidth')">
          </span>
          <span
            class="edit-boxtool-button edit-button-bordercolor"
            id="edit-button-bordercolor"
            :data-title="$t('boxToolbar.bordercolor')"
            @click.stop="showPannel($event, 'borderColor')">
            <span class="color" :style="{'background-color': boxData.border_color || '#fff'}"></span>
          </span>
        </template>

        <span
          v-if="boxType == 'colorbox' || (boxType == 'decoration' && boxData.type  &&  boxData.type != 1)"
          class="edit-boxtool-button edit-button-bgcolor"
          id="edit-button-bgcolor"
          :data-title="$t('boxToolbar.bgcolor')"
          @click.stop="showPannel($event, 'bgColor')">
          <span class="color" :style="{'background-color': boxData.color || '#fff'}"></span>
          <span class="assets"></span>
        </span>

        <template v-if="boxType == 'textbox' || boxData.text">
          <span
            class="edit-font-button edit-button-fontfamily"
            id="edit-button-fontfamily"
            :data-title="$t('boxToolbar.fontfamily')"
            :data-text="boxType == 'textbox' ? fontfamilyList[boxData.font_id] : fontfamilyList[boxData.text.font_id]"
            @click.stop="showPannel($event, 'fontfamily')">
          </span>
          <span
            class="edit-font-button edit-button-fontsize"
            id="edit-button-fontsize"
            :data-title="$t('boxToolbar.fontsize')"
            :data-text="boxType == 'textbox' ? boxData.pt: boxData.text.pt +'点'"
            @click.stop="showPannel($event, 'fontsize')">
          </span>
          <span
            class="edit-boxtool-button edit-button-textcolor"
            id="edit-button-textcolor"
            :data-title="$t('boxToolbar.textcolor')"
            @click.stop="showPannel($event, 'textcolor')">
            <span class="color" v-if="boxType == 'textbox'" :style="{'background-color': boxData.color || '#000'}"></span>
            <span class="color" :style="{'background-color': boxData.text.color || '#000'}" v-else></span>
          </span>
          <span
            class="edit-boxtool-button edit-button-leftalign"
            id="edit-button-leftalign"
            :class="{'active': (boxType == 'textbox' && boxData.align == 'left') || (boxData.text && boxData.text.align == 'left')}"
            :data-title="$t('boxToolbar.leftalign')"
            @click="changeAlign('left')">
          </span>
          <span
            class="edit-boxtool-button edit-button-centeralign"
            id="edit-button-centeralign"
            :class="{'active': (boxType == 'textbox' && boxData.align == 'center') || (boxData.text && boxData.text.align == 'center')}"
            :data-title="$t('boxToolbar.centeralign')"
            @click="changeAlign('center')">
          </span>
          <span
            class="edit-boxtool-button edit-button-rightalign"
            id="edit-button-rightalign"
            :class="{'active': (boxType == 'textbox' && boxData.align == 'right') || (boxData.text && boxData.text.align == 'right')}"
            :data-title="$t('boxToolbar.rightalign')"
            @click="changeAlign('right')">
          </span>
          <span
            class="edit-boxtool-button edit-button-letterspace"
            id="edit-button-letterspace"
            :data-title="$t('boxToolbar.letterspace')"
            @click.stop="showPannel($event, 'letterspace')">
          </span>
          <span
            class="edit-boxtool-button edit-button-lineheight"
            id="edit-button-lineheight"
            :data-title="$t('boxToolbar.lineheight')"
            @click.stop="showPannel($event, 'lineheight')">
          </span>
        </template>

        <span
          v-if="boxType == 'colorbox'"
          class="edit-boxtool-button edit-button-opacity"
          id="edit-button-opacity"
          :data-title="$t('boxToolbar.opacity')"
          @click.stop="showPannel($event, 'opacity')">
        </span>

        <span
          v-if="boxData.image && boxData.image != null"
          class="edit-boxtool-button edit-button-find"
          :data-title="$t('boxToolbar.find')"
          @click.stop="findImg()"></span>

        <span
          v-if="boxType != 'patternbox' && !isSpecialCover"
          class="edit-boxtool-button edit-button-props"
          id="edit-button-props"
          :data-title="$t('boxToolbar.props')"
          @click.stop="showPannel($event, 'props')">
        </span>

        <span
          class="edit-boxtool-button edit-button-remove"
          v-if="!isSpecialCover"
          :data-title="$t('boxToolbar.remove')"
          @click="deleteBox">
        </span>

        <div class="click_lock" v-if="$store.state.isLockedDrag">
          <div>点击解锁</div>
          <div class="arrows"></div>
        </div>
      </div>

      <transition name="fadeIn">
        <div
          id="tooltip" ref="tooltip"
          v-if="tipText != ''"
          v-text="tipText"></div>
      </transition>

      <template v-if="boxData">
        <!--边框颜色-->
        <div class="edit_dialog"
          :style="getPannelStyle('#edit-button-bordercolor')"
          v-if="openPannelName == 'borderColor'"
          key="bordercolor"
          @click.stop>
          <ColorPicker @change="changeBorderColor" :colors="{'hex': boxData.border_color}" :themeColors="themeColor"></ColorPicker>
        </div>

        <!--文字颜色-->
        <div class="edit_dialog"
          :style="getPannelStyle('#edit-button-textcolor')"
          v-if="openPannelName == 'textcolor'"
          key="textcolor"
          @click.stop>
          <ColorPicker @change="changeTextColor" :colors="{ 'hex': boxType == 'textbox' ? boxData.color : boxData.text.color }" :themeColors="themeColor"></ColorPicker>
        </div>

        <!--背景颜色-->
        <div class="edit_dialog"
          :style="getPannelStyle('#edit-button-bgcolor')"
          v-if="openPannelName == 'bgColor'"
          key="bgColor"
          @click.stop>
          <ColorPicker @change="changeBgColor" :colors="{'hex': boxData.color }" :themeColors="themeColor"></ColorPicker>
        </div>

        <!--边框大小-->
        <transition name="jumpIn">
          <SelectDialog
            v-if="openPannelName == 'borderWidth'"
            :selected="boxData.border_width"
            :styles="getPannelStyle('#edit-button-borderwidth')"
            width="50"
            data="21"
            textAdd="点"
            type="arr"
            @change="changeBorderWidth"></SelectDialog>
        </transition>

        <!--选择字体-->
        <transition name="jumpIn">
          <SelectDialog
            v-if="openPannelName == 'fontfamily'"
            :selected="boxType == 'textbox' ? boxData.font_id : boxData.text.font_id"
            :styles="getPannelStyle('#edit-button-fontfamily')"
            width="110"
            :data="fontfamilyList"
            type="json"
            @change="changeFontFamily"></SelectDialog>
        </transition>

        <!--字体大小-->
        <transition name="jumpIn">
          <SelectDialog
            v-if="openPannelName == 'fontsize'"
            :selected="boxType == 'textbox' ? boxData.pt : boxData.text.pt"
            :styles="getPannelStyle('#edit-button-fontsize')"
            width="62"
            :data="fontsizeList"
            textAdd="点"
            type="arr"
            @change="changeFontSize"></SelectDialog>
        </transition>

        <!--字距-->
        <transition name="jumpIn">
          <SelectDialog
            v-if="openPannelName == 'letterspace'"
            :styles="getPannelStyle('#edit-button-letterspace')"
            width="50"
            :selected="boxType == 'textbox' ? boxData.space : boxData.text.space"
            :data="letterspaceList"
            type="arr"
            @change="changeLetterSpace"></SelectDialog>
        </transition>

        <!--行高-->
        <transition name="jumpIn">
          <SelectDialog
            v-if="openPannelName == 'lineheight'"
            :selected="boxType == 'textbox' ? (boxData.leading == -1 ? '0' : boxData.leading) : (boxData.text.leading == -1 ? '0' : boxData.text.leading)"
            :styles="getPannelStyle('#edit-button-lineheight')"
            width="50"
            :data="leadingList"
            type="json"
            @change="changeLineHeight"></SelectDialog>
        </transition>

        <!--透明度-->
        <transition name="jumpIn">
          <div
            id="edit-opacity"
            class="set-opacity-info edit_dialog"
            v-if="openPannelName == 'opacity'"
            :style="getPannelStyle('#edit-button-opacity')">
            <ul class="clearfix">
              <li>{{$t('boxToolbar.opacity')}}：</li>
              <li class="opacity-scrollbar" >
                <a class="edit-opacity-scroll-btn" :style="{left: (boxData.opacity) * 148 +'px'}" @mousedown="changeOpacity($event)"></a>
              </li>
              <li class="edit-opacity-info" v-text="boxData.opacity ? ((boxData.opacity * 100).toFixed(0) + '%') : 0"></li>
            </ul>
          </div>
        </transition>

      </template>
    </div>
</template>

<script>
import SelectDialog from '@/components/common/selectDialog'
import imageUnit from '@/components/common/imageUnit'
import ColorPicker from '@/components/common/colorpicker'
export default {
  name: 'editBoxTool',
  props: {
    selected: {
      type: Object,
      default(){
        return {}
      }
    },
    themeColor: {
      type: Array,
      default () {
        return []
      }
    },
    edit: {
      type: Element,
      default(){
        return null
      }
    },
    isSpecialCover: {
      type: Boolean,
      default(){
        return false
      }
    }
  },
  components: {
    ColorPicker,
    SelectDialog
  },
  data() {
    return {
      borderColor: '',
      bgColor: '',
      tipText: '',
      openPannelName: '',
      editBodyLeft: '',
      isOpacity: false,
      letterspaceList: this.Config.letterspaceList,
      leadingList: this.Config.leadingList,
      fontsizeList: this.Config.fontsizeList,
      fontfamilyList: [],
      timer: null, //图片缩放需要的定时器
      timeout: null, //图片缩放需要的定时器
      beforePageData: {},
    }
  },
  watch: {
    boxData() {
      this.$nextTick(()=>{
        this.setEditBodyLeft()
      })
    },
  },
  computed: {
    multipleSelected() {
      return this.$store.state.multipleSelected;
    },
    boxData() {
      return this.selected.data.name ? this.selected.data : '';
    },
    boxType() {
      if(this.selected.pageNum !== undefined){
        var elNowBox = this.selected.el;
        if(elNowBox){
          return elNowBox.getAttribute("data-type");
        }
      }
      return ''
    },
  },
  methods: {
    setFontFamilyList() {
      var list = this.Config.fontfamilyList;
      var theme = this.$store.state.theme;
      var fontInfo = this.Config.fontInfo,
          themeType = fontInfo.themeType;

      for(var i in fontInfo.themeId){
        if(i == theme.id){
          fontInfo.themeId[i].map((item, j) => {
            list[item.value] = item.name;
          })
        }
      }

      for(var i in themeType){
        if(i == theme.type){
          themeType[i].map((item) => {
            list[item.value] = item.name;
          })
        }
      }

      this.fontfamilyList = list;
    },
    //设置按钮组的位置
    setEditBodyLeft() {
      editBodyWidth = parseInt(document.querySelectorAll(".edit-boxtool-button").length * 30)
      if(!this.isOpacity){
        this.openPannelName = '';

        this.$store.commit('setState', {boxToolbar: {
          pannelName: '',
          x: 0,
          y: 0
        }});
      }
      this.tipText = '';
      var boxData = this.boxData;
      if(this.boxType == 'textbox' || boxData.text){ //文本框需要加长度
          editBodyWidth += 188
      }
      //按钮组父级的样式
      var left = 0,
          selected = this.selected;
      if(boxData && selected.el){
        var editBodyWidth = parseInt(editBodyWidth),
            elNowPage = document.querySelector(".page[data-num = '"+ selected.pageNum +"']"),
            editPannleLeft = document.querySelector("#edit-pagetool").parentNode.getBoundingClientRect().left,
            boxPosit = selected.el.getBoundingClientRect(),
            boxWidth = boxPosit.right - boxPosit.left;

          left = boxPosit.left - editPannleLeft + boxWidth / 2 - editBodyWidth / 2 - 8;
          left = left < 0 ? '0' : left;
          var maxL = parseInt(window.getComputedStyle(document.querySelector("#edit-pagetool")).width) - editBodyWidth - 10;
          left = left >= maxL ? maxL : left;
      }
      this.editBodyLeft = left+'px'
    },

    //弹出层的样式
    getPannelStyle(send) {
      var elSend = document.querySelector(send).getBoundingClientRect(),
        elEdit = this.edit.getBoundingClientRect();

      var left = elSend.left - elEdit.left,
        top = elSend.bottom - elEdit.top;

      return {
        left: left + "px",
        top: top + "px",
        position: 'absolute'
      }
    },

    //修改
    changeBoxData(data) {
      this.$store.commit("changeBoxData", {
        pageNum: this.selected.pageNum,
        boxType: this.boxType,
        boxId: this.selected.data.box_id,
        data: data,
        dontSaveUndo: data.dontSaveUndo
      })
    },

    //关闭其他层
    showPannel(e, send) {
      var elRect = e.target.getBoundingClientRect();

      var state = this.openPannelName;
      this.openPannelName = state == send ? '' : send
      this.tipText = '';

      this.$store.commit('setState', {boxToolbar: {
        pannelName: this.openPannelName,
        x: elRect.left,
        y: elRect.top + elRect.height
      }});
    },

    //改变锁定
    changeLock() {
      this.changeBoxData({locked: !this.boxData.locked})
    },

    //图片适应
    imgAdJust() {
      var data = imageUnit.autoImage(this.boxData, this.GetImage(this.boxData.image.id), 'adjust');
      this.changeBoxData({image: {...data}})
      this.$store.dispatch("animation")
    },

    //图片填充
    imgFill() {
      var data = imageUnit.autoImage(this.boxData, this.GetImage(this.boxData.image.id), 'fill');
      this.changeBoxData({image: {...data}})
      this.$store.dispatch("animation")
    },

    //放大图片
    imgZoom(size) {
      this.setImgZoom(size)
      this.$store.dispatch("animation")
      this.timeout = setTimeout(() => {
        this.$store.commit('setState', { isZoom: true })
        this.timer = setInterval(() => {
          this.setImgZoom(size)
        }, 1000 / 60)
      }, 250)
    },

    setImgZoom(size) {
      var data = imageUnit.imgZoom(this.boxData, this.GetImage(this.boxData.image.id), size);
      this.changeBoxData({image: {...data}})
    },

    clearTimer() {
      this.$store.commit('setState', { isZoom: false })
      clearTimeout(this.timeout);
      clearInterval(this.timer)
    },

    //左旋转图片
    imgAntiRotate() {
      var rotation = this.boxData.image.rotation || 0;
      if(rotation <= -360){
        rotation += 270;
      }
      else{
        rotation -= 90;
      }
      var box = JSON.parse(JSON.stringify(this.boxData));
      box.image.rotation = rotation;

      var data = imageUnit.autoImage(box, this.GetImage(this.boxData.image.id), 'fill');

      this.changeBoxData({image: {rotation: rotation, ...data}})
    },

    //右旋转图片
    imgRotate() {
      var rotation = this.boxData.image.rotation || 0;
      if(rotation >= 360){
        rotation -= 270;
      }
      else{
        rotation += 90;
      }
      var str =  'abcd1234';


      var box = JSON.parse(JSON.stringify(this.boxData));
      box.image.rotation = rotation;

      var data = imageUnit.autoImage(box, this.GetImage(this.boxData.image.id), 'fill');

      this.changeBoxData({image: {rotation: rotation, ...data}})
    },

    //查找照片
    findImg() {
      var data = this.boxData;
      if(data.image){
        this.$store.commit("setState", {photoListFocusId: data.image.id})
        setTimeout(() => {
          this.$store.commit("setState", {photoListFocusId: ''})
        }, 1000)
      }
    },

    //修改边框颜色
    changeBorderColor(color) {
      this.changeBoxData({border_color: color.hex});
      this.openPannelName = '';
      this.$store.dispatch("animation")
    },

    //修改边框宽度
    changeBorderWidth(width) {
      this.changeBoxData({border_width: width})
      this.openPannelName = '';
    },

    //修改box背景颜色
    changeBgColor(color) {
      this.changeBoxData({color: color.hex});
      this.openPannelName = '';
      this.$store.dispatch("animation")
    },

    //修改文本颜色
    changeTextColor(color) {
      if(this.boxData.text){
        this.changeBoxData({text: {color: color.hex}});
      }
      else{
        this.changeBoxData({color: color.hex});
      }
      this.openPannelName = '';
      this.$store.dispatch("animation")
    },

    //修改透明度
    changeOpacity(e) {
      var that = this,
        startX = e.pageX,
        opacity = that.boxData.opacity;
      this.isOpacity = true;
      var bookInfo = this.$store.state.bookInfo;
      var pageNum = this.selected.pageNum;
      var pageData = isNaN(pageNum) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
      this.beforePageData = JSON.parse(JSON.stringify(pageData));
      document.body.onmousemove = function (e) {
        var moveX = e.pageX;
        var py = ((opacity * 148 + moveX - startX) / 148).toFixed(2)
        py = py <= 0 ? 0 : py * 1
        py = py >= 1 ? 1 : py * 1
        that.changeBoxData({opacity: py, dontSaveUndo: true})
      }
      document.body.onmouseup = function (e) {
        that.isOpacity = false;
        that.$store.commit("setState", {isMove: false})
        that.$store.commit("addUndoData", { pageNum : pageNum, data: that.beforePageData})
        document.body.onmousemove = null
        document.body.onmouseup = null
      }
    },

    //文本框
    changeFontFamily(value) {
      if(this.boxType == 'textbox'){
        this.changeBoxData({font_id: value});
      }
      else{
        this.changeBoxData({text: {font_id: value}});
      }
      this.openPannelName = '';
    },
    changeAlign(value) {
      if(this.boxType == 'textbox'){
        this.changeBoxData({align: value});
      }
      else{
        this.changeBoxData({text: {align: value}});
      }
      this.openPannelName = '';
    },
    changeLetterSpace(value) {
      if(this.boxType == 'textbox'){
        this.changeBoxData({space: value});
      }
      else{
        this.changeBoxData({text: {space: value}});
      }
      this.openPannelName = '';
    },
    changeLineHeight(value) {
      value = value == 0 ? -1 : value;

      if(this.boxType == 'textbox'){
        this.changeBoxData({leading: value});
      }
      else{
        this.changeBoxData({text: {leading: value}});
      }
      this.openPannelName = '';
    },
    changeFontSize(value) {
      if(this.boxType == 'textbox'){
        this.changeBoxData({pt: value});
      }
      else{
        this.changeBoxData({text: {pt: value}});
      }
      this.openPannelName = '';
    },
    //删除盒子
    deleteBox() {
      var pageNum = this.selected.pageNum,
        data = this.boxData,
        type = this.boxType,
        hasImage = false;
      if (data.image && data.image != null) {
        hasImage = true
      }

      if (hasImage) { //删除图片
        this.changeBoxData({image: null})
        this.$nextTick(() => {
          this.setEditBodyLeft();
        })
      }
      else { //删除框
        this.$store.commit("deleteBox", {pageNum: pageNum, boxType: type, boxId: data.box_id})
      }

      if (!hasImage) this.tipText = ''
    }
  },
  mounted() {
    this.setFontFamilyList();
    this.$refs.pagetool.onmouseover = (e) => {
      var tarRect = e.target.getBoundingClientRect(),
        editRect = this.edit.getBoundingClientRect();

      var left = tarRect.left - editRect.left,
        top = tarRect.top - editRect.top + tarRect.height;

      var tipText = e.target.getAttribute("data-title") || e.target.parentNode.getAttribute("data-title"),
        data = this.boxData;

      if (tipText != null) {
        if (e.target.classList.contains("edit-button-remove") && data.image && data.image != null) {
          tipText = this.$t('boxToolbar.removeimg')
        }
        this.tipText = tipText;
        this.$nextTick(function () {
          if (this.$refs.tooltip) {
            this.$refs.tooltip.style.left = left + 'px';
            this.$refs.tooltip.style.top = top + 'px';
          }
        })
      }else{
        this.tipText = ''
      }
    }
  }
}
</script>

<style lang="scss">
#edit-pagetool {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  right: 0;
  height: 28px;
  z-index: 2;
  background: #666;
  background: -moz-linear-gradient(top, #777, #555);
  background: -webkit-gradient(linear, top, from(#777), to(#555));
  background: -webkit-linear-gradient(top, #777, #555);
  background: -o-linear-gradient(top, #777, #555);
  background: linear-gradient(to bottom, #777 0%, #555 100%);
  -webkit-box-shadow: 0 1px 6px #999;
  box-shadow: 0 1px 6px #999;
}

.dialog_cover {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
}

.edit_dialog {
  z-index: 1004!important;
}

#edit-boxtool-body {
  position: absolute;
  top: 0;
  left: 0;
  padding: 2px 0 0 10px;
  border-radius: 4px;
  font-size: 0;
  .edit-boxtool-button {
    position: relative;
    display: inline-block;
    height: 24px;
    width: 24px;
    margin-right: 6px;
    cursor: pointer;
    border-radius: 2px;
    box-sizing: border-box;
    vertical-align: middle;
    border: 1px solid transparent;
    text-align: center;
    &:after {
      content: ' ';
      display: inline-block;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0%;
      top: 0;
      margin: auto;
      box-sizing: border-box;
      background-repeat: no-repeat;
    }
    &:hover {
      border: 1px solid #404040;
      background: rgba(96, 96, 96, 0.8);
    }
    &:active {
      border: 1px solid #404040 !important;
      background: rgba(64, 64, 64, 0.8) !important;
    }
    &.active {
      border: 1px solid #404040 !important;
      background: rgba(64, 64, 64, 0.8) !important;
    }
  }
  .edit-font-button{
    position: relative;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    margin-right: 6px;
    &.edit-button-fontfamily{
      width: 112px;
      height: 19px;
      background: url(../../assets/book/image/boxToolbar/texttool_selectbg_1.png);
      &:hover{
        -webkit-box-shadow: inset 0 1px 4px black;
        box-shadow: inset 0 1px 4px black;
      }
      &:active{
        background: url(../../assets/book/image/boxToolbar/texttool_selectbg_1-active.png);
      }
      &:after{
        content: attr(data-text);
        font-size: 12px;
        position: absolute;
        top: 1px;
        left: 5px;
        color: #cecece;
        width: 90px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    &.edit-button-fontsize{
      width: 62px;
      height: 19px;
      background: url(../../assets/book/image/boxToolbar/texttool_selectbg2.png);
      &:hover{
        -webkit-box-shadow: inset 0 1px 4px black;
        box-shadow: inset 0 1px 4px black;
      }
      &:active{
        background: url(../../assets/book/image/boxToolbar/texttool_selectbg2-active.png);
      }
      &:after{
        content: attr(data-text);
        font-size: 12px;
        position: absolute;
        top: 1px;
        left: 5px;
        color: #cecece;
        width: 42px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}

.edit-button-lock.lock {
  border: 1px solid #404040 !important;
  background: rgba(64, 64, 64, 0.8) !important;
  &:after {
    background-image: url(../../assets/book/image/boxToolbar/imgtool-lock.png)
  }
}

#edit-opacity {
  position: absolute;
  background: #EDEDED;
  z-index: 200;
  top: 30px;
  left: 0;
  height: 44px;
  border-radius: 3px;
  border: #AFABAB 1px solid;
  ul {
    width: 270px;
    padding: 15px;
    li {
      float: left;
      &.opacity-scrollbar {
        width: 163px;
        height: 21px;
        margin-left: 10px;
        margin-right: 15px;
        margin-top: -2px;
        position: relative;
        background: url(../../assets/book/image/boxToolbar/scroll-bar.png) center center no-repeat;
        .edit-opacity-scroll-btn {
          position: absolute;
          width: 15px;
          height: 21px;
          background: url(../../assets/book/image/boxToolbar/float-chamber.png) 0 0 no-repeat;
          left: 0;
          top: 0;
          cursor: pointer;
        }
      }
    }
  }
  &.active {
    display: block;
    animation: jump-in .1s;
    visibility: visible
  }
}

.edit-button-lock:after {
  width: 12px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/imgtool-unlock.png)
}

.edit-button-zoomin:after {
  width: 17px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/imgtool-zoomin.png)
}

.edit-button-zoomout:after {
  width: 17px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/imgtool-zoomout.png)
}

.edit-button-adjust:after {
  width: 17px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/imgtool-adjust.png)
}

.edit-button-imgfill:after {
  width: 17px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/imgtool-fill.png)
}

.edit-button-antirotatephoto:after {
  width: 17px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/imgtool-anti_rotate.png)
}

.edit-button-rotatephoto:after {
  width: 17px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/imgtool-rotate.png)
}

.edit-button-find:after {
  width: 17px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/imgtool-find.png)
}

.edit-button-borderwidth:after {
  width: 17px;
  height: 18px;
  background-image: url(../../assets/book/image/boxToolbar/boxtool-borderwidth.png)
}

.edit-button-bordercolor {
  .color {
    position: absolute;
    left: 0;
    right: 4px;
    left: 4px;
    bottom: 2px;
    height: 2px;
  }
  &:after {
    width: 14px;
    height: 18px;
    background-image: url(../../assets/book/image/boxToolbar/boxtool-bordercolor.png)
  }
}

.edit-button-bgcolor {
  .color {
    position: absolute;
    background: #2db572;
    height: 12px;
    width: 10px;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    transform: rotate(45deg);
    &:before {
      content: ' ';
      display: block;
      position: absolute;
      background: inherit;
      left: -5px;
      top: 5px;
      width: 10px;
      height: 2px;
      transform: rotate(-45deg);
    }
    &:after {
      content: ' ';
      display: block;
      position: absolute;
      background: inherit;
      left: 8px;
      top: 2px;
      width: 6px;
      height: 3px;
      transform: rotate(45deg);
    }
  }
  .assets {
    position: absolute;
    height: 2px;
    width: 7px;
    left: -2px;
    right: 0;
    top: -7px;
    bottom: 0;
    margin: auto;
    background: #ddd;
  }
}

.edit-button-opacity:after {
  width: 13px;
  height: 17px;
  background-image: url(../../assets/book/image/boxToolbar/boxtool-bgcolor.png);
}

.edit-button-textcolor{
  .color{
    position: relative;
    display: inline-block;
    width: 18px;
    height: 18px;
    border: none;
    margin: 2px;
    &:after {
      content: ' ';
      position: absolute;
      display: inline-block;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../../assets/book/image/boxToolbar/texttool_color.png);
    }
  }
}

.edit-button-leftalign:after{
  width: 12px;
  height: 12px;
  background-image: url(../../assets/book/image/boxToolbar/texttool_leftalign.png)
}
.edit-button-centeralign:after{
  width: 12px;
  height: 12px;
  background-image: url(../../assets/book/image/boxToolbar/texttool_centeralign.png)
}
.edit-button-rightalign:after{
  width: 12px;
  height: 12px;
  background-image: url(../../assets/book/image/boxToolbar/texttool_rightalign.png)
}

.edit-button-letterspace:after{
  width: 19px;
  height: 15px;
  background-image: url(../../assets/book/image/boxToolbar/texttool_letterspace.png)
}

.edit-button-lineheight:after{
  width: 24px;
  height: 15px;
  background-image: url(../../assets/book/image/boxToolbar/texttool_lineheight.png)
}

.edit-button-remove:after {
  width: 13px;
  height: 17px;
  background-image: url(../../assets/book/image/boxToolbar/imgtool-remove.png)
}

.edit-button-props:after {
  width: 18px;
  height: 19px;
  background-image: url(../../assets/book/image/boxToolbar/boxtool-props.png)
}


#tooltip {
  position: absolute;
  padding: 3px;
  top: 29px;
  border: 1px solid #ccc;
  background: white;
  font-size: 12px;
  color: #999;
  z-index: 2001;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1)
}

.fadeIn-enter-active,
.fadeIn-leave-active {
  transition: opacity .3s;
}

.fadeIn-enter,
.fadeIn-leave-to {
  opacity: 0;
}

.fadeIn-enter-to,
.fadeIn-leave {
  opacity: 1;
}

.click_lock{
  background: rgb(144, 71, 71);
  color: rgb(255, 255, 255);
  width: 75px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 3px;
  position: absolute;
  left: 40px;
  top: 0;
  font-size: 12px;
  .arrows{
    border-width: 8px;
    border-style: solid;
    border-color: transparent rgb(144, 71, 71) transparent transparent;
    position: absolute;
    left: -14px;
    top: 7px;
  }
}
.blink{
  animation: blink .2s 3;
}
@keyframes blink {
    0% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

</style>



