<template>
  <div class="layoutpannel-dialog" v-bind:class="{'--wide': settings.type == 'wide'}">
    <div class="layoutpannel-dialog-body" v-bind:class="{'--wide': settings.type == 'wide'}">
      <p class="layoutpannel-dialog-title" v-if="settings.title.show" v-text="settings.title.word"><!--确认--></p>
      <p class="layoutpannel-dialog-content" v-if="settings.content.show" v-html="settings.content.word"><!--真的要删除模版？--></p>
      <div class="layoutpannel-dialog-btns">
        <span class="layoutpannel-dialog-btn --yes" v-on:click="confirm" v-if="settings.confirm.show" v-text="settings.confirm.word"><!--是--></span>
        <span class="layoutpannel-dialog-btn --cancel" v-on:click="cancel" v-if="settings.cancel.show" v-text="settings.cancel.word"><!--否--></span>
      </div>
      <div class="layoutpannel-dialog-close" v-on:click="close" v-if="settings.close.show"><span class="layoutpannel-dialog-close-text">×</span></div>
    </div>
  </div>
</template>

<script>
const conf = {
  type: 'narrow', // 'wide'
  title: {
    show: false,
    word: '',
  },
  content: {
    show: false,
    word: ''
  },
  confirm: {
    show: false,
    word: ''
  },
  cancel: {
    show: false,
    word: ''
  },
  close: {
    show: false
  }
}

export default {
  name: 'layoutpannelDialog',
  props: {
    config: {
      type: Object,
      default () {
        return conf
      }
    }
  },
  data () {
    return {
      
    }
  },
  computed: {
    settings () {
      return Object.assign({ }, conf, this.config)
    }
  },
  methods: {
    close () {
      if (this.settings.close.event) {
        this.settings.close.event()
      }
    },
    cancel () {
      if (this.settings.cancel.event) {
        this.settings.cancel.event()
      }
    },
    confirm () {
      if (this.settings.confirm.event) {
        this.settings.confirm.event()
      }
    },
  }
}
</script>

<style lang="scss">
  .layoutpannel-dialog {
    position: absolute;
    left: 0;
    top: 0;
    color: #000;
    font-size: 14px;
    backface-visibility: hidden;
    z-index: 2000;
    min-width: 132px;
    &.--wide {
      width: 210px;
    }
  }
  .layoutpannel-dialog-body {
    padding: 8px 10px;
    box-shadow: 0 1px 6px #999;
    border-radius: 5px;
    background: #fff;
    &.--wide {
      padding: 15px;
    }
  }
  .layoutpannel-dialog-title {
    font-size: 20px;
  }
  .layoutpannel-dialog-content {
    margin: 2px 0 7px;
  }
  .layoutpannel-dialog-btns {
    display: flex;
    overflow: hidden;
    justify-content: space-around;
  }
  .layoutpannel-dialog-btn {
    display: flex;
    box-sizing: border-box;
    height: 23px;
    min-width: 48px;
    background: #d2523e;
    border-radius: 5px;
    color: #fff;
    padding: 0 10px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  .layoutpannel-dialog-close {
    position: absolute;
    top: 0;
    right: 0;
    height: 25px;
    width: 25px;
    font-family: SimHei;
    border-radius: 0 5px 0 25px;
    cursor: pointer;
    background: #ddd;
    &:hover {
      box-shadow: inset 1px -1px 2px #ccc;
    }
  }
  .layoutpannel-dialog-close-text {
    position: absolute;
    top: 2px;
    right: 3px;
    color: #6088ce;
    font-weight: bold;
    font-size: 14px;
    pointer-events: none;
  }
</style>





