<template>
  <transition name="addFadeIn">
    <div class='box textbox' data-type="textbox" :data-name="textBox.name + textBox.box_id" :class="{
          'no_content': textBox.content == '',
          'active': active,
          'edit': edit
        }" @mousedown.prevent="mouseDown($event)" @click="clickEvent($event)" :style="{
          width: mmToPx(textBox.width) + 'px',
          height: mmToPx(textBox.height) + 'px',
          textAlign: textBox.align,
          letterSpacing: (textBox.space / 1000) + 'em',
          zIndex: textBox.index,
          lineHeight: textBox.leading == -1 ? '1.2em' : ptToPx(textBox.leading) + 'px',
          fontSize: ptToPx(textBox.pt) + 'px',
          fontFamily: 'font' + textBox.font_id + ', font0, \'Microsoft YaHei\'',
          transform: 'translate(' + mmToPx(textBox.x - bleed) +'px, ' + mmToPx(textBox.y - bleed) + 'px) rotate(' + textBox.rotation + 'deg)',
        }">
      <div class="content" ref="content" :txt="afterText" :style="{color: textBox.color || '#000',}"  v-text="textBox.content"></div>
      <MultipleSelected :box="textBox" :pageNum="page.num"></MultipleSelected>
    </div>
  </transition>
</template>

<script>
import MultipleSelected from '@/components/common/multipleSelected'
export default {
  name: 'textbox',
  props: {
    scale: {
      type: Number,
      defalut() {
        return 1
      }
    },
    page: {
      type: Object,
      defalut() {
        return {}
      }
    },
    textBox: {
      type: Object,
      defalut() {
        return {}
      }
    },
    bleed: {
      type: Number,
      defalut() {
        return 0
      }
    }
  },
  data() {
    return {
      edit: false,
      isMineCancel: false,
      beforePageData: {},
    }
  },
  components: {
    MultipleSelected
  },
  watch: {
    selected() {
      if (this.textBox.name != this.selected.data.name || this.textBox.box_id != this.selected.data.box_id || this.selected.pageNum != this.page.num) {
        this.edit = false;
      }
    },
    showEditTextPannle() {
      if(!this.showEditTextPannle && !this.isMineCancel){
        this.edit = false;
      }
    }
  },
  computed: {
    selected() {
      return this.$store.state.selected
    },
    active() {
      return this.isActive(this.page.num, this.textBox)
    },
    afterText() {
      if(this.textBox.content !== '') {
        return ''
      }
      if(this.edit){
        return '';
      }
      else if(this.active){
        return this.$t("textBox.text2")
      }
      return this.$t("textBox.text1")
    },
    showEditTextPannle() {
      return this.$store.state.showEditTextPannle;
    }
  },
  methods: {
    mouseDown(e) {
      if(e.button == 2) return;
      if (this.active && !this.IsMultipleSelect()) {
        this.DragDown(e, {
          dragStart: this.dragStart,
          drag: this.drag,
          dragEnd: this.dragEnd
        })
      }
      if(this.$store.state.showEditTextPannle){ //有可能是右键改变了显示
        this.edit = true;
      }
      this.$store.commit('setState', { showEditTextPannle: false })
    },
    clickEvent(e){
      this.SelectElement(e, {
        el: this.$el,
        box: this.textBox,
        pageNum: this.page.num,
        select: this.select
      })
    },
    dragStart(e, args) {
      this.startX = this.textBox.x;
      this.startY = this.textBox.y;
      this.isMineCancel = true;
      this.beforePageData = JSON.parse(JSON.stringify(this.page));
    },
    drag(e, args) {
      if(!this.textBox.locked){
        this.edit = false;
        var xy = this.BoxLimitSize({
          dragX: this.pxToMm(args.x),
          dragY: this.pxToMm(args.y),
          boxX: this.startX,
          boxY: this.startY,
          scale: this.scale,
          boxW: this.textBox.width,
          boxH: this.textBox.height,
          pageWidth: this.page.width,
          pageHeight: this.page.height,
        });
        this.$store.commit("changeBoxData", {
          pageNum: this.page.num,
          boxType: 'textbox',
          boxId: this.textBox.box_id,
          data: { x: xy.x, y: xy.y},
          selected:{ state: false }
        })

        //拖动尺寸tip
        this.$store.commit('setState', {sublineType: 'move', dragResize: {type: 'xy', x: e.pageX + 20, y: e.pageY}})
      }
      else{
        this.$store.dispatch("lockedDrag");
      }
    },
    dragEnd(e, args) {
      if (!this.$store.state.isMove && this.active) {
        this.edit = !this.edit
      }

      this.isMineCancel =false;

      this.$store.commit('setState', {
        selected: {state: !this.edit, el: this.$el, data: this.textBox, pageNum: this.page.num},
        showEditTextPannle: this.edit,
        sublineType: '',
        dragResize: {type: null, x: 0, y: 0} //拖动尺寸tip
      })

      this.$store.commit("addUndoData", {pageNum: this.page.num, data: this.beforePageData});
    },
    select(e) {

    }
  },
  mounted() {
  }
}
</script>

<style lang="scss">
.textbox {
  overflow: hidden;
  &.no_content,
  &:hover,
  &.active {
    background: rgba(200, 200, 200, 0.3);
    box-shadow: inset 1px 0 10px #ccc;
    cursor: pointer;
  }
  &:hover {
    background: rgba(230, 230, 230, 0.3);
    box-shadow: inset 1px 0 10px #ccc;
  }
  .content {
    white-space: pre-wrap;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    overflow: hidden;
    width: 100%;
    height: 100%;
    &:after{
      content: attr(txt)
    }
  }
  &.edit{
    &:hover{
      cursor: auto;
    }
  }
}

.ispreview{
  .no_content{
    display: none;
  }
}

</style>



