<template>
  <transition name="jumpIn">
    <div id="edit-props" ref="propspannel" class="edit-props topbar-select-dialog" v-if="showState" :style="pannelOffset" @mouseover="switchTip($event, 'open')" @mouseleave="switchTip($event, 'close')">
      <div class="toolbar-select-topbar" :data-title="pannel.state ? '按住拖动' : ''" :data-drag="pannel.state" @mousedown="pannel.state ? DragDown($event, {
        dragStart: dragStart,
        drag: drag,
        dragEnd: dragEnd
      }) : undefined">
        <div class="topbar_nail" :data-title="pannel.state ? '默认关闭' : '默认打开'" :style="{
          transform: 'rotate(' + (pannel.state ? 0 : 45) + 'deg)'
        }" @click.stop="changeState()"></div>
      </div>
      <div class="toolbar-select-body">
        <ul>
          <li class="props_list">
            <span class="input_name">W:</span>
            <input type="text" class="input_box" :value="boxW" @change="setBoxData($event, 'width')">
            <span class="input_unit">mm</span>
          </li>
          <li class="props_list">
            <div class="empty lock" :data-lock="lock" :data-fixlock="fixZoomScaleBox.indexOf(box.name) != -1" data-title="锁定比例" @click="fixZoomScaleBox.indexOf(box.name) == -1 ? changeLock() : undefined"></div>
          </li>
          <li class="props_list">
            <span class="input_name">H:</span>
            <input type="text" class="input_box" :value="boxH" @change="setBoxData($event, 'height')">
            <span class="input_unit">mm</span>
          </li>
          <li class="props_list">
            <span class="input_name">X:</span>
            <input type="text" class="input_box" :value="boxX" @change="setBoxData($event, 'x')">
            <span class="input_unit">mm</span>
          </li>
          <li class="props_list">
            <div class="empty"></div>
          </li>
          <li class="props_list">
            <span class="input_name">Y:</span>
            <input type="text" class="input_box" :value="boxY" @change="setBoxData($event, 'y')">
            <span class="input_unit">mm</span>
          </li>
          <li class="props_list">
            <span class="input_name">R:</span>
            <input type="text" class="input_box" :value="boxR" @change="setBoxData($event, 'rotation')">
            <span class="input_unit">°</span>
          </li>
        </ul>
      </div>
      <div class="btn_tip" v-if="tip.text" :style="{
        transform: 'translate(' + tip.x + 'px, ' + tip.y + 'px)'
      }">{{tip.text}}</div>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'propspannel',
  data() {
    return {
      lock: false,//锁定比例
      pannel: {
        state: false,//是否固定位置
        x: 0,
        y: 0,
        w: 0,
        h: 0,
        startX: 0,
        startY: 0
      },
      tip: {
        text: '',
        x: 0,
        y: 0
      },
      minW: 5//最小box尺寸mm
    }
  },
  props: {
    showState: {
      type: Boolean,
      default(){
        return false
      }
    },
    boxToolXY: {
      type: Object,
      default() {
        return {
          x: 0,
          y: 0
        }
      }
    },
    box: {
      type: Object,
      default() {
        return {}
      }
    },
    page: {
      type: Object,
      default(){
        return {}
      }
    },
    fixZoomScaleBox: {//只能同比缩放的，素材之类 boxname
      type: Array,
      default() {
        return []
      }
    }
  },
  computed: {
    pannelOffset() {
      var x = this.pannel.state ? this.pannel.x : this.boxToolXY.x,
        y = this.pannel.state ? this.pannel.y : this.boxToolXY.y;

      return {
        transform: 'translate(' + x + 'px, ' + y + 'px)'
      }
    },
    boxW() {
      return this.toFloat(this.box.width, 2);
    },
    boxH() {
      return this.toFloat(this.box.height, 2);
    },
    boxX() {
      return this.toFloat(this.getBoxXY('x'), 2);
    },
    boxY() {
      return this.toFloat(this.getBoxXY('y'), 2);
    },
    boxR() {
      return this.toFloat(this.box.rotation, 2);
    }
  },
  methods: {
    getBoxXY(type){
      var box, c, v, t1, t2, bw, boxW, borderW;

      if(type == 'x'){
        t1 = 'width';
        t2 = 'height';
      }

      if(type == 'y'){
        t1 = 'height';
        t2 = 'width';
      }

      box = this.box;

      box[type] = box[type] * 1;
      box[t1] = box[t1] * 1;
      box[t2] = box[t2] * 1;

      bw = this.ptToMm(box.border_width || 0);

      //三角函数换算出来旋转后box、border的宽
      boxW = this.CommonMethods.convertRotateWH(box[t1], box[t2], box.rotation);
      borderW = this.CommonMethods.convertRotateWH(bw, bw, box.rotation);

      c = box[type] + box[t1] / 2 + bw;//中心点
      v = c - (boxW / 2 + borderW);//反推xy

      return v;
    },
    setBoxData(e, type) {
      var value = e.target.value;

      var data = {};

      //*1 避免字符串
      var w = this.box.width * 1,
        h = this.box.height * 1,
        x = this.box.x * 1,
        y = this.box.y * 1,
        r = this.box.rotation * 1;

      value = isNaN(value) ? this.box[type] : value * 1;

      //限制下数值
      if (type == 'width') {
        value = value < this.minW ? this.minW : value;

        //中心点缩放
        if (this.lock || this.fixZoomScaleBox.indexOf(this.box.name) != -1) {
          //锁定比例
          data.height = value / (this.box.width / this.box.height);
          data.x = x + (w - value) / 2;
          data.y = y + (h - data.height) / 2;
        } else {
          data.x = x + (w - value) / 2;
        }
      }

      if(type == 'height'){
        value = value < this.minW ? this.minW : value;

        //中心点缩放
        if (this.lock || this.fixZoomScaleBox.indexOf(this.box.name) != -1) {
          //锁定比例
          data.width = value * (this.box.width / this.box.height)
          data.x = x + (w - data.width) / 2;
          data.y = y + (h - value) / 2;
        } else {
          data.y = y + (h - value) / 2;
        }
      }

      if(type == 'x' || type == 'y'){
        //转换xy
        value = this.convertXY(value, type);
      }

      data[type] = value;

      this.$emit('changeData', data);
    },
    //看到的假xy转真实的xy，并限制最大最小数值
    convertXY(value, type){
      var box, c, v, t1, t2, bw, boxW, borderW;

      if(type == 'x'){
        t1 = 'width';
        t2 = 'height';
      }

      if(type == 'y'){
        t1 = 'height';
        t2 = 'width';
      }

      box = this.box;

      bw = this.ptToMm(box.border_width || 0);

      //三角函数换算出来旋转后box、border的宽
      boxW = this.CommonMethods.convertRotateWH(box[t1], box[t2], box.rotation);
      borderW = this.CommonMethods.convertRotateWH(bw, bw, box.rotation);

      //假to真
      c = value + boxW / 2 + borderW;//中心点
      v = c - box[t1] / 2 - bw;//反推xy

      //限制最大最小
      v = v < -(box[t1] / 2 + bw) ? -(box[t1] / 2 + bw) : v;
      v = v > this.page[t1] - (box[t1] / 2 + bw) ? this.page[t1] - (box[t1] / 2 + bw) : v;

      return v;
    },
    dragStart(e, args) {
      var elRect = this.$refs.propspannel.getBoundingClientRect();

      this.pannel.startX = this.pannel.x;
      this.pannel.startY = this.pannel.y;
      this.pannel.w = elRect.width;
      this.pannel.h = elRect.height;
    },
    drag(e, args) {
      var xy = this.LimitSize({
        dragX: args.x,
        dragY: args.y,
        boxW: this.pannel.w,
        boxH: this.pannel.h,
        boxX: this.pannel.startX,
        boxY: this.pannel.startY,
        pageWidth: window.innerWidth,
        pageHeight: window.innerHeight
      });

      this.pannel.x = xy.x;
      this.pannel.y = xy.y;
    },
    dragEnd(e, args) {

    },
    changeState() {
      this.pannel.state = !this.pannel.state;
      if (this.pannel.state) {
        this.pannel.x = this.boxToolXY.x;
        this.pannel.y = this.boxToolXY.y;
      }
    },
    switchTip(e, type){
      var title = e.target.getAttribute('data-title');
      if(title && this.$refs.propspannel){
        var elRect = e.target.getBoundingClientRect(),
          pannelRect = this.$refs.propspannel.getBoundingClientRect();

        this.tip.text = title;
        this.tip.x = elRect.left - pannelRect.left;
        this.tip.y = elRect.top + elRect.height - pannelRect.top;
      }else{
        this.tip.text = '';
        this.tip.x = 0;
        this.tip.y = 0;
      }
    },
    changeLock(){
      this.lock = !this.lock;
    }
  }
}
</script>
<style lang="scss">
#edit-props {
  background: #ededed;
  width: 280px;
  z-index: 1005;

  .toolbar-select-topbar {
    height: 24px;
    background: #ddd;
    background: -moz-linear-gradient(top, #ddd, #aaa);
    background: -webkit-gradient(linear, top, from(#ddd), to(#aaa));
    background: -webkit-linear-gradient(top, #ddd, #aaa);
    background: -o-linear-gradient(top, #ddd, #aaa);
    background: linear-gradient(to bottom, #ddd 0%, #aaa 100%);
    -webkit-box-shadow: 0 1px 6px #999;
    box-shadow: 0 1px 6px #999;

    &[data-drag="true"] {
      cursor: -webkit-grab;
    }

    .topbar_nail {
      background: url(../../assets/book/image/boxtoolbar/boxtool-nail.png);
      background-size: 100%;
      background-position: 0px 2px;
      width: 20px;
      height: 20px;
      margin-left: 10px;
      cursor: pointer;
    }
  }

  .props_list {
    display: inline-block;
    vertical-align: middle;
    margin: 5px;

    .empty {
      width: 15px;
      height: 15px;
      border: solid 1px #ededed;
    }

    .lock {
      position: relative;
      background: url(../../assets/book/image/boxtoolbar/boxtool-sizelock.png);
      background-size: 100%;
      cursor: pointer;

      &:hover {
        border: solid 1px #aaa;
      }

      &[data-lock="true"],
      &[data-fixlock="true"]{
        border: solid 1px #aaa;
        box-shadow: 1px 1px 3px #aaa inset;
      }

      &[data-fixlock="true"]:after{
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: #aaa;
        opacity: .8;
        cursor: no-drop;
      }
    }
  }

  &:before {
    content: none;
  }

  .btn_tip{
    position: absolute;
    left: 0;
    top: 0;
    padding: 3px;
    border: 1px solid #ccc;
    background: white;
    font-size: 12px;
    color: #999;
    z-index: 2001;
    -webkit-box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
  }
}

.input_name {
  display: inline-block;
  width: 15px;
  text-align: right;
}

.input_box {
  width: 50px;
  height: 24px;
  line-height: 24px;
  padding-left: 5px;
  border: solid 1px #ddd;
  font-size: 12px;
  box-shadow: 1px 1px 3px #ddd inset;
}
</style>



