<template>
  <div class="edit-definition_warning_content" ref="tip" v-show="warningTip.visible" :style="tipStyle()">
    <h3>清晰度不足</h3>
    <p class="advice_size">建议尺寸{{warningTip.w}}x{{warningTip.h}}</p>
    <p>当前状态下此照片印刷效果会不清晰<br>请尝试缩小照片、更换照片、更换模板</p>
  </div>
</template>
<script>
export default {
  name: 'warningPannel',
  props: [
  ],
  data() {
    return {
      
    }
  },
  computed: {
    warningTip() {
      return this.$store.state.warningTip;
    }
  },
  methods: {
    tipStyle() {
      var tip = this.warningTip, 
          elRect = tip.elRect;
      if(tip.visible && this.$el){
        setTimeout(() => {
          var rect = this.$el.getBoundingClientRect();
          var editRect = document.querySelector("#edit").getBoundingClientRect();
          var left = (elRect.width - rect.width) / 2 + elRect.left - editRect.left;
          var top = elRect.top - rect.height - 5 - editRect.top;
          this.$el.style.left = left + 'px'
          this.$el.style.top = top + 'px'
          this.$el.style.opacity = 1;
        }, 0)
        return {
          "visibility": 'visible',
          "animation": 'fadeIn .2s'
        }
      }
    }
  },
  mounted() {
  }
}
</script>
<style lang="scss">
.edit-definition_warning_content{
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  font-size: 12px;
  padding: 7px;
  color: black;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 8px #999;
  box-shadow: 0 1px 8px #999;
  -webkit-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  z-index: 2000;
  h3{
    margin-bottom: 2px;
    display: inline-block;
    font-size: 16px;
  }
  p.advice_size{
    display: inline;
  }
}
@keyframes fadeIn {
  0%{ opacity: 0; }
  100%{ opacity: 1; }
}
</style>



