<script>
export default {
  install(Vue) {
    Vue.prototype.DragDown = function(e, args) {
      e.stopPropagation();
      e.preventDefault();

      var that = this;

      var dragStart = args.dragStart,//按下回调
        drag = args.drag,//拖动中回调
        dragEnd = args.dragEnd;//松开回调

      var startX = e.pageX,
        startY = e.pageY;

      //拖动标记
      this.$store.commit('setState', { isMove: false });

      //拖动开始回调
      if (typeof dragStart === 'function') {
        dragStart(e, {
          x: startX,
          y: startY
        });
      }

      document.onmousemove = function(e) {
        e.stopPropagation();

        if (!that.$store.state.isMove) {
          that.$store.commit('setState', { isMove: true });
        }

        //拖动中回调
        if (typeof drag === 'function') {
          drag(e, {
            x: e.pageX - startX,
            y: e.pageY - startY
          });
        }
      }

      document.onmouseup = function(e) {
        e.stopPropagation();

        //拖动结束回调
        if (typeof dragEnd === 'function') {
          dragEnd(e, {
            x: e.pageX - startX,
            y: e.pageY - startY
          });
        }

        //移除拖动 这里去掉整个文档的不大合理，后边再完善下
        document.onmousemove = null;
        document.onmouseup = null;
      }
    };

    //限制移动【不能超出】
    Vue.prototype.LimitSize = function(args) {
      var dragX = args.dragX * 1,//拖动偏移X
        dragY = args.dragY * 1,//拖动偏移Y
        elW = args.boxW * 1,//拖动目标的w
        elH = args.boxH * 1,//拖动目标的h
        elX = args.boxX * 1,//拖动目标的x
        elY = args.boxY * 1,//拖动目标的y
        scale = args.scale || 1,//拖动目标缩放比例
        pageW = args.pageWidth * 1,
        pageH = args.pageHeight * 1;

      var x = elX + dragX / scale,
        y = elY + dragY / scale;

      x = x < 0 ? 0 : x;
      x = x > pageW - elW ? pageW - elW : x;
      y = y < 0 ? 0 : y;
      y = y > pageH - elH ? pageH - elH : y;

      return {
        x: x,
        y: y
      };
    }

    //box限制移动 【限制超出1/2】
    Vue.prototype.BoxLimitSize = function(args) {
      var dragX = args.dragX * 1,//拖动偏移X
        dragY = args.dragY * 1,//拖动偏移Y
        elW = args.boxW * 1,//拖动目标的w
        elH = args.boxH * 1,//拖动目标的h
        elX = args.boxX * 1,//拖动目标的x
        elY = args.boxY * 1,//拖动目标的y
        scale = args.scale || 1,//拖动目标缩放比例
        pageW = args.pageWidth * 1,
        pageH = args.pageHeight * 1;

      var x = elX + dragX / scale,
        y = elY + dragY / scale;

      x = x < -(elW / 2) ? -(elW / 2) : x;
      x = x > pageW - (elW / 2) ? pageW - (elW / 2) : x;
      y = y < -(elH / 2) ? -(elH / 2) : y;
      y = y > pageH - (elH / 2) ? pageH - (elH / 2) : y;

      return {
        x: x,
        y: y
      };
    }

    //image限制移动
    Vue.prototype.ImageLimitSize = function(args) {
      var dragX = args.dragX,//拖动偏移X
        dragY = args.dragY,//拖动偏移Y
        elW = args.elW,//拖动目标的w
        elH = args.elH,//拖动目标的h
        elX = args.elX,//拖动目标的x
        elY = args.elY,//拖动目标的y
        scale = args.scale || 1,//拖动目标缩放比例
        boxW = args.boxW,
        boxH = args.boxH;

      var x = elX + dragX / scale,
        y = elY + dragY / scale;

      if (elW >= boxW) {
        if (x <= boxW - elW) {
          x = boxW - elW
        }
        if (x > 0) {
          x = 0;
        }
      }
      else {
        if (x <= 0) {
          x = 0
        }
        if (x >= boxW - elW) {
          x = boxW - elW
        }
      }
      if (elH >= boxH) {
        if (y <= boxH - elH) {
          y = boxH - elH
        }
        if (y >= 0) {
          y = 0;
        }
      }
      else {
        if (y <= 0) {
          y = 0
        }
        if (y >= boxH - elH) {
          y = boxH - elH
        }
      }

      return {
        x: x,
        y: y
      };
    }
  }
}
</script>



