<template>
  <div id="subline" v-if="showSubline">
    <div class="alignment_line" v-for="(item, index) in lines" :key="index" :style="lineStyle(item)"></div>
  </div>
</template>

<script>

export default {
  name: 'subline',
  props: [
    'scale'
  ],
  props: {
    scale: {
      type: Number,
      default() {
        return 1
      }
    }
  },
  data() {
    return {
      showSubline: true,
      lines: []
    }
  },
  computed: {
    selected () {
      return this.$store.state.selected
    },
    boxData() {
      return {...this.selected.data};
    },
    sublineType() {
      return this.$store.state.sublineType;
    }
  },
  watch: {
    boxData: {
      handler(val, oldVal) {
        this.$nextTick(() => {
          var sublineType = this.sublineType;
          if (sublineType != '' && this.selected.el) {
            var thatPosit = this.selected.el.getBoundingClientRect(),
              rects = this.rects(),
              scale = this.scale,
              data = val,
              rectThis = {
                name: val.name + val.box_id,
                top: thatPosit.top,
                right: thatPosit.right,
                bottom: thatPosit.bottom,
                left: thatPosit.left,
                centerX: thatPosit.left + (thatPosit.right - thatPosit.left) / 2,
                centerY: thatPosit.top + (thatPosit.bottom - thatPosit.top) / 2
              },
              hited = {
                x: null,
                y: null
              };
            for (let i = 0; i < rects.length; i++) {
              let item = rects[i];
              if (item.name == rectThis.name) {
                continue
              }
              else {
                for (var key in rectThis) {
                  for (var key2 in item) {
                    switch (key) {
                      case "left":
                      case "centerX":
                      case "right":
                        if (['left', 'centerX', 'right'].contains(key2)) {
                          var delta = rectThis[key] - item[key2]
                          if (Math.abs(delta) < 6) {
                            if (!hited.x || Math.abs(delta) < Math.abs(hited.x.delta)) {
                              hited.x = {
                                type: key,
                                targetType: key2,
                                curRect: item,
                                delta: delta
                              }
                            }
                          }
                        }
                        break
                      case "top":
                      case "centerY":
                      case "bottom":
                        if (['top', 'centerY', 'bottom'].contains(key2)) {
                          var delta = rectThis[key] - item[key2]
                          if (Math.abs(delta) < 6) {
                            if (!hited.y || Math.abs(delta) < Math.abs(hited.y.delta)) {
                              hited.y = {
                                type: key,
                                targetType: key2,
                                curRect: item,
                                delta: delta
                              }
                            }
                          }
                        }
                        break
                    }
                  }
                }
              }
            }
            if ((hited.x != null && hited.x.delta != 0) || (hited.y != null && hited.y.delta != 0)) {
              if (sublineType === 'move') { //移动吸附
                var x = hited.x ? hited.x.delta / scale : 0,
                    y = hited.y ? hited.y.delta / scale : 0;
                var selected = this.selected;
                var copyVal = JSON.parse(JSON.stringify(val))
                copyVal.x -= this.pxToMm(x);
                copyVal.y -= this.pxToMm(y);
                var boxType = selected.el.getAttribute("data-type");

                //防止无限更新
                if(Math.abs(copyVal.x - selected.data.x) >= 0.001 || Math.abs(copyVal.y - selected.data.y) >= 0.001){
                  this.$store.commit("changeBoxData", {pageNum: selected.pageNum, boxType: boxType, boxId: selected.data.box_id, data: {x: copyVal.x, y: copyVal.y}});
                }
              }
              else { //放大缩小吸附
                
              }
            }
            //已经把框吸过去了 开始生成辅助线
            this.lines = [];
            var lines = {
              x: {
                left: null,
                centerX: null,
                right: null
              },
              y: {
                top: null,
                centerY: null,
                bottom: null
              }
            };
            var lineTop, lineBottom, lineLeft, lineRight;
            thatPosit = this.selected.el.getBoundingClientRect();

            rectThis = {
              name: val.name + val.box_id,
              top: thatPosit.top,
              right: thatPosit.right,
              bottom: thatPosit.bottom,
              left: thatPosit.left,
              centerX: thatPosit.left + (thatPosit.right - thatPosit.left) / 2,
              centerY: thatPosit.top + (thatPosit.bottom - thatPosit.top) / 2
            }
            for (let i = 0; i < rects.length; i++) {
              let item = rects[i];
              if (item.name == rectThis.name) {
                continue
              }
              else {
                for (var key in rectThis) {
                  for (var key2 in item) {
                    switch (key) {
                      case "left":
                      case "centerX":
                      case "right":
                        if (['left', 'centerX', 'right'].contains(key2)) {
                          lineTop = Math.min(item.top, rectThis.top);
                          lineBottom = Math.max(item.bottom, rectThis.bottom);
                          if (Math.abs(item[key2] - rectThis[key]) < this.scale * 1) {
                            if (!lines.x[key]) {
                              lines.x[key] = {
                                value: rectThis[key],
                                top: lineTop,
                                bottom: lineBottom,
                                targetRect: item,
                                targetType: key2
                              }
                            }
                            else if (lineTop < lines.x[key].top) {
                              lines.x[key].top = lineTop;
                              lines.x[key].targetRect = item;
                              lines.x[key].targetType = key2;
                            }
                            else if (lineBottom > lines.x[key].bottom) {
                              lines.x[key].bottom = lineBottom;
                              lines.x[key].targetRect = item;
                              lines.x[key].targetType = key2;
                            }
                          }
                        }
                        break;
                      case "top":
                      case "centerY":
                      case "bottom":
                        if (['top', 'centerY', 'bottom'].contains(key2)) {
                          lineLeft = Math.min(item.left, rectThis.left);
                          lineRight = Math.max(item.right, rectThis.right);
                          if (Math.abs(item[key2] - rectThis[key]) < this.scale * 1) {
                            if (!lines.y[key]) {
                              lines.y[key] = {
                                value: rectThis[key],
                                left: lineLeft,
                                right: lineRight,
                                targetRect: item,
                                targetType: key2
                              }
                            }
                            else if (lineLeft < lines.y[key].left) {
                              lines.y[key].left = lineLeft;
                              lines.y[key].targetRect = item;
                              lines.y[key].targetType = key2;
                            }
                            else if (lineRight > lines.y[key].right) {
                              lines.y[key].right = lineRight;
                              lines.y[key].targetRect = item;
                              lines.y[key].targetType = key2;
                            }
                          }
                        }
                        break;
                    }
                  }
                }
              }
            }
            for (let key in lines.x) {
              var item = lines.x[key];
              if (!item) {
                continue
              }
              this.lines.push({
                "left": item.value,
                "top": item.top,
                "height": item.bottom - item.top,
                "width": 0,
                "color": (item.targetRect.name === 'page' ? '#01FF70' : item.targetType.indexOf('center') !== -1 ? '#FFDC00' : '#FF4136'),
              })
            }
            for (let key in lines.y) {
              var item = lines.y[key];
              if (!item) {
                continue
              }
              this.lines.push({
                "left": item.left,
                "top": item.value,
                "height": 0,
                "width": item.right - item.left,
                "color": (item.targetRect.name === 'page' ? '#01FF70' : item.targetType.indexOf('center') !== -1 ? '#FFDC00' : '#FF4136'),
              })
            }
          }
        })
      },
      deep: true
    },
    sublineType() {
      if(this.sublineType == ''){
        this.lines = [];
      }
    }
  },
  methods: {
    //根据类名、自定义属性获取元素
    getElementsByAttr(parent, className, attr, value) {
      var parent = parent || document;
      var aElements = parent.querySelectorAll(className);
      var aEle = '';
      for (var  i = 0; i < aElements.length; i++) {
        if (aElements[i].getAttribute(attr) == value) {
          aEle =  aElements[i];
        }
      }
      return  aEle;
    },
    rects() {
      var rects = [];
      var page = document.querySelector(".page.active").getBoundingClientRect();
      rects.push({
        name: 'page',
        top: page.top,
        right: page.right,
        bottom: page.bottom,
        left: page.left,
        centerX: page.left + (page.right - page.left) / 2,
        centerY: page.top + (page.bottom - page.top) / 2
      })
      var boxList = document.querySelectorAll(".page.active .box");
      for (let i = 0; i < boxList.length; i++) {
        let item = boxList[i].getBoundingClientRect();
        rects.push({
          name: boxList[i].getAttribute("data-name"),
          top: item.top,
          right: item.right,
          bottom: item.bottom,
          left: item.left,
          centerX: item.left + (item.right - item.left) / 2,
          centerY: item.top + (item.bottom - item.top) / 2
        })
      }
      return rects;
    },
    lineStyle(item) {
      var editTop = document.querySelector("#edit").getBoundingClientRect().top
      var editLeft = document.querySelector("#edit").getBoundingClientRect().left
      var width = item.width,
        height = item.height;
      return {
        "width": width ? width + "px" : '',
        "height": height ? height + "px" : '',
        "left": item.left - editLeft + "px",
        "top": item.top - editTop + "px",
        "border-top": width ? '1px dashed ' + item.color : '',
        "border-left": height ? '1px dashed ' + item.color : '',
      }
    }
  },
  mounted() {
    Array.prototype.contains = function (str){
      for(var i = 0; i< this.length; i++){
        if(this[i] == str){
          return true;
        }
      }
      return false;
    }
  }
}
</script>

<style lang="scss">
.alignment_line {
  z-index: 1111;
  position: absolute;
}
</style>



