<template>
  <transition name="jumpIn">
    <div id='edit_text_pannel' :class="'arrow_'+direction" v-if="showPannle" :style="pannelStyle" @click.stop>
      <div class="content">
        <p class="edit_text_notic1">{{$t('editTextPannel.text1')}}</p>
        <textarea class="edit_input" ref="textarea" v-model="text" :style="{height: inputHeight + 'px', width: pannelWidth - 40 + 'px'}" @click="changeText(true)"></textarea>
        <p class="edit_text_notic2">{{$t('editTextPannel.text2')}}</p>
        <a class="edit_text_btn" @click="changeText(false)">{{$t('editTextPannel.yes')}}</a>
      </div>
      <div class="edit_text_pannel_arrow" :style="arrowStyle"></div>
      <div 
        class="textbox_preview"
        :style="{
          width: mmToPx(selected.data.width) + 'px',
          height: mmToPx(selected.data.height) + 'px',
          textAlign: selected.data.align,
          letterSpacing: (selected.data.space / 1000) + 'em',
          lineHeight: selected.data.leading == -1 ? '1.2em' : ptToPx(selected.data.leading) + 'px',
          fontSize: ptToPx(selected.data.pt) + 'px',
          fontFamily: 'font' + selected.data.font_id + ', font0, \'Microsoft YaHei\'',
        }"
      >
        <div class="textbox_preview_content" :style="{width: mmToPx(selected.data.width) + 'px'}" ref="preview" v-text="selected.data.content || $t('editTextPannel.text3')"></div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'editTextPannel',
  props: {
    scale: {
      type: Number,
      default() {
        return 1
      }
    }
  },
  data() {
    return {
      text: '',
      show: false,
      pannelWidth: 430,
      pannelHeight: 186,
      inputHeight: 100,
      direction: '',
    }
  },
  watch: {
    selected() {
      this.$nextTick(() => {
        if(this.boxType == 'textbox'){
          this.text = this.selected.data.content !== '' ? this.selected.data.content : '';
        }
        else if(this.boxType == 'decorationbox'){
          this.text = this.selected.data.text && this.selected.data.text.content !== '' ? this.selected.data.text.content : ''
        }

        this.$nextTick(() => {
          this.$refs.textarea && this.$refs.textarea.focus();
        })
      })
    }
  },
  computed: {
    showPannle() {
      var show = this.$store.state.showEditTextPannle && (this.boxType == 'textbox' || this.selected.data.text);
      return show;
    },
    boxType() {
      if(this.selected.data.name){
        if(this.selected.data.name == 'txt'){
          return 'textbox'
        }
        else if(this.selected.data.name == 'decora'){
          return 'decorationbox'
        }
      }
      return '';
    },
    selected() {
      return this.$store.state.selected;
    },
    pannelStyle() {
      var left = 0; 
      var top = 0;
      if(this.selected.data){
        var selected = this.selected;
        var elPosit = this.selected.el.getBoundingClientRect();
        var pagePosit = this.selected.el.parentNode.getBoundingClientRect()
        var editLeft = this.$el.parentNode.offsetLeft,
          editTop = this.$el.parentNode.offsetTop;
        var direction = '';
        var pageHeight = pagePosit.bottom - pagePosit.top,
          elHeight = elPosit.bottom - elPosit.top;
        if (isNaN(selected.pageNum * 1)) { //不是pageList
          direction = selected.pageNum == 'front_cover' ? 'right' : 'left'
        }
        else {
          direction = selected.pageNum * 1 % 2 == 0 ? 'right' : 'left'
        }
        if (elHeight / pageHeight >= .25) { //高度大于25% 左右显示
          this.direction = direction;
          left = direction == 'right' ? elPosit.right - editLeft + 10 : elPosit.left - editLeft - this.pannelWidth - 10;
          top = (elHeight - this.pannelHeight) / 2 + elPosit.top - editTop;
        }
        else {
          direction = pagePosit.top + pageHeight / 2 >= elPosit.top ? 'bottom' : 'top';
          this.direction = direction;
          left = elPosit.left - editLeft + (elPosit.right - elPosit.left - this.pannelWidth) / 2;
          top = direction == 'bottom' ? elPosit.bottom + 10 - editTop : elPosit.top - this.pannelHeight - editTop - 10;
        }
      }
      return {
        left: left + "px",
        top: top + "px"
      }
    },
    arrowStyle() {
      var direction = this.direction,
          color1 = '5px solid rgba(75,75,75,0.7)',
          color2 = '5px solid transparent';
      var style = {
        top: direction == 'bottom' ? '-10px' : 0,
        left: direction == 'right' ? '-10px' : 0,
        bottom: direction == 'top' ? '-10px' : 0,
        right: direction == 'left' ? '-10px' : 0,
        borderTop: direction == 'top' ? color1 : color2,
        borderLeft: direction == 'left' ? color1 : color2,
        borderRight: direction == 'right' ? color1 : color2,
        borderBottom: direction == 'bottom' ? color1 : color2,
      }
      switch (direction) {
        case 'top':
          style.top = ''
          break
        case 'bottom':
          style.bottom = ''
          break
        case 'left':
          style.left = ''
          break
        case 'right':
          style.right = ''
          break
      }
      return style
    }
  },
  methods: {
    changeText(tag) {
      var selected = this.selected;
      var pageNum = selected.pageNum;
      var data = JSON.parse(JSON.stringify(selected.data));

      var type = '';
      if(selected.data.text){ //修改的是decorationbox
        type = 'decoration'
        data.text.content = this.text;
      }
      else{ //修改的是textbox
        type = 'textbox'
        data.content = this.text;
      }

      this.$store.commit("setState", {selected: { el: selected.el, data: data, state: selected.state, pageNum: pageNum } })

      this.$nextTick(() => {
        if(type == 'textbox'){
          var height = this.$refs.preview.offsetHeight;
          data.height = height != 0 ? this.pxToMm(height) : 3.5;
          this.$store.commit("changeBoxData", {pageNum: pageNum, boxType: type, boxId: data.box_id, data: { content: data.content, height: data.height }})
        }
        else{
          this.$store.commit("changeBoxData", {pageNum: pageNum, boxType: type, boxId: data.box_id, data: { text: {content : data.text.content} }})
        }
        setTimeout(() => {
          this.$store.commit("setState", { "showEditTextPannle": tag, selected: {state: !tag, el: this.selected.el, pageNum: this.selected.pageNum, data: this.selected.data}})
        }, 0)
      })
    }
  },
  mounted() {
    this.text = this.selected.data.content;
  }
}
</script>

<style lang="scss">
#edit_text_pannel{
  position: absolute;
  z-index: 1005;
  transform-origin: center top;
  .content{
    position: relative;
    background: rgba(75,75,75,0.8);
    box-shadow: 0 1px 6px #999;
    border-radius: 5px;
    padding: 20px;
    text-align: left;
    overflow: hidden;
    .edit_input{
      resize: none;
      box-sizing: border-box;
      border: none;
      margin-bottom: 10px;
      padding: 5px;
      font-size: 12px;
      overflow-y: scroll;
      outline: none;
    }
    p{
      font-size: 12px;
      margin: 0;
      color: white;
      &.edit_text_notic1{
        margin-bottom: 5px;
        font-weight: bold;
      }
      &.edit_text_notic2{
        margin-bottom: -5px;
        color: #f4803a;
      }
    }
    .edit_text_btn{
      cursor: pointer;
      position: absolute;
      font-size: 12px;
      right: 20px;
      bottom: 9px;
      display: inline-block;
      height: 28px;
      line-height: 28px;
      box-sizing: border-box;
      background: #d2523e;
      border-radius: 5px;
      color: white;
      padding: 0 10px 0 10px;
      &:hover{
        opacity: .9;
      }
      &:active{
        opacity: .8;
      }
    }
  }
  .edit_text_pannel_arrow{
    position: absolute;
    width: 0;
    height: 0;
    margin: auto;
  }
  .textbox_preview{
    position: absolute;
    opacity: 0;
    visibility: hidden;
    white-space: pre-wrap;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    .textbox_preview_content{
      position: absolute;
      white-space: pre-wrap;
      -ms-word-wrap: break-word;
      word-wrap: break-word;
    }
  }
}



</style>



