<template>
	<transition name="jumpIn">
  <div class="vc-container-wrapper" @click.stop>
    <div class="vc-container">
      <div class="vc-compact-container" v-if="themePalette.length != 0">
        <p class="vc-chapter">当前主题颜色</p>
        <compact-picker v-model="pColor"  v-bind:palette="themePalette"  @input="updataColor"></compact-picker>
      </div>
      <div class="vc-compact-container">
        <p class="vc-chapter">其他常用颜色</p>
        <compact-picker v-model="pColor"  v-bind:palette="commonPalette" @input="updataColor" ></compact-picker>
      </div>
      <div class="vc-morecolor">
        <label class="label_applytoallpages" v-if="canChangeAll">
          <input type="checkbox" v-model="checkAll"/>应用到所有页
        </label>
        <p class="vc-chapter" v-on:click="showMore = !showMore" :style="showMore?'color:#333':''">更多颜色...</p>
      </div>
      <div class="vc-container-arrow" v-if="showArrow"></div>
    </div>
    <transition name="jumpIn">
      <div class="vc-chrome-container" v-show="showMore">
        <chrome-picker v-model="pColor"> </chrome-picker> <!-- @input="updataValue" -->
        <div class="vc-chrome-ctrs">
          <div class="vc-chrome-ctrs-btn" v-on:click="updataColor">确定</div>
          <div class="vc-chrome-ctrs-sep"></div>
          <div class="vc-chrome-ctrs-btn" v-on:click="showMore = false">取消</div>
        </div>
      </div>
    </transition>
  </div>
	</transition>
</template>

<script>
	import { Compact, Chrome  } from 'vue-color'

	export default {
		name: 'colorpicker',
		props: {
			colors: { //格式 { hex: '#194d33', rgba: {r:25, g: 77, b:51, a:1} }
				type: Object,
				default () {
					return { hex: '#194d33'}
				}
			},
			themeColors: {
				type: Array,
				default () {
					return []
				}
			},
			commonColors: {
				type: Array,
				default () {
					return [
						"#FFFFFF", "#000000", "#DD0061", "#359CE1", "#C2B6A5", "#A0B6C0", "#F1B5A1",
						"#5E7EA0", "#F5BE12", "#E98100", "#CE0000", "#74BA36", "#D5EAF4", "#674331",
						"#A91B1E", "#1A5032", "#FBE795", "#F6D9A4", "#CBDB98", ]
				}
			},
      showArrow: {
        type: Boolean,
        default () {
          return false
        }
      },
      canChangeAll: {
        type: Boolean,
        default() {
          return false
        }
      }
		},
		components: {
			'chrome-picker': Chrome,
			'compact-picker': Compact,
		},
		data () {
			return {
				themePalette: this.themeColors,
				commonPalette: this.commonColors,
				showMore: false,
        tColor: {hex: '#fff'},
        checkAll: false,
			}
		},
    watch: {

    },
		computed: {
      pColor : {
				get () {
					return this.colors
				},
				set (newValue) {
					this.tColor = newValue
				}
			},
		},
		methods: {
			updataColor: function() {
				this.$emit('change', this.tColor, this.checkAll)
        this.showMore = false;
			}
		}
	}
</script>

<style lang="scss">

.vc-container-wrapper {
  position: absolute;
  z-index: 3001;
}

.vc-container-arrow {
  position: absolute;
  width: 8px;
  height: 8px;
  background: #fff;
  transform: rotate(45deg);
  top: -4px;
  left: 18px;
}

.vc-container {
  position: absolute;
  left: 0;
  top: 0;
  background: #fff;
  box-shadow: 0 1px 6px #666;
  padding: 10px 7px 10px 10px;
  user-select: none;
  transform-origin: 0 0;
}

.vc-chapter {
  color: #8c8c8c;
  padding: 5px 0;
  font-size: 12px;
}

.vc-compact-container {
  margin: 5px 0;

  .vc-compact {
    box-sizing: border-box;
    width: 195px;
    box-shadow: none;
    padding: 0;

    .vc-compact-colors{
      padding: 3px;

      .vc-compact-color-item {
        box-shadow: 1px 1px 5px #aaa;
        transition: all .3s ease;
        margin: 3px;

        &:hover {
          transform: scale(1.3);
        }
      }
    }
  }
}

.vc-morecolor {
  position: relative;
  .vc-chapter {
    cursor: pointer;
    display: inline-block;
  }
}

.vc-chrome-container {
  position: absolute;
  left: 212px;
  top: 0;
  transform-origin: 0 0;
}

.vc-chrome-ctrs {
  display: flex;
  width: 225px;
  box-shadow: 0 0 2px rgba(0, 0, 0, .3), 0 4px 8px rgba(0, 0, 0, .3);
  background: #fff;
  .vc-chrome-ctrs-btn {
    display: flex;
    flex: 1;
    justify-content: center;
    height: 24px;
    line-height: 24px;
    cursor: pointer;
    &:hover {
      color: #333;
    }
  }
  .vc-chrome-ctrs-sep {
    display: block;
    border-left: 1px solid #ccc;
    flex: 0;
  }
}

.label_applytoallpages {
  float: right;
  margin-top: 7px;
  input{
    vertical-align: middle;
    margin-right: 3px;
    margin-top: -2px;
  }
}

.vc-chrome-alpha-wrap {
  display: none;
}

.vc-chrome-hue-wrap {
  margin-bottom: 0;
  margin-top: 4px;
}

.vc-chrome-controls .vc-chrome-color-wrap {
  .vc-chrome-active-color,
  .vc-checkerboard {
    width: 20px;
    height: 20px;
  }
}

.vc-chrome-fields .vc-chrome-field:nth-of-type(4) {
  display: none;
}

.jump-enter,
.jump-leave-to {
  opacity: 0;
  transform: scale(0);
}

.jump-enter-active,
.jump-leave-active {
  transition: all .3s ease;
}

.jump-enter-to,
.jump-leave {
  opacity: 1;
  transform: scale(1);
}

</style>



