<template>
  <div id="zoom_bar">
    <div class="zoom_bar_btn zoom_bar_zoomout" @mousedown="zoomDown($event, 'zoomout')" @mouseup="zoomUp($event)" @click="eventDown($event, 'zoomout')"></div>
    <div class="zoom_bar_line" ref="zoomline" @click="clickLine($event)">
      <div class="zoom_bar_btn zoom_bar_pointer" @mousedown="dragDown($event)" :style="{
        transform: 'translate(' + x + 'px, 0px)',
        transition: zooming ? 'all .3s' : undefined
      }"></div>
    </div>
    <div class="zoom_bar_btn zoom_bar_zoomin" @mousedown="zoomDown($event, 'zoomin')" @mouseup="zoomUp($event)" @click="eventDown($event, 'zoomin')"></div>
    <div class="zoom_bar_btn zoom_bar_auto" @mousedown="zoomDown($event, 'auto')" @mouseup="zoomUp($event)"  @click="eventDown($event, 'auto')"></div>
  </div>
</template>
<script>
export default {
  name: 'editzoombar',
  props: [
    'el',
    'scale',
    'zoomScale',
    'zoomPannel'
  ],
  data() {
    var zoombar = this.Config.zoombar;

    return {
      zoomValue: zoombar.spacing / ((zoombar.maxPx - zoombar.minPx) / (zoombar.maxScale - zoombar.minScale)), //每次缩放的倍数值
      startX: 0,
      zooming: false,
      transitionTimer: null,//过渡效果计时器
      pinTimer: null //按住计时器
    }
  },
  computed:{
    x(){
      var zoombar = this.Config.zoombar;
      return (this.scale - zoombar.minScale) * (zoombar.maxPx - zoombar.minPx) / (zoombar.maxScale - zoombar.minScale);
    }
  },
  methods: {
    eventDown(e, type) {
      clearInterval(this.pinTimer)
      this.zoomDown(e, type);
      setTimeout(() => {
        this.zoomUp(e);
      }, 0)
    },
    zoomDown(e, type) {
      var that = this;

      that.setScaleByType(type);

      this.pinTimer = setInterval(function() {
        that.setScaleByType(type);
      }, 50)
    },
    zoomUp(e){
      clearInterval(this.pinTimer)
      this.pinTimer = null;
    },
    clickLine(e){
      //拖动中就跳过触发click
      if(this.$store.state.isMove){
        this.$store.commit('setState', { isMove: false });
        return;
      }

      this.setAnimation();

      var x =  e.pageX - this.$refs.zoomline.getBoundingClientRect().left;
      this.setScaleByX(x);
    },
    dragDown(e) {
      this.DragDown(e, {
        dragStart: this.dragStart,
        drag: this.drag,
        dragEnd: this.dragEnd
      });
    },
    dragStart(e, args){
      var zoombar = this.Config.zoombar;
      this.startX = (this.scale - zoombar.minScale) * (zoombar.maxPx - zoombar.minPx) / (zoombar.maxScale - zoombar.minScale);
    },
    drag(e, args){
      var x = this.startX + args.x;
      this.setScaleByX(x);
    },
    dragEnd(e, args){

    },
    setScaleByX(x){
      var zoombar = this.Config.zoombar;
      x = x > zoombar.maxPx ? zoombar.maxPx : x;
      x = x < zoombar.minPx ? zoombar.minPx : x;
      var scale = x / zoombar.maxPx * (zoombar.maxScale - zoombar.minScale) + zoombar.minScale;

      this.$emit('btnZoomPannel', scale);
    },
    setScaleByType(type, xy) {
      this.setAnimation();

      var zoombar = this.Config.zoombar;
      var scale;
      switch (type) {
        case 'zoomout':
          scale = this.scale - this.zoomValue < zoombar.minScale ? zoombar.minScale : this.scale - this.zoomValue;
          xy ? this.$emit('wheelZoomPannel', scale, xy) : this.$emit('btnZoomPannel', scale);
          break;
        case 'zoomin':
          scale = this.scale + this.zoomValue > zoombar.maxScale ? zoombar.maxScale : this.scale + this.zoomValue;
          xy ? this.$emit('wheelZoomPannel', scale, xy) : this.$emit('btnZoomPannel', scale);
          break;
        case 'auto':
          this.$emit('autoPannel');
          break;
      }
    },
    //设置过渡效果
    setAnimation() {
      var that = this;
      this.zooming = true;

      if (this.transitionTimer) {
        clearTimeout(this.transitionTimer);
        this.transitionTimer = null;
      }
      this.transitionTimer = setTimeout(function() {
        that.zooming = false;
        that.transitionTimer = null;
      }, 300);
    }
  }
}
</script>
<style lang="scss">
#zoom_bar {
  position: absolute;
  bottom: 5px;
  left: 0;
  right: 0;
  margin: auto;
  width: 235px;
  height: 30px;

  .zoom_bar_btn {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 20px;
    height: 20px;
    margin: auto;
    cursor: pointer;
  }

  .zoom_bar_zoomout {
    background: url(../../assets/book/image/zoombar/icon_zoomout.png) no-repeat;
    left: 0;
  }
  .zoom_bar_zoomin {
    background: url(../../assets/book/image/zoombar/icon_zoomin.png) no-repeat;
    right: 0;
  }
  .zoom_bar_line {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 196px;
    height: 7px;
    margin: auto;
    background: url(../../assets/book/image/zoombar/icon_bar.png) no-repeat;
    cursor: pointer;
  }
  .zoom_bar_auto {
    background: url(../../assets/book/image/zoombar/icon_auto.png) no-repeat;
    background-position: center;
    left: -25px;
  }
  .zoom_bar_pointer {
    background: url(../../assets/book/image/zoombar/icon_pointer.png) no-repeat;
    width: 16px;
    height: 26px;
  }
}
</style>



