<script>
export default {
  install(Vue) {
    //获取当前页的所有box
    var getBoxList = function (pageData) {
      var boxList = [];
      for(let i in pageData){
        if(i == 'patternbox_list'){
          continue;
        }
        else{
          if(i.indexOf("_list") >= 0 && pageData[i]){
            for(let j = 0; j < pageData[i].length; j++){
              boxList.push(pageData[i][j])
            }
          }
        }
      }
      return boxList
    }

    //重新排序index
    var formatIndex = function(boxList) {
      var indexList = [];
      for(let i = 0; i < boxList.length; i++){
        indexList.push({
          i: i,
          index: boxList[i].index
        });
      }
      indexList.sort((a, b) => a.index - b.index);
      for(let i = 0; i < indexList.length; i++){
        var item = indexList[i];
        boxList[item.i].index = i;
      }
    }

    //获取最高的index
    Vue.prototype.GetLastIndex = function(pageData) {
      var boxList = getBoxList(pageData);
      var max = 0;
      for(let i in boxList){
        if(boxList[i].index > max){
          max = boxList[i].index;
        }
      }
      return max
    };

    //修改index
    Vue.prototype.ChangeIndex = function (type) {
      var bookInfo = this.$store.state.bookInfo,
          selected = this.$store.state.selected,
          pageNum = selected.pageNum,
          data = selected.data ? JSON.parse(JSON.stringify(selected.data)): '';

      if(selected.data){
        var pageData = !isNaN(pageNum) ? JSON.parse(JSON.stringify(bookInfo.page_list[pageNum])) : JSON.parse(JSON.stringify(bookInfo[pageNum]));
        var boxList = getBoxList(pageData);
        var maxIndex = this.GetLastIndex(pageData),
            dataIndex = data.index;

        if(selected.data.name == 'pattern'){ //底纹不变
          return ;
        }
        switch(type){
          case 'up':
            for(var i in boxList){
              if(boxList[i].name == data.name && boxList[i].box_id == data.box_id){
                boxList[i].index = boxList[i].index == maxIndex ? maxIndex :  boxList[i].index + 1;
                data = boxList[i]
              }
              else{
                if(boxList[i].index == dataIndex + 1){
                  boxList[i].index = data.index <= 0 ? 0 : data.index - 1;
                }
              }
            }
            break;
          case 'down':
            for(var i in boxList){
              if(boxList[i].name == data.name && boxList[i].box_id == data.box_id){
                boxList[i].index = boxList[i].index <= 0 ? 0 : boxList[i].index - 1;
                data = boxList[i]
              }
              else{
                if(boxList[i].index == dataIndex - 1){
                  boxList[i].index = boxList[i].index + 1
                }
              }
            }
            break;
          case 'top':
            for(var i in boxList){
              if(boxList[i].name == data.name && boxList[i].box_id == data.box_id){
                boxList[i].index = maxIndex + 1;
                data = boxList[i]
              }
            }
            formatIndex(boxList);
            break;
          case 'bottom':
            for(var i in boxList){
              if(boxList[i].name == data.name && boxList[i].box_id == data.box_id){
                boxList[i].index = 0;
                data = boxList[i]
              }
              else{
                boxList[i].index += 1;
              }
            }
            formatIndex(boxList);
            break;
          default: 
            break
        }
        this.$store.commit("changePageData", {pageNum : pageNum, data: pageData})
        this.$nextTick(() => {
          this.$store.commit("setState",{selected: {pageNum, el: selected.el, data: data, state: selected.state}})
        })
      }
    }

  }
}
</script>



