<template>
  <transition name="addFadeIn">
    <div class='box imgbox' data-type="imagebox" :data-id="imgBox.box_id"  :data-name="imgBox.name + imgBox.box_id"
      :class="{'active': active, 'focus': checkFocus(), 'edit': edit, 'drag-in': photoDragIn()}"
      :style="{
        width: mmToPx(imgBox.width) + 'px',
        height: mmToPx(imgBox.height) + 'px',
        zIndex: imgBox.index,
        borderWidth: imgBox.border_width ? ptToPx(imgBox.border_width) +'px' : 0,
        borderColor: imgBox.border_color,
        transform: 'translate(' + mmToPx(imgBox.x  - bleed) + 'px, ' + mmToPx(imgBox.y - bleed) + 'px) rotate(' + imgBox.rotation + 'deg)',
      }"
      @mousedown.prevent="mouseDown($event)"  @click.prevent="clickEvent($event)"
    >
      <div class="img_cover" v-if="imgBox.image && edit" :style="{
          width: mmToPx(imgBox.image.width) + 'px',
          height: mmToPx(imageHeight) + 'px',
          left: mmToPx(imgBox.image.x) +'px',
          top: mmToPx(imgBox.image.y) +'px',
          transform: 'rotate('+ imgBox.image.rotation +'deg)'
        }">
        <img :src="image && this.CommonMethods.joinPhotoHost(image.src)" />
      </div>
      <div class="definition_warning" v-if="state()" ref="tip" :style="{'transform': 'scale('+ precisionScale +')' }" @mouseover.stop="showTips(true)" @mouseout.stop="showTips(false)" @mousedown.prevent>!</div>
      <div class="no_image" :text="$t('imgBox.text')" v-if="!imgBox.image"></div>
      <span class="require_size" v-if="!imgBox.image || edit" v-text="definition.w + 'x' + definition.h"></span>
      <transition name="addImg" :key="imgBox.image && imgBox.image.id">
        <div class="img_content" v-if="imgBox.image">
          <img ref="image" v-if="imgBox.image.id != 'logo'" :src="image && this.CommonMethods.joinPhotoHost(image.src)"
            :style="{
              width: mmToPx(imgBox.image.width) + 'px',
              left: mmToPx(imgBox.image.x) +'px',
              top: mmToPx(imgBox.image.y) +'px',
              transform: 'rotate('+ imgBox.image.rotation +'deg)'
            }"/>
            <img v-else :src="'http://www.mimoprint.com' + imgBox.image.src"
              :style="{
              width: mmToPx(imgBox.image.width) + 'px',
              left: mmToPx(imgBox.image.x) +'px',
              top: mmToPx(imgBox.image.y) +'px',
              transform: 'rotate('+ imgBox.image.rotation +'deg)'
            }"/>
        </div>
      </transition>
      <MultipleSelected :box="imgBox" :pageNum="page.num"></MultipleSelected>
    </div>
  </transition>
</template>

<script>
import MultipleSelected from '@/components/common/multipleSelected'
export default {
  name: 'imgbox',
  props: {
    scale: {
      type: Number,
      defalut() {
        return 1
      }
    },
    page: {
      type: Object,
      defalut() {
        return {}
      }
    },
    imgBox: {
      type: Object,
      defalut() {
        return {}
      }
    },
    bleed: {
      type: Number,
      defalut() {
        return 0
      }
    }
  },
  data() {
    return {
      edit: false,
      startX: 0,
      startY: 0,
      mouseStartX: 0,
      mouseStartY: 0,
      beforePageData: {},
      precisionScale: 0,
    }
  },
  components: {
    MultipleSelected,
  },
  computed: {
    image() {
      return this.imgBox.image && this.GetImage(this.imgBox.image.id)
    },
    imageHeight() {
      return this.image.originalHeight / this.image.originalWidth * this.imgBox.image.width
    },
    active() {
      var isActive = this.isActive(this.page.num, this.imgBox);
      this.edit = !isActive ? false : this.edit
      return isActive;
    },
    editImage() {
      return this.$store.state.editImage;
    },
    definition() {
      return {
        w: Math.floor(this.DefinitionMmToPx(this.imgBox.width)),
        h: Math.floor(this.DefinitionMmToPx(this.imgBox.height))
      }
    },
    selected() {
      return this.$store.state.selected;
    }
  },
  watch: {
    selected(val) {
      if(!val.data || (val.data.box_id != this.imgBox.box_id || val.data.name != this.imgBox.name)){
        this.$el.style.zIndex = this.imgBox.index;
      }
    }
  },
  methods: {
    mouseDown(e){
      if(e.button == 2) return
      if(!this.imgBox.image) {
        this.edit = false;
      }
      if(this.active && !this.IsMultipleSelect()){
        if(this.edit){
          //拖动图片
          if(!this.imgBox.image) return ;
          this.DragDown(e, {
            dragStart: this.imageDragStart,
            drag: this.imageDrag,
            dragEnd: this.imageDragEnd
          })
        }else{
          //拖动box
          this.DragDown(e, {
            dragStart: this.boxDragStart,
            drag: this.boxDrag,
            dragEnd: this.boxDragEnd
          })
        }
      }
    },
    clickEvent(e) {
      var selected = this.$store.state.selected;
      if(this.editImage && this.page.num == selected.pageNum && this.imgBox.box_id == selected.data.box_id){
        this.edit = true;
      }
      this.SelectElement(e, {
        el: this.$el,
        box: this.imgBox,
        pageNum: this.page.num,
        select: this.select
      });
      this.$nextTick(() => {
        if(this.edit){
          this.$el.style.zIndex = 1000;
        }
        else{
          this.$el.style.zIndex = this.imgBox.index;
        }
      })
    },
    imageDragStart(e, args) {
      this.mouseStartX = e.pageX;
      this.mouseStartY = e.pageY;
      this.startX = this.imgBox.image.x;
      this.startY = this.imgBox.image.y;

      this.beforePageData = JSON.parse(JSON.stringify(this.page));

    },
    imageDrag(e, args) {
      var elW = this.imgBox.image.width,
          elH = this.imageHeight,
          elX = this.startX,
          elY = this.startY,
          boxW = this.imgBox.width,
          boxH = this.imgBox.height,
          rotation = this.imgBox.image.rotation;

      var x = 0;
      var y = 0;

      var isVertical = Math.abs(rotation) == 90 || Math.abs(rotation) == 270;
      if(isVertical){
        var moveX = e.pageX,
            moveY = e.pageY;
        y = this.startY + this.pxToMm((moveY - this.mouseStartY)) / this.scale;
        x = this.startX + this.pxToMm((moveX - this.mouseStartX)) / this.scale;

        var w = elH,
            h = elW,
            minX = 0,
            maxX = 0,
            minY = 0,
            maxY = 0;
        minX = w > boxW ? boxW - w : 0;
        minX = isVertical ? minX - (elW - elH) / 2 : minX;
        maxX = w > boxW ? 0 : boxW - w;
        maxX = isVertical ? maxX - (elW - elH) / 2 : maxX;

        minY = h > boxH ? boxH - h : 0;
        minY = isVertical ? minY - (elH - elW) / 2 : minY;
        maxY = h > boxH ? 0 : boxH - h;
        maxY = isVertical ? maxY - (elH - elW) / 2 : maxY;

        x = x < minX ? minX : x;
        x = x > maxX ? maxX : x;
        y = y < minY ? minY : y;
        y = y > maxY ? maxY : y;
      }
      else{
        var xy = this.ImageLimitSize({
          dragX: this.pxToMm(args.x),
          dragY: this.pxToMm(args.y),
          elX: elX,
          elY: elY,
          scale: this.scale,
          elW: elW,
          elH: elH,
          boxW: boxW,
          boxH: boxH,
        });
        x = xy.x;
        y = xy.y;
      }

      this.$store.commit("changeBoxData", {
        pageNum: this.page.num,
        boxType: 'imagebox',
        boxId: this.imgBox.box_id,
        data: {image: {x: x, y: y}},
        selected:{ state: false }
      })
    },
    imageDragEnd(e, args) {
      if (!this.$store.state.isMove && this.imgBox.image && this.active && !this.edit) {
        this.edit = true;
      }
      else if (!this.$store.state.isMove) {
        this.edit = false;
      }
      this.$store.commit("addUndoData", {pageNum: this.page.num, data: this.beforePageData});
    },
    boxDragStart(e, args) {
      this.startX = this.imgBox.x;
      this.startY = this.imgBox.y;
      this.beforePageData = JSON.parse(JSON.stringify(this.page));
    },
    boxDrag(e, args) {
      if (this.imgBox.locked){
        this.$store.dispatch("lockedDrag");
        return 
      }
      var xy = this.BoxLimitSize({
        dragX: this.pxToMm(args.x),
        dragY: this.pxToMm(args.y),
        boxX: this.startX,
        boxY: this.startY,
        scale: this.scale,
        boxW: this.imgBox.width,
        boxH: this.imgBox.height,
        pageWidth: this.page.width,
        pageHeight: this.page.height,
      });

      this.$store.commit("changeBoxData", {
        pageNum: this.page.num,
        boxType: 'imagebox',
        boxId: this.imgBox.box_id,
        data: { x: xy.x, y: xy.y },
        selected: { state: false }
      })

      this.$store.commit('setState', {
        warningTip: { visible: false, elRect: { x: 0, y: 0 } },
        sublineType: 'move',
        dragResize: { type: 'xy', x: e.pageX + 20, y: e.pageY }//拖动尺寸tip
      });
    },
    boxDragEnd(e, args) {
      if (!this.$store.state.isMove && this.imgBox.image && this.active && !this.edit) {
        this.edit = true;
      }
      else if (!this.$store.state.isMove) {
        this.edit = false;
      }

      this.$store.commit('setState', {
        selected: { state: !this.edit, el: this.$el, data: this.imgBox, pageNum: this.page.num },
        sublineType: '',
        dragResize: { type: null, x: 0, y: 0 }//拖动尺寸tip
      })

      this.$store.commit("addUndoData", { pageNum: this.page.num, data: this.beforePageData });
    },
    checkFocus() {
      var that = this;
      var state = this.imgBox.image && this.imgBox.image.id == this.$store.state.focusPhotoId ? true : false;
      setTimeout(function() {
        that.$store.commit('setState', { focusPhotoId: '' });
      }, 1000);
      return state;
    },
    photoDragIn() {
      //图片拖拽进来了
      var dragPhoto = this.$store.state.dragStorePhoto;
      return this.page.num == dragPhoto.pageNum && this.imgBox.box_id == dragPhoto.boxId;
    },
    select(e) {
      //选中回调
    },
    computeDefinition(imgOriginalWidthPx, imgWidthMM) {
      var imgOriginalWidthMM = this.pxToMm(imgOriginalWidthPx); //算出图原样印刷出来的毫米宽度
      return imgOriginalWidthMM / imgWidthMM; //清晰度 = 原始宽度 / 印刷宽度
    },
    state() {
      var photo = this.image;
      if (photo) {
        var definition = this.computeDefinition(photo.originalWidth, this.imgBox.image.width);
        if (definition < 0.9) {
          this.precisionScale = Math.min((1 / definition), 3);
          return true
        } else {
          return false;
        }
      }
      return false;
    },
    showTips(tag) {
      var rect = '';
      if(this.$refs.tip){
        rect = this.$refs.tip.getBoundingClientRect()
      }
      this.$store.commit("setState", { warningTip: {visible: tag, elRect: rect, w: this.definition.w, h: this.definition.h} })
    },
  },
  mounted: function() {
    if(this.imgBox.image && this.imgBox.image.id == 'logo'){
      this.$nextTick(() => {
        this.$el.style.pointerEvents = "none"
      })
    }
  },
}
</script>

<style lang="scss">
@keyframes focus {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.focus {
  animation: focus 1s;
}

.imgbox {
  overflow: hidden;
  .img_content {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    left: 0;
    top: 0;
    img {
      position: absolute;
    }
  }
  .img_cover {
    position: absolute;
    left: 0;
    background: #000;
    visibility: visible;
    opacity: .8;
    img {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: .3;
    }
  }

  .require_size{
    position: absolute;
    color: white;
    top: 10px;
    left: 10px;
    font-size: 20px;
  }
  .no_image {
    width: 100%;
    height: 100%;
    background: #ccc;

    &:after {
      content: attr(text);
      position: absolute;
      font-size: 14px;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      height: 20px;
      text-align: center;
    }
  }

  &:hover,
  &:active{
    background: rgba(230, 230, 230, 0.3);
    box-shadow: inset 1px 0 10px #ccc;
    cursor: pointer;
  }
  &.edit {
    background: #ccc;
    overflow: visible;
    &:hover{
      cursor: all-scroll;
    }
  }
  &.drag-in{
    .no_image{
      background-color: #cdc;
    }
  }
}

.addImg-enter-active,.addImg-leave-active{
  transition-property: transform, opacity;
  transition-duration: .2s;
  transition-timing-function: ease-out;
}
.addImg-enter,.addImg-leave-to{
  transform: scale(1.7);
  opacity: 0;
}
.addImg-enter-to,.addImg-leave{
  transform: scale(1);
  opacity: 1;
}

.definition_warning{
    position: absolute;
    top: 20px;
    right: 20px;
    margin: auto;
    background: red;
    font-family: SimHei;
    font-size: 26px;
    line-height: 36px;
    height: 36px;
    width: 36px;
    font-weight: bold;
    text-align: center;
    color: white;
    -webkit-box-shadow: 0 1px 8px #999;
    box-shadow: 0 1px 8px #999;
    border-radius: 4px;
    z-index: 2000;
    cursor: pointer;
    -webkit-transform-origin: right top;
    -moz-transform-origin: right top;
    -ms-transform-origin: right top;
    transform-origin: right top;
}

.ispreview{
  .no_image{
    display: none;
  }
  .require_size{
    display: none;
  }
}

</style>



