<template>
  <transition name="addFadeIn">
    <div
      class='box decorabox' :class="{'active': active}"
      data-type="decoration" :data-name="decoraBox.name + decoraBox.box_id"
      @mousedown.prevent="mouseDown($event)" @click="clickEvent($event)"
      :style="{
        width: mmToPx(decoraBox.width) + 'px',
        height: mmToPx(decoraBox.height) + 'px',
        zIndex: decoraBox.index,
        transform: 'translate(' + mmToPx(decoraBox.x - bleed) +'px, ' + mmToPx(decoraBox.y - bleed) + 'px) rotate(' + decoraBox.rotation + 'deg)',
      }"
    >
      <div class="content">
        <img :src="'http://cdn-mimocampaign.mimoprint.com' + decoraBox.src.replace('Theme', 'theme') + '@!w500'" v-if="decoraBox.type != 2 && decoraBox.type != 3" />
        <img :src="canvasImg" v-else>
        <div
          class="decora_text" v-if="decoraBox.text"
          :class="{
            'no_content': decoraBox.text.content == '',
            'active': active,
            'edit': edit
          }"
          :style="{
            fontSize: ptToPx(decoraBox.text.pt) + 'px',
            fontFamily: 'font' + decoraBox.text.font_id + ', font0, \'Microsoft YaHei\'',
            width: mmToPx(decoraBox.text.width * textScale) + 'px',
            height: mmToPx(decoraBox.text.height * textScale) + 'px',
            left: mmToPx(decoraBox.text.x * textScale) + 'px',
            top: mmToPx(decoraBox.text.y * textScale) + 'px',
            color: decoraBox.text.color || '#000',
            textAlign: decoraBox.text.align,
            lineHeight: decoraBox.text.leading == -1 ? '1.2em' : ptToPx(decoraBox.text.leading) + 'px',
            letterSpacing: (decoraBox.text.space / 1000) + 'em',
          }"
        >
          <div class="text_content" :txt="afterTxt" v-text="decoraBox.text.content"></div>
        </div>
      </div>
      <canvas ref="canvas" class="mycanvas" :style="{width: mmToPx(decoraBox.width) + 'px', height: mmToPx(decoraBox.height) + 'px'}"></canvas>
      <MultipleSelected :box="decoraBox" :pageNum="page.num"></MultipleSelected>
    </div>
  </transition>
</template>

<script>
import MultipleSelected from '@/components/common/multipleSelected'
export default {
  name: 'decorabox',
  props: {
    scale: {
      type: Number,
      defalut() {
        return 1
      }
    },
    page: {
      type: Object,
      defalut() {
        return {}
      }
    },
    decoraBox: {
      type: Object,
      defalut() {
        return {}
      }
    },
    bleed: {
      type: Number,
      defalut() {
        return 0
      }
    }
  },
  data() {
    return {
      edit: false,
      canvasImg: '',
      color: this.decoraBox.color,
      isMineCancel: false,
      beforePageData: {},
    }
  },
  watch: {
    decoraBox: {
      handler(newValue, oldValue) {
        if(newValue.color != this.color){
          this.getCanvasImg((src) => {
            this.canvasImg = src;
            this.color = newValue.color;
          });
        }
      },
      deep: true
    },
    showEditTextPannle() {
      if(!this.showEditTextPannle && !this.isMineCancel){
        this.edit = false;
      }
    }
  },
  components: {
    MultipleSelected
  },
  computed: {
    textScale() {
      if (this.decoraBox.text) {
        return this.decoraBox.width / this.decoraBox.text.d_width
      }
      return 1;
    },
    active() {
      return this.isActive(this.page.num, this.decoraBox)
    },
    showEditTextPannle() {
      return this.$store.state.showEditTextPannle;
    },
    afterTxt() {
      if(this.decoraBox.text && this.decoraBox.text.content === ''){
        if(this.edit){
          return ''
        }
        if(this.active){
          return this.$t("decoraBox.text2")
        }
        return this.$t("decoraBox.text1")
      }
      return '';
    }
  },
  methods: {
    mouseDown(e) {
      if(e.button == 2) return;
      if (this.active && !this.IsMultipleSelect()) {
        this.DragDown(e, {
          dragStart: this.dragStart,
          drag: this.drag,
          dragEnd: this.dragEnd
        })
      }
      this.$store.commit('setState', { showEditTextPannle: false })
    },
    clickEvent(e) {
      this.SelectElement(e, {
        el: this.$el,
        box: this.decoraBox,
        pageNum: this.page.num,
        select: this.select
      });
    },
    dragStart(e, args) {
      this.startX = this.decoraBox.x;
      this.startY = this.decoraBox.y;
      this.isMineCancel = true;
      this.beforePageData = JSON.parse(JSON.stringify(this.page));
    },
    drag(e, args) {
      if (!this.decoraBox.locked) {
        var xy = this.BoxLimitSize({
          dragX: this.pxToMm(args.x),
          dragY: this.pxToMm(args.y),
          boxX: this.startX,
          boxY: this.startY,
          scale: this.scale,
          boxW: this.decoraBox.width,
          boxH: this.decoraBox.height,
          pageWidth: this.page.width,
          pageHeight: this.page.height,
        });

        this.$store.commit("changeBoxData", {
          pageNum: this.page.num,
          boxType: 'decoration',
          boxId: this.decoraBox.box_id,
          data: { x: xy.x, y: xy.y},
          selected:{ state: false }
        })
        this.$store.commit('setState', {
          sublineType: 'move',
          dragResize: {type: 'xy', x: e.pageX + 20, y: e.pageY} //拖动尺寸tip
        })
      }
      else{
        this.$store.dispatch("lockedDrag");
      }
    },
    dragEnd(e, args) {
      if (this.active && !this.$store.state.isMove) {
        this.edit = !this.edit
      }

      this.isMineCancel = false;

      if(this.decoraBox.type == 3){
        this.$store.commit('setState', {
          selected: {state: !this.edit, el: this.$el, data: this.decoraBox, pageNum: this.page.num},
          showEditTextPannle: this.edit,
          sublineType: '',
          dragResize: {type: null, x: 0, y: 0} //拖动尺寸tip
        })
      }
      else{
        this.$store.commit('setState', {
          selected: {state: true, el: this.$el, data: this.decoraBox, pageNum: this.page.num},
          sublineType: '',
          dragResize: {type: null, x: 0, y: 0} //拖动尺寸tip
        })
      }
      this.$store.commit("addUndoData", {pageNum: this.page.num, data: this.beforePageData});
    },
    select(e) {

    },
    getCanvasImg(cb) {
      var that = this;
      var width = this.mmToPx(this.decoraBox.width),
        height = this.mmToPx(this.decoraBox.height),
        boxColor = this.decoraBox.color || '#fff';
      if (this.$refs.canvas) {
        var ctx = this.$refs.canvas.getContext('2d');
        this.$refs.canvas.width = width
        this.$refs.canvas.height = height
        var img = new Image();
        img.crossOrigin = "*";
        img.src = this.CommonConfig.materialHost + this.decoraBox.src.replace(/Theme/, "theme") + '@!w500';
        img.onload = function (){
          ctx.drawImage(img, 0, 0, width, height);
          var imagedata = ctx.getImageData(0, 0, width, height);
          var color = that.toRgb(boxColor);
          for (var i = 0, n = imagedata.data.length; i < n; i += 4) {
            imagedata.data[i + 0] = color[0]; //r
            imagedata.data[i + 1] = color[1]; //g
            imagedata.data[i + 2] = color[2]; //b
          }
          ctx.putImageData(imagedata, 0, 0);

          var url = that.$refs.canvas.toDataURL("image/png");
          cb(url)
        }
      }
    },
    toRgb(color) {
      var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
      var sColor = color.toLowerCase();
      if (sColor && reg.test(sColor)) {
        if (sColor.length === 4) {
          var sColorNew = "#";
          for (var i = 1; i < 4; i += 1) {
            sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
          }
          sColor = sColorNew;
        }
        //处理六位的颜色值
        var sColorChange = [];
        for (var i = 1; i < 7; i += 2) {
          sColorChange.push(parseInt("0x" + sColor.slice(i, i + 2)));
        }
        return sColorChange;
      } else {
        return sColor;
      }
    },

  },
  mounted: function(){
    this.getCanvasImg((src) => {
      this.canvasImg = src;
    });
  },
}
</script>

<style lang="scss">
.decorabox {
  .content {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &:hover{
    cursor: pointer;
  }
  .mycanvas {
    opacity: 0;
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
  }

  .decora_text {
    position: absolute;
    white-space: pre-wrap;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    overflow: hidden;
    .text_content{
      &:after{
        content: attr(txt)
      }
    }
    &:hover,
    &.active {
      background: rgba(200, 200, 200, 0.3);
      box-shadow: inset 1px 0 10px #ccc;
      cursor: pointer;
    }
    &:hover {
      background: rgba(230, 230, 230, 0.3);
      box-shadow: inset 1px 0 10px #ccc;
    }
  }

}
</style>



