<template>
  <transition name="addFadeIn">
    <div class='box shapebox' data-type="colorbox" :class="{'active': active}" :data-name="shapeBox.name + shapeBox.box_id" @mousedown.prevent="mouseDown($event)" @click="clickEvent($event)" :style="{
              width: mmToPx(shapeBox.width) + 'px',
              height: mmToPx(shapeBox.height) + 'px',
              borderWidth: ptToPx(shapeBox.border_width) +'px' || 0,
              borderColor: shapeBox.border_color || '#fff',
              zIndex: shapeBox.index,
              transform: 'translate(' + mmToPx(shapeBox.x - bleed) +'px, ' + mmToPx(shapeBox.y - bleed) + 'px) rotate(' + shapeBox.rotation + 'deg)',
            }">
      <div class="content" :style="{ opacity: shapeBox.opacity, backgroundColor: shapeBox.color,}" ></div>
      <MultipleSelected :box="shapeBox" :pageNum="page.num"></MultipleSelected>
    </div>
  </transition>
</template>

<script>
import MultipleSelected from '@/components/common/multipleSelected'
export default {
  name: 'shapebox',
  props: {
    scale: {
      type: Number,
      defalut() {
        return 1
      }
    },
    page: {
      type: Object,
      defalut() {
        return {}
      }
    },
    shapeBox: {
      type: Object,
      defalut() {
        return {}
      }
    },
    bleed: {
      type: Number,
      defalut() {
        return 0
      }
    }
  },
  data() {
    return {
      selected: false,
      beforePageData: {},
    }
  },
  components: {
    MultipleSelected
  },
  computed: {
    active() {
      return this.isActive(this.page.num, this.shapeBox);
    }
  },
  methods: {
    mouseDown(e) {
      if(e.button == 2) return ;
      if(this.active && !this.IsMultipleSelect()){
        this.DragDown(e, {
          dragStart: this.dragStart,
          drag: this.drag,
          dragEnd: this.dragEnd
        });
      }
    },
    clickEvent(e) {
      this.SelectElement(e, {
        el: this.$el,
        box: this.shapeBox,
        pageNum: this.page.num,
        select: this.select
      });
    },
    dragStart(e, args) {
      this.startX = this.shapeBox.x;
      this.startY = this.shapeBox.y;

      this.beforePageData = JSON.parse(JSON.stringify(this.page));
    },
    drag(e, args) {
      if(!this.shapeBox.locked){
        var xy = this.BoxLimitSize({
          dragX: this.pxToMm(args.x),
          dragY: this.pxToMm(args.y),
          boxX: this.startX,
          boxY: this.startY,
          scale: this.scale,
          boxW: this.shapeBox.width,
          boxH: this.shapeBox.height,
          pageWidth: this.page.width,
          pageHeight: this.page.height,
        });

        this.$store.commit("changeBoxData", {
          pageNum: this.page.num,
          boxType: 'colorbox',
          boxId: this.shapeBox.box_id,
          data: { x: xy.x, y: xy.y},
          selected:{ state: false }
        })
        //拖动尺寸tip
        this.$store.commit('setState', {sublineType: 'move', dragResize: {type: 'xy', x: e.pageX + 20, y: e.pageY}})
      }
      else{
        this.$store.dispatch("lockedDrag");
      }
    },
    dragEnd(e, args) {
      this.$store.commit('setState', {
        selected: {state: true, pageNum: this.page.num, data: this.shapeBox, el: this.$el, },
        sublineType: '',
        dragResize: {type: null, x: 0, y: 0} //拖动尺寸tip
      })

      this.$store.commit("addUndoData", {pageNum: this.page.num, data: this.beforePageData});
    },
    select(e, args) {

    }
  },
  mounted() {
  }
}
</script>

<style lang="scss">
.shapebox {
  .content{
    width: 100%;
    height: 100%;
  }
  &:hover{
    cursor: pointer;
  }
}
</style>



