<script>
export default {
  install(Vue, dpi) {
    Vue.prototype.mmToPx = function(mm) {
      return mm * dpi * (1 / 25.4);
    };
    Vue.prototype.pxToMm = function(px) {
      return px / dpi / (1 / 25.4);
    };
    Vue.prototype.mmToPt = function(mm) {
      return mm * (72 / 25.4);
    };
    Vue.prototype.ptToMm = function(pt) {
      return pt * (25.4 / 72);
    };
    Vue.prototype.ptToPx = function(pt) {
      return pt / 72 * dpi;
    };
    Vue.prototype.pxToPt = function(px) {
      return px / dpi * 72;
    };
    Vue.prototype.toFloat = function(value, num = 6) {
      value = value * 1;//确保不是字符串
      return parseFloat(value.toFixed(num));
    };
    Vue.prototype.DefinitionMmToPx = function(mm) {
      var d = 250;
      return mm * d * (1 / 25.4);
    };
    Vue.prototype.DefinitionPxToMm = function(px) {
      var d = 250;
      return px / d / (1 / 25.4);
    };
  }
}
</script>



