<script>
export default {
  autoImage(imgBox, photo, autoType) {
    let x, y, w, h, _w, _h,
        oriW, oriH,
        imgBoxW, imgBoxH,
        isRotate,
        scale, scale1, scale2;

    oriW = photo.originalWidth;
    oriH = photo.originalHeight;

    imgBoxW = imgBox.width;
    imgBoxH = imgBox.height;
    isRotate = imgBox.image ? imgBox.image.rotation && (imgBox.image.rotation　/ 90) % 2 != 0 : false; 
    
    _w = isRotate ? oriH : oriW;
    _h = isRotate ? oriW : oriH;

    scale1 = imgBoxW / _w;
    scale2 = imgBoxH / _h;

    scale = autoType == "fill" ? (scale1 > scale2 ? scale1 : scale2) : (scale1 > scale2 ? scale2 : scale1);

    w = oriW * scale;
    h = oriH * scale;

    x = (imgBoxW - w) / 2;
    y = (imgBoxH - h) / 2;

    return {
      width: w,
      x: x,
      y: y
    }
  },
  //缩放
  imgZoom(boxData, pic, size) {
    var data = JSON.parse(JSON.stringify(boxData));
    var picW = pic.originalWidth,
        picH = pic.originalHeight,
        boxH = data.height,
        boxW = data.width,
        x = data.image.x,
        y = data.image.y,
        width = data.image.width,
        height = (width + size) / picW  * picH,
        ratio = width / height;

    if(width + size > 10){
      if(boxW > width){
        if(boxW < width + size){
          x = boxW - width - size;
        }
        else{
          if(data.image.x - size / 2 < 0){
            x = 0;
          }
          else{
            x -= size / 2;
          }
        }
      }
      else{
        x -= size / 2;
      }
      width += size;


      if(boxH > height){ //box高度 > img高度
        if(boxH < height){ //这次改变后就小于
          if(y - size / 2 < 0){
            y = 0;
          }
          else{
            y = boxH - height - size / 2;
          }
        }
        else{
          y = (boxH - height) / 2;
        }
      }
      else{
        y -= size / 2 / ratio;
      }
    }

    
    return {
      width: width,
      x: x,
      y: y
    }
  }
}
</script>



