<script>
export default {
  install: function (Vue){
    var getBoxType = function (name){
      var type = '';
      switch(name){
        case 'shape':
          type = 'colorbox';
          break
        case 'img':
          type = 'imagebox';
          break
        case 'txt':
          type = 'textbox';
          break
        case 'pattern':
          type = 'patternbox';
          break
        case 'decora':
          type = 'decoration';
          break
      };
      return type;
    };
    //获取元素
    var getElFromAttr = function (parentEl, className, tagName, value){
      var els = parentEl.querySelectorAll(className);
      var el = '';
      Array.prototype.forEach.call(els, (item) => {
        if(item.getAttribute(tagName) == value){
          el = item
        }
      })
      return el;
    };
    var fn = {
      timer: null,
      changeBoxXY(that, direction, value){
        clearTimeout(this.timer)
        var selected = that.$store.state.selected,
            scale = that.$store.state.editPannelScale,
            boxType = getBoxType(selected.data.name),
            pageNum = selected.pageNum,
            list = that.$store.state.multipleSelected.list;
        if(list.length > 1){ //多选移动
          // var bookInfo = that.$store.state.bookInfo;
          // var prePageData = isNaN(pageNum * 1) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
          for(var i = 0; i < list.length; i++){
            var type = getBoxType(list[i].name);
            v = list[i][direction] + that.pxToMm(value);
            if(direction == 'x'){
              that.$store.commit("changeBoxData", {pageNum: pageNum, boxType: type, boxId: list[i].box_id, data: {x: v}, selected: {state: false}, dontSaveUndo: true})
            }
            else{
              that.$store.commit("changeBoxData", {pageNum: pageNum, boxType: type, boxId: list[i].box_id, data: {y: v}, selected: {state: false}, dontSaveUndo: true})
            }
          }
          // that.$store.commit("addUndoData", {pageNum: pageNum, data: prePageData})
        }
        else{
          var v = selected.data[direction] + that.pxToMm(value);
          if(direction == 'x'){
            that.$store.commit("changeBoxData", {pageNum: pageNum, boxType: boxType, boxId: selected.data.box_id, data: {x: v}, selected: {state: false} })
          }
          else{
            that.$store.commit("changeBoxData", {pageNum: pageNum, boxType: boxType, boxId: selected.data.box_id, data: {y: v}, selected: {state: false} })
          }
          that.$store.commit('setState', {isKeyDownDire: true});

          this.timer = setTimeout(() => {
            that.$store.commit("setState", {selected: {data: selected.data, state: true, pageNum: pageNum, el: selected.el}})
            that.$store.commit('setState', {isKeyDownDire: false});
          }, 300)
        }

      },
      undo(that, pageNum){
        var undoData = that.$store.state.undoData[pageNum];
        if(undoData && undoData.length > 0){
          that.$store.commit("undo", pageNum);
          that.$store.dispatch("animation")
        }
      },
      redo(that, pageNum){
        var redoData = that.$store.state.redoData[pageNum];
        if(redoData && redoData.length > 0){
          that.$store.commit("redo", pageNum);
          that.$store.dispatch("animation")
        }
      },
      copy(that) {
        that.$store.commit("copyBox")
      },
      cut(that) {
        that.DeleteSelectedBox();
        that.$store.commit("copyBox")
      },
      paste(that) {
        var pageNum = that.$store.state.selected.pageNum,
            bookInfo = that.$store.state.bookInfo,
            pageData = isNaN(pageNum * 1) ? bookInfo[pageNum] : bookInfo.page_list[pageNum],
            lastIndex = that.GetLastIndex(pageData) || 1;
        var copyData = JSON.parse(JSON.stringify(that.$store.state.copyData));

        if(copyData.length > 0){
          for(var i = 0; i < copyData.length; i++){
            var type = getBoxType(copyData[i].name) + '_list';

            var list = pageData[type];

            var lastBoxId = list.length > 0 ? list[list.length - 1].box_id : 0;

            checkCover(copyData[i]);

            if(copyData[i].x >= pageData.width - copyData[i].width / 2){
              copyData[i].x = pageData.width - copyData[i].width / 2
            }
            if(copyData[i].y >= pageData.height - copyData[i].height / 2){
              copyData[i].y = pageData.height - copyData[i].height / 2
            }

            copyData[i].index = lastIndex + 1 + copyData[i].index;
            copyData[i].box_id = lastBoxId + 1;


            that.$store.commit("addBox",{pageNum: pageNum, boxType: type, box: copyData[i]});

            //不让复制的元素在之前有重叠的
            function checkCover(item){
              var isCover = false;
              if(!list.length) return ;
              for(var i = 0; i < list.length; i++){
                if(item.x == list[i].x && item.y == list[i].y && item.width == list[i].width && item.height == list[i].height){
                  isCover = true;
                }
              }
              if(isCover){
                item.x += 10;
                item.y += 10;
                checkCover(item);
              }
            }
          }

          if(copyData.length > 1){
            that.$store.commit("setState", {selected: {data: {}, pageNum: pageNum, el: null, state: false} ,multipleSelected: {pageNum: pageNum, list: copyData}})
          }
          else{
            that.$nextTick(() => {
              var pageEl = document.querySelector(".page.active");
              var el = pageEl.querySelectorAll(".box[data-name='" + copyData[0].name + copyData[0].box_id + "']")[0]
              // var pageEl = getElFromAttr(document, '.page', 'data-num', pageNum);
              // var el = getElFromAttr(pageEl, '.box', 'data-name', copyData[0].name + copyData[0].box_id);
              setTimeout(() => {
                that.$store.commit("setState",{selected: {pageNum: pageNum, el: el, state: true, data: copyData[0]}})
              }, 200)
            })
          }
        }
      },
      checkAll(that) {
        that.$store.commit("checkAll")
      },
    }

    Vue.prototype.KeyDown = function (e){
      var checkList = this.$store.state.multipleSelected,
          selected = this.$store.state.selected;
      var keyCode = e.keyCode;
      var check = checkList.list.length;
      var activeEl = document.activeElement;
      var isPreview = this.$store.state.viewType == 'preview';

      if(activeEl.tagName == 'INPUT' || activeEl.tagName == "TEXTAREA") return;

      if(e.ctrlKey){
        switch(keyCode){
          case 65:  //Ctrl + A
            !isPreview && fn.checkAll(this);
            break;
          case 67: //Ctrl + C
            !isPreview && fn.copy(this);
            break;
          case 86: //Ctrl + V
            !isPreview && fn.paste(this);
            break;
          case 88: //Ctrl + X
            !isPreview && fn.cut(this);
            break;
          case 89: //Ctrl + Y
            !isPreview && fn.redo(this, checkList.pageNum);
            break;
          case 90: //Ctrl + Z
            !isPreview && fn.undo(this, checkList.pageNum);
            break;
          case 48:
          case 96: //Ctrl + 0
            document.querySelector(".zoom_bar_auto").click();
            break;
          case 189: //Ctrl + +
            document.querySelector(".zoom_bar_zoomout").click();
            e.preventDefault();
            break;
          case 187: //Ctrl + -
            document.querySelector(".zoom_bar_zoomin").click();
            e.preventDefault();
            break
        }
      }
      else{
        switch(keyCode){
          case 46: //DelDel
            check && this.DeleteSelectedBox();
            break;
          case 37:
          case 38:
          case 39:
          case 40:
            if(this.$store.state.viewType == 'preview'){
              //预览翻页
              if(keyCode == 37){
                document.querySelector(".navigator-pagination-btn.--left").click();
              }
              if(keyCode == 39){
                document.querySelector(".navigator-pagination-btn.--right").click();
              }
            }
            else{
              if(!selected.data) return;
              var type = (keyCode == 37 || keyCode == 39) ? 'x':'y';
              var num = (keyCode == 37 || keyCode == 38) ? -1: 1;
              check && fn.changeBoxXY(this, type, num)
            }
            break;
        }
      }
    }
  }
}
</script>



