<script>
import imageUnit from '@/components/common/imageUnit'
export default {
  install: function (Vue) {
    //基础数据
    var baseBoxData = {
      "imagebox_list": {
        name: 'img',
        box_id: 0,
        height: 60,
        image: null,
        index: 0,
        locked: false,
        rotation: 0,
        width: 80,
        x: 0,
        y: 0,
        border_width: 0,
        border_color: '#ffffff'
      },
      "textbox_list": {
        align: "left",
        color: "#000000",
        content: "",
        font_id: "101",
        height: 20,
        index: 0,
        leading: -1,
        locked: false,
        max_length: -1,
        max_line: -1,
        name: "txt",
        pt: 14,
        rotation: 0,
        space: 0,
        width: 100,
        x: 0,
        y: 0
      },
      "colorbox_list": {
        border_color: "",
        border_width: 0,
        color: "#2db572",
        color_id: "301",
        box_id: 0,
        height: 25,
        index: 6,
        locked: false,
        name: "shape",
        opacity: 1,
        rotation: 0,
        width: 25,
        x: 0,
        y: 0,
      },

    }
    //获取某个类型的最后一个box_id
    var getLastBoxId = function (pageData, propName){
      var lastBoxId = 0;
      if(pageData[propName]){
        var maxId = 0;
        pageData[propName].forEach((item) => {
          if(item.box_id > maxId){
            maxId = item.box_id
          }
        })
        lastBoxId = maxId;
      }
      return lastBoxId + 1;
    }

    var getBoxType = function (name){
      var type = '';
      switch(name){
        case 'shape':
          type = 'colorbox';
          break
        case 'img':
          type = 'imagebox';
          break
        case 'txt':
          type = 'textbox';
          break
        case 'pattern':
          type = 'patternbox';
          break
        case 'decora':
          type = 'decoration';
          break
      };
      return type;
    }

    //获取当前页数据
    var getPageData = function (bookInfo, pageNum){
      return !isNaN(pageNum) ? bookInfo.page_list[pageNum] : bookInfo[pageNum]
    }

    //获取要修改的这个框
    var getEditBoxInfo = function (pageData, boxId) {
      for (var i in pageData) {
        if (pageData[i].box_id == boxId) {
          return {
            data: pageData[i],
            index: i
          }
        }
      }
    }

    //根据元素类名及自定义属性找到元素
    var getEl = function (parent, className, attr, value){
      var list = parent.querySelectorAll(className);
      var el;
      if(list.length == 0){
        el = null;
      }
      else{
        for(var i = 0; i < list.length; i++){
          if(list[i].getAttribute(attr) == value){
            el = list[i]
          }
        }
      }
      return el;
    }

    //添加box
    Vue.prototype.AddBox = function (pageNum, type, data) { //type 和box组件中的类名同名
      var bookInfo = this.$store.state.bookInfo;
      var pageData = getPageData(bookInfo, pageNum);
      var lastIndex = this.GetLastIndex(pageData) + 1;

      if(pageNum == 'spine') return
      var newBox = '',
          className = '',
          boxPropName = '',
          lastBoxId = 1;

      switch(type){
        case 'imgbox':
          boxPropName = 'imagebox_list';
          resetData();
          if(data){
            newBox.width = 60;
            newBox.height = 40;
            newBox.x = data.x;
            newBox.y = data.y;
            var image = this.GetImage(data.imgId);
            newBox.image = {
              fileName: image.fileName,
              id: data.imgId,
              rotation:0,
              uploaded: image.uploadProgress == 'success',
              width: 0,
              x: 0,
              y: 0
            }
            var data = imageUnit.autoImage(newBox, image, 'fill');
            newBox.image.width = data.width,
            newBox.image.height = data.height,
            newBox.image.x = data.x,
            newBox.image.y = data.y;
          }
          break;
        case 'textbox':
          boxPropName = 'textbox_list';
          resetData();
          newBox.font_id = data;
          newBox.pt = bookInfo.de_font_pt;
          break;
        case 'shapebox':
          boxPropName = 'colorbox_list';
          resetData();
          break;
        case 'decorabox':
          boxPropName = 'decoration_list';
          newBox = {
            color: "#ffffff",
            decoration_id: data.Id,
            box_id: 1,
            height: 35 / data.Width * data.Height,
            index: lastIndex,
            locked: false,
            name: "decora",
            rotation: 0,
            src: data.Url,
            text: null,
            type: data.Type,
            width: 35,
            x: 34,
            y: 44,
          }
          if(data.Type == 3){
            newBox.text = JSON.parse(data.DesginData)
          }
          break;
      }

      lastBoxId = getLastBoxId(pageData, boxPropName);
      newBox.box_id = lastBoxId;


      this.$store.commit("addBox", { pageNum: pageNum, boxType: boxPropName, box: newBox})

      this.$nextTick(() => {
        setTimeout(()=>{ //点击事件会清空selected
          var page = document.querySelectorAll(".page[data-num='"+ pageNum +"']")[0]
          var el = page.querySelectorAll("." + type +"[data-name='" + newBox.name + newBox.box_id + "']")[0]
          this.$store.commit("setState", {selected: {state: true, el: el, data: newBox, pageNum: pageNum}});
        }, 220)
      })

      function resetData() {
        newBox = JSON.parse(JSON.stringify(baseBoxData[boxPropName]));
        var x = (pageData.width - newBox.width) / 2,
            y = (pageData.height - newBox.height) / 2;
        newBox.index = lastIndex;
        newBox.x = x;
        newBox.y = y;
      }
    }

    //添加底纹
    Vue.prototype.ChangePattern = function (pageNum, data) {
      var bookInfo = this.$store.state.bookInfo;
      var pageData = getPageData(bookInfo, pageNum);
      if(data) {
        var baseData = {
          height: pageData.height,
          id: data.Id,
          box_id: 1,
          imgWidth: this.pxToMm(data.imgWidth || data.Width),
          index: -1,
          locked: true,
          name: "pattern",
          rotation: 0,
          src: data.Url,
          type: data.Type || 1,
          width: pageData.width,
          x: 0,
          y: 0,
        }
        if(pageData.patternbox_list[0]){
          this.$store.commit("changeBoxData", {pageNum: pageNum , boxType: 'patternbox', boxId: 1, data: baseData, selected: {state: false} })
        }
        else{
          this.$store.commit("addBox", {pageNum: pageNum , boxType: 'patternbox_list', box: baseData})
        }
        if(pageData.background_color == '#fff' || pageData.background_color == '#FFFFFF'){
          this.$store.commit("changePageBg", { pageNum: pageNum, color: '#424142' })
          this.$store.dispatch("animation")
        }
      }
      else{
        this.$store.commit("deleteBox", {pageNum: pageNum , boxType: 'patternbox', boxId: 1})
      }
      this.$nextTick(() => {
        this.$store.commit("setState", {selected: {el: null, data: {}, state: false, pageNum: pageNum}});
      })
    }

    //改变图片
    Vue.prototype.ChangeImage = function (pageNum, boxId, imgId){
      var bookInfo = this.$store.state.bookInfo,
          pageData = getPageData(bookInfo, pageNum),
          boxData = getEditBoxInfo(pageData.imagebox_list, boxId).data;

      var pic = this.GetImage(imgId),
          image = {};

      if(!boxData.image) {
        image = {
          fileName: "",
          id: "",
          rotation:0,
          uploaded:true,
          width: 0,
          x: 0,
          y: 0
        }
      }
      else{
        image = JSON.parse(JSON.stringify(boxData.image));
      }

      image.rotation = 0;
      image.id = imgId;
      image.fileName = pic.fileName;
      image.uploaded = pic.uploadProgress == 'success';
      
      var imageData = imageUnit.autoImage(boxData, pic, 'fill');

      image.width = imageData.width;
      image.x = imageData.x;
      image.y = imageData.y;
      this.$store.commit('changeBoxData', { pageNum: pageNum, boxType: 'imagebox', boxId: boxId, data: { image: image }})

      this.$nextTick(() => {
        setTimeout(() => {
          var el = getEl(document.querySelector(".page.active"), ".imgbox", 'data-name', boxData.name+boxData.box_id);
          this.$store.commit("setState", {selected: {el: el, state: true, pageNum: pageNum, data: boxData}})
        }, 0)
      })
    }

    //删除选中的box
    Vue.prototype.DeleteSelectedBox = function () {
      var selected = this.$store.state.selected,
          pageNum = selected.pageNum,
          list = this.$store.state.multipleSelected.list;
      if(list.length > 1){
        var types = [], boxIds = [];
        for(var i = 0; i < list.length; i++){
          var type = getBoxType(list[i].name);
          types.push(type);
          boxIds.push(list[i].box_id);
        }
        this.$store.commit("deleteBox", {pageNum: pageNum, boxType: types, boxId: boxIds})
      }
      else{
        var type = getBoxType(selected.data.name);
        this.$store.commit("deleteBox", {pageNum: selected.pageNum, boxType: type, boxId: selected.data.box_id})
      }
    }

  }
}
</script>



