<script>
import data from '@/assets/data/data'
import axios from 'axios'
import qs from 'qs'

export default {
  install(Vue, callback) {
    var that = this;
    var workId = getParameterByName('id');
    getData();

    function getParameterByName(name) {
      var match = RegExp('[?&]' + name + '=([^&]*)')
        .exec(window.location.search);
      return match && decodeURIComponent(match[1].replace(/\+/g, ' '));
    }

    var getProductInfo = function() {
      return axios.post('http://www.mimoprint.com/editor/GetProductInfo', qs.stringify({
        worksId: workId,
        hostName: window.location.origin
      }))
    };

    var getPhotoList = function() {
      return axios.post('http://www.mimoprint.com/editor/GetPhotoList', qs.stringify({
        worksId: workId,
        hostName: window.location.origin
      }))
    };

    var getCusIp = function() {
      return axios.get('http://www.mimoprint.com/UserImage/GetCusIp', qs.stringify({
        hostName: window.location.origin
      }))
    };

    var getTemplates = function() {
      return axios.post('http://www.mimoprint.com/editor/GetTemplates', qs.stringify({
        worksId: workId,
        hostName: window.location.origin
      }))
    };

    function getData() {

      var productInfo = data.productInfo,
          photoList = data.photoList,
          templates = data.templates,
          cusIp = data.cusIp;

      var bookConfig = {}, commonConfig = {}, state = {};
      bookConfig.workId = workId;
      state.initLoading = false;

      //书本信息
      if (productInfo.data.ret == 1) {
        // bookConfig.userId = productInfo.data.userId;
        bookConfig.workUserId = productInfo.data.workUserId;
        bookConfig.productId = productInfo.data.productId;

        state.theme = {
          id: productInfo.data.themeId,
          color: productInfo.data.themeColor ? productInfo.data.themeColor.split('_') : [],
          type: productInfo.data.themeType,
          name: productInfo.data.themeName,
          info: productInfo.data.themeinfo ? JSON.parse(productInfo.data.themeinfo) : null
        }
        state.bookInfo = formatBookInfo(JSON.parse(productInfo.data.bookinfo));
        state.binding = parseInt(productInfo.data.optionBinding);
        state.paper = parseInt(productInfo.data.optionPaper);
        state.pageCount = parseInt(productInfo.data.pageCount);
      } else {
        alert(productInfo.data.msg);
      }

      //照片信息
      if (photoList.data.ret == 1) {
        for (var i = 0; i < photoList.data.list.length; i++) {
          var photo = photoList.data.list[i];
          photo.uploadProgress = 'success';
        }
        state.photos = photoList.data.list;
      } else {
        alert(photoList.data.msg);
      }

      //版式信息
      if (templates.data.ret == 1) {
        state.templates = formatTemplate(JSON.parse(templates.data.templates));
        state.myTemplates = formatTemplate(JSON.parse(templates.data.myTemplates || '[]'));
        state.fullTemplates = JSON.parse(templates.data.fullTemplates || '[]');
      } else {
        alert(templates.data.msg);
      }

      commonConfig.uploader = {
        cusIp: cusIp.data
      }

      if (typeof callback == 'function') {
        callback({
          bookConfig: bookConfig,
          commonConfig: commonConfig,
          state: state
        });
      }
    }

    function formatBookInfo(bookInfo) {
      for (var i in bookInfo) {
        if (i == 'page_list') {
          for (var j = 0; j < bookInfo.page_list.length; j++) {
            if (bookInfo.page_list[j].num != undefined) {
              for (var k in bookInfo.page_list[j]) {
                var boxName = '';
                switch (k) {
                  case "colorbox_list":
                    boxName = 'shape'
                    break;
                  case "decoration_list":
                    boxName = 'decora'
                    break;
                  case "imagebox_list":
                    boxName = 'img'
                    break;
                  case "patternbox_list":
                    boxName = 'pattern'
                    break;
                  case "textbox_list":
                    boxName = 'txt'
                    break;
                  default:
                    break;
                }
                if (boxName != '' && bookInfo.page_list[j][k]) {
                  for (var l = 0; l < bookInfo.page_list[j][k].length; l++) {
                    bookInfo.page_list[j][k][l].name = boxName;
                    bookInfo.page_list[j][k][l].box_id = l + 1
                  }
                }
              }
            }
          }
        }
        else if (bookInfo[i] && bookInfo[i].num != undefined) {
          for (var j in bookInfo[i]) {
            var boxName = '';
            switch (j) {
              case "colorbox_list":
                boxName = 'shape'
                break;
              case "decoration_list":
                boxName = 'decora'
                break;
              case "imagebox_list":
                boxName = 'img'
                break;
              case "patternbox_list":
                boxName = 'pattern'
                break;
              case "textbox_list":
                boxName = 'txt'
                break;
              default:
                break;
            }
            if (boxName != '' && bookInfo[i][j] != null) {
              for (var k = 0; k < bookInfo[i][j].length; k++) {
                bookInfo[i][j][k].name = boxName;
                bookInfo[i][j][k].box_id = k + 1
              }
            }
          }
        }
      }
      return bookInfo;
    }

    function formatTemplate(templates) {
      var list = []
      for (var name in templates) {
        if (name == 'page') {
          templates[name]['left'] && (list.push(templates[name]['left']))
          templates[name]['right'] && (list.push(templates[name]['right']))
        } else {
          list.push(templates[name])
        }
      }
      list.forEach(function(boxes, ind) {
        boxes.forEach(function(box, boxIndex) {
          for (var name in box) {
            var boxName = ''
            switch (name) {
              case 'decoration_list':
              case 'decorationboxList':
                boxName = 'decora'
                break
              case 'colorbox_list':
              case 'shapeBoxList':
                boxName = 'shape'
                break
              case 'imagebox_list':
              case 'imageBoxList':
                boxName = "img"
                break
              case 'shadingbox_list':
              case 'shadingBoxList':
                boxName = "shading"
                break
              case 'textbox_list':
              case 'textBoxList':
                boxName = 'txt'
                break
              case 'patternbox_list':
                boxName = 'pattern'
                break

            }
            if (boxName != '' && box[name] != null) {
              box[name].forEach(function(item, itemInd) {
                item.name = boxName
                item.box_id = itemInd + 1
              })
            }
          }
        })
      })

      return templates;
    }

  }

}
</script>



