<template>
  <div class="resize-wrap" v-if="selected.state">
    <div class="selection-border selection-border-top" :style="borderStyle('top')"></div>
    <div class="selection-border selection-border-left" :style="borderStyle('left')"></div>
    <div class="selection-border selection-border-right" :style="borderStyle('right')"></div>
    <div class="selection-border selection-border-bottom" :style="borderStyle('bottom')"></div>
    <template v-if="!selected.data.locked">
      <div class="selection-handle top-left" data-type="top-left" @mousedown="dragHandle($event, 'top-left')" :style="dotStyle('top-left')"></div>
      <div class="selection-handle top-center" data-type="top-center" @mousedown="dragHandle($event, 'top-center')" :style="dotStyle('top-center')"></div>
      <div class="selection-handle top-right" data-type="top-right" @mousedown="dragHandle($event, 'top-right')" :style="dotStyle('top-right')"></div>
      <div class="selection-handle center-left" data-type="center-left" @mousedown="dragHandle($event, 'center-left')" :style="dotStyle('center-left')"></div>
      <div class="selection-handle center-right" data-type="center-right" @mousedown="dragHandle($event, 'center-right')" :style="dotStyle('center-right')"></div>
      <div class="selection-handle bottom-left" data-type="bottom-left" @mousedown="dragHandle($event, 'bottom-left')" :style="dotStyle('bottom-left')"></div>
      <div class="selection-handle bottom-center" data-type="bottom-center" @mousedown="dragHandle($event, 'bottom-center')" :style="dotStyle('bottom-center')"></div>
      <div class="selection-handle bottom-right" data-type="bottom-right" @mousedown="dragHandle($event, 'bottom-right')" :style="dotStyle('bottom-right')"></div>
      <div class="border rotatelink" :style="borderStyle('rotatelink')"></div>
      <div class="selection-handle rotate" data-type="rotate" @mousedown="dragHandle($event, 'rotate')" :style="dotStyle('rotate')"></div>
    </template>
  </div>
</template>

<script>
export default {
  name: 'resizse',
  props: [
    'edit',
    'editPannel',
    'scale',
    'fixZoomScaleBox'
  ],
  data() {
    return {
      borderW: 2,//线的宽度
      rotatelinkH: 15,//旋转线的长度
      rotateBtnSize: 20,//旋转按钮尺寸
      handleW: 8,//拖动手柄的宽度
      handleBorderW: 2,//拖动手柄边框的宽度
      minW: 5,//最小box尺寸mm
      handleType: null,//拖动的手柄类型
      isFixZoom: false//是否同比例缩放
    }
  },
  computed: {
    selected() {
      return this.$store.state.selected;
    },
  },
  methods: {
    dragHandle(e, type) {
      this.handleType = type;

      this.DragDown(e, {
        dragStart: this.dragStart,
        drag: this.drag,
        dragEnd: this.dragEnd
      });

      var tipType = this.handleType == "rotate" ? 'r' : 'wh';
      this.$store.commit('setState', { dragResize: { type: tipType, x: e.pageX + 20, y: e.pageY} });
    },
    dragStart(e, args) {
      var selected = this.$store.state.selected;
      var clientRect = selected.el.getBoundingClientRect(),
        editPannelBounding = this.editPannel.getBoundingClientRect();

      //*1确保不是字符串，避免字符串相加
      var boxW = selected.data.width * 1,
        boxH = selected.data.height * 1,
        boxX = selected.data.x * 1,
        boxY = selected.data.y * 1,
        boxR = selected.data.rotation * 1;

      this.boxId = selected.data.box_id;
      this.startW = boxW;
      this.startH = boxH;
      this.startX = boxX;
      this.startY = boxY;
      this.startR = boxR;
      this.htmlCx = clientRect.left + clientRect.width / 2;//box居于html的中心点，单位px
      this.htmlCy = clientRect.top + clientRect.height / 2;
      this.pageCx = (boxX + boxW / 2) * this.scale;//box居于page的中心点，单位mm
      this.pageCy = (boxY + boxH / 2) * this.scale;
      //同比例缩放
      this.isFixZoom = this.fixZoomScaleBox.indexOf(selected.data.name) != -1 ? true : false;
    },
    drag(e, args) {
      var that = this;

      var selected = this.$store.state.selected;

      var x = this.pxToMm(args.x) / this.scale,
        y = this.pxToMm(args.y) / this.scale;

      var cosV = Math.cos(this.startR * Math.PI / 180),
        sinV = Math.sin(this.startR * Math.PI / 180),
        tanV = Math.tan(this.startR * Math.PI / 180);

      var data = {};

      var left = function() {
        var len = x / cosV + (y - x * tanV) * sinV;

        //限制最小值
        if (that.startW - len >= that.minW) {
          data.width = that.startW - len;
          data.x = that.startX + len;
        } else {
          data.width = that.minW;
          data.x = that.startX + (that.startW - that.minW);
          len = that.startW - that.minW;
        }

        //正确的中心点
        var cx = that.pageCx + len / 2 * cosV * that.scale,
          cy = that.pageCy + len / 2 * sinV * that.scale;

        //同比缩放
        if (that.isFixZoom) {
          data.height = data.width / (that.startW / that.startH);
          cy = cy - (data.height - that.startH) / 2 * that.scale;
        }

        return {
          x: cx,
          y: cy
        }
      }, right = function() {
        var len = x / cosV + (y - x * tanV) * sinV;

        //限制最小值
        if (that.startW + len >= that.minW) {
          data.width = that.startW + len;
        } else {
          data.width = that.minW;
          len = that.minW - that.startW;
        }

        //正确的中心点
        var cx = that.pageCx + len / 2 * cosV * that.scale,
          cy = that.pageCy + len / 2 * sinV * that.scale;

        //同比缩放
        if (that.isFixZoom) {
          data.height = data.width / (that.startW / that.startH);
          cy = cy - (data.height - that.startH) / 2 * that.scale;
        }

        return {
          x: cx,
          y: cy
        }
      }, top = function() {
        var len = y / cosV + (- x - y * tanV) * sinV;

        //限制最小值
        if (that.startH - len >= that.minW) {
          data.height = that.startH - len;
          data.y = that.startY + len;
        } else {
          data.height = that.minW;
          data.y = that.startY + (that.startH - that.minW);
          len = that.startH - that.minW;
        }

        //正确的中心点
        var cx = that.pageCx - len / 2 * sinV * that.scale,
          cy = that.pageCy + len / 2 * cosV * that.scale;

        //同比缩放
        if (that.isFixZoom) {
          data.width = data.height * (that.startW / that.startH);
          cx = cx - (data.width - that.startW) / 2 * that.scale;
        }

        return {
          x: cx,
          y: cy
        }
      }, bottom = function() {
        var len = y / cosV + (- x - y * tanV) * sinV;

        //限制最小值
        if (that.startH + len >= that.minW) {
          data.height = that.startH + len;
        } else {
          data.height = that.minW;
          len = that.minW - that.startH;
        }

        //正确的中心点
        var cx = that.pageCx - len / 2 * sinV * that.scale,
          cy = that.pageCy + len / 2 * cosV * that.scale;

        //同比缩放
        if (that.isFixZoom) {
          data.width = data.height * (that.startW / that.startH);
          cx = cx - (data.width - that.startW) / 2 * that.scale;
        }

        return {
          x: cx,
          y: cy
        }
      }

      switch (this.handleType) {
        case 'top-left'://左上
        case 'top-right'://右上
        case 'bottom-left'://左下
        case 'bottom-right'://右下
          var c1, c2, center;

          if (this.handleType == 'top-left') {
            c1 = top();
            c2 = left();
          }

          if (this.handleType == 'top-right') {
            c1 = top();
            c2 = right();
          }

          if (this.handleType == 'bottom-left') {
            c1 = bottom();
            c2 = left();
          }

          if (this.handleType == 'bottom-right') {
            c1 = bottom();
            c2 = right();
          }

          center = {
            x: c2.x + (c1.x - that.pageCx),
            y: c2.y + (c1.y - that.pageCy)
          }

          //同比例缩放的在原有中心点基础上另外算偏移。。
          if(this.isFixZoom){
            var len = (data.height - that.startH) / 2 * that.scale;

            if (this.handleType == 'top-left') {
              center.x = c2.x + len * sinV;
              center.y = c2.y + len * (1 - cosV);
            }

            if (this.handleType == 'top-right') {
              center.x = c2.x + len * sinV;
              center.y = c2.y + len * (1 - cosV);
            }

            if (this.handleType == 'bottom-left') {
              center.x = c2.x - len * sinV;
              center.y = c2.y + len * (1 + cosV);
            }

            if (this.handleType == 'bottom-right') {
              center.x = c2.x - len * sinV;
              center.y = c2.y + len * (1 + cosV);
            }
          }

          data.x = (center.x - data.width / 2 * that.scale) / that.scale;
          data.y = (center.y - data.height / 2 * that.scale) / that.scale;
          break;
        case 'center-left'://左
          var center = left();

          data.x = (center.x - data.width / 2 * that.scale) / that.scale;
          data.y = (center.y - that.startH / 2 * that.scale) / that.scale;
          break;
        case 'center-right'://右
          var center = right();

          data.x = (center.x - data.width / 2 * that.scale) / that.scale;
          data.y = (center.y - that.startH / 2 * that.scale) / that.scale;
          break;
        case 'top-center'://上
          var center = top();

          data.x = (center.x - that.startW / 2 * that.scale) / that.scale;
          data.y = (center.y - data.height / 2 * that.scale) / that.scale;
          break;
        case 'bottom-center'://下
          var center = bottom();

          data.x = (center.x - that.startW / 2 * that.scale) / that.scale;
          data.y = (center.y - data.height / 2 * that.scale) / that.scale;
          break;
        case 'rotate':
          data.rotation = that.getAngle(that.htmlCx, that.htmlCy, e.pageX, e.pageY);
          break;
      }

      this.$store.commit('changeBoxData', {
        pageNum: selected.pageNum,
        boxType: selected.el.getAttribute("data-type"),
        boxId: this.boxId,
        data: data,
      });

      var tipType = this.handleType == "rotate" ? 'r' : 'wh';
      //拖动时开启tip信息
      this.$store.commit('setState', { dragResize: { type: tipType, x: e.pageX + 20, y: e.pageY } });
      //辅助线
      this.$store.commit("setState", { sublineType: 'resize' });
    },
    dragEnd(e, args) {
      //关闭tip信息
      this.$store.commit('setState', { dragResize: { type: null, x: 0, y: 0 } });
      //辅助线
      this.$store.commit("setState", { sublineType: '' });
    },
    dotStyle(type) {
      var editBounding = this.edit.getBoundingClientRect(),
        editPannelBounding = this.editPannel.getBoundingClientRect(),
        data = this.selected.data;

      var pageLeft = this.selected.el.parentNode.offsetLeft,
        pageTop = this.selected.el.parentNode.offsetTop;

      var editPannelLeft = editPannelBounding.left,
        editPannelTop = editPannelBounding.top,
        cosV = Math.cos(data.rotation * Math.PI / 180),
        sinV = Math.sin(data.rotation * Math.PI / 180);

      //兼容字符串，转成数字，避免后续计算问题
      var x = data.x * 1,
        y = data.y * 1,
        w = data.width * 1,
        h = data.height * 1,
        bw = data.border_width * 1 || 0;

      var normX = editPannelLeft + (this.mmToPx(x + w / 2) + pageLeft + this.ptToPx(bw)) * this.scale - editBounding.left,
        normY = editPannelTop + (this.mmToPx(y + h / 2) + pageTop + this.ptToPx(bw)) * this.scale - editBounding.top;

      var cosVW = cosV * this.mmToPx(w) * this.scale / 2,
        cosVH = cosV * this.mmToPx(h) * this.scale / 2,
        sinVW = sinV * this.mmToPx(w) * this.scale / 2,
        sinVH = sinV * this.mmToPx(h) * this.scale / 2;

      var style = {
        width: this.handleW + 'px',
        height: this.handleW + 'px'
      };
      switch (type) {
        case 'top-left':
          style.transform = 'translate(' + this.toFloat(normX + sinVH - cosVW) + 'px, ' + this.toFloat(normY - cosVH - sinVW) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)';
          break;
        case 'top-center':
          style.transform = 'translate(' + this.toFloat(normX + sinVH) + 'px, ' + this.toFloat(normY - cosVH) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)';
          break;
        case 'top-right':
          style.transform = 'translate(' + this.toFloat(normX + sinVH + cosVW) + 'px, ' + this.toFloat(normY - cosVH + sinVW) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)';
          break;
        case 'center-left':
          style.transform = 'translate(' + this.toFloat(normX - cosVW) + 'px, ' + this.toFloat(normY - sinVW) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)';
          break;
        case 'center-right':
          style.transform = 'translate(' + this.toFloat(normX + cosVW) + 'px, ' + this.toFloat(normY + sinVW) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)';
          break;
        case 'bottom-left':
          style.transform = 'translate(' + this.toFloat(normX - cosVW - sinVH) + 'px, ' + this.toFloat(normY + cosVH - sinVW) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)';
          break;
        case 'bottom-center':
          style.transform = 'translate(' + this.toFloat(normX - sinVH) + 'px, ' + this.toFloat(normY + cosVH) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)';
          break;
        case 'bottom-right':
          style.transform = 'translate(' + this.toFloat(normX - sinVH + cosVW) + 'px, ' + this.toFloat(normY + cosVH + sinVW) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)';
          break;
        case 'rotate':
          style.width = this.rotateBtnSize + 'px',
            style.height = this.rotateBtnSize + 'px',
            style.transform = 'translate(' + this.toFloat(normX + sinVH + 22 * sinV) + 'px, ' + this.toFloat(normY - cosVH - 22 * cosV) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)';
          break;
      }

      return style;
    },
    borderStyle(type) {
      var editBounding = this.edit.getBoundingClientRect(),
        editPannelBounding = this.editPannel.getBoundingClientRect(),
        data = this.selected.data;

      var pageLeft = this.selected.el.parentNode.offsetLeft,
        pageTop = this.selected.el.parentNode.offsetTop;

      var editPannelLeft = editPannelBounding.left,
        editPannelTop = editPannelBounding.top,
        cosV = Math.cos(data.rotation * Math.PI / 180),
        sinV = Math.sin(data.rotation * Math.PI / 180);

      //兼容字符串，转成数字，避免后续计算问题
      var x = data.x * 1,
        y = data.y * 1,
        w = data.width * 1,
        h = data.height * 1,
        bw = data.border_width * 1 || 0;

      var normX = editPannelLeft + (this.mmToPx(x + w / 2) + pageLeft + this.ptToPx(bw)) * this.scale - editBounding.left,
        normY = editPannelTop + (this.mmToPx(y + h / 2) + pageTop + this.ptToPx(bw)) * this.scale - editBounding.top;

      var cosVW = cosV * this.mmToPx(w) * this.scale / 2,
        cosVH = cosV * this.mmToPx(h) * this.scale / 2,
        sinVW = sinV * this.mmToPx(w) * this.scale / 2,
        sinVH = sinV * this.mmToPx(h) * this.scale / 2;

      var style = {};
      switch (type) {
        case 'top':
          style = {
            "width": this.mmToPx(data.width) * this.scale + this.borderW + 'px',
            "transform": 'translate(' + (normX + sinVH) + 'px, ' + (normY - cosVH) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)'
          }
          break;
        case 'left':
          style = {
            "height": this.mmToPx(data.height) * this.scale + this.borderW + 'px',
            "transform": 'translate(' + (normX - cosVW) + 'px, ' + (normY - sinVW) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)'
          }
          break;
        case 'right':
          style = {
            "height": this.mmToPx(data.height) * this.scale + this.borderW + 'px',
            "transform": 'translate(' + (normX + cosVW) + 'px, ' + (normY + sinVW) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)'
          }
          break;
        case 'bottom':
          style = {
            "width": this.mmToPx(data.width) * this.scale + this.borderW + 'px',
            "transform": 'translate(' + (normX - sinVH) + 'px, ' + (normY + cosVH) + 'px) rotate(' + data.rotation + 'deg) translate(-50%, -50%)'
          }
          break;
        case 'rotatelink':
          style = {
            "height": this.rotatelinkH + 'px',
            "transform": 'translate(' + (normX + sinVH - this.rotatelinkH * sinV) + 'px, ' + (normY - cosVH + this.rotatelinkH * cosV) + 'px) ' + 'rotate(' + data.rotation + 'deg)  translate(-50%, -200%)'
          }
          break;
      }
      return style;
    },
    //获取旋转角度
    getAngle(px, py, mx, my) {
      var x = Math.abs(px - mx);
      var y = Math.abs(py - my);
      var z = Math.sqrt(Math.pow(x, 2) + Math.pow(y, 2));

      var cos = y / z;
      var radina = Math.acos(cos);//用反三角函数求弧度
      var angle = Math.floor(180 / (Math.PI / radina));//将弧度转换成角度

      if (mx > px && my > py) {//鼠标在第四象限
        angle = 180 - angle;
      }

      if (mx == px && my > py) {//鼠标在y轴负方向上
        angle = 180;
      }

      if (mx > px && my == py) {//鼠标在x轴正方向上
        angle = 90;
      }

      if (mx < px && my > py) {//鼠标在第三象限
        angle = 180 + angle;
      }

      if (mx < px && my == py) {//鼠标在x轴负方向
        angle = 270;
      }

      if (mx < px && my < py) {//鼠标在第二象限
        angle = 360 - angle;
      }

      angle = angle % 360;
      angle = angle > 180 ? (angle % 180) - 180 : angle;

      return angle;
    }
  }
}
</script>

<style>
.resize-wrap {
  z-index: 1;
  position: relative;
  top: 0;
  left: 0;
}

.resize-wrap .selection-border {
  position: absolute;
  z-index: 1000;
  width: 2px;
  height: 2px;
  background: #5587ce;
  transform-origin: 0 0;
}

.resize-wrap .selection-handle {
  position: absolute;
  z-index: 1001;
  background: #fff;
  border: 2px solid #5587ce;
  cursor: pointe;
  transform-origin: 0 0;
}

.resize-wrap .border {
  position: absolute;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 2px;
  height: 2px;
  background: #5587ce;
  box-shadow: 0 0 4px #aaa;
  transform-origin: 0 0;
}

.resize-wrap .border .rotatelink {
  position: absolute;
  z-index: 1000;
  height: 12px;
}

.resize-wrap .selection-handle.top-left {
  cursor: se-resize;
}

.resize-wrap .selection-handle.top-right {
  cursor: sw-resize;
}

.resize-wrap .selection-handle.bottom-center {
  cursor: s-resize;
  z-index: 1002;
}

.resize-wrap .selection-handle.center-right {
  cursor: e-resize;
  z-index: 1003;
}

.resize-wrap .selection-handle.top-center {
  cursor: n-resize;
  z-index: 1002;
}

.resize-wrap .selection-handle.bottom-right {
  cursor: nw-resize;
}

.resize-wrap .selection-handle.center-left {
  cursor: e-resize;
  z-index: 1003;
}

.resize-wrap .selection-handle.bottom-left {
  cursor: ne-resize;
}

.resize-wrap .selection-handle.rotate {
  cursor: -webkit-grab;
  border-radius: 100%;
  background: #60a0e0;
  border: none;
}
</style>



