import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios'
import qs from 'qs'
import Methods from '@/store/book/methods';

Vue.use(Vuex);

var pushTask = function (state, name, pageNum, data) {
  if (!state[name][pageNum]) {
    Vue.set(state[name], pageNum, []);
  }
  state[name][pageNum].push(data);
}

const state = {
  editPannelScale: 1, //编辑区域缩放比例
  bookInfo: '',//书本数据
  photos: [],
  photoUseInfo: {},//照片使用状态
  templates: [],//版式数据
  myTemplates: [],//自定义版式数据
  fullTemplates: [],//整书数据
  materialList: [], //素材数据
  patternList: [], //版式数据
  binding: 0,
  paper: 0,
  pageCount: 0,
  initLoading: true,//初始化loading
  theme: {
    id: 0,
    color: [],
    type: '',
    name: '',
    info: null
  },
  boxToolbar: {//box工具栏信息
    pannelName: '',//显示的面板名
    x: 0,
    y: 0
  },
  selected: {//选中-单选
    state: false,
    el: null,
    data: {},
    pageNum: 'front_cover',
  },
  multipleSelected: {//选中-多选
    pageNum: 'front_cover',
    list: []
  },
  dragResize: {//box拖动变更尺寸
    type: null,
    x: 0,
    y: 0
  },
  bookParamet: false,//书本参数层
  focusPhotoId: '', //焦点照片id
  photoListFocusId: '', //照片列表的焦点照片id
  currentPage: ['front_cover'],//展示的页面
  currentPageIsCross: false,//是否跨页
  showEditTextPannle: false,//文字编辑
  editImage: false,//图片编辑状态
  animation: false, //动画
  timer: null,
  sublineType: '', //吸附线
  dragStorePhoto: { //拖动图片储存区域的照片
    pageNum: '',
    boxId: '',
    imgId: '',
    state: false,
    src: '',
    w: 0,
    h: 0,
    x: 0,
    y: 0
  },
  isMove: false,//是否拖动状态
  isKeyDownDire: false, //是否一直按方向键
  isZoom: false,//是否持续缩放状态
  isChangePage: false,//是否变更页， 页排序、加减页标记
  viewType: 'common', // preview 预览视图
  undoData: {},
  redoData: {},
  showPageSort: false, //显示页序
  copyData: [], //复制的数据
  warningTip: {
    visible: false,
    elRect: '',
    w: 0,
    h: 0,
  },
  canvasImageList: [],//照片加载后丢这里边，拖动左侧照片列表才不用重新加载照片
  saveTip: false,
  isLockedDrag: false, //不可以动
};

const mutations = {
  setState(state, data) {
    for (var i in data) {
      state[i] = data[i]
    }
  },
  addPhoto(state, data) {
    state.photos.push(data);
  },
  photoSrot(state, data) {
    state.photos.sort(function (a, b) {
      var v1, v2;

      if (data == 'uploadtime') {
        v1 = a.uploadTime.replace(/[^0-9]/ig, '');
        v2 = b.uploadTime.replace(/[^0-9]/ig, '');
      }
      if (data == 'shoottime') {
        v1 = a.shoottime ? a.shoottime.replace(/[^0-9]/ig, '') : 0;
        v2 = b.shoottime ? b.shoottime.replace(/[^0-9]/ig, '') : 0;
      }
      if (data == 'filename') {
        v1 = a.fileName.toLocaleLowerCase();
        v2 = b.fileName.toLocaleLowerCase()
      }

      if (v1 > v2) {
        return 1;
      } else if (v1 < v2) {
        return -1;
      } else {
        return 0;
      }
    });
  },
  changeUploadProgress(state, data) {
    for (var i = 0; i < state.photos.length; i++) {
      if (state.photos[i].id == data.id) {
        Vue.set(state.photos[i], 'uploadProgress', data.uploadProgress);
      }
    }
  },
  channgeMultipleSelected(state, data) {
    var list = state.multipleSelected.list;

    if (data.limitName.indexOf(data.box.name) != -1) return;

    for (var i = 0; i < list.length; i++) {
      var thisBox = list[i];
      if (thisBox.name == data.box.name && thisBox.box_id == data.box.box_id) {
        list.splice(i, 1);
        return;
      }
    }
    list.push(data.box);
  },
  changeMultipleBoxData(state, data) {
    var box = state.multipleSelected.list[data.index];
    box.x = data.x;
    box.y = data.y;
    state.warningTip.visible = false;
  },

  //修改box属性
  changeBoxData(state, data) {
    var pageNum = data.pageNum,
      boxType = data.boxType,
      boxId = data.boxId,
      list = data.data,
      selectedData = data.selected,
      dontSaveUndo = data.dontSaveUndo;  // 不保存撤销数据
    boxType += "_list";
    var bookInfo = state.bookInfo,
      selected = state.selected,
      pageData = isNaN((pageNum) * 1) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
    var beforePageData = JSON.parse(JSON.stringify(pageData));

    var boxInfo = Methods.getBoxInfo(pageData, boxType, boxId);
    var box = boxInfo.data;
    for (var i in list) {
      if (typeof (list[i]) == 'object') {
        if (box[i] == null) {
          box[i] = {};
        }
        for (var j in list[i]) {
          var val = checkNaN(list[i][j])
          if (box[i][j] === undefined) {
            Vue.set(box[i], j, val)
          }
          else {
            box[i][j] = val
          }
        }
        if (list[i] == null) {
          box[i] = null
        }
      }
      else {
        var val = checkNaN(list[i])
        if (box[i] === undefined) {
          Vue.set(box, i, val)
        }
        else {
          box[i] = val
        }
      }
    }

    function checkNaN(value) {
      if(value === ''){
        return value
      }
      if(value === 0){
        return 0
      }
      return isNaN(value * 1) ? value : value * 1;
    }

    var selectedState = selectedData ? selectedData.state : selected.state;

    if (!state.isMove && !dontSaveUndo) {
      pushTask(state, 'undoData', pageNum, beforePageData);
    }
    if (state.redoData[pageNum]) {
      Vue.set(state.redoData, pageNum, []);
    }

    state.selected = { data: box, pageNum: pageNum, state: selectedState, el: selected.el };

    if(boxType == 'imagebox_list' && (list.image === null || (list.image && list.image.id))){
      //照片使用状态
      this.commit('setPhotosUseState');
    }
  },

  //添加box
  addBox(state, data) {
    var pageNum = data.pageNum,
      boxType = data.boxType,
      box = data.box,
      bookInfo = state.bookInfo,
      pageData = isNaN((pageNum) * 1) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
    var beforePageData = JSON.parse(JSON.stringify(pageData));
    pageData[boxType].push(box);
    Vue.set(state.redoData, pageNum, []);
    pushTask(state, 'undoData', pageNum, beforePageData);
    state.multipleSelected.list = [];

    if(data.boxType == 'imagebox_list' && data.box.image){
      //照片使用状态
      this.commit('setPhotosUseState');
    }
  },

  //删除box
  deleteBox(state, data) {
    var pageNum = data.pageNum,
      boxType = data.boxType,
      boxId = data.boxId,
      bookInfo = state.bookInfo,
      pageData = isNaN((pageNum) * 1) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
    var beforePageData = JSON.parse(JSON.stringify(pageData));

    var removeImage = false;

    if (typeof boxId == 'object') { //删除多个box
      for (var i = 0; i < boxId.length; i++) {
        var type = boxType[i] + '_list';
        var boxInfo = Methods.getBoxInfo(pageData, type, boxId[i]);
        pageData[type].splice(boxInfo.index, 1);
        if(boxType == 'imagebox_list' && boxInfo.data.image){
          removeImage = true;
        }
      }
    }
    else {
      boxType += '_list';
      var boxInfo = Methods.getBoxInfo(pageData, boxType, boxId);
      pageData[boxType].splice(boxInfo.index, 1);
      if(boxType == 'imagebox_list' && boxInfo.data.image){
        removeImage = true;
      }
    }

    Vue.set(state.redoData, pageNum, []);
    pushTask(state, 'undoData', pageNum, beforePageData);

    Vue.nextTick(() => {
      state.selected = { el: null, data: {}, pageNum: pageNum, state: false }
      state.multipleSelected.list = [];
    })

    if(removeImage){
      //照片使用状态
      this.commit('setPhotosUseState');
    }
  },

  //修改page数据
  changePageData(state, data) {
    var pageNum = data.pageNum,
      bookInfo = state.bookInfo,
      newPageData = data.data,
      pageData = isNaN(pageNum) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
    var beforePageData = JSON.parse(JSON.stringify(pageData));
    if (isNaN(pageNum)) {
      state.bookInfo[pageNum] = newPageData;
    }
    else {
      state.bookInfo.page_list.splice(pageNum, 1, newPageData)
    }
    state.isChangePage = true;
    Vue.set(state.redoData, pageNum, []);
    pushTask(state, 'undoData', pageNum, beforePageData);

    //照片使用状态
    this.commit('setPhotosUseState');
  },

  changePageList(state, list) {
    state.bookInfo.page_list = list;
  },

  //修改所有页的背景颜色
  changeAllPageBg(state, data) {
    var bookInfo = state.bookInfo;
    for (var i in bookInfo) {
      if (i == 'page_list') {
        for (var j = 0; j < bookInfo.page_list.length; j++) {
          Vue.set(bookInfo.page_list[j], 'background_color', data)
        }
      }
      else if (bookInfo[i] && bookInfo[i].num) {
        Vue.set(bookInfo[i], 'background_color', data)
      }
    }

    bookInfo.de_background_color = data;
  },

  //修改某页背景颜色
  changePageBg(state, data) {
    var bookInfo = state.bookInfo
    var pageNum = data.pageNum;
    var pageData = isNaN(pageNum) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
    var beforePageData = JSON.parse(JSON.stringify(pageData))
    if (isNaN(pageNum)) {
      Vue.set(bookInfo[pageNum], 'background_color', data.color)
    }
    else {
      Vue.set(state.bookInfo.page_list[pageNum], 'background_color', data.color)
    }
    Vue.set(state.redoData, pageNum, []);
    pushTask(state, 'undoData', pageNum, beforePageData);
  },

  //修改所有页的字体
  changeAllPageFont(state, data) {
    let bookInfo = state.bookInfo;
    for (let i in state.bookInfo) {
      if (i == 'page_list') {
        for (let j = 0; j < bookInfo.page_list.length; j++) {
          for (let k = 0; k < bookInfo.page_list[j].textbox_list.length; k++) {
            Vue.set(bookInfo.page_list[j].textbox_list[k], 'font_id', data)
          }
        }
      }
      else if (bookInfo[i] && bookInfo[i].num) {
        for (let j = 0; j < bookInfo[i].textbox_list.length; j++) {
          Vue.set(bookInfo[i].textbox_list[j], 'font_id', data)
        }
      }
    }
  },

  //修改页数
  changePageCount(state, data) {
    var pageCount = data.pageCount,
      pageType = data.pageType,
      bookInfo = state.bookInfo,
      theme = state.theme,
      template = state.templates;

    var normalPageCount = pageCount - 2;
    var i;
    if (normalPageCount > bookInfo.page_list.length) {
      for (var num = bookInfo.page_list.length; num < normalPageCount; num++) {
        bookInfo.page_list_recycler = bookInfo.page_list_recycler == null ? [] : bookInfo.page_list_recycler;
        var recyclerPage = bookInfo.page_list_recycler.shift();
        var recoveryPage;
        if (recyclerPage && recyclerPage.num == bookInfo.page_list.length) {
          //从回收站恢复
          recoveryPage = recyclerPage;
        } else {
          //生成新页
          if (false && theme.id != 0 && theme.id != '' && theme.info) {
            //主题模板生成新页
            recoveryPage = theme.info.page_list[num];
          } else {
            //普通书随机生成新页
            var pageTemplates;
            var namespage;
            if (num % 2 === 0) {
              //左页
              namespage = 'page.left';
            } else {
              namespage = 'page.right';
              //右页
            }

            pageTemplates = template[namespage.split('.')[0]][namespage.split('.')[1]];
            var pageTemplate = pageTemplates[Math.floor(pageTemplates.length * Math.random())];
            recoveryPage = Methods.generateNewPage(num, namespage, pageTemplate.cross_type === 'T' ? pageType['跨页'] : pageType['普通单页'], pageTemplate);
          }
        }
        bookInfo.page_list.push(recoveryPage);
      }
    } else if (normalPageCount < bookInfo.page_list.length) {
      var page;
      bookInfo.page_list_recycler = bookInfo.page_list_recycler == null ? [] : bookInfo.page_list_recycler;
      for (i = bookInfo.page_list.length - 1; i >= normalPageCount; i--) {
        page = bookInfo.page_list.pop() //删除页
        bookInfo.page_list_recycler.unshift(page); //放到回收数组
      }
    }

    bookInfo.page_count = pageCount;

    //设置store记录的页数
    state.pageCount = pageCount;
    //修改标记
    state.isChangePage = true;
    //如果当前选中的页数大于修改后的页面，那么跳回首页
    if (!isNaN(state.currentPage[0]) && state.currentPage[0] > normalPageCount) {
      state.selected.pageNum = 'front_cover';
      state.currentPage = ['front_cover'];
    }
  },

  //添加撤销数据
  addUndoData(state, data) {
    var pageNum = data.pageNum,
      pageData = data.data;
    pushTask(state, 'undoData', pageNum, pageData);
  },

  //撤销
  undo(state, pageNum) {
    var undoData = state.undoData[pageNum];
    if (undoData && undoData.length) {
      var bookInfo = state.bookInfo;
      var beforePageData = isNaN(pageNum * 1) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
      if (isNaN(pageNum)) {
        Vue.set(bookInfo, pageNum, undoData[undoData.length - 1])
      }
      else {
        bookInfo.page_list.splice(pageNum, 1, undoData[undoData.length - 1])
      }
      undoData.pop();
      state.isChangePage = true;
      state.selected = { el: null, pageNum: state.selected.pageNum, data: {}, state: false }
      pushTask(state, 'redoData', pageNum, beforePageData);

      //照片使用状态
      this.commit('setPhotosUseState');
    }
  },

  //重做
  redo(state, pageNum) {
    var redoData = state.redoData[pageNum];

    if (redoData && redoData.length) {
      var bookInfo = state.bookInfo;
      var beforePageData = isNaN(pageNum) ? bookInfo[pageNum] : bookInfo.page_list[pageNum];
      if (isNaN(pageNum)) {
        Vue.set(bookInfo, pageNum, redoData[redoData.length - 1])
      }
      else {
        bookInfo.page_list.splice(pageNum, 1, redoData[redoData.length - 1])
      }
      redoData.pop();
      state.isChangePage = true;
      state.selected = { el: null, pageNum: state.selected.pageNum, data: {}, state: false }
      pushTask(state, 'undoData', pageNum, beforePageData);

      //照片使用状态
      this.commit('setPhotosUseState');
    }
  },

  //清空页
  deleteAllBox(state, data) {
    var pageNum = state.selected.pageNum;
    var pageData = isNaN(pageNum) ? state.bookInfo[pageNum] : state.bookInfo.page_list[pageNum];

    var beforePageData = JSON.parse(JSON.stringify(pageData));
    for (var i in pageData) {
      if (i.indexOf("_list") > -1) {
        pageData[i] = [];
      }
    }
    Vue.set(state.redoData, pageNum, []);
    pushTask(state, 'undoData', pageNum, beforePageData);

    //照片使用状态
    this.commit('setPhotosUseState');
  },

  //复制
  copyBox(state) {
    var multipleSelected = state.multipleSelected;
    var selected = state.selected;
    if (multipleSelected.list.length > 1) {
      for (var i = 0; i < multipleSelected.list.length; i++) {
        if (multipleSelected.list[i].name == 'pattern') {
          multipleSelected.list.splice(i, 1);
        }
      }
      state.copyData = multipleSelected.list;
    }
    else {
      if (selected.data.name == 'pattern') return
      state.copyData = [selected.data];
    }
  },

  //选择所有
  checkAll(state) {
    var pageNum = state.selected.pageNum;
    var pageData = isNaN(pageNum * 1) ? state.bookInfo[pageNum] : state.bookInfo.page_list[pageNum];
    var list = [];
    for (var i in pageData) {
      if (i.indexOf("_list") != -1 && typeof pageData[i] == 'object' && pageData[i] != null && Vue.prototype.Config.limitMultiple.indexOf(i.replace(/box_list/,'')) == -1) {
        pageData[i].forEach((item) => {
          list.push(item)
        })
      }
    }
    state.selected = { el: null, state: false, pageNum: pageNum, data: {} };
    state.multipleSelected = { pageNum: pageNum, list: list };
  },

  setPhotosUseState(state){
    var bookInfo = state.bookInfo,
      pageType = ['front_cover', 'front_flap', 'title_page', 'copyright', 'back_cover'],
      photoUseInfo = {};

    var findImgbox = function(page) {
      if (page && page.imagebox_list) {
        for (var j = 0; j < page.imagebox_list.length; j++) {
          var box = page.imagebox_list[j];
          if (box.image && box.image.id != 'logo') {
            !photoUseInfo[box.image.id] && (photoUseInfo[box.image.id] = []);
            photoUseInfo[box.image.id].push(page.num);
          }
        }
      }
    }

    for (var i = 0; i < pageType.length; i++) {
      bookInfo[pageType[i]] && findImgbox(bookInfo[pageType[i]]);
    }

    for (var i = 0; i < bookInfo.page_list.length; i++) {
      bookInfo.page_list[i] && findImgbox(bookInfo.page_list[i]);
    }

    state.photoUseInfo = photoUseInfo;
  },

  setDragStorePhoto(state, data){
    for(var item in data){
      state.dragStorePhoto[item] = data[item];
    }
  },

  addCanvasImage(state, data){
    !state.canvasImageList[data.id] && (state.canvasImageList[data.id] = data.img);
  },

  changePhotoUseInfo(){

  }
};

const actions = {
  animation(context) {
    clearTimeout(context.state.timer)
    context.commit("setState", {
      animation: true,
      timer: setTimeout(() => {
        context.commit("setState", { animation: false })
      }, 250)
    });
  },
  lockedDrag(context) {
    clearTimeout(context.state.timer)
    context.commit("setState", {
      isLockedDrag: true,
      timer: setTimeout(() => {
        context.commit("setState", { isLockedDrag: false })
      }, 250)
    });
  },
  saveBookData(store){
    var that = this;
    var book = store.state.bookInfo;

    //计算完成率
    var computeCompletedParcent = function () {
      var allPages = (function() {
        var allPages = [].concat(book.page_list);

        if (book.title_page) {
          allPages.unshift(book.title_page);
        }
        if (book.back_flap) {
          allPages.unshift(book.back_flap);
        }
        if (book.front_flap) {
          allPages.unshift(book.front_flap);
        }
        if (book.spine) {
          allPages.unshift(book.spine);
        }
        if (book.front_cover) {
          allPages.unshift(book.front_cover);
        }
        if (book.copyright) {
          allPages.push(book.copyright);
        }
        if (book.back_cover) {
          allPages.push(book.back_cover);
        }

        return allPages;
      })();

      var i, j, k, curPage, curImgbox, curTextbox, isCross,
        imgCount, textCount, decorationCount, colorboxCount;

      var completePageCount = 0;

      for (i = 0; i < allPages.length; i++) {
        curPage = allPages[i];

        if (isCross) { isCross = false; completePageCount++; continue; }
        isCross = curPage.type == 2;

        imgCount = curPage.imagebox_list.length;
        textCount = curPage.textbox_list.length;
        decorationCount = curPage.decorationCount ? curPage.decorationCount.length : 0;
        colorboxCount = curPage.colorbox_list ? curPage.colorbox_list.length : 0;

        for (j = 0; j < imgCount; j++) {
          curImgbox = curPage.imagebox_list[j];
          if (curImgbox.name == "imglogo") { continue; }
          if (!curImgbox || !curImgbox.image || !curImgbox.image.id) {
            break;
          }
        }
        if (j != 0 && j === imgCount) {
          completePageCount++;
        }
        //没有图片时，算页面的文本有没有填充
        if (imgCount == 0) {
          for (k = 0; k < textCount; k++) {
            curTextbox = curPage.textbox_list[k];
            if (!curTextbox || curTextbox.content == "" || curTextbox.content == null) {
              break;
            }
          }
          if (k != 0 && k === textCount) {
            completePageCount++;
          } else if (curPage.decoration_list && decorationCount > 0) {
            completePageCount++;
          } else if (curPage.colorbox_list && colorboxCount > 0) {
            completePageCount++;
          }
        }
        if (0 === imgCount && 0 === textCount && 0 === decorationCount && 0 === colorboxCount) {
          completePageCount++;
        }
      }

      return completePageCount / allPages.length;
    }

    axios.post('http://www.mimoprint.com/editor/save', qs.stringify({
      workId: Vue.prototype.Config.workId,
      userId: Vue.prototype.Config.workUserId,
      bookname: book.name,
      bookdata: LZString.compressToBase64(JSON.stringify(book)), //压缩一把，不压缩竟然100KB去
      completedParcent: computeCompletedParcent(), //完成率
      totalPageCount: book.page_count,
      whereFrom: Vue.prototype.Config.whereFrom,
      hostName: window.location.origin
    })).then(function(res){
      var data = res.data;
      if(data.isSuccess){
        store.commit('setState', { saveTip: true });
      }
    })
  }
}

export default new Vuex.Store({
  state,
  mutations,
  actions
});



