<template>
  <transition name="jumpIn">
    <div id="book_paramet" v-if="$store.state.bookParamet">
      <div class="title section_title">您希望拥有一本怎样的照片书</div>
      <div class="paramet_vessel">
        <div class="paramet_item">
          <ul>
            <li>
              <div class="item_title">装订方式</div>
              <div class="item_vessel">
                <ul>
                  <li v-for="(item, index) in craft.binding" :key="index">
                    <div><img :src="'/static/book/image/craft/binding_' + item + '.png'"></div>
                    <div class="item_binding" :class="{active: item == binding}" :data-binding="item" @click="changeBinding(item)">{{bindingInfo[item]}}</div>
                  </li>
                </ul>
              </div>
            </li>
            <li>
              <div class="item_title">纸张</div>
              <div class="item_vessel item_vessel_paper" ref="paper">
                <ul>
                  <li v-for="(item, index) in craft.paper[binding]" :key="index">
                    <div class="item_paper" :class="{active: item == paper}" :data-paper="item" @click="changePaper(item)" @mouseenter="showPaperTip($event, item)" @mouseleave="hidePaperTip($event)">{{paperInfo[item].name}}</div>
                  </li>
                </ul>
                <p class="paper_tip" v-if="paperTip.state" :style="{left: paperTip.x + 'px', top: paperTip.y + 'px'}">{{paperInfo[paperTip.id].content}}</p>
              </div>
            </li>
            <li>
              <div class="item_title">页数</div>
              <div class="item_vessel">
                <div class="item_pageBar" @click="showPageDropDown($event)">{{pageCount}}P</div>
                <div class="item_pageBar_select mini-scroll" v-show="pageDropDown">
                  <div class="item_pageCount" v-for="(item, index) in craft.pageCount" :key="index" :class="{active: item == pageCount}" :data-pageCount="item" @click="changePageCount(item)">{{item}}P</div>
                </div>
              </div>
            </li>
            <li>
              <div class="item_title"></div>
              <div class="item_vessel">
                <p>1． 一张纸正反两面算2P,您可以编辑过程中随时更改页数</p>
                <p>2． 1P可以放1~N张照片,我们不限制入册数量,视乎您如何排版</p>
              </div>
            </li>
          </ul>
        </div>
        <div class="paramet_price">
          <div class="price_title">产品价格估算:</div>
          <div class="price_vessel">
            <p>四色印刷
              <span>￥68.00元</span>
            </p>
            <p>六色印刷
              <span>￥68.00元</span>
            </p>
          </div>
        </div>
      </div>
      <div class="paramet_btn_list">
        <div class="paramet_btn cancel" @click="changeCarft($event, 'cancel')">取消</div>
        <div class="paramet_btn confirm" @click="changeCarft($event, 'confirm')">确定</div>
      </div>
    </div>
  </transition>
</template>
<script>
import axios from 'axios'
import qs from 'qs'

export default {
  name: 'bookParamet',
  data() {
    return {
      binding: this.$store.state.binding,
      paper: this.$store.state.paper,
      pageCount: this.$store.state.pageCount,
      bindingInfo: window.MimoBook.binding,//装订名称
      paperInfo: window.MimoBook.paper,//纸张名称
      craftInfo: window.MimoBook.craftInfo[this.Config.productId],
      paperTip: {
        state: false,//纸张介绍展示
        x: 0,
        y: 0
      },
      pageDropDown: false
    }
  },
  computed: {
    craft() {
      var craft = {
        binding: [],
        paper: {},
        pageCount: []
      };

      for (var i = 0; i < this.craftInfo.info.length; i++) {
        var item = this.craftInfo.info[i];
        craft.binding.indexOf(item.binding) == -1 && craft.binding.push(item.binding);

        !craft.paper[item.binding] && (craft.paper[item.binding] = []);
        if (craft.paper[item.binding].indexOf(item.paper) == -1) {
          craft.paper[item.binding].push(item.paper)
        }

        if (item.binding == this.binding && item.paper == this.paper) {
          for (var j = item.page.basePage; j <= item.page.maxPage; j += item.page.addPage) {
            craft.pageCount.push(j);
          }
        }
      }

      return craft;
    }
  },
  methods: {
    changeBinding(value) {
      this.binding = value;
      //切换装订后如果没有对应的原来纸张id，就默认选中第一个
      this.craft.paper[this.binding].indexOf(this.paper) == -1 && (this.paper = this.craft.paper[this.binding][0]);
    },
    changePaper(value) {
      this.paper = value;
      this.craft.pageCount.indexOf(this.pageCount) == -1 && (this.pageCount = this.craft.pageCount[this.craft.pageCount.length - 1]);
    },
    changePageCount(value) {
      this.pageCount = value;
      this.showPageDropDown();
    },
    showPaperTip(e, value) {
      var elRect = e.target.getBoundingClientRect(),
        paperRect = this.$refs.paper.getBoundingClientRect();

      this.paperTip.state = true;
      this.paperTip.id = value;
      this.paperTip.x = elRect.left - paperRect.left;
      this.paperTip.y = elRect.top + elRect.height - paperRect.top;
    },
    hidePaperTip(e) {
      this.paperTip.state = false;
      this.id = 0;
      this.paperTip.x = 0;
      this.paperTip.y = 0;
    },
    showPageDropDown() {
      this.pageDropDown = !this.pageDropDown;

      if (this.pageDropDown) {
        document.addEventListener('click', this.closeDropDwon);
      } else {
        document.removeEventListener('click', this.closeDropDwon);
      }
    },
    closeDropDwon(e) {
      if (!this.CommonMethods.matches(e.target, '.item_pageBar, .item_pageBar *, .item_pageBar_select, .item_pageBar_select *')) {
        this.showPageDropDown();
      }
    },
    changeCarft(e, type) {
      this.$store.commit('setState', { bookParamet: false });

      if (type == 'confirm') {
        var request = false;
        if(this.$store.state.binding != this.binding){
          request = true;
          this.$store.commit('setState', {binding: this.binding});
        }

        if(this.$store.state.paper != this.paper){
          request = true;
          this.$store.commit('setState', {paper: this.paper});
        }

        if(this.$store.state.pageCount != this.pageCount){
          request = true;
          this.$store.commit('changePageCount', {
            pageType: this.Config.pageType,
            pageCount: this.pageCount
          });
        }

        if(request){
          axios.post('http://www.mimoprint.com/editor/SaveCoverOption', qs.stringify({
            works_id: this.Config.workId,
            option_cover: this.Config.productInfo[this.Config.productId].cover,
            option_binding: this.binding,
            option_paper: this.paper,
            hostName: window.location.origin
          })).then(function(res){
            var data = res.data;
            if(data.ret == 1){
              console.log('修改成功');
            }
          });
        }
      }
    }
  }
}
</script>
<style lang="scss">
#book_paramet {
  position: absolute;
  width: 905px;
  height: 530px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2001;
  margin: auto;
  border: solid 1px #bbb;
  border-radius: 3px;
  background: #f8f8f8;
  box-shadow: 1px 1px 5px #909090;

  .title {
    height: 40px;
    line-height: 40px;
    color: #fff;
    padding-left: 20px;
    font-size: 15px;
    border-radius: 3px 3px 0 0;
  }

  .paramet_vessel {
    width: 843px;
    height: 380px;
    border: solid 1px #f0f0f0;
    background: #fff;
    margin: 30px 30px 20px;

    .paramet_item {
      display: inline-block;
      vertical-align: top;
      width: 613px;
      border-right: dashed 1px #ccc;
      margin: 15px 0;

      >ul {

        >li {
          margin: 0 30px;
          padding: 12px 0;
          border-bottom: solid 1px #ddd;

          &:last-child {
            border: none;
          }

          .item_title {
            display: inline-block;
            vertical-align: middle;
            width: 20px;
            color: #943539;
            font-size: 14px;
            font-weight: bold;
            padding: 5px;
            text-align: center;
            line-height: 20px;
          }

          .item_vessel {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin-left: 25px;
            width: 490px;

            >ul {

              >li {
                display: inline-block;
                vertical-align: middle;
                margin-right: 10px;

                .item_binding {
                  width: 148px;
                  height: 38px;
                  line-height: 38px;
                  border: solid 2px #f0f3f6;
                  background: #f0f3f6;
                  color: #555555;
                  padding-top: 2px;
                  margin-top: 5px;
                  text-align: center;
                  cursor: pointer;

                  &.active {
                    border: solid 2px #5488cd;
                  }
                }

                .item_paper {
                  width: 100px;
                  height: 32px;
                  line-height: 32px;
                  background: #f0f3f6;
                  border: solid 2px #f0f3f6;
                  text-align: center;
                  margin-top: 5px;
                  cursor: pointer;
                  position: relative;

                  &.active {
                    border: solid 2px #5488cd;
                  }
                }
              }
            }

            .item_pageBar {
              width: 106px;
              height: 34px;
              line-height: 34px;
              text-align: left;
              padding-left: 7px;
              color: #555;
              background: #fcfcfc;
              border: solid 1px #ddd;
              border-radius: 3px;
              font-size: 12px;
              cursor: pointer;
              position: relative;

              &:before {
                content: "";
                width: 1px;
                height: 34px;
                background: #ddd;
                float: right;
                margin-right: 16px;
              }

              &:after {
                content: "";
                border-width: 5px;
                border-style: solid;
                border-color: #888 transparent transparent transparent;
                position: absolute;
                right: 3px;
                top: 14px;
              }
            }

            .item_pageBar_select {
              width: 113px;
              border: 1px solid #ddd;
              position: absolute;
              top: 34px;
              height: 112px;
              overflow-y: scroll;
              background: #fff;
              z-index: 1;

              .item_pageCount {
                line-height: 20px;
                padding-left: 7px;

                &:hover {
                  background: #ddd;
                }

                &.active {
                  border: solid 2px #5488cd;
                }
              }
            }

            &.item_vessel_paper {
              height: 82px;
            }

            .paper_tip {
              width: 400px;
              line-height: 18px;
              padding: 8px;
              margin-top: 8px;
              border: dashed 1px #ede8d0;
              background: #fffff8;
              position: absolute;
              z-index: 1;

              &:before {
                content: '';
                position: absolute;
                left: 7px;
                top: -12px;
                width: 0px;
                height: 0px;
                border: dashed 6px rgba(0, 0, 0, 0);
                border-bottom: dashed 6px #DADA25;
              }

              &:after {
                content: '';
                position: absolute;
                left: 7px;
                top: -11px;
                width: 0px;
                height: 0px;
                border: dashed 6px rgba(0, 0, 0, 0);
                border-bottom: dashed 6px #fff;
              }
            }

            >p {
              line-height: 20px;
            }
          }
        }
      }
    }

    .paramet_price {
      display: inline-block;
      vertical-align: top;
      width: 165px;
      padding: 30px;

      .price_title {
        color: #943539;
        font-size: 14px;
        font-weight: bold;
      }

      .price_vessel {
        >p {
          margin-top: 10px;
          font-size: 14px;

          span {
            color: #fd8243;
            font-weight: bold;
          }
        }
      }
    }
  }

  .paramet_btn_list{
    margin: 0 30px;

    .paramet_btn{
      width: 120px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      color: #fff;
      border: solid 1px #f3671d;
      border-radius: 3px;
      background: #fe8444;
      font-size: 16px;
      font-weight: bold;
      cursor: pointer;

      &.cancel{
        float: left;
      }

      &.confirm{
        float: right;
      }
    }
  }
}
</style>



