<script>
export default {
  install: function(Vue) {
    //选中事件
    Vue.prototype.SelectElement = function(e, args) {
      e.stopPropagation();
      var el = args.el,
        box = args.box,
        pageNum = args.pageNum.toString(), //页码
        empty = args.empty || false, //清空选中列表
        callback = args.select, //回调
        hide = args.hide; //是否隐藏

      //拖动中就跳过触发click
      if (this.$store.state.isMove) {
        this.$store.commit("setState", { isMove: false });
        //选中回调
        if (typeof callback === "function") {
          callback(e, {});
        }
        return;
      }
      var ctrl = e.ctrlKey;

      //清空选中列表
      if (pageNum != this.$store.state.selected.pageNum || empty || !ctrl) {
        this.$store.commit("setState", {
          multipleSelected: { pageNum: pageNum, list: [] }, //清空多选
          selected: { state: false, el: "", data: {}, pageNum: pageNum } //清空单选
        });

        //如果点了页面空白地方
        if (empty) {
          //选中回调
          if (typeof callback === "function") {
            callback(e, {});
          }
          return;
        }
      }

      //插入选中列表
      this.$store.commit("channgeMultipleSelected", {
        box: box,
        limitName: this.Config.limitMultiple
      });

      var data = {};
      //多选了就标记不出现那个框框咯
      if (this.$store.state.multipleSelected.list.length == 1) {
        hide = hide == 1 ? false : true;
        data = { state: hide, el: el, data: box, pageNum: pageNum };
      } else {
        data = { state: false, el: "", data: {}, pageNum: pageNum };
      }
      this.$store.commit("setState", {
        selected: data
      });

      //选中回调
      if (typeof callback === "function") {
        callback(e, {});
      }
    };

    Vue.prototype.IsMultipleSelect = function() {
      var multipleSelected = this.$store.state.multipleSelected;
      if (multipleSelected.list.length >= 2) {
        return true;
      }
      return false;
    };

    Vue.prototype.isActive = function(pageNum, data) {
      var selected = this.$store.state.selected;
      return (
        selected.data &&
        pageNum == selected.pageNum &&
        data.box_id == selected.data.box_id &&
        data.name == selected.data.name
      );
    };
  }
};
</script>



