<template>
  <span class="selected_icon" :class="{selected_focus: isFocus()}" v-if="isShow()">
  </span>
</template>
<script>
export default {
  name: 'multipleSelected',
  props: [
    'box',
    'pageNum'
  ],
  methods: {
    isShow() {
      var multipleSelected = this.$store.state.multipleSelected,
        selected = this.$store.state.selected;

      if (this.pageNum == multipleSelected.pageNum && multipleSelected.list.length >= 2 && !selected.state) {
        for (var i = 0; i < multipleSelected.list.length; i++) {
          var box = multipleSelected.list[i];
          if (box.box_id == this.box.box_id && box.name == this.box.name) {
            return true;
          }
        }
      }

      return false;
    },
    isFocus() {
      var multipleSelected = this.$store.state.multipleSelected,
        selected = this.$store.state.selected;

      if (this.pageNum == multipleSelected.pageNum && !selected.state) {
        var box = multipleSelected.list[0]
        if (box.box_id == this.box.box_id && box.name == this.box.name) {
          return true;
        }
      }

      return false;
    }
  }
}
</script>
<style lang="scss">
.selected_icon {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1000;
  width: 28px;
  height: 28px;
  background: url(../../assets/common/image/select/multiple_icon.png) no-repeat;

  &.selected_focus {
    background: url(../../assets/common/image/select/multiple_icon_focus.png) no-repeat;
  }
}
</style>



