<template>
  <div id="save_tip" :data-state="state" v-if="saveTip">
    <span>保存成功</span>
  </div>
</template>
<script>
export default {
  name: "savetip",
  data() {
    return {
      state: 0 //状态 0：开始 1：显示 2：隐藏
    };
  },
  props: {
    saveTip: {
      type: Boolean,
      default() {
        return false;
      }
    }
  },
  watch: {
    saveTip() {
      if (this.saveTip) {
        this.state = 0;
        setTimeout(() => {
          this.state = 1;

          setTimeout(() => {
            this.state = 2;

            setTimeout(() => {
              this.$emit('changeState', false);
            }, 300);
          }, 2000);
        }, 100);
      }
    }
  }
};
</script>
<style lang="scss">
#save_tip {
  position: absolute;
  top: 130px;
  left: 0;
  right: 0;
  width: 130px;
  height: 58px;
  line-height: 58px;
  margin: auto;
  padding-left: 60px;
  background: url(../../assets/book/image/icon_savetip_bg.png) no-repeat;
  font-size: 26px;
  -webkit-text-shadow: 0 0 2px #aaa;
  text-shadow: 0 0 2px #aaa;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  z-index: 2001;
  -webkit-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;

  &[data-state="0"] {
    visibility: hidden;
    transition: none;
    transform: translateY(10px);
    opacity: 0;
  }

  &[data-state="1"] {
    visibility: visible;
    transition: all 0.3s;
    transform: translateY(0);
    opacity: 1;
  }

  &[data-state="2"] {
    visibility: hidden;
    transition: all 0.3s;
    transform: translateY(-10px);
    opacity: 0;
  }
}
</style>




