<template>
  <div class="layoutpannel">
    <div class="layoutpannel-tabs" :class="{'en': $i18n.locale == 'en-us'}">
      <span class="layoutpannel-tab" v-on:click="formatType = 'system'" v-bind:class="{'--select' : formatType == 'system'}">{{$t('layoutPannel.system')}}</span>
      <span class="layoutpannel-tab" v-on:click="formatType = 'mine'" v-bind:class="{'--select' : formatType == 'mine'}">{{$t('layoutPannel.mine')}}</span>
    </div>
    <div class="layoutpannel-content-wrapper">
      <div v-show="formatType == 'system'" class="layoutpannel-content --system">
        <div class="layoutpannel-system-template-select-wrapper">
          <select v-model="curTemplateType" class="layoutpannel-system-template-select">
            <option value="theme">{{$t('layoutPannel.theme')}}</option>
            <option v-on:click="getSimpleTemplates" value="simple">{{$t('layoutPannel.simple')}}</option>
          </select>
          <select v-model="systemTemplateSort" class="layoutpannel-system-template-select">
            <option value="all">{{$t('layoutPannel.all')}}</option>
            <option value="crosspage">{{$t('layoutPannel.crosspage')}}</option>
            <option value="textonly">{{$t('layoutPannel.textonly')}}</option>
            <option value="singleimgbox">{{$t('layoutPannel.singleimgbox')}}</option>
            <option value="2imgbox">{{$t('layoutPannel.2imgbox')}}</option>
            <option value="3imgbox">{{$t('layoutPannel.3imgbox')}}</option>
            <option value="4imgbox">{{$t('layoutPannel.4imgbox')}}</option>
            <option value="5imgboxplus">{{$t('layoutPannel.5imgboxplus')}}</option>
          </select>
        </div>
        <div class="layoutpannel-system-template-list-wrapper mini-scroll">
          <ul class="layoutpannel-system-template-list" v-if="!(Config.productInfo[Config.productId].noCover || Config.productInfo[Config.productId].specialCover)">
            <li v-for="item in curSystemTemplateList" @click.stop="applyTemplate(item)" v-if="!item.name || item.name!='BLANK'" v-bind:key="item.id" v-bind:style="{height: thumbHeight, backgroundImage: 'url('+host+item.thumbImageUrl+')'}"  class="layoutpannel-system-template-list-item" v-bind:class="{'--cross': item.cross_type == 'T', 'twinkle-red': namespace+'.'+item.name ==curTemplateItemName}"></li>
          </ul>
        </div>
      </div>

      <div v-show="formatType == 'mine'" class="layoutpannel-content --mine">
        <div class="layoutpannel-mine-tabs">
          <span class="layoutpannel-mine-tab" @click="mineType ='single'" v-bind:class="{'--selected': mineType == 'single'}">{{$t('layoutPannel.single')}}</span>
          <span class="layoutpannel-mine-tab" @click="mineType ='full'" v-bind:class="{'--selected': mineType == 'full'}">{{$t('layoutPannel.full')}}</span>
        </div>
        <div class="layoutpannel-mine-template-list-wrapper mini-scroll">
          <ul v-show="mineType == 'single'" class="layoutpannel-mine-template-list layoutpannel-mine-single-template-list">
            <li class="layoutpannel-mine-single-template-list-item --noboxshadow" v-bind:style="{height: thumbHeight}">
              <span class="layoutpannel-template-save-btn"></span>
            </li>
            <li v-for="item in mySinglePageTemplate()" @click="applyTemplate(item)" v-bind:key="item.name" class="layoutpannel-mine-single-template-list-item" v-bind:class="{'--cross': item.cross_type == 'T', 'twinkle-red': namespace + '.' + item.name == curTemplateItemName}" v-bind:style="{height: thumbHeight}">
              <PageThumb v-bind:template="item" ></PageThumb>
            </li>
          </ul>
          <ul v-show="mineType == 'full'" class="layoutpannel-mine-template-list layoutpannel-mine-full-template-list">
            <li class="layoutpannel-mine-full-template-list-item">
              <span class="layoutpannel-template-save-btn"></span>
            </li>
            <li class="layoutpannel-mine-full-template-list-item">
              <span class="layoutpannel-template-revert-btn"></span>
            </li>
            <li v-for="item in fullTemplates" @click="showApplyFullTemplate(item, $el)" v-bind:key="item.Id" class="layoutpannel-mine-full-template-list-item">
              <div class="layoutpannel-mine-full-book" v-text="item.Name">
              </div>
              <div class="layoutpannel-template-del-btn">
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <LayoutPannelDialog style="display: none; right: 0; left: initial;" v-bind:config="dialog.deleteMineSingleTemplate">
    </LayoutPannelDialog>
    <LayoutPannelDialog v-show="flag.selectFullTemplateVisible" v-bind:style="dialog.selectFullTemplate.style" v-bind:config="dialog.selectFullTemplate">
    </LayoutPannelDialog>

  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import PageThumb from '@/components/book/pageThumb'
import LayoutPannelDialog from '@/components/book/layoutPannelDialog'

export default {
  name: 'layoutPannel',
  data () {
    return {
      formatType: 'system', // mine
      mineType: 'single', //full
      host: 'http://mimoprint.com',
      curTemplateType: 'theme',
      simpleTemplatesAll: null,
      curTemplateItemName: '',
      systemTemplateSort: 'all',
      scrollTimer: null,
      selectedTemplateEl: null,
      thumbHeights: {
        h: '45px',
        s: '84px',
        f: '60px'
      },
      dialog: {
        deleteMineSingleTemplate: {
          type: 'narrow',
          title: {
            show: true,
            word: this.$t('layoutPannel.deleteMineTemplate.text')
          },
          content: {
            show: true,
            word: this.$t('layoutPannel.deleteMineTemplate.text1')
          },
          confirm: {
            show: true,
            word: this.$t('layoutPannel.deleteMineTemplate.yes')
          },
          cancel: {
            show: true,
            word: this.$t('layoutPannel.deleteMineTemplate.no')
          },
          close: {
            show: true
          }
        },
        addMineSingleTemplate: {
          type: 'wide',
          content: {
            show: true,
            word: this.$t('layoutPannel.addMineTemplate.text')
          },
          confirm: {
            show: true,
            word: this.$t('layoutPannel.addMineTemplate.yes')
          },
          close: {
            show: true,
          }
        },
        selectFullTemplate: {
          type: 'wide',
          style: {
            width: '256px',
            right: '164px',
            left: 'initial'
          },
          content: {
            show: true,
            word: this.$t('layoutPannel.selectFullTemplate.yes')
          },
          confirm: {
            show: true,
            word: this.$t('layoutPannel.selectFullTemplate.yes')
          },
          cancel: {
            show: true,
            word: this.$t('layoutPannel.selectFullTemplate.no')
          },
          close: {
            show: true,
            event: this.closeSelectFullTemplate
          }
        }
      },
      flag: {
        selectFullTemplateVisible: false
      }
    }
  },
  components: {
    PageThumb,
    LayoutPannelDialog,
  },
  computed: {
    page () {
      var page,
          pn = this.$store.state.selected.pageNum,
          bookInfo = this.$store.state.bookInfo;
      if (/^\s*$/.test(pn)) {
        page = null
      } else if (/^\d+$/.test(pn)) {
        page = bookInfo.page_list[pn]
      } else {
        page = bookInfo[pn]
      }
      return page
    },
    thumbHeight () {
      return this.thumbHeights[this.Config.productInfo[this.Config.productId].templateSize]
    },
    pageNum () {
      return this.$store.state.selected.pageNum
    },
    templateType () {
      var tempType = ''
      if (this.page) {
        var tempName = this.page.template_name
        if (!tempName) return themeTemp
        var tempType = tempName.split(".")[0]
        tempType = tempType.replace(/M/, '') //兼容自定义模板的名称
      }
      return tempType
    },
    namespace () {
      var ns = null
      if (this.page) {
        switch (this.templateType) {
          case "cover":
          case "spine":
          case "front_flap":
          case "copyright":
          case "back_cover":
            ns = this.templateType;
            break
          case "page":
            if (Number.parseInt(this.pageNum) % 2 == 0) {
              ns = this.templateType +'.left'
            } else {
              ns = this.templateType + '.right'
            }
            break
        }
      }
      return ns
    },
    themeTemplates () {
      var themeTemp = []
      if (this.page)　{
        switch (this.templateType) {
          case "cover":
          case "spine":
          case "front_flap":
          case "copyright":
          case "back_cover":
            themeTemp = this.$store.state.templates[this.templateType]
            break
          case "page":
            var pageType = this.namespace.split(".")[1]
            themeTemp = this.$store.state.templates[this.templateType][pageType]
            break
        }
      }
      return themeTemp
    },
    fullTemplates () {
      var ft = this.$store.state.fullTemplates
      return ft
    },
    curSystemTemplateList() {
      var target = [];
      if (this.curTemplateType == 'theme') {
        target = this.themeTemplates;
      }
      else if (this.curTemplateType == 'simple') {
        switch (this.templateType) {
          case "cover":
          case "spine":
          case "front_flap":
          case "copyright":
          case "back_cover":
            if (this.simpleTemplatesAll){
              target = this.simpleTemplatesAll[this.templateType];
            }
            break;
          case "page":
             var pageType = this.namespace.split(".")[1];
            if (this.simpleTemplatesAll){
              target = this.simpleTemplatesAll[this.templateType][pageType];
            }
            break;
        }
      }
      if (!target || target.length == 0) {
        target = [];
        return target;
      }
      return this.filterTemplates(target, this.systemTemplateSort);
    },

  },
  watch: {
    pageNum: {
      handler (pn) {
        if (/^\s*$/.test(pn)) {

        } else if (/^\d+$/.test(pn)) {
          this.curTemplateItemName = this.page.template_name
        } else {
          this.curTemplateItemName = this.page.template_name
        }
      },
      deep: true

    },
    curTemplateItemName (newval, oldval) {
      //if( newval == oldval) return
      var vm = this
      this.$nextTick(function () {
        var cls = '.twinkle-red'
        var el = document.querySelector('.layoutpannel-system-template-list ' + cls)
        if (el) {
          let targetTop = el.offsetTop - 10
          let wrapEl = document.querySelector('.layoutpannel-system-template-list-wrapper')
          let orignTop = wrapEl.scrollTop
          let dist = (targetTop - orignTop) / 200 * 17
          if (dist == 0 ) return
          wrapEl.scrollTop = targetTop
          // window.clearInterval(vm.scrollTimer)
          // vm.scrollTimer = window.setInterval(function () {
          //   console.log(vm.scrollTimer)
          //   wrapEl.scrollTop += dist
          //   var hasArrive = (dist < 0 && (wrapEl.scrollTop < targetTop) || wrapEl.scrollTop <= 0 ) || (dist > 0 && (wrapEl.scrollTop > targetTop || wrapEl.scrollTop >= wrapEl.offsetHeight ))
          //   if (hasArrive) {
          //     wrapEl.scrollTop = targetTop
          //     window.clearInterval(vm.scrollTimer)
          //   }
          // }, 17)
        }
      })
    },
    curTemplateType (newval) {
      if (newval == 'simple') {
        this.getSimpleTemplates()
      }
    }
  },
  methods: {
    getSimpleTemplates () {
      console.log('getsimple')
      var vm = this
      var url =  'http://www.mimoprint.com/Editor/ChangeDesignFormatList'
      if (vm.simpleTemplatesAll) {
        return
      }
      axios.post(url, qs.stringify({
        productId: this.Config.productId,
        device: 'pc',
        hostName: window.location.origin
      })).then(function (response) {
        if (response.data.isOk == 0) {
          alert(response.data.msg)
        } else {
          var simpleTemp = vm.sortTemplate(JSON.parse(response.data.data), 'imageCount')
          vm.simpleTemplatesAll = vm.formatTemplate(simpleTemp)
        }
      })
    },
    sortTemplate(template, sender){//版式按图片数量排序
      var newTempInfo = {
        cover: [],
        page_left: [],
        page_right: [],
        copyright: [],
        back_cover: []
      };

      var noCross = {
        cover: [],
        page_left: [],
        page_right: [],
        copyright: [],
        back_cover: []
      };
      var noCrossName = {
        cover: [],
        page_left: [],
        page_right: [],
        copyright: [],
        back_cover: []
      };

      var isCross = {
        cover: [],
        page_left: [],
        page_right: [],
        copyright: [],
        back_cover: []
      };
      var isCrossName = {
        cover: [],
        page_left: [],
        page_right: [],
        copyright: [],
        back_cover: []
      };

      var thisTemplate = [template.cover, template.page.left, template.page.right, template.copyright, template.back_cover];
      var templateInfo = {
          isCross: {
            cover: [],
            page_left: [],
            page_right: [],
            copyright: [],
            back_cover: []
          },
          noCross: {
            cover: [],
            page_left: [],
            page_right: [],
            copyright: [],
            back_cover: []
          }
      }

      //分类 是否跨页
      for (var i = 0; i < thisTemplate.length; i++) {
        var type;
        switch (i) {
          case 0:
            type = "cover";
            break;
          case 1:
            type = "page_left";
            break;
          case 2:
            type = "page_right";
            break;
          case 3:
            type = "copyright";
            break;
          case 4:
            type = "back_cover";
            break;
        }
        thisTemplate[i].forEach(function (info) {
          var item = info.cross_type;
          if (item == "T") {
            templateInfo.isCross[type].push(info);
          } else {
            templateInfo.noCross[type].push(info);
          }
        })
      }

      //排序
      for (var i = 0; i < thisTemplate.length; i++) {
        var type;
        switch (i) {
          case 0:
              type = "cover";
              break;
          case 1:
              type = "page_left";
              break;
          case 2:
              type = "page_right";
              break;
          case 3:
              type = "copyright";
              break;
          case 4:
              type = "back_cover";
              break;
        }

        if (sender == "imageCount") {
          thisTemplate[i].forEach(function (info) {
            var cross = info.cross_type;
            if (cross == "T") {
                isCross[type] = templateInfo.isCross[type].sort(by('imageBoxList', by('name')));
            } else {
                noCross[type] = templateInfo.noCross[type].sort(by('imageBoxList', by('name')));
            }
          })
        }

        if (sender == "number") {
          thisTemplate[i].forEach(function (info) {
            var cross = info.cross_type;
            if (cross == "T") {
                isCross[type] = templateInfo.isCross[type].sort(rank);
            } else {
                noCross[type] = templateInfo.noCross[type].sort(rank);
            }
          })
        }
      }

      function by(name, minor) {
        return function (o, p) {
          var a, b;
          if (o && p && typeof o === 'object' && typeof p === 'object') {
            if (name == "imageBoxList") {
                a = o[name].length;
                b = p[name].length;
            } else {
                a = o[name];
                b = p[name];
            }
            if (a === b) {
                return typeof minor === 'function' ? minor(o, p) : 0;
            }
            if (typeof a === typeof b) {
                return a < b ? -1 : 1;
            }
            return typeof a < typeof b ? -1 : 1;
          } else {
            thro("error");
          }
        }
      }

      function rank(a, b) {
        return a.number - b.number;
      }

      newTempInfo.cover = noCross.cover.concat(isCross.cover);
      newTempInfo.page_left = noCross.page_left.concat(isCross.page_left);
      newTempInfo.page_right = noCross.page_right.concat(isCross.page_right);
      newTempInfo.copyright = noCross.copyright.concat(isCross.copyright);
      newTempInfo.back_cover = noCross.back_cover.concat(isCross.back_cover);

      template.cover = newTempInfo.cover;
      template.page.left = newTempInfo.page_left;
      template.page.right = newTempInfo.page_right;
      template.copyright = newTempInfo.copyright;
      template.back_cover = newTempInfo.back_cover;

      return template;
    },
    filterTemplates(sourceArray, filterStr) {
      switch (filterStr) {
        case 'crosspage':
          return sourceArray.filter(function (item) { return item.cross_type === 'T' });
        case 'textonly':
          return sourceArray.filter(function (item) {
              return (!item.imageBoxList || item.imageBoxList.length === 0) && item.textBoxList.length > 0;
          });
        case 'singleimgbox':
          return sourceArray.filter(function (item) {
              return item.imageBoxList && item.imageBoxList.length == 1;
          });
        case 'multipleimgbox':
          return sourceArray.filter(function (item) {
              return item.imageBoxList && item.imageBoxList.length > 1;
          });
        case '2imgbox':
        case '3imgbox':
        case '4imgbox':
          var imgboxCount = parseFloat(filterStr.charAt(0));
          return sourceArray.filter(function (item) {
              return item.imageBoxList && item.imageBoxList.length === imgboxCount;
          });
        case '5imgboxplus':
          return sourceArray.filter(function (item) {
              return item.imageBoxList && item.imageBoxList.length >= 5;
          });
        default:
          return sourceArray;
      }
    },
    applyTemplate (item) {
      if(this.$store.state.showPageSort) return false;

      this.curTemplateItemName = this.namespace + '.' + item.name
      console.log(this.curTemplateItemName)
      var pageType = (parseFloat(this.page.type) === this.Config.pageType['普通单页'] || (parseFloat(this.page.type)) === this.Config.pageType['跨页']) ? (item.cross_type === 'T' ? this.Config.pageType['跨页'] : this.Config.pageType['普通单页']) : this.page.type;
      var newPage = this.genNewPage(this.pageNum, this.namespace, pageType, item)
      console.log(newPage)

      var photos = this.$store.state.photos
      for (var i = 0; i < this.page.imagebox_list.length; i++) {
        var curImgbox = this.page.imagebox_list[i], curPhotos
        if (!curImgbox.image || curImgbox.themeImage) {
          continue
        }
        curPhotos = photos.filter(c => c.id == curImgbox.image.id)
        if (curPhotos.length == 0) {
          continue
        }
        var curPhoto = curPhotos[0]
        var closestDefinition = 0, closestNewImgbox = null  //#region 从原来的页上找出精度最接近的图片框
        for (var j = 0; j < newPage.imagebox_list.length; j++) {
          var curNewImgbox = newPage.imagebox_list[j]
          if (curNewImgbox.image) {
            continue
          }
          var curDefinition
          if (curPhoto.originalWidth / curPhoto.originalHeight >= curNewImgbox.width / curNewImgbox.height) {
              curDefinition = this.computeDefinition(curPhoto.originalWidth, curNewImgbox.width);
          } else {
              curDefinition = this.computeDefinition(curPhoto.originalWidth, curNewImgbox.height * curPhoto.originalWidth / curPhoto.originalHeight);
          }

          if (curDefinition > closestDefinition) {
              closestDefinition = curDefinition;
              closestNewImgbox = curNewImgbox;
          }
        }

        if (closestNewImgbox) {
          var photo = curPhoto
          var imageWidth, imageHeight
          if (photo.originalWidth / photo.originalHeight >= closestNewImgbox.width / closestNewImgbox.height) {
            imageWidth = closestNewImgbox.height * photo.originalWidth / photo.originalHeight;
            imageHeight = closestNewImgbox.height;
          } else {
            imageWidth = closestNewImgbox.width;
            imageHeight = closestNewImgbox.width * photo.originalHeight / photo.originalWidth;
          }

          var imageX = (closestNewImgbox.width - imageWidth) / 2,
              imageY = (closestNewImgbox.height - imageHeight) / 2;

          closestNewImgbox.image = {
            x: imageX,
            y: imageY,
            id: curImgbox.image.id,
            width: imageWidth,
            rotation: 0,
            fileName: photo.fileName
          };
        }
      }

      for (var i = 0; i < newPage.textbox_list.length; i++) {
        var textbox = this.page.textbox_list[i]
        var newTextbox = newPage.textbox_list[i]
        if (textbox) {
          // newTextbox.content = editorStatus == 0 ? textbox.content : newTextbox.content;
          //换模版保持原来字体
          // newTextbox.font_id = editorStatus == 0 ? textbox.font_id : newTextbox.font_id;
        } else {
          // newTextbox.content = editorStatus == 0 ? '' : newTextbox.content;
        }
      }
      this.$store.commit('changePageData',{pageNum: this.$store.state.selected.pageNum, data: newPage})
      this.$store.commit('setState', {selected: {el: null, pageNum: this.$store.state.selected.pageNum, data: {}, state: false } })
      this.$store.dispatch("animation")

    },
    computeDefinition (imgOriginalWidthPx, imgWidthMM) {
      var imgOriginalWidthMM = this.pxToMm(imgOriginalWidthPx); //算出图原样印刷出来的毫米宽度
      return imgOriginalWidthMM / imgWidthMM; //清晰度 = 原始宽度 / 印刷宽度
    },
    genNewPage (pageNum, namespace, pageType, template, isFixImageTheme) {
      var vm = this
      var genNewTextbox = vm.genNewTextbox
      var genNewShapebox = vm.genNewShapebox
      var genNewDecoration = vm.genNewDecoration
      var genNewShading = vm.genNewShading
      var genNewPattern = vm.genNewPattern
      var gen = function (list, genNewBox) {
        var boxList = []
        for (var i = 0; i < list.length; i++) {
          var box = genNewBox(list[i])
          boxList.push(box)
        }
        return boxList
      }

      return {
        num: pageNum.toString(),
        template_name: (namespace + '.' + template.name),
        width: template.width,
        height: template.height,
        imagebox_list: (function () {
          var imgboxList = []
          var boxList = template.imageBoxList || template.imagebox_list || [] //兼容版式数据跟自定义版式数据的格式问题
          for (var i = 0; i < boxList.length; i++) {
            var newImgbox, imgbox = boxList[i]
            if ((isFixImageTheme && pageType == 3) || (imgbox.name == 'logo' && pageType == 4)) {
              // if(window.IsWorkShop == 2) continue;
              newImgbox = vm.genNewImgboxFixImage(boxList[i])
            } else {
              newImgbox = vm.genNewImgbox(boxList[i])
            }
            imgboxList.push(newImgbox)
          }
          return imgboxList
        })(),
        textbox_list: gen(template.textBoxList || template.textbox_list || [], genNewTextbox),
        colorbox_list: gen(template.shapeBoxList || template.colorbox_list || [], genNewShapebox),
        decoration_list: gen(template.decorationboxList || template.decoration_list || [], genNewDecoration),
        shadingbox_list: gen(template.shadingBoxList || template.shadingbox_list || [], genNewShading),
        patternbox_list: gen(template.patternBoxList || template.patternbox_list || [], genNewPattern),
        background_color: template.backgroundColor || template.background_color || "#fff",
        type: pageType

      }
    },
    genNewImgbox (imgboxTemplate) {
      return {
        name: imgboxTemplate.name,
        index: imgboxTemplate.index == undefined ? 1 : imgboxTemplate.index,
        width: imgboxTemplate.width * 1,
        height: imgboxTemplate.height * 1,
        x: imgboxTemplate.x * 1,
        y: imgboxTemplate.y * 1,
        rotation: imgboxTemplate.rotation  == undefined ? 0 : imgboxTemplate.rotation,
        border_width: imgboxTemplate.border_width || 0,
        border_color: imgboxTemplate.border_color || '#ffffff',
        box_id: imgboxTemplate.box_id || null,
        locked: imgboxTemplate.locked == false ? false : true,
        image: null,
      }
    },
    genNewImgboxFixImage(imgboxTemplate) {//保留照片信息的
      return {
        name: imgboxTemplate.name,
        index: imgboxTemplate.index == undefined ? 1 : imgboxTemplate.index,
        width: imgboxTemplate.width * 1,
        height: imgboxTemplate.height * 1,
        x: imgboxTemplate.x * 1,
        y: imgboxTemplate.y * 1,
        rotation: imgboxTemplate.rotation == undefined ? 0 : imgboxTemplate.rotation,
        border_width: imgboxTemplate.border_width || 0,
        border_color: imgboxTemplate.border_color || '#ffffff',
        box_id: imgboxTemplate.box_id || null,
        locked: imgboxTemplate.locked == false ? false : true,
        image: imgboxTemplate.image || null,
        themeImage: imgboxTemplate.themeImage || null
      };
    },
    genNewTextbox(textboxTemplate) {
      return {
        name: textboxTemplate.name,
        index: textboxTemplate.index == undefined ? 1 : textboxTemplate.index,
        width: textboxTemplate.width * 1,
        height: textboxTemplate.height * 1,
        x: textboxTemplate.x * 1,
        y: textboxTemplate.y * 1,
        rotation: textboxTemplate.rotation == undefined ? 0 : textboxTemplate.rotation,
        content: window.EditorStatus == 0 ? '' : textboxTemplate.content == undefined ? "" : textboxTemplate.content,
        pt: textboxTemplate.pt,
        color: textboxTemplate.color,
        box_id: textboxTemplate.box_id || null,
        font_id: textboxTemplate.font_id || "101",
        align: textboxTemplate.align,
        space: textboxTemplate.space,
        leading: textboxTemplate.leading || -1, //((parseFloat(textboxTemplate.leading) === 0 || parseFloat(textboxTemplate.leading) < 0) ? -1 : textboxTemplate.pt * textboxTemplate.leading)
        opacity: textboxTemplate.opacity || 1,
        readonly: textboxTemplate.readonly || 1,
        locked: textboxTemplate.locked == false ? false : true,
      };
    },
    genNewShapebox(shapgeTemplate) {
      return {
        border_color: shapgeTemplate.border_color,
        border_width: shapgeTemplate.border_width,
        color: shapgeTemplate.color,
        color_id: shapgeTemplate.color_id,
        height: shapgeTemplate.height * 1,
        index: shapgeTemplate.index == undefined ? 1 : shapgeTemplate.index,
        locked: shapgeTemplate.locked == false ? false : true,
        name: shapgeTemplate.name,
        box_id: shapgeTemplate.box_id || null,
        opacity: shapgeTemplate.opacity,
        rotation: shapgeTemplate.rotation == undefined ? 0 : shapgeTemplate.rotation,
        width: shapgeTemplate.width * 1,
        x: shapgeTemplate.x * 1,
        y: shapgeTemplate.y * 1
      };
    },
    genNewDecoration(decorationTemplate) {
      return {
        decoration_id: decorationTemplate.decoration_id,
        height: decorationTemplate.height * 1,
        index: decorationTemplate.index == undefined ? 1 : decorationTemplate.index,
        locked: decorationTemplate.locked == false ? false : true,
        name: decorationTemplate.name,
        rotation: decorationTemplate.rotation == undefined ? 0 : decorationTemplate.rotation,
        src: decorationTemplate.src,
        box_id: decorationTemplate.box_id || null,
        width: decorationTemplate.width * 1,
        x: decorationTemplate.x * 1,
        y: decorationTemplate.y * 1,
        type: decorationTemplate.type || null,
        color: decorationTemplate.color,
        text: decorationTemplate.text || null
      }
    },
    genNewShading(shadingTemplate) {
      return {
        id: shadingTemplate.id,
        height: shadingTemplate.height * 1,
        index: shadingTemplate.index == undefined ? 1 : shadingTemplate.index,
        locked: shadingTemplate.locked == false ? false : true,
        box_id: shadingTemplate.box_id || null,
        name: shadingTemplate.name,
        imgWidth: shadingTemplate.imgWidth,
        rotation: shadingTemplate.rotation == undefined ? 0 : shadingTemplate.rotation,
        width: shadingTemplate.width * 1,
        x: shadingTemplate.x * 1,
        y: shadingTemplate.y * 1,
        editUrl: shadingTemplate.editUrl,
        thumbUrl: shadingTemplate.thumbUrl
      }
    },
    genNewPattern(patternTemplate) {
      return {
        id: patternTemplate.id,
        x: patternTemplate.x * 1,
        y: patternTemplate.y * 1,
        box_id: patternTemplate.box_id || null,
        width: patternTemplate.width * 1,
        height: patternTemplate.height * 1,
        index: patternTemplate.index == undefined ? -1 : patternTemplate.index,
        locked: patternTemplate.locked == false ? false : true,
        name: patternTemplate.name,
        imgWidth: patternTemplate.imgWidth,
        rotation: patternTemplate.rotation == undefined ? 0 : patternTemplate.rotation,
        src: patternTemplate.src,
        type: patternTemplate.type
      }
    },
    showApplyFullTemplate (item,el) {
      console.log(el.offsetTop)
      var vm = this
      this.dialog.selectFullTemplate.style.top = el.offsetTop + 'px'
      this.dialog.selectFullTemplate.cancel.event = function () {
         vm.applyFullTemplate(item, false)
         vm.flag.selectFullTemplateVisible = false
      }
      this.dialog.selectFullTemplate.confirm.event = function () {
         vm.applyFullTemplate(item, true)
         vm.flag.selectFullTemplateVisible = false
      }
      this.flag.selectFullTemplateVisible = true

    },
    applyFullTemplate (item, shouldWithImage) {
      console.log(item)
      var templateData = JSON.parse(item.TemplateData)
      var bookInfo = this.$store.state.bookInfo
      if(shouldWithImage) {
        var backCoverImgs = []
        var backFlapImgs = []
        var copyRightImgs = []
        var frontCoverImgs = []
        var frontFlapImgs = []
        var spineImgs = []
        var titlePageImgs = []
        var pageListImgs = {}
        var info = Object.assign({}, bookInfo)
        var useInfo = templateData

        var shift = function (imgbox, arr) {
          imgbox.imagebox_list.forEach(function (list, ind) {
            if(list.image !=null && list.name != 'logo') {
              arr.push(list.image)
            }
          })
        }

        info.back_cover && shift(info.back_cover, backCoverImgs);
        info.back_flap && shift(info.back_flap, backFlapImgs);
        info.copyright && shift(info.copyright, copyRightImgs);
        info.front_cover && shift(info.front_cover, frontCoverImgs);
        info.front_flap && shift(info.front_flap, frontFlapImgs);
        info.spine && shift(info.spine, spineImgs);
        info.title_page && shift(info.title_page, titlePageImgs);

        info.page_list.forEach(function (page, ind) {
          if(page.imagebox_list.length != 0) {
            page.imagebox_list.forEach(function(box, boxIndex) {
              !pageListImgs[ind] && (pageListImgs[ind] = [])
              if (box.image != null) {
                pageListImgs[ind].push(box.image)
              }
            })
          } else {
            pageListImgs[ind] = []
          }
        })
        var photos = this.$store.state.photos
        var insertBoxImg = function (useImgBox, imgArr) {
          var boxList = useImgBox.imagebox_list,
              len = Math.min(boxList.length, imgArr.length);
          for (var i = 0; i < len; i++) {
              var photo =  photos.filter(c => c.id == imgArr[i].id)
              if(photo.length == 0) {
                continue
              } else {
                photo = photo[0]
              }

              var imageWidth, imageHeight;
              if (photo.originalWidth / photo.originalHeight >= boxList[i].width / boxList[i].height) {
                  imageWidth = boxList[i].height * photo.originalWidth / photo.originalHeight;
                  imageHeight = boxList[i].height;
              } else {
                  imageWidth = boxList[i].width;
                  imageHeight = boxList[i].width * photo.originalHeight / photo.originalWidth;
              }

              var imageX = (boxList[i].width - imageWidth) / 2,
                  imageY = (boxList[i].height - imageHeight) / 2;

              boxList[i].image = {
                  x: imageX,
                  y: imageY,
                  id: imgArr[i].id,
                  width: imageWidth,
                  rotation: 0,
                  fileName: imgArr[i].fileName
              }
          }
        }

        if (backCoverImgs.length != 0 && useInfo.back_cover) {
          insertBoxImg(useInfo.back_cover, backCoverImgs)
        }

        if (backFlapImgs.length != 0 && useInfo.back_flap) {
          insertBoxImg(useInfo.back_flap, backFlapImgs)
        }

        if (copyRightImgs.length != 0 && useInfo.copyright) {
          insertBoxImg(useInfo.copyright, copyRightImgs)
        }

        if (frontCoverImgs.length != 0 && useInfo.front_cover) {
          insertBoxImg(useInfo.front_cover, frontCoverImgs)
        }

        if (frontFlapImgs.length != 0 && useInfo.front_flap) {
          insertBoxImg(useInfo.front_flap, frontFlapImgs)
        }

        if (spineImgs.length != 0 && useInfo.spine) {
          insertBoxImg(useInfo.spine, spineImgs)
        }

        if (titlePageImgs != 0 && useInfo.title_page) {
          insertBoxImg(useInfo.title_page, titlePageImgs)
        }

        var usePageNum = Math.min(info.page_list.length, useInfo.page_list.length);
        for (var i = 0; i < usePageNum; i++) {
          insertBoxImg(useInfo.page_list[i], pageListImgs[i]);
        }
        this.$store.commit('setState', {bookInfo: useInfo})

      } else {
        for (var i = 0; i < bookInfo.back_cover.imagebox_list.length; i++) {
          var backImgBox = bookInfo.back_cover.imagebox_list[i];
          if (backImgBox.name == "logo" && backImgBox.image) {
            for (var j = 0; j < templateData.back_cover.imagebox_list.length; j++) {
              if (templateData.back_cover.imagebox_list[j].name == "logo") {
                  templateData.back_cover.imagebox_list[j].image = backImgBox.image;
              }
            }
          }
        }
        this.$store.commit('setState', {bookInfo: templateData})
      }
    },
    mySinglePageTemplate () {
      var finalTemplates = []
      if (this.page){
        var templateName = this.page.template_name
        var names = templateName.split('.')
        names.pop()
        var namespace = names[0][0] == 'M' ? names[0] : 'M' + names[0]
        namespace = namespace == 'Mcopyright' ? 'Mpage' : namespace
        var templates = this.getMyTemplates(namespace)
        var pageNum = this.$store.state.selected.pageNum
        if (parseFloat(pageNum) == this.$store.state.bookInfo.page_list.length || parseFloat(pageNum) == 1 || pageNum == "title_page" || this.templateType == "right" || pageNum == "copyright") {
          finalTemplates = templates.filter(function(item) {
            return item.cross_type !== 'T'
          })
        } else {
          finalTemplates = templates
        }
      }
      return finalTemplates
    },
    getMyTemplates(namespace) {
      var names = namespace.split('.')
      var current;
      var myTemplates = this.$store.state.myTemplates
      for (var i = 0; i < names.length; i++) {
        var name =names[i]
        if (myTemplates[name] instanceof Array) {
          current = myTemplates[name]
        } else if (myTemplates[name] == undefined) {
          current = []
        }else {
          current = current[name]
        }
      }

      return current
    },
    closeSelectFullTemplate () {
      this.flag.selectFullTemplateVisible = false
    },
    formatTemplate(templates) {
      var list = []
      for(var name in templates) {
        if (name == 'page') {
          templates[name]['left'] && (list.push(templates[name]['left']))
          templates[name]['right'] && (list.push(templates[name]['right']))
        } else {
          list.push(templates[name])
        }
      }
      list.forEach(function (boxes, ind) {
        boxes.forEach(function (box, boxIndex) {
          for (var name in box) {
            var boxName = ''
            switch(name) {
              case 'decoration_list':
              case 'decorationboxList':
                boxName = 'decora'
                break
              case 'colorbox_list':
              case 'shapeBoxList':
                boxName = 'shape'
                break
              case 'imagebox_list':
              case 'imageBoxList':
                boxName = "img"
                break
              case 'shadingbox_list':
              case 'shadingBoxList':
                boxName = "shading"
                break
              case 'textbox_list':
              case 'textBoxList':
                boxName  = 'txt'
                break
              case 'patternbox_list':
                boxName ='pattern'
                break

            }
            if (boxName != '' && box[name] != null) {
              box[name].forEach(function (item, itemInd) {
                item.name = boxName
                item.box_id = itemInd + 1
              })
            }
          }
        })
      })

      return templates;
    },

  },
  mounted () {

  }
}
</script>

<style lang="scss">
  .layoutpannel {
    position: absolute;
    width: 166px;
    right: 0;
    top: 105px;
    bottom: 0;
    z-index: 1004;
  }
  .layoutpannel-tabs {
    height: 28px;
    overflow: hidden;
    box-sizing: border-box;
    font-size: 15px;
    font-weight: normal;
    letter-spacing: 1px;
    text-shadow: 0 0 1px rgba(255,255,255,0.7);
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    color: #fff;
    background: linear-gradient(#6d3538, #411a1f);
    padding: 5px 0 0 5px;
    &.en{
      font-size: 12px;
    }
  }
  .layoutpannel-tab {
    float: left;
    position: relative;
    display: block;
    box-sizing: border-box;
    width: 78px;
    height: 23px;
    line-height: 23px;
    padding: 0 7px;
    border-radius: 4px 4px 0 0;
    cursor: pointer;
    &.--select, &:active {
      background: #fff;
      color: #4d1e24;
      text-shadow: 0 0 1px rgba(77,30,36,0.7);
      box-shadow: 0 1px 6px #000;
      z-index: 1;
    }
  }

  .layoutpannel-content-wrapper {
    position: absolute;
    top: 28px;
    bottom: 0;
    left: 0;
    right: 0;
    background: #ffffff;
    border-left: 1px solid #fff;
  }

  .layoutpannel-content {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  .layoutpannel-system-template-select-wrapper {
    position: relative;
    line-height: 29px;
    box-sizing: border-box;
    padding: 0 10px 1px;
    text-align: center;
  }

  .layoutpannel-system-template-select {
    vertical-align: middle;
    width: 100%;
  }
  .layoutpannel-system-template-list-wrapper {
    display: flex;
    flex: 1;
    overflow-y: scroll;
  }
  .layoutpannel-system-template-list {
    position: relative;
    padding-bottom: 10px;
    width: 100%;
    height: initial;
  }
  .layoutpannel-system-template-list-item {
    position: relative;
    height: 84px;
    margin: 6px;
    cursor: pointer;
    float: left;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 1px 6px #aaa;
    background-size: 100% 100%;
    &.--cross {
      width: 120px;
      margin-left: 12px;
    }
  }

  .layoutpannel-mine-tabs {
    position: absolute;
    height: 32px;
    line-height: 29px;
    top: 0;
    left: 0;
    right: 0;
    box-sizing: border-box;
    padding: 0 10px;
    text-align: center;
  }
  .layoutpannel-mine-tab {
    float: left;
    cursor: pointer;
    color: #343434;
    font-weight: bold;
    width: 50%;
    font-size: 13px;
    &.--selected {
      border-bottom: 2px solid #4d1e24;
      color: #4d1e24;
    }
  }

  .layoutpannel-mine-template-list-wrapper {
    position: absolute;
    top: 32px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
  }

  .layoutpannel-mine-template-list {
    position: relative;
    padding-bottom: 10px;
    width: 100%;
    height: initial;
  }
  .layoutpannel-template-save-btn {
    width: 50px;
    height: 50px;
    border: 1px dashed #ccc;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    display: block;
    &:before, &:after {
      content: '';
      position: absolute;
      width: 34px;
      height: 2px;
      background: #ccc;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
    &:after {
      width: 2px;
      height: 34px;
    }
  }

  .layoutpannel-template-revert-btn {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 50px;
    height: 50px;
    border: 1px dashed #ccc;
    display: block;
    background: url('../../assets/book/image/layoutpannel-revert.png') center center / 62% 62% no-repeat;
  }

  .layoutpannel-mine-single-template-list-item {
    position: relative;
    margin: 6px;
    box-shadow: 0 1px 6px #aaa;
    cursor: pointer;
    display: block;
    float: left;
    width: 60px;
    height: 84px;
    &.--noboxshadow {
      box-shadow: none;
    }
    &.--cross {
      width: 120px;
      margin-left: 12px;
    }
  }

  .layoutpannel-mine-full-template-list-item {
    position: relative;
    margin: 6px;
    cursor: pointer;
    display: block;
    float: left;
    width: 60px;
    height: 60px;
    text-align: center;
  }

  .layoutpannel-mine-full-book {
    box-sizing: border-box;
    height: 60px;
    padding-top: 43px;
    background: url('../../assets/book/image/layoutpannel-full-book.png') center 14px / contain no-repeat;
  }

  .layoutpannel-template-del-btn {
      position: absolute;
      display: none;
      top: -6px;
      right: -10px;
      width: 20px;
      height: 20px;
      background: #f65314;
      border-radius: 15px;
      cursor: pointer;
      &:before, &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;
        width: 2px;
        height: 60%;
        background: #fff;
      }
      &:before {
        transform: rotate(45deg);
      }
      &:after {
        transform: rotate(-45deg);
      }
  }

  .layoutpannel-mine-full-template-list-item:hover {
    .layoutpannel-template-del-btn {
      display: block;
      z-index: 1;
    }
  }

  .twinkle-red {
    box-shadow: 0 1px 6px red;
    animation: twinkling 0.6s ease-in;
  }
  @keyframes twinkling {
    0% {
      opacity: 0;
    }
    25% {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
</style>





