<template>
  <div id="store_thumbimage"
    v-if="thumbInfo.src != ''"
    :style="{
      width: thumbInfo.w / 2 +'px',
      height: thumbInfo.h / 2 + 'px',
      top: thumbInfo.y - thumbInfo.h / 4 + 'px',
      left: thumbInfo.x - thumbInfo.w / 4 + 'px',
      }">
      <photoStoreThumb :photo="thumbInfo" :canvasImg="canvasImageList[thumbInfo.imgId]" @addCanvasImg="addCanvasImg"></photoStoreThumb>
  </div>
</template>
<script>
import PhotoStoreThumb from '@/components/common/photoStoreThumb'
export default {
  name: 'dragstoreimage',
  data(){
    return {}
  },
  props: {
    thumbInfo: {
      type: Object,
      default(){
        return {}
      }
    },
    canvasImageList: {
      type: Array,
      default(){
        return []
      }
    }
  },
  components: {
    PhotoStoreThumb
  },
  methods: {
    addCanvasImg(data){
      this.$store.commit('addCanvasImage', data);
    }
  }
}
</script>
<style lang="scss">
#store_thumbimage{
  position: fixed;
  z-index: 2005;
  pointer-events:none;

  canvas{
    width: 100%;
    height: 100%;
  }
}
</style>



