<template>
  <div class="select-list_wrapper mini-scroll edit_dialog" :style="trueStyle">
    <ul ref="ul" v-if="type == 'arr'">
      <li v-for="(item, index) in list" :key="index" v-text="item +''+ (textAdd ? textAdd : '')"  :title="item +''+ (textAdd ? textAdd : '')" @click="selectItem(item)" :class="selected == item ? 'active': ''" ></li>
    </ul>
    <ul ref="ul" v-else>
      <li v-for="(item, index) in list" :key="index" v-text="item" :title="item" @click="selectItem(index)" :class="selected == index ? 'active': ''"></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'selectdialog',
  props: [
    'data',
    'styles',
    'width',
    'textAdd',
    'selected',
    'type'
  ],
  data() {
    return {
    }
  },
  computed: {
    list() {
      var list = [];
      if(typeof this.data == 'object'){
        list = this.data;
      }
      else{
        for(var i = 0; i < this.data * 1 + 1; i++){
          list.push(i)
        }
      }
      return list;
    },
    trueStyle() {
      this.styles.maxWidth = this.width +'px';
      return this.styles;
    }
  },
  methods: {
    selectItem(value) {
      this.$emit("change", value)
    }
  },
  mounted() {
    if(this.$refs.ul.querySelector(".active")){
      var activeTop = this.$refs.ul.querySelector(".active").offsetTop;
      this.$el.scrollTop = activeTop - 24 * 2
    }
  }
}
</script>

<style lang="scss">
.select-list_wrapper {
  position: absolute;
  z-index: 201;
  background: #ededed;
  -webkit-box-shadow: 0 1px 4px #999;
  box-shadow: 0 1px 4px #999;
  margin-left: 1px;
  max-height: 120px;
  transform-origin: center top;
  overflow: hidden;
  overflow-y: scroll;
  ul {
    li {
      display: block;
      text-align: center;
      height: 24px;
      padding: 5px;
      font-size: 12px;
      box-sizing: border-box;
      color: #474747;
      cursor: pointer;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      &:hover,
      &.active{
        background: #89cef4;
      }
    }
  }
}
</style>



