<template>
  <div id='app'>
    <div style=" text-align: center;" v-if="$store.state.initLoading">loading...</div>
    <router-view v-else></router-view>
  </div>
</template>

<script>

export default {
  name: 'app',
}

</script>
<style>
#app {
  font-family: 'Microsoft YaHei', 'Lantinghei SC', 'Hiragino Sans GB', 'WenQuanYi Micro Hei', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  font-size: 12px;
  color: #8c8c8c;
}
</style>



