<template>
  <div
    class='box patternbox'
    data-type="patternbox"
    :data-name="patternBox.name + patternBox.box_id"
    :class="{'active': active}"
    @click="clickEvent($event)"
    :style="{
      width: mmToPx(patternBox.width) + 'px',
      height: mmToPx(patternBox.height) + 'px',
      backgroundColor: patternBox.color,
      zIndex: patternBox.index,
      transform: 'translate(' + mmToPx(patternBox.x - bleed) +'px, ' + mmToPx(patternBox.y - bleed) + 'px) rotate(' + patternBox.rotation + 'deg)',
    }">
    <div class="content">
      <img :src="'http://cdn-mimocampaign.mimoprint.com'+ patternBox.src +'?x-oss-process=image/crop,x_0,y_0,w_'+ Math.ceil(mmToPx(patternBox.width)) +',h_' + Math.ceil(mmToPx(patternBox.height))" draggable="false">
    </div>
    <MultipleSelected :box="patternBox" :pageNum="page.num"></MultipleSelected>
  </div>
</template>

<script>
import MultipleSelected from '@/components/common/multipleSelected'
export default {
  name: 'patternbox',
  props: {
    scale: {
      type: Number,
      defalut() {
        return 1
      }
    },
    page: {
      type: Object,
      defalut() {
        return {}
      }
    },
    patternBox: {
      type: Object,
      defalut() {
        return {}
      }
    },
    bleed: {
      type: Number,
      defalut() {
        return 0
      }
    }
  },
  data() {
    var patternBox = this.patternBox;
    return {
      active: false,
      selected: false,
    }
  },
  components: {
    MultipleSelected
  },
  methods: {
    clickEvent(e) {
      this.SelectElement(e, {
        el: this.$el,
        box: this.patternBox,
        pageNum: this.page.num,
        select: this.select
      })
      this.$store.commit('setState', { selected: { state: true, el: this.$el, data: this.patternBox, pageNum: this.page.num }});
    },
    select(e) {

    }
  }
}
</script>

<style lang="scss">
.patternbox {
  .content{
    overflow: hidden;
  }
}
</style>



