<template>
  <canvas ref="canvas"></canvas>
</template>
<script>
export default {
  name: "photostorethumb",
  props: {
    photo: {
      type: Object,
      default() {
        return {};
      }
    },
    canvasImg: {
      type: Element,
      default() {
        return null;
      }
    }
  },
  data() {
    return {};
  },
  mounted() {
    this.drawThumb();
  },
  methods: {
    drawThumb() {
      var that = this;

      var canvas = this.$refs.canvas;
      var ctx = canvas.getContext("2d");

      var draw = function(img) {
        var w, h;
        if (img.width > img.height) {
          w = 86;
          h = img.height / (img.width / w);
        } else {
          h = 86;
          w = img.width / (img.height / h);
        }

        canvas.width = w * 2;
        canvas.height = h * 2;

        ctx.drawImage(img, 0, 0, w * 2, h * 2);
      };

      if(this.canvasImg){
        draw(this.canvasImg);
      }else{
        var img = new Image();
        img.onload = function() {
          draw(img);
          that.$emit('addCanvasImg', {id: that.photo.id, img: img});
        };
        img.src = this.CommonMethods.joinPhotoHost(this.photo.src, "_thumb");
      }
    }
  }
};
</script>
<style lang="scss">

</style>



