<template>
  <div
    class="page" v-if="isShowPage" :class="{
      'active': $store.state.selected.pageNum == pageNum,
      'animation': $store.state.animation,
      'ispreview': isPreview
    }"
    :data-num="page.num" @mousedown="mouseDown($event)" @click="clickEvent($event)"
    :style="{
      width: mmToPx(pageWidth) + 'px',
      height: mmToPx(pageHeight) + 'px',
      background: page.background_color
    }"
  >
    <Patternbox v-for="(item, index) in page.patternbox_list" :key="'pattern'+item.box_id" :scale="scale" :bleed="boxBleed" :page="page" :patternBox="item"></Patternbox>
    <Imgbox v-for="(item, index) in page.imagebox_list" :key="'img'+item.box_id" :scale="scale" :page="page" :bleed="boxBleed" :imgBox="item"></Imgbox>
    <Textbox v-for="(item, index) in page.textbox_list" :key="'text'+item.box_id" :scale="scale" :page="page" :bleed="boxBleed" :textBox="item"></Textbox>
    <Shapebox v-for="(item, index) in page.colorbox_list" :key="'shape'+item.box_id" :scale="scale" :page="page" :bleed="boxBleed" :shapeBox="item"></Shapebox>
    <Decorabox v-for="(item, index) in page.decoration_list" :key="'decor'+item.box_id" :scale="scale" :page="page" :bleed="boxBleed" :decoraBox="item"></Decorabox>
    <Bleed :page="page" :bleed="bleed" :active="$store.state.selected.pageNum == pageNum" :state="bleedState && !isPreview"></Bleed>
    <div class="locator" v-if="isPreview"></div>
  </div>
</template>

<script>
import Patternbox from '@/components/common/patternbox'
import Imgbox from '@/components/common/imgbox'
import Textbox from '@/components/common/textbox'
import Shapebox from '@/components/common/shapebox'
import Decorabox from '@/components/common/decorabox'
import Bleed from '@/components/common/bleed'

export default {
  name: 'page',
  props: [
    'pageNum',
    'scale'
  ],
  data() {
    var bleed, bleedState;

    //封面封底不展示出血位
    if (this.Config.noBleed.indexOf(this.pageNum) != -1) {
      bleed = MimoBook.backCoverInfo[this.Config.productId] ? MimoBook.backCoverInfo[this.Config.productId].bleed : this.Config.bleed;
      bleedState = false;
    } else {
      bleed = this.Config.bleed;
    }

    return {
      bleed: bleed,
      multipleStartXY: [],
    }
  },
  computed: {
    isPreview() {
      return this.$store.state.viewType == 'preview';
    },
    page() {
      var bookInfo = this.$store.state.bookInfo;
      return isNaN(this.pageNum) ? bookInfo[this.pageNum] : bookInfo.page_list[this.pageNum];
    },

    isShowPage(){
      if(this.Config.productInfo[this.Config.productId].noCover && this.page.num == 'front_cover'){
        return false;
      }

      if(!isNaN(this.page.num) && this.page.num % 2 == 0){
        if(this.page.type == 2){
          this.$store.commit('setState', {currentPageIsCross: true});
        }else{
          this.$store.commit('setState', {currentPageIsCross: false});
        }
      }
      if(!isNaN(this.page.num)){
        if(this.page.num % 2 == 1 && this.$store.state.currentPageIsCross){
          return false;
        }
      }
      return true;
    },
    bleedState() {
      return !(this.Config.noBleed.indexOf(this.pageNum) != -1 || this.viewType == 'preview')
    },
    pageWidth() {
      if(this.bleedState && this.isPreview){
        return this.page.width - this.bleed * 2
      }
      return this.page.width;
    },
    pageHeight() {
      if( this.bleedState && this.isPreview){
        return this.page.height - this.bleed * 2
      }
      return this.page.height;
    },
    boxBleed() {
      return this.bleedState && this.isPreview ? this.bleed : 0
    }
  },
  components: {
    Patternbox,
    Imgbox,
    Textbox,
    Shapebox,
    Decorabox,
    Bleed
  },
  methods: {
    mouseDown(e){
      if(this.IsMultipleSelect()){
        this.DragDown(e, {
          dragStart: this.dragStart,
          drag: this.drag,
          dragEnd: this.dragEnd
        })
      }
    },
    clickEvent(e){
      this.SelectElement(e, {
        pageNum: this.pageNum,
        empty: true
      })
    },
    dragStart(e, args) {
      //设置box的起始坐标
      this.multipleStartXY = [];
      var multipleSelected = this.$store.state.multipleSelected;
      for (var i = 0; i < multipleSelected.list.length; i++) {
        var box = multipleSelected.list[i];
        this.multipleStartXY.push({
          x: box.x,
          y: box.y
        });
      }
    },
    drag(e, args) {
      //拿到偏移量加工下再丢给store存起来咯
      var multipleSelected = this.$store.state.multipleSelected;
      for (var i = 0; i < multipleSelected.list.length; i++) {
        var box = multipleSelected.list[i];
        var startXY = this.multipleStartXY[i];

        var xy = this.BoxLimitSize({
          dragX: this.pxToMm(args.x),
          dragY: this.pxToMm(args.y),
          boxX: startXY.x,
          boxY: startXY.y,
          scale: this.scale,
          boxW: box.width,
          boxH: box.height,
          pageWidth: this.page.width,
          pageHeight: this.page.height,
        });

        this.$store.commit('changeMultipleBoxData', {
          index: i,
          x: xy.x,
          y: xy.y
        });
      }
    },
    dragEnd(e, args) {

    },
  },
  mounted() {
    console.log(1)
  }
}
</script>

<style lang="scss">
.page {
  position: relative;
  z-index: 1;
  display: inline-block;
  overflow: hidden;
  .locator{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 2000;
    cursor: -webkit-grab;
  }
  .box {
    position: absolute;
    border: 0 solid white;
  }

  &[data-num='front_flap']{
    box-shadow: 0 1px 10px #aaa;
  }

  &.animation {
    transition: all .2s;
    .box {
      transition: all .2s;
      .img_cover {
        transition: all .2s;
      }
      img {
        transition: all .2s;
      }
      .content{
        transition: all .2s;
      }
    }
  }
}
</style>



