<template>
  <div id="top-toolbar" class="top-toolbar">
    <div class="top-toolbar-buttons" :class="{'en': $i18n.locale == 'en-us'}">
      <span id="top-toolbar-button_undo" class="top-toolbar-button" :class="{'--disabled': !undoData[selected.pageNum] || undoData[selected.pageNum].length == 0}" @click.stop="undo()">{{$t('topToolbar.undo')}}</span>
      <span id="top-toolbar-button_redo" class="top-toolbar-button" :class="{'--disabled': !redoData[selected.pageNum] || redoData[selected.pageNum].length == 0}" @click.stop="redo()">{{$t('topToolbar.redo')}}</span>

      <span class="top-toolbar-separator"></span>

      <span id="top-toolbar-globle_font" class="top-toolbar-button --downtriangle " @click.stop="showPannel('fontId')">{{$t('topToolbar.font')}}</span>
      <span id="top-toolbar-button_background_color" @click.stop="clickBackgroundColor" class="top-toolbar-button --downtriangle">{{$t('topToolbar.background')}}</span>
      <span id="top-toolbar-button_pattern" v-if="hideList.indexOf('pattern') == -1" class="top-toolbar-button --downtriangle" @click.stop="showPattern()">{{$t('topToolbar.pattern')}}</span>
      <span id="top-toolbar-button_material" v-if="hideList.indexOf('decora') == -1" class="top-toolbar-button --downtriangle"  @click.stop="showMaterial()">{{$t('topToolbar.material')}}</span>

      <span class="top-toolbar-separator" v-if="hideList.indexOf('image') == -1 || hideList.indexOf('text') == -1 || hideList.indexOf('shape') == -1"></span>

      <span id="top-toolbar-button_imagebox" v-if="hideList.indexOf('image') == -1" class="top-toolbar-button" @click.stop="AddBox(selected.pageNum, 'imgbox')">{{$t('topToolbar.imagebox')}}</span>
      <span id="top-toolbar-button_textbox" v-if="hideList.indexOf('text') == -1" class="top-toolbar-button" @click.stop="AddBox(selected.pageNum, 'textbox', defaultFont)">{{$t('topToolbar.textbox')}}</span>
      <span id="top-toolbar-button_shape" v-if="hideList.indexOf('shape') == -1" class="top-toolbar-button" @click.stop="AddBox(selected.pageNum, 'shapebox')">{{$t('topToolbar.shape')}}</span>

      <span class="top-toolbar-separator" style="display: block;"></span>
      <!-- 自动排版 -->
      <!-- <span id="top-toolbar-button_composing" class="top-toolbar-button">{{$t('topToolbar.composing')}}</span> -->
      <span id="top-toolbar-button_z_up" class="top-toolbar-button top-toolbar-button_zindex preventHideSelectionHandles" @click.stop="ChangeIndex('up')">{{$t('topToolbar.up')}}</span>
      <span id="top-toolbar-button_z_down" class="top-toolbar-button top-toolbar-button_zindex preventHideSelectionHandles" @click.stop="ChangeIndex('down')">{{$t('topToolbar.down')}}</span>
      <span id="top-toolbar-button_z_top" class="top-toolbar-button top-toolbar-button_zindex preventHideSelectionHandles" @click.stop="ChangeIndex('top')">{{$t('topToolbar.top')}}</span>
      <span id="top-toolbar-button_z_bottom" class="top-toolbar-button top-toolbar-button_zindex preventHideSelectionHandles" @click.stop="ChangeIndex('bottom')">{{$t('topToolbar.bottom')}}</span>
      <span id="top-toolbar-button_align"  class="top-toolbar-button --downtriangle" @click.stop="showAlign($event)">{{$t('topToolbar.align')}}</span>

      <span class="top-toolbar-separator"></span>

      <span id="top-toolbar-button_pagecount" class="top-toolbar-button" @click="showBookParamet()">{{$t('topToolbar.pagecount')}}</span>
      <span id="top-toolbar-button_adjustpage" class="top-toolbar-button" data-canhide="canhide" @click="showPageSort()">{{$t('topToolbar.adjustpage')}}</span>

      <span class="top-toolbar-separator"></span>

      <span id="top-toolbar-button_preview" class="top-toolbar-button" @click.stop="showEditPreview()">{{$t('topToolbar.preview')}}</span>
      <span id="top-toolbar-button_check" class="top-toolbar-button">{{$t('topToolbar.check')}}</span>
      <span id="top-toolbar-button_save" class="top-toolbar-button" @click="saveBookData()">{{$t('topToolbar.save')}}</span>
      <span id="top-toolbar-button_help" class="top-toolbar-button">{{$t('topToolbar.help')}}</span>

      <div class="cover" v-if="$store.state.showPageSort"></div>
    </div>
    <span id="top-toolbar-button_shopcar" class="top-toolbar-button" @click.stop="showShopcar = true"></span>
    <Colorpicker :showArrow="true" :colors="{hex: page.background_color}" :themeColors="$store.state.theme.color" :canChangeAll="true" v-show="openPannelName == 'bgcolor'" :style="bgPannelStyle" @change="setBackgroundColor"></Colorpicker>

    <transition name="jumpIn">
      <div v-show="openPannelName == 'pattern'" class="topbar-select-dialog" ref="pattern" :style="getDialogStyle('top-toolbar-button_pattern', 136, 360)" @click.stop>
        <div class="toolbar-select-body">
          <div class="material_vessel_loading" v-if="loadingData">{{$t('topToolbar.initPattern')}}</div>
          <div class="material_vessel " v-else>
            <div class="material_classify">
              <ul>
                <li :class="{'active': checkPatternClassify == index}" v-for="(item, index) in patternList[0].TempClassify" :data-id="item.Id" :key="index" v-text="item.Type"></li>
              </ul>
            </div>
            <ul class="material_item mini-scroll toolbar-btn-shadow">
              <li data-id="0" @mouseout="previewSrc = ''" @mouseover="showPreview('')" @click="changePattern('')"></li>
              <li v-for="(item, index) in patternList[0].TempClassify[checkPatternClassify].TempMaterial" :key="index" @mouseout="previewSrc = ''" @mouseover="showPreview(item)" @click="changePattern(item)">
                <img :src="'http://cdn-mimocampaign.mimoprint.com' + item.Url + '?x-oss-process=image/crop,x_0,y_0,w_300,h_300&amp;v='+ item.TimeStamp" />
              </li>
            </ul>
            <div class="material_preview" v-if="previewSrc != '' || previewSrc == 'black'">
              <img :src="previewSrc" v-if="previewSrc != 'black'"/>
            </div>
          </div>
        </div>

      </div>
    </transition>

    <transition name="jumpIn">
      <div v-show="openPannelName == 'material'" class="topbar-select-dialog" :style="getDialogStyle('top-toolbar-button_material', 136, 444)" @click.stop>
        <div class="toolbar-select-body">
          <div class="material_vessel_loading" v-if="loadingData">{{$t('topToolbar.initPattern')}}</div>
          <template v-else>
            <ul class="material_great_classify mini-scroll">
              <li v-for="(item, index) in materialList" :key="index" v-if="materialList[index].TempClassify.length != 0" :class="{'active': checkMaterial == index}" v-text="item.Type" @click="changeMaterialIndex(index)"></li>
            </ul>
            <div class="material_vessel">
              <div class="material_shrink" :class="{'shrink_up': showMoreItem}" @click="changeShowMoreItem()"></div>
              <div class="material_classify decora_classify" :style="{height: decoraListHeight+'px'}">
                <ul ref="decoraList">
                  <li v-for="(item, index) in materialList[checkMaterial].TempClassify" :key="index" :class="{'active': checkMaterialClassify == index}" v-text="item.Type" @click="checkMaterialClassify = index"></li>
                </ul>
              </div>
              <ul class="material_item mini-scroll decora_item">
                <li v-for="(item, index) in materialList[checkMaterial].TempClassify[checkMaterialClassify].TempMaterial" :key="index" @click="addDecoraBox(item)">
                  <img :src="'http://cdn-mimocampaign.mimoprint.com'+ item.Url + '?x-oss-process=image/resize,m_pad,h_70,w_70&amp;v=' + item.TimeStamp"/>
                </li>
              </ul>
            </div>
          </template>
        </div>
      </div>
    </transition>

    <transition name="jumpIn">
      <div v-show="openPannelName == 'fontId'" class="topbar-select-dialog" :style="getDialogStyle('top-toolbar-globle_font', 308, 308)" @click.stop>
        <div id="toolbar-select-font-body" class="toolbar-select-body">
          <p>{{$t('topToolbar.fontText1')}}</p><p>{{$t('topToolbar.fontText2')}}</p>
          <ul>
              <li class="select-item" :class="{'active': item.id == defaultFont }" v-for="item in defaultFontList" :key="item.name" :style="{fontFamily: 'font'+item.id}" v-text="item.text" @click="defaultFont = item.id"></li>
          </ul>
          <p>
            <button class="dialog-button saveButton" @click="changePageFont()">{{$t('topToolbar.fontSave')}}</button>
            <button class="dialog-button cancelButton" @click="openPannelName = ''">{{$t('topToolbar.fontCancel')}}</button>
            <!--<label id="isGlobel"><input type="checkbox"> 应用到所有页</label>-->
          </p>
        </div>
      </div>
    </transition>

    <Align v-bind:style="alignStyle" v-show="openPannelName == 'align'"></Align>

    <transition name="jumpIn">
      <div id="dialog_before_addtoshopcar" v-if="showShopcar">
        <div id="dialog_before_addtoshopcar-body" class="dialog-body">
          <h2>加入购物车</h2>
          <p class="notice">别着急,为确保印刷质量,建议您一定要完成以下两件事情</p>
          <ol>
            <li class="hint_list">1.浏览"印前检查清单",解决其中的错误与警告
              <button id="dialog_before_addtoshopcar-button_check" class="dialog-button">立即检查</button>
            </li>
            <li class="hint_list">2.利用"预览"功能,从头到尾完整预览一遍
              <button id="dialog_before_addtoshopcar-button_preview" class="dialog-button" @click="showEditPreview()">立即预览</button>
            </li>
          </ol>
          <div class="button_area">
            <button id="dialog_before_addtoshopcar-button_next" class="dialog-button" data-ca="mimo-mywork-continiu">继续</button>
            <button id="dialog_before_addtoshopcar-button_return" class="dialog-button"  @click="showShopcar = false">返回编辑</button>
          </div>
        </div>
        <span id="dialog_before_addtoshopcar-button_close" class="dialog-button_close" @click="showShopcar = false">
          <span class="text">×</span>
        </span>
      </div>
    </transition>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  import Colorpicker from '@/components/common/colorpicker'
  import Align from '@/components/common/align'
  export default {
    name: 'topToolbar',
    components: {
      Colorpicker,
      Align
    },
    data () {
      return {
        bgPannelStyle: null,
        openPannelName: '',
        loadingData: true,
        isLoadingData: false,
        checkPatternClassify: 0, //选中的底纹小类
        checkMaterial: 0, //选中的素材大类
        checkMaterialClassify: 0, //选中的素材小类
        showMoreItem: false, //下拉
        decoraListHeight: 21,
        previewSrc: '',
        defaultFontList: this.Config.defaultFontList,
        defaultFont: 101,
        alignStyle: null,
        showShopcar: false,
      }
    },
    computed: {
      hideList(){
        var pageNum = this.$store.state.selected.pageNum;
        var list;
        if(pageNum == 'front_cover'){
          if(this.Config.productInfo[this.Config.productId].noCover || this.Config.productInfo[this.Config.productId].specialCover){
            list = this.Config.topToolbar[pageNum]
          }else{
            list = [];
          }
        }else{
          list = this.Config.topToolbar[pageNum] || [];
        }
        return list;
      },
      page () {
        var page,
            pn = this.$store.state.selected.pageNum,
            bookInfo = this.$store.state.bookInfo;
        if (/^\s*$/.test(pn)) {
          page = null
        } else if (/^\d+$/.test(pn)) {
          page = bookInfo.page_list[pn]
        } else {
          page = bookInfo[pn]
        }
        return page
      },
      selected () {
        return this.$store.state.selected;
      },
      patternList() {
        return this.$store.state.patternList;
      },
      materialList() {
        return this.$store.state.materialList;
      },
      multipleSelected () {
        return this.$store.state.multipleSelected;
      },
      undoData() {
        return this.$store.state.undoData;
      },
      redoData() {
        return this.$store.state.redoData;
      }
    },
    watch: {
      selected() {
        this.$nextTick(() => {
          this.openPannelName = '';
        })
      },
      multipleSelected () {
        this.$nextTick(()=>{
          this.openPannelName = '';
        })
      }
    },
    methods: {
      //显示某层
      showPannel(send) {
        var state = this.openPannelName;
        this.openPannelName = state == send ? '' : send
      },

      //弹出层样式
      getDialogStyle(send, loadingWidth, dialogWidth) {
        if(!document.getElementById(send)){
          return '';
        }
        var wrapEl = document.getElementById("top-toolbar").getBoundingClientRect();
        var elPosit = document.getElementById(send).getBoundingClientRect();
        var left = elPosit.width / 2 + elPosit.left - dialogWidth / 2;
        if(this.loadingData){
          left = elPosit.width / 2 + elPosit.left - loadingWidth / 2
        }
        var top = wrapEl.bottom - wrapEl.top;
        return {
          left: left+'px',
          top: top+'px'
        }
      },

      undo() {
        this.$store.commit("undo", this.selected.pageNum);
        this.$store.dispatch("animation")
      },

      redo() {
        this.$store.commit("redo", this.selected.pageNum);
        this.$store.dispatch("animation")
      },

      changePageFont() {
        this.$store.commit("changeAllPageFont", this.defaultFont)
        this.openPannelName = ''
      },

      //底纹
      showPattern() {
        console.log(this.patternList)
        if(this.patternList.length){
          this.loadingData = false;
        }
        else{
          if(!this.isLoadingData){
            this.getMaterial()
          }
        }
        this.showPannel('pattern')
      },

      showPreview(item) {
        if(item == ''){
          this.previewSrc = 'black';
        }
        else{
          this.previewSrc = 'http://cdn-mimocampaign.mimoprint.com' + item.Url + '?x-oss-process=image/resize,m_pad,h_900,w_900/crop,x_0,y_0,w_300,h_300'
        }
      },

      changePattern(item) {
        this.openPannelName = '';
        this.ChangePattern(this.selected.pageNum, item)
      },

      //素材
      showMaterial() {
        if(this.materialList.length){
          this.loadingData = false;
        }
        else{
          if(!this.isLoadingData){
            this.getMaterial()
          }
        }
        this.showPannel('material')
      },
      changeMaterialIndex(index) {
        this.checkMaterial = index;
        this.decoraListHeight = 21;
        this.showMoreItem = false;
        this.checkMaterialClassify = 0;
      },
      changeShowMoreItem() {
        if(this.showMoreItem){
          this.decoraListHeight = 21;
        }
        else{
          this.decoraListHeight = this.$refs.decoraList.offsetHeight
        }
        this.showMoreItem = !this.showMoreItem;
      },
      addDecoraBox(item) {
        this.openPannelName = '';
        this.AddBox(this.selected.pageNum, 'decorabox', item);
      },

      //获取数据
      getMaterial() {
        this.isLoadingData = true;
        var workId = this.Config.workId;
        axios.post('http://www.mimoprint.com/Material/GetThemeMaterial', qs.stringify({
          worksId: workId,
          hostName: window.location.origin
        })).then( (res)=> {
          if(res.data.ret == 0){
            alert(res.data.msg)
            return false;
          }
          this.isLoadingData = false;
          this.$store.commit("setState", {patternList: res.data.patternList, materialList: res.data.materialList})
          this.loadingData = false;
        })
      },
      //修改page背景颜色
      clickBackgroundColor(event) {
        var el = event.target
        this.bgPannelStyle = {
          left: el.offsetLeft + 'px',
          top: el.offsetTop + el.offsetHeight + 3 + 'px'
        }
        this.showPannel('bgcolor')
      },
      setBackgroundColor (colors, changeAll) {
        var color = colors.hex
        if(changeAll){
          this.$store.commit("changeAllPageBg", color)
        }
        else{
          this.$store.commit('changePageBg', {pageNum: this.selected.pageNum, color: color});
        }
        this.openPannelName = '';
        this.$store.dispatch("animation")
      },
      showAlign (event) {
        var el = event.target
        this.showPannel("align");
        this.alignStyle = {
          left: el.offsetLeft + 'px',
          top: el.offsetTop + el.offsetHeight + 3 + 'px'
        }
      },
      showEditPreview () {
        this.showShopcar = false;
        this.$store.commit('setState', {viewType: 'preview', showPageSort: false})
      },
      showPageSort() {
        this.$store.commit("setState", {showPageSort: true})
      },
      showBookParamet(){
        this.$store.commit("setState", {bookParamet: true})
      },
      saveBookData(){
        this.$store.dispatch('saveBookData')
      }
    },
    mounted() {

    }
  }
</script>


<style lang="scss">
  .top-toolbar {
    position: absolute;
    z-index: 1003;
    top: 40px;
    left: 0;
    width: 100%;
    height: 65px;
    background: url(../../assets/book/image/topToolbar/background.png);
  }
  .top-toolbar-buttons {
    padding: 5px 0 0 19px;
    height: 60px;
    overflow: hidden;
    width: 1030px;
    position: relative;
    &.en{
      width: 1225px;
      .top-toolbar-button{
        width: 52px;
      }
    }
  }

  .top-toolbar-button {
    float: left;
    position: relative;
    cursor: pointer;
    box-sizing: border-box;
    height: 56px;
    width: 42px;
    padding-top: 38px;
    font-size: 12px;
    color: #474747;
    text-align: center;
    font-family: '宋体';
    white-space: nowrap;
    &:hover {
      border-radius: 5px;
      box-shadow: 0 1px 4px #ccc inset;
    }
    &:active {
      box-shadow: 0 2px 8px #aaa inset;
    }
    &:before {
      content: '';
      display: block;
      height: 30px;
      width: 28px;
      position: absolute;
      top: 5px;
      left: 50%;
      transform: translateX(-50%);
      background: url(../../assets/book/image/topToolbar/sprites.png) no-repeat 0 top;
    }
    &.--downtriangle {
      &:after {
        content: '';
        position: absolute;
        right: -2px;
        bottom: 21px;
        width: 0;
        border: 4px solid transparent;
        border-top: 4px solid #000;
      }
    }
  }

  .top-toolbar-button.--disabled {
    box-shadow: none;
    cursor: default;
  }

  #top-toolbar-button_composing { width: 46px; }

  #top-toolbar-button_undo:before { background-position: -206px 0; }
  #top-toolbar-button_redo:before { background-position: -112px 0; }
  #top-toolbar-button_undo.--disabled:before { background-position: -178px 0; }
  #top-toolbar-button_redo.--disabled:before { background-position: -84px 0; }
  #top-toolbar-globle_font:before { background-position: -807px 0; }
  #top-toolbar-button_background_color:before { background-position: -234px 0; }
  #top-toolbar-button_pattern:before { background-position: -272px 0; }
  #top-toolbar-button_material:before { background-position: -446px 0; }
  #top-toolbar-button_imagebox:before { background-position: -8px 0; }
  #top-toolbar-button_textbox:before { background-position: -50px 0; }
  #top-toolbar-button_shape:before { background-position: -990px 0; }
  #top-toolbar-button_composing:before {background-position: -763px 0;}
  #top-toolbar-button_z_up:before {background-position: -990px 0;}
  #top-toolbar-button_z_down:before {background-position: -952px 0;}
  #top-toolbar-button_z_top:before {background-position: -512px 0;}
  #top-toolbar-button_z_bottom:before {background-position: -484px 0;}
  #top-toolbar-button_align:before {background-position: -140px 0;}
  #top-toolbar-button_pagecount:before {background-position: -540px 0;}
  #top-toolbar-button_adjustpage:before {background-position: -318px 0;}
  #top-toolbar-button_preview:before {background-position: -568px 0;}
  #top-toolbar-button_check:before {background-position: -352px 0;}
  #top-toolbar-button_save:before {background-position: -596px 0;}
  #top-toolbar-button_help:before {background-position: -418px 0;}

  .top-toolbar-separator {
    float: left;
    height: 44px;
    width: 0;
    border-left: 1px solid #c5c5c5;
    border-right: 1px solid #ededed;
    margin: 6px 12px 0 13px;
  }
  #top-toolbar-button_shopcar{
    width: 128px;
    height: 45px;
    display: block;
    background: url(../../assets/book/image/topToolbar/sprites.png) -624px 0;
    position: absolute;
    right: 24px;
    bottom: 10px;
    &::before{
      display: none;
    }
    &:hover{
      box-shadow: 0 1px 5px #555 inset;
    }
    &:active{
      box-shadow: 0 2px 10px #333 inset;
    }
  }

  .topbar-select-dialog{
    position: absolute;
    background: #ededed;
    z-index: 1001;
    -webkit-box-shadow: 0 1px 6px #666;
    box-shadow: 0 1px 6px #666;
    top: 0;
    left: 0;
    box-sizing: border-box;
    -ms-transform-origin: center top;
    -webkit-transform-origin: center top;
    -moz-transform-origin: center top;
    -o-transform-origin: center top;
    transform-origin: center top;
    -webkit-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
    &:before{
      content: '';
      position: absolute;
      width: 8px;
      height: 8px;
      background: inherit;
      -webkit-box-shadow: 0 1px 6px #666;
      box-shadow: 0 1px 6px #666;
      -ms-transform: scaleX(0.8) rotate(45deg);
      -webkit-transform: scaleX(0.8) rotate(45deg);
      -moz-transform: scaleX(0.8) rotate(45deg);
      -o-transform: scaleX(0.8) rotate(45deg);
      transform: scaleX(0.8) rotate(45deg);
      top: -4px;
      left: 0;
      right: 0;
      margin: auto;
    }
    .toolbar-select-body{
      position: relative;
      padding: 10px;
      background: #ededed;
    }
    .material_great_classify{
      display: inline-block;
      vertical-align: top;
      width: 80px;
      height: 365px;
      overflow-y: scroll;
      li{
        width: 60px;
        height: 20px;
        line-height: 20px;
        cursor: pointer;
        margin: 5px 0;
        text-align: center;
        &:hover{
          background: #ccc;
          color: #fff;
        }
        &.active{
          background: #aaa;
          color: #fff;
        }
      }
    }
    .material_vessel{
      width: 340px;
      display: inline-block;
      vertical-align: top;
      background: inherit;
      position: relative;
      .material_shrink{
        position: absolute;
        top: 5px;
        right: 0;
        width: 20px;
        height: 20px;
        cursor: pointer;
        &:before,
        &:after{
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          margin: auto;
          width: 6px;
          height: 6px;
          border-style: solid;
          border-color: transparent transparent #aaa #aaa;
          border-width: 2px;
          -ms-transform: rotate(-45deg);
          -webkit-transform: rotate(-45deg);
          -moz-transform: rotate(-45deg);
          -o-transform: rotate(-45deg);
          transform: rotate(-45deg);
        }
        &:after{
          top: -10px;
        }

        &.shrink_up{
          &:before,
          &:after{
            -ms-transform: rotate(135deg);
            -webkit-transform: rotate(135deg);
            -moz-transform: rotate(135deg);
            -o-transform: rotate(135deg);
            transform: rotate(135deg);
          }
          &:after{
            top: 10px;
          }
        }
      }
      .material_classify{
        padding: 5px 0;
        height: 18px;
        overflow: hidden;
        margin-right: 25px;
        li{
          vertical-align: top;
          display: inline-block;
          cursor: pointer;
          border-radius: 2px;
          margin-left: 5px;
          padding: 2px 3px;
          margin-bottom: 6px;
          &:hover{
            background: #ccc;
            color: #fff;
          }
          &.active{
            background: #aaa;
            color: #fff;
          }
        }
        &.decora_classify{
          transition: all .3s;
          height: 21px;
        }
      }
      .material_item{
        width: 340px;
        height: 340px;
        margin: 0 auto;
        font-size: 0;
        overflow-y: scroll;
        li{
          display: inline-block;
          vertical-align: middle;
          margin: 5px;
          border: solid 1px #fff;
          cursor: pointer;
          width: 70px;
          height: 70px;
          background: #bbb;
          img{
            width: 100%;
          }
        }
        &.decora_item{
          li{
            background: none;
            &:hover{
              -webkit-box-shadow: 0 1px 4px #ccc inset;
              -moz-box-shadow: 0 1px 4px #ccc inset;
              box-shadow: 0 1px 4px #ccc inset;
            }
            &:active{
              -webkit-box-shadow: 0 2px 8px #aaa inset;
              -moz-box-shadow: 0 1px 4px #ccc inset;
              box-shadow: 0 2px 8px #aaa inset;
            }
          }
        }
      }
      .material_preview{
        position: absolute;
        right: -315px;
        top: -10px;
        width: 300px;
        height: 300px;
        background: #ededed;
        box-shadow: 0 1px 6px #666;
        overflow: hidden;
        background: #424142;
        img{
          width: 100%;
        }
      }
    }
    .toolbar-select-font-body{
      width: 298px;
      background: #ededed;
      padding: 10px;
    }
    #toolbar-select-font-body{
      p{
        font-size: 12px;
        overflow: hidden;
        color: #c5916c;
        margin-top: 8px;
        &:first-child{
          font-size: 14px;
          font-weight: bold;
          color: #333;
        }
      }
      ul{
        margin-top: 8px;
        background-color: #fff;
        padding: 10px;
        li{
          padding: 5px 0 5px 33px;
          cursor: default;
          &.active{
            color: #fff;
            background-color: #5587ce;
          }
        }
      }
      button{
        float: left;
        margin-right: 10px;
        height: 28px;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        background: #d2523e;
        border-radius: 5px;
        color: white;
        padding: 0 10px 0 10px;
        outline: none;
        box-shadow: none;
        border: none;
        &:hover{
          opacity: .9;
        }
      }
      #isGlobel{
        display: block;
        padding-top: 4px;
        input{
          vertical-align: middle;
          margin-top: -2px;
        }
      }
    }
  }


  #dialog_before_addtoshopcar{
    position: absolute;
    width: 500px;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    top: 300%;
    left: 0;
    right: 0;
    margin: 0 auto;
    #dialog_before_addtoshopcar-body{
      -webkit-box-shadow: 0 1px 6px #999;
      box-shadow: 0 1px 6px #999;
      border-radius: 5px;
      background: white;
      padding: 16px 30px;
      h2{
        margin: 10px 0 20px 0;
        &:before{
          content: '\e670';
          font-family: iconfont;
          font-size: 25px;
          line-height: 1;
          vertical-align: top;
          color: #d2523e;
          margin-right: 6px;
        }
      }
      p.notice{
        margin-bottom: 20px;
        font-size: 15px;
      }
      ol{
        margin-bottom: 13px;
        background: #f1f6fa;
        padding: 8px 10px;
        li{
          margin: 5px 0;
          overflow: hidden;
          height: 26px;
          line-height: 26px;
          .dialog-button{
            box-sizing: border-box;
            background: #d2523e;
            border-radius: 5px;
            color: white;
            float: right;
            height: 26px;
            line-height: 26px;
            font-size: 13px;
          }
        }
      }
      .button_area{
        text-align: center;
      }
      .dialog-button{
        height: 28px;
        line-height: 28px;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        background: #d2523e;
        border-radius: 5px;
        border: none;
        color: white;
        padding: 0 10px 0 10px;
        margin: 0 5px;
        &:hover{
          opacity: .9;
        }
      }
    }
    .dialog-button_close{
      position: absolute;
      top: 0;
      right: 0;
      height: 25px;
      width: 25px;
      font-family: SimHei;
      border-bottom-left-radius: 25px;
      border-top-right-radius: 5px;
      cursor: pointer;
      background: #ddd;
      .text{
        position: absolute;
        top: 2px;
        right: 3px;
        color: #6088ce;
        font-weight: bold;
        font-size: 14px;
        pointer-events: none;
      }
    }
  }
.cover{
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: .6;
  top: 0;
  left: 0;
  background: url(../../assets/book/image/topToolbar/background.png);
}
</style>




