<template>
  <div id="special_cover" v-if="isShow">
    <img class="cover_image" :src="'http://www.mimoprint.com/static/img/editorhtml5/series/b' + Config.productId + '/book_series_b' + Config.productId + '_1_' + $store.state.coverColor + '.png'" :style="setSize">
  </div>
</template>

<script>
export default {
  name: 'cover',
  data(){
    return {

    }
  },
  props: [
    'currentPage',
    'scale'
  ],
  computed: {
    isShow(){
      var pageNum = this.currentPage[0];
      if(pageNum == 'front_cover' && (this.Config.productInfo[this.Config.productId].noCover || this.Config.productInfo[this.Config.productId].specialCover)){
        return true;
      }
      return false;
    },
    page(){
      var pageNum = this.currentPage[0];
      if(pageNum == 'front_cover'){
        return this.$store.state.bookInfo[pageNum];
      }
      return null;
    },
    setSize(){
      var style = {};
      if(this.Config.productInfo[this.Config.productId].specialCover){
        var coverInfo = this.Config.specialCoverInfo[this.Config.productId];
        style.width = coverInfo.coverWidth + 'px';
        style.height = coverInfo.coverHeight + 'px';
        style.left = coverInfo.coverLeft + 'px';
        style.top = coverInfo.coverTop + 'px';
      }
      return style;
    }
  },
  methods: {

  }
}
</script>

<style lang="scss">
#special_cover{

  .cover_image{
    position: absolute;
    z-index: 2;
    width: 100%;
    pointer-events: none;
  }
}
</style>



