<template>
  <div class="navigator" :class="{'--preview': viewType == 'preview'}">
    <div class="navigator-blocks" >
      <div class="navigator-block --left" v-show="viewType == 'common'">
        <span class="navigator-page-display-type-word">页面显示方式：</span>
        <ul class="navigator-page-display-type-btns">
          <li class="navigator-page-display-type-btn --doubleview" @click="changeCurView(false)" :class="{'--selected': !$store.state.showPageSort}"></li>
          <li class="navigator-page-display-type-btn --listview" @click="changeCurView(true)" :class="{'--selected': $store.state.showPageSort}" title="排序"></li>
        </ul>
      </div>
      <div class="navigator-block --center">
        <span class="navigator-pagination-btn --left" @click="prePage"></span>
        <div class="navigator-page-select-wrapper">
          <div @click="showPageList" class="navigator-page-select" v-text='pageName'>
            封面
          </div>
          <transition name="jumpUp">
            <div class="navigator-page-select-list-wrapper" v-show="pageListVisible">
              <ul class="navigator-page-select-list" >
                <li v-for="item in pageList" @click="jumpPage(item)" :key="'navigatorpageselect'+item.cname" class="navigator-page-select-list-item" :class="{'--selected' : item.num.indexOf(pageNum) != -1 }" v-text='item.cname'></li>
              </ul>
            </div>
          </transition>
        </div>
        <span class="navigator-pagination-btn --right" @click="nextPage"></span>
      </div>
      <div class="navigator-block --right" v-show="viewType == 'common'">
        <button @click="showPreview" class="navigator-preview-btn">预览</button>
      </div>
    </div>
    <div class="navigator-thumbnails-box">
      <div class="navigator-thumbnails-wrapper mini-scroll" ref="listWrap" @mousewheel="scrollBar($event)">
        <ul class="navigator-thumbnails">
          <li v-for="item in pageList" :key="item.cname" class="navigator-thumbnail-box" >
            <div class="navigator-thumbnail" :data-num="item.num">
              <div v-for="(pi,ind) in item.pages" v-show="item.type == pi.type" @click="jumpPage(item, pi.num)" :key="'navigatorthumb'+pi.num" :style="setStyle(pi)">
                <PageThumb :class="{'navigator-pagethumb-selected' : pi.num==pageNum, '--left': pi.type != 2 && /^\d+$/.test(pi.num) && pi.num % 2 == 0, '--right':pi.type !=2 && /^\d+$/.test(pi.num) && pi.num % 2 == 1}" :template="pi" :size="getSize(pi)" ></PageThumb>
              </div>
            </div>
            <p @click="jumpPage(item)" class="navigator-thumbnail-name" v-text="item.cname"></p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import PageThumb from '@/components/book/pageThumb'
export default {
  name: 'navigator',
  data () {
    return {
      curView: 'doubleview',
      pageListVisible: false,
      pageList: []
    }
  },
  components: {
    PageThumb,
  },
  computed: {
    viewType () {
      return this.$store.state.viewType;
    },
    pageNum () {
      return this.$store.state.selected.pageNum
    },
    pageName () {
      var pn = this.pageNum
      if (/^\d+$/.test(pn)) {
        pn = Number(pn)
      }
      var p = this.pageList.filter(page => page.num.indexOf(pn) != -1)
      if (p.length > 0) {
        return p[0].cname
      }
    },
    selected() {
      return this.$store.state.selected;
    },
    bookInfo () {
      return this.$store.state.bookInfo
    },
    isChangePage() {
      return this.$store.state.isChangePage
    }
  },
  watch: {
    isChangePage: {
      handler() {
        //这里似乎只当页数变更、页排序后才需要触发重新渲染
        if(this.isChangePage){
          this.pageList = this.getPageList();
          this.$store.commit('setState', {isChangePage: false});
        }
      },
      deep: true
    }
  },
  methods: {
    getPageList () {
      var book = this.$store.state.bookInfo
      var pages
      var list = []

      if (book.front_cover) {
        list.push({
          cname: '封面',
          num: [book.front_cover.num],
          pages: [book.front_cover],
          type: book.front_cover.type
        })
      }
      if (book.slipcase) {
        list.push({
          cname: '书壳',
          num: [book.slipcase.num],
          pages: [book.slipcase],
          type: book.slipcase.type
        })
      }
      if (book.spine && !this.Config.productInfo[this.Config.productId].limitDraw) { // || window.JpgBook == 1) {
        list.push({
          cname: '书脊',
          num: [book.spine.num],
          pages: [book.spine],
          type: book.spine.type
        })
      }
      if (book.front_flap && !this.Config.productInfo[this.Config.productId].limitDraw) {// || window.JpgBook == 1) {
        list.push({
          cname: '折页',
          num: [book.front_flap.num],
          pages: [book.front_flap],
          type: book.front_flap.type
        })
      }
      if(book.title_page) {
        list.push({
          cname: '1(扉页)',
          num: [book.title_page.num],
          pages: [book.title_page],
          type: book.title_page.type
        })
      }

      for (var i = 0; i < book.page_list.length; i += 2) {
        var bp1 = book.page_list[i]
        var bp2 = book.page_list[i + 1]
        list.push({
          cname: [i+2, i+3].join(' - '),
          num: [i, i+1],
          pages: [bp1, bp2],
          type: bp1.type
        })
      }

      /*最后copyright....*/
      if (book.copyright) {
        list.push({
          cname: '版权页',
          num: [book.copyright.num],
          pages: [book.copyright],
          type: book.copyright.type
        })
      }
      if (book.back_cover) { //||window.jpgBook ==1
        list.push({
          cname: '封底',
          num: [book.back_cover.num],
          pages: [book.back_cover],
          type: book.back_cover.type
        })
      }
      return list

    },
    showPageList () {
      this.pageListVisible = !this.pageListVisible;
      if (this.pageListVisible) {
        this.$nextTick(function () {
          var el = document.querySelector('.navigator-page-select-list-item.--selected')
          if(el) {
            el.scrollIntoView();
          }
        })
      }
    },
    jumpPage (item, pageNum) {
      var target = item.num
      var selected = Object.assign({}, this.$store.state.selected)
      selected.pageNum = target[0]
      if (pageNum) {
        selected.pageNum = pageNum
      }
      this.$store.commit('setState', {currentPage: target})
      this.$store.commit('setState', {selected: selected})
      this.pageListVisible = false;
    },
    prePage (item) {
      var index = this.getCurPageIndex();
      index = index - 1
      if (index < 0) {
        index = 0
      }
      var page = this.pageList[index]
      this.jumpPage(page)

    },
    nextPage (item) {
      var index = this.getCurPageIndex();
      index = index + 1
      if (index >= this.pageList.length) {
        index = this.pageList.length - 1
      }
      var page = this.pageList[index]
      this.jumpPage(page)
    },
    getCurPageIndex (item) {
      var list = this.pageList
      var pn = this.pageNum
      var page
      var index = 0;
      if (/^\d+$/.test(pn)) {
        pn = Number(pn)
      }
      for (var i = 0; i < list.length; i++) {
        page = list[i]
        if (page.num.indexOf(pn) != -1) {
          index = i
          break;
        }
      }
      return index
    },
    showPreview () {
      this.$store.commit('setState', {viewType: 'preview', showPageSort: false})
    },
    changeCurView(type) {
      this.$store.commit("setState", {showPageSort: type})
    },
    scrollBar(e) {
      var delta = e.wheelDelta || e.delta;
      var elWrap = this.$refs.listWrap;
      var s = delta > 0 ? -30 : 30;

      elWrap.scrollLeft += s;
    },
    setStyle(page){
      var style = {};

      var productInfo = this.Config.productInfo[this.Config.productId];
      var coverInfo = this.Config.specialCoverInfo[this.Config.productId];

      var w, h;
      if(page.num == 'front_cover' && (productInfo.noCover || productInfo.specialCover)){
        w = coverInfo.coverWidth;
        h = coverInfo.coverHeight;
      }else{
        w = page.width;
        h = page.height;
      }

      style.width = w / h * 35 + 'px';
      style.height = 35 + 'px';

      return style;
    },
    getSize(page){
      var data = {};

      var productInfo = this.Config.productInfo[this.Config.productId];
      var coverInfo = this.Config.specialCoverInfo[this.Config.productId];

      var w, h;
      if(page.num == 'front_cover' && (productInfo.noCover || productInfo.specialCover)){
        w = coverInfo.coverWidth;
        h = coverInfo.coverHeight;
      }else{
        w = page.width;
        h = page.height;
      }

      data.w = w / h * 35 + 'px';
      data.h = 35 + 'px';

      return data;
    }
  },
  mounted () {
    this.pageList = this.getPageList()
  }
}
</script>

<style lang="scss">
  .navigator{
    position: absolute;
    left: 215px;
    right: 166px;
    bottom: 0px;
    height: 127px;
    border-top: 1px solid #c1c1c1;
    z-index: 1001;
    border: solid 1px #dedede;
    &.--preview {
      left: 0;
      right: 0;
    }
  }
  .navigator-blocks {
    height: 38px;
    box-sizing: border-box;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    background: #dedede;
    display: flex;
    justify-content: space-between;
  }
  .navigator-block {
    display: flex;
    flex: 1;
    align-items: center;
    &.--left {
      justify-content: flex-start;
    }
    &.--right {
      justify-content: flex-end;
    }
    &.--center{
      justify-content: center;
    }
  }
  .navigator-page-display-type-word {
    color: #000;
    padding-left: 10px;
    width: 84px;
  }
  .navigator-page-display-type-btns {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  .navigator-page-display-type-btn {
    margin-right: 5px;
    border: 3px solid #979797;
    box-shadow: 0 1px 1px #979797;
    cursor: pointer;
    height: 19px;
    &.--doubleview {
      background: url('../../assets/book/image/navigator_doubleview.png');
      width: 31px;
    }
    &.--listview {
      background: url('../../assets/book/image/navigator_listview.png');
      width: 39px;
    }
    &.--selected {
      border-color: #5587ce;
    }
  }
  .navigator-preview-btn {
    padding: 0;
    background: linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
    color: #666;
    text-shadow: 0 0 1px rgba(102, 102, 102, 0.3);
    font-size: 12px;
    letter-spacing: 0px;
    border-radius: 4px;
    box-sizing: border-box;
    border: 1px solid #d6d6d6;
    box-shadow: 0 1px 1px #dbdbdb;
    cursor: pointer;
    height: 31px;
    width: 50px;
    margin-right: 10px;
    &:active {
      background: linear-gradient(to bottom, #e5e5e5 0%, #fff 100%);
      box-shadow: 0 2px 3px #dbdbdb inset;
    }
  }
  .navigator-pagination-btn {
    height: 29px;
    width: 23px;
    cursor: pointer;
    &.--left {
      background: url('../../assets/book/image/navigator_leftbtn.png') no-repeat;
    }
    &.--right {
      background: url('../../assets/book/image/navigator_rightbtn.png') 0 1px no-repeat;
    }
  }
  .navigator-page-select-wrapper {
    width: 115px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    box-sizing: border-box;
    background: url('../../assets/book/image/navigator_selectbg.png') no-repeat;
    margin: auto 10px;
    position: relative;
  }
  .navigator-page-select {
    padding-right: 25px;
    color: #000;
    cursor: pointer;
    &:active {
      box-shadow: 0 1px 6px #999 inset;
    }
  }
  .navigator-page-select-list-wrapper {
    position: absolute;
    overflow: hidden;
    overflow-y: scroll;
    background: #ededed;
    box-shadow: 0 1px 4px #999;
    box-sizing: border-box;
    z-index: 1;
    width: 113px;
    height: 216px;
    display: block;
    bottom: 25px;
    left: 1px;
    transform-origin: center bottom;
  }
  .navigator-page-select-list-item {
    height: 24px;
    padding: 5px;
    box-sizing: border-box;
    line-height: 14px;
    color: #474747;
    cursor: pointer;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    &:hover {
      background: #89cef4;
    }
    &.--selected {
      background: #5d86cd;
      color: #fff;
    }
  }
  .navigator-thumbnails-box {
    position: relative;
    height: 89px;
    box-sizing: border-box;
    border-top: 1px solid #c1c1c1;
    background: #fafafa;
  }
  .navigator-thumbnails-wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow-x: scroll;
    overflow-y: hidden;
  }
  .navigator-thumbnails {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    box-sizing: border-box;
    height: 100%;
    &:after{
      content: '';
      display: flex;
      width: 1px;
      flex: none;
    }
  }
  .navigator-thumbnail-box {
    cursor: pointer;
    display: flex;
    flex: none;
    flex-direction: column;
    align-items: center;
    margin: 12px 9px 0;
  }
  .navigator-thumbnail {
    position: relative;
    display: flex;
    height: 35px;
    box-sizing: border-box;
    background: #fff;
    box-shadow: 0 1px 4px #999;

    &:after{
      content: '';
      width: 1px;
      background: #ccc;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      box-shadow: 0 0 1px #ccc;
      z-index: 1;
    }

    &[data-num='front_cover']:after,
    &[data-num='front_flap']:after,
    &[data-num='back_cover']:after,
    &[data-num='spine']:after,
    &[data-num='title_page']:after,
    &[data-num='copyright']:after{
      content: none;
    }
  }
  .navigator-thumbnail-name {
    height: 20px;
    line-height: 20px;
  }
  .navigator-pagethumb-selected {
    // box-sizing: border-box;
    border: 2px solid #5587ce;
    margin: -2px 0 0 -2px;
    z-index: 1;
    &.--left {
      border-right: none;
      margin: -2px 0 0 -2px
    }
    &.--right {
      border-left: none;
      margin: -2px -2px 0 0;
    }
  }
</style>
<style lang="scss" scoped>
  .jumpUp-enter-active,.jumpUp-leave-active{
  transition-property: transform, opacity;
  transition-duration: .2s;
  }
  .jumpUp-enter,.jumpUp-leave-to{
    transform: perspective(1000px) translateZ(-100px) rotateY(0deg) rotateX(-15deg);
    opacity: 0;
  }
  .jumpUp-enter-to,.jumpUp-leave{
    opacity: 1;
    transform: perspective(1000px) translateZ(0px) rotateY(0deg) rotateX(0deg);
  }
</style>






