<script>
var config = {
  //是否外网
  isNet: window.location.host.indexOf("mimoprint.com") == -1 ? false : true,
  photoHost: 'http://cdn-mimoprint.mimoprint.com',//照片域名
  materialHost: 'http://cdn-mimocampaign.mimoprint.com',//素材域名
  uploader: {
    cusIp: {},//城市信息 接口获取
    cityList: ["香港", "台湾", "澳门", "澳大利亚"],//海外城市
    hkUploadHost: 'http://hkupload.mimoprint.com',//海外上传域名
    chinaUploadHost: 'http://uploadcal.mimoprint.com',//大陆上传域名
    checkUrl: '/UserImage/Check',//检查照片接口
    uploadUrl: '/UserImage/ResumableUpload',//上传接口
    processUrl: '/UserImage/CompleteResumableUpload',//处理接口
    partSize: 1024 * 1024 * 4,//片段大小
    concurrencyCount: 2,//并发数
  }
}

//是否本地照片
function isBlob(src) {
  if (src) {
    return src.split(":")[0] == "blob" ? true : false;
  }
  return null;
};

//拼接
function joinPhotoHost(src, type) {
  if (isBlob(src)) {
    return src;
  }

  //容错，有些src前边少了斜杠
  src = src[0] == '/' ? src : '/' + src;

  type && (src = src.replace('_edit', type));

  return config.photoHost + src;
}

//获取上传域名
function getUploadHost() {
  var city = config.uploader.cusIp.name[1];
  var uploadHost = config.uploader.cityList.indexOf(city) != -1 ? config.uploader.hkUploadHost : config.uploader.chinaUploadHost;
  return config.isNet ? location.host : uploadHost;
}

function replaceText(item, data){
  for(var name in data){
    item = item.replace(new RegExp('{' + name + '}', "g"), data[name]);
  }
  return item;
}

function matches(el, selector) {
  return (el.matches || el.matchesSelector || el.msMatchesSelector || el.mozMatchesSelector || el.webkitMatchesSelector || el.oMatchesSelector).call(el, selector);
}

function closest(el, selector) {
  var matchesSelector = el.matches || el.webkitMatchesSelector || el.mozMatchesSelector || el.msMatchesSelector;
  if (matchesSelector.call(el, selector)) {
    if(!matchesSelector.call(el.parentNode, selector)){
      return el
    }
    return closest(el.parentNode, selector)
  }
  return el
}

//换算旋转后的宽高
function convertRotateWH(w, h, r){
  var sinV = Math.sin(r * Math.PI / 180),
  cosV = Math.cos(r * Math.PI / 180),
  tanV = Math.tan(r * Math.PI / 180);

  return w / cosV + (h - w * tanV) * sinV;
}

export default {
  install(Vue, args) {
    config.uploader.cusIp = args.uploader.cusIp;

    Vue.prototype.CommonConfig = config;

    Vue.prototype.CommonMethods = {
      isBlob: isBlob,
      joinPhotoHost: joinPhotoHost,
      getUploadHost: getUploadHost,
      replaceText: replaceText,
      matches: matches,
      closest: closest,
      convertRotateWH: convertRotateWH
    }
  }
}
</script>



