<template>
  <div id="bleed" :class="active ? 'active' : ''" v-if="state">
    <div ref="bleedchecked" class="bleed_checked bleed_top" data-type="top" :style="{height: mmToPx(bleed * 2) + 'px'}" v-if="active && checkXY('top')"></div>
    <div ref="bleedchecked" class="bleed_checked bleed_bottom" data-type="bottom" :style="{height: mmToPx(bleed * 2) + 'px'}" v-if="active && checkXY('bottom')"></div>
    <div ref="bleedchecked" class="bleed_checked bleed_left" data-type="left" :style="{width: mmToPx(bleed * 2) + 'px'}" v-if="active && checkXY('left')"></div>
    <div ref="bleedchecked" class="bleed_checked bleed_right" data-type="right" :style="{width: mmToPx(bleed * 2) + 'px'}" v-if="active && checkXY('right')"></div>
    <div class="bleed_line bleed_top" :style="{height: mmToPx(bleed) + 'px'}"></div>
    <div class="bleed_line bleed_bottom" :style="{height: mmToPx(bleed) + 'px'}"></div>
    <div class="bleed_line bleed_left" :style="{width: mmToPx(bleed) + 'px'}"></div>
    <div class="bleed_line bleed_right" :style="{width: mmToPx(bleed) + 'px'}"></div>
    <div class="bleed_tip" :class="tipArrow ? 'tip_' + tipArrow : ''" v-if="active && tipState && tipArrow">
      <p>请不要将文字太贴近边缘，</p>
      <p>可能会被裁切</p>
    </div>
  </div>
</template>
<script>
export default {
  name: 'bleed',
  props: [
    'page',//页的数据
    'bleed',//出血位宽度
    'active',//是否选择当前页
    'state',//是否开启出血位
  ],
  data() {
    return {
      tipArrow: '',//提示箭头方向
      tipState: true,//提示tip是否显示
      timer: null
    }
  },
  updated() {
    //限制tip展示
    this.limitShow();
  },
  methods: {
    limitShow() {
      var that = this;
      if (this.$refs.bleedchecked) {
        this.tipArrow = this.$refs.bleedchecked.getAttribute('data-type');
        if (!this.timer) {
          this.timer = setTimeout(function() {
            that.tipState = false;
            clearTimeout(that.timer);
            that.timer = null;
          }, 6000)
        }
      } else {
        this.tipArrow = '';
        this.timer && (this.tipState = true);
      }
    },
    checkXY(type) {
      var page = this.page;

      var state = false;
      if (page.textbox_list) {
        for (var i = 0; i < page.textbox_list.length; i++) {
          var box = page.textbox_list[i];
          switch (type) {
            case 'top':
              box.y <= this.bleed * 2 && (state = true);
              break;
            case 'bottom':
              box.y + box.height >= page.height - this.bleed * 2 && (state = true);
              break;
            case 'left':
              box.x <= this.bleed * 2 && (state = true);
              break;
            case 'right':
              box.x + box.width >= page.width - this.bleed * 2 && (state = true);
              break;
          }
        }
      }

      return state;
    }
  }
}
</script>
<style lang="scss">
@keyframes fadeout {
  80% {
    opacity: 1;
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }

  100% {
    opacity: 0;
    -moz-transform: translateX(8px);
    -ms-transform: translateX(8px);
    -o-transform: translateX(8px);
    -webkit-transform: translateX(8px);
    transform: translateX(8px);
  }
}

#bleed {
  pointer-events: none;

  &.active {

    .bleed_line {
      background: #fcc;
    }
  }

  .bleed_checked {
    position: absolute;
    opacity: 0.3;
    z-index: 1002;
    background: #f00;
  }

  .bleed_line {
    position: absolute;
    opacity: 0.8;
    z-index: 1001;
    background: #eee;
    box-shadow: 0 0 2px #888;
  }

  .bleed_left {
    top: 0;
    bottom: 0;
    left: 0;
  }

  .bleed_right {
    top: 0;
    bottom: 0;
    right: 0;
  }

  .bleed_top {
    left: 0;
    right: 0;
    top: 0;
  }

  .bleed_bottom {
    left: 0;
    right: 0;
    bottom: 0;
  }

  .bleed_tip {
    font-size: 18px;
    position: absolute;
    background-color: #fec;
    padding: 13px 25px 13px 25px;
    border-radius: 5px;
    border: 1px solid #aa0;
    z-index: 1020;
    color: #666;
    animation: fadeout 6s;

    &.tip_left {
      top: 100px;
      left: 6%;
    }

    &.tip_right {
      top: 100px;
      right: 6%;
    }

    &.tip_top {
      left: 200px;
      top: 6%;
    }

    &.tip_bottom {
      left: 200px;
      bottom: 6%;
    }

    &:before {
      content: '';
      position: absolute;
      width: 8px;
      height: 8px;
      background: inherit;
      -ms-transform: scaleX(1) rotate(45deg);
      -webkit-transform: scaleX(1) rotate(45deg);
      -moz-transform: scaleX(1) rotate(45deg);
      -o-transform: scaleX(1) rotate(45deg);
      transform: scaleX(1) rotate(45deg);
    }

    &.tip_left:before {
      left: -5px;
      border-left: 1px solid #aa0;
      border-bottom: 1px solid #aa0;
    }

    &.tip_right:before {
      right: -5px;
      border-right: 1px solid #aa0;
      border-top: 1px solid #aa0;
    }

    &.tip_top:before {
      left: 20px;
      top: -5px;
      border-left: 1px solid #aa0;
      border-top: 1px solid #aa0;
    }

    &.tip_bottom:before {
      left: 20px;
      bottom: -5px;
      border-right: 1px solid #aa0;
      border-bottom: 1px solid #aa0;
    }
  }
}
</style>



