<template>
  <div id="drag_size_tip" v-if="$store.state.dragResize.type" :style="{
    transform: 'translate(' + $store.state.dragResize.x + 'px,' + $store.state.dragResize.y + 'px)'
  }">
    <div v-if="$store.state.dragResize.type == 'xy'">X：{{x}}mm</div>
    <div v-if="$store.state.dragResize.type == 'xy'">Y：{{y}}mm</div>
    <div v-if="$store.state.dragResize.type == 'wh'">{{$t('dragSizeTip.width')}}：{{w}}mm</div>
    <div v-if="$store.state.dragResize.type == 'wh'">{{$t('dragSizeTip.height')}}：{{h}}mm</div>
    <div v-if="$store.state.dragResize.type == 'r'">{{r}}°</div>
  </div>
</template>
<script>
export default {
  name: 'dragCoord',
  props: {
    scale: {
      type: Number,
      default(){
        return 1
      }
    }
  },
  computed: {
    x(){
      var el, rect, box, cx, x;

      el = this.$store.state.selected.el;
      rect = el.getBoundingClientRect();

      box = this.$store.state.selected.data;
      cx = (box.x + box.width / 2 + this.ptToMm(box.border_width || 0)) * this.scale;
      x = cx - this.pxToMm(rect.width) / 2;

      return this.toFloat(x, 1);
    },
    y(){
      var el, rect, box, cy, y;

      el = this.$store.state.selected.el;
      rect = el.getBoundingClientRect();

      box = this.$store.state.selected.data;
      cy = (box.y + box.height / 2 + this.ptToMm(box.border_width || 0)) * this.scale;
      y = cy - this.pxToMm(rect.height) / 2;

      return this.toFloat(y, 1);
    },
    w(){
      var box = this.$store.state.selected.data;
      return this.toFloat(box.width, 1);
    },
    h(){
      var box = this.$store.state.selected.data;
      return this.toFloat(box.height, 1);
    },
    r(){
      var box = this.$store.state.selected.data;
      return box.rotation;
    }
  }
}
</script>
<style lang="scss">
#drag_size_tip{
  position: absolute;
  z-index: 2000;
  top: 0;
  left: 0;
  // white-space: pre-wrap;
  background: #5587ce;
  color: white;
  padding: 3px;
  font-size: 12px;
  border-radius: 4px;
  -moz-backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
</style>



